<template>
  <div class="InventoryManage">
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class="container haveHeadcontainer">
  	  <div class='FunctionPoint'>
  	  	<div class="ModuleHead"><span>功能</span></div>
  	    <router-link tag='li' to="/InventoryQuery">
        	<i class="iconfont icon-kucunchaxun"></i>
        	<span>库存查询</span>
        </router-link>
        <router-link tag='li' to="/InventoryRegister">
        	<i class="iconfont icon-indexResident"></i>
        	<span>库存登记</span>
        </router-link>
        <router-link tag='li' to="/WarehousingDetail">
        	<i class="iconfont icon-ruku"></i>
        	<span>入库明细</span>
        </router-link>
        <router-link tag='li' to="/AllOutgoingDetails">
          <i class="iconfont icon-chuku"></i>
          <span>出库明细</span>
        </router-link>
        <router-link v-if='$store.state.CurrentRole == "osp"' tag='li' :to="{path:'/ChooseGoods', query:{whoPush:'InventoryManage', orderType:'OTHER_IN',ReceiptInfo:InventoryOspInfo}}">
        	<i class="iconfont icon-qitachukudan"></i>
        	<span>其他入库</span>
        </router-link>
        <router-link v-if='$store.state.CurrentRole == "osp"' tag='li' :to="{path:'/ChooseGoods', query:{whoPush:'InventoryManage', orderType:'OTHER_OUT',DeliveryInfo:InventoryOspInfo}}">
          <i class="iconfont icon-qitarukudan"></i>
          <span>其他出库</span>
        </router-link>
        <router-link tag='li' :to="{path:'/ChooseGoods', query:{whoPush:'InventoryManage', orderType:'SALE_RETURN_IN',ReceiptInfo:InventoryOspInfo, isShowSelectStore:'ture'}}">
        	<i class="iconfont icon-tuihuo"></i>
        	<span>销售退货</span>
        </router-link>
        <router-link tag='li' :to="{path:'/ChooseGoods', query:{whoPush:'InventoryManage',orderType:'TRANSFER', DeliveryInfo:InventoryOspInfo}}">
          <i class="iconfont icon-tiaobochuku1"></i>
          <span>调拨</span>
        </router-link>
      </div>
      <div class='customerInfo'>
  	  	<div class="ModuleHead"><span>客户</span></div>
        <div><span>客户名称：</span><span>{{InventoryOspInfo.CUSTOMER_NAME }}</span></div>
  	    <div><span>客户类型：</span><span>{{InventoryOspInfo.CUSTOMER_TYPE}}</span></div>
  	    <div><span>客户代码：</span><span>{{InventoryOspInfo.CUSTOMER_CODE}}</span></div>
  	    <div><span>客户城市：</span><span>{{InventoryOspInfo.CUSTOMER_CITY_CHINESE}}</span></div>
  	  </div>
  	  <div class='customerInfo'>
  	  	<div class="ModuleHead"><span>联系人</span></div>
        <div><span>联系人名：</span><span>{{InventoryOspInfo.CONTACT_NAME}}</span></div>
  	    <div><span>联系电话：</span><span>{{InventoryOspInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i @click="callPhone(InventoryOspInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
  	    <div><span>地址详情：</span><span>{{InventoryOspInfo.CUSTOMER_ADDRESS}}</span></div>
  	  </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"库存管理",//页面标题
        InventoryOspInfo:{},//osp信息
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      this.InventoryOspInfo = this.$store.state.InventoryOspInfo;

      this.$store.state.BackBottle = {
        g_submitArr:[],
        totalNum:0,
        ConsigneeData_selected:[],//收货人信息
        DeliveryAddr_selected:[],//发货人信息
      }
      this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
      this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        this.$router.push({
          name:"Index",
        })
    	},
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .InventoryManage{
  	overflow-x: hidden;
  	.container{
  	   &>div:not(.FunctionPoint)>div{
  	  	padding-left:30/50rem;
  	  	box-sizing:border-box;
  	  	line-height: 60/50rem;
  	  }
  	  &>div{
  	  	border-bottom: 8/50rem solid #f0f0f0;
  	  	&>div.ModuleHead{
  	  	  font-size: 32/50rem;
  	  	  font-weight: 600;
  	  	  padding:20/50rem 20/50rem 0;
  	  	  box-sizing: border-box;
  	  	  span{
  	  	  	border-left: 5/50rem solid @PublicColor;
  	  	  	padding-left: 20/50rem;
  	  	  }
  	  	}
  	  }
  	}
  	&>div>.FunctionPoint{
  	  overflow:hidden;font-size: 28/50rem;
  	  background-color: #fff;
  	  &>div.ModuleHead{
  	    padding-bottom: 20/50rem;
  	  }
  	  &>li{
  	  	float: left;
  	  	width: 250/50rem;
  	  	text-align: center;
  	  	overflow:hidden;
  	  	margin-bottom: 40/50rem;
  	  	i{
  	  	  height: 60/50rem;
  	  	  font-size: 50/50rem;
  	  	  display: block;
  	  	  margin-bottom: 10/50rem;
  	  	  color: @PublicColor;
  	  	}
  	  	i.icon-tuihuo{
  	  	  font-size: 42/50rem;
  	  	}
  	  }
  	}
    .customerInfo>div{
      display: flex;
      font-size: 28/50rem;
      span:first-child{
        min-width: 130/50rem;
        flex-shrink:0;
      }
      span:nth-child(2){
        flex-grow: 1;
      }
    }
  }
</style>


