<template>
  <div class="CheckInventory">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="ScreeningNav">
        <div>
          <div class="StateSelect" @click='SelectBoxShowBrand'>
            <span>品牌：{{GoodsBrand}}</span>
            <i class="iconfont icon-xiangxia"></i>
          </div>
          <div class="StateSelect" @click='SelectBoxShowType'>
            <span>类型：{{goodsType}}</span>
            <i class="iconfont icon-xiangxia"></i>
          </div>
        </div>
        <div class="CustomerSearch">
          <div>
            <input v-model='searchValue'  placeholder="按商品名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
        </div>
      </div>
  	  <div class="goodsList">
        <mt-loadmore   topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
    	  	<div class="goodsListItem" v-for='(dataItem,index) in GoodsListData'  :id="index"  :key='index'>
    	      <div class="inv_goodsInfo">
              <div class="inventoryLeft">
                <div style="font-weight: 900;">{{dataItem.PRODUCT_NAME}}</div>
              </div>
            </div>
            <div class="InventoryQty">
              <div class="_inventoryQty">
                <span>库存数量：</span>
                <input :id='index' type="number" pattern="[0-9]*" v-model='dataItem.value' @blur='AddCheckedList(index,"number")'>
                <span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
              </div>
              <div class="_inventoryDate">
                <span>生产日期：</span>
                <el-date-picker @change='AddCheckedList(index,"Date")' :editable='false' clear-icon='' v-model="dataItem.DateInProduced" type="date" placeholder="选择日期" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" :picker-options="expireTimeOption"></el-date-picker>
                <span class="DateInProduced">{{dataItem.DateInProduced}}</span>
                <i v-if='dataItem.DateInProduced' @click="clearInput_check(index)" class="iconfont icon-guanbi"></i>
              </div>
            </div>
    	  	</div>
        </mt-loadmore>
        <div class="kong"></div>
  	  </div>
      <div class="classGoodNoData" v-if='classGoodNoData'>未搜索到商品数据</div>
	  </div>
    <div class="SelectMeng" v-if="SelectBoxFlagBrand">
      <div class="SelectWarehouses" >
        <div class="SelectTitle">请选择品牌</div>
        <div class="SelectItem">
          <div v-for='(dataItem,index) in SelectBoxListBrand'  :id="index"  :key='index'  @click="SelectComitBrand(index)">{{dataItem.PRODUCT_BRAND}}</div>
        </div>
        <div class="cancel"   @click="SelectCancelBrand">取消</div>
      </div>
    </div>
    <div class="SelectMeng" v-if="SelectBoxFlagType">
      <div class="SelectWarehouses" >
        <div class="SelectTitle">请选择类型</div>
        <div class="SelectItem">
          <div v-for='(dataItem,index) in SelectBoxListType'  :id="index"  :key='index'  @click="SelectComitType(index)">{{dataItem.PRODUCT_TYPE}}</div>
        </div>
        <div class="cancel"   @click="SelectCancelType">取消</div>
      </div>
    </div>
    <!--购物车  -->
    <div class='foot_cart'>
      <div class='foot_top'   @click="animat_cartShowIf" >
        <div class='totalNum'>
          <i class="iconfont icon-shoppingCart-full"></i><span>已检查库存</span>
        </div>
        <div class='submit' @click='toSuggestOrder' >确认</div>
      </div>
      <div class='foot_bottom'>
        <div class="OutermostGray"  @click="animat_cartShowIf"></div>
        <div class="footBo_move">
          <!-- <div class="footBottom_head">已检查库存：</div> -->
          <div class="footBottom_fixedBox">
            <div class='cartGoodsList' v-for='(CheckedItem,Checkedidx) in CheckedListData'  :id="Checkedidx"  :key='Checkedidx' >
              <div class='cartListLeft_'>
                <div>{{CheckedItem.PRODUCT_NAME}}</div>
                <div><span>生产日期：</span>{{CheckedItem.DateInProduced}}</div>
              </div>
              <div class="cartListCenter_">{{CheckedItem.value}}{{CheckedItem.UNIT_OF_MEASURE_STR}}</div>
              <div class="cartListRight_">
                <i @click='removeGoods(Checkedidx)' class="iconfont icon-Untitled-"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"检查库存",//页面标题
      	GoodsBrand:'',//品牌
      	goodsType:'',//类型
        BrandIdx:0,//选择第几个品牌
        TypeIdx:0,//选择第几个分类
        searchValue:'',//搜索的值
        SelectBoxFlagBrand:false,//弹出选择框是否显示 品牌
      	SelectBoxFlagType:false,//弹出选择框是否显示 类型
        SelectBoxListBrand:[],//弹出框选择列表 品牌
        SelectBoxListType:[],//弹出框选择列表 类型
      	GoodsListData:[],//商品列表
        CheckedListData:[],//已检查库存列表
        remark:'',//库存备注
        classGoodNoData:false,//分类没有查询到商品数据时
        numberPerPage: 15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        expireTimeOption: {
          disabledDate(date) {
              return date.getTime() >= Date.now();
          }
        },//生产日期不可大于当前日期
      }
    },
    components:{
	    componentHeader,
	    componentSelectBox
	  },
    created(){

      var that = this;

      var classData = {
        "businessId":that.$store.state.businessInfo.businessId,//业务id
        "appUsersId": that.$store.state.userInfo.user_id,//用户id
      }
      console.log(classData)
      // 查询货品品牌和分类
      that.httpRequest("searchProductType.do",classData,function(classRes){
         console.log(classRes)

         that.SelectBoxListBrand = classRes.data;
         if(that.SelectBoxListBrand.length){
            that.BrandIdx = 0;
            that.GoodsBrand = that.SelectBoxListBrand[0].PRODUCT_BRAND;

            if(that.SelectBoxListBrand[0].TYPE[0]){
              that.SelectBoxListType = that.SelectBoxListBrand[0].TYPE;
              that.goodsType = that.SelectBoxListBrand[0].TYPE[0].PRODUCT_TYPE;
              that.TypeIdx = 0;
            }
         }

         that.reqGoodList();
      })
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqGoodList();
      }
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      	if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush =='CreateNewVisit'){
          this.$router.push({
            name:"CustomerVisit",
          })
        }else{
          this.$router.go(-1);
        }
      },
      // 切换品牌和类型时 查询商品列表
      reqGoodList(load){

        var that = this;

        var ProductData = {
          "productName":that.searchValue,//货品名称
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "productBrand":that.SelectBoxListBrand[that.BrandIdx].PRODUCT_BRAND_ID,//货品类型
          "productType":that.SelectBoxListBrand[that.BrandIdx].TYPE[that.TypeIdx].PRODUCT_TYPE_ID,//货品分类
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//每页显示多少行数据
          'consignee_id':'',//收货人id
          "userType":that.$store.state.G_AppOrWX,//判断是app还是公众号
        }
        console.log(ProductData)
        // 查询商品列表
        that.httpRequest("searchProductList.do",ProductData,function(ProductRes){
          console.log(ProductRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(ProductRes.data.length){
              that.GoodsListData = that.GoodsListData.concat(ProductRes.data);
            }
          
          } else {
            that.GoodsListData = ProductRes.data;
          }

          // 判断是否能上拉加载
          if (ProductRes.data.length < that.numberPerPage) {
       
            that.allLoaded = true;
          } else {
              
            that.allLoaded = false;
          }

          if(!that.GoodsListData.length){
            that.classGoodNoData = true;
          }else{
            that.classGoodNoData = false;
          }

          // 如果已检查库存有值  赋值到页面输入框
          if(that.CheckedListData.length && that.GoodsListData.length){
            for(var i = 0;i<that.CheckedListData.length;i++){
              for(var j = 0;j<that.GoodsListData.length;j++){
                if(that.CheckedListData[i].PRODUCT_ID == that.GoodsListData[j].PRODUCT_ID){
                  that.GoodsListData[j].value = that.CheckedListData[i].value;
                  that.GoodsListData[j].DateInProduced = that.CheckedListData[i].DateInProduced;
                  break;
                }
              }
            }
          }
        })
      },
      // 失去焦点  添加已检查库存
      AddCheckedList(idx,type){
        var that = this,addFlag = true;

        for(var i = 0;i<that.CheckedListData.length;i++){
          if(that.CheckedListData[i].PRODUCT_ID == that.GoodsListData[idx].PRODUCT_ID){

            if(type == 'number'){
              if(parseInt(that.GoodsListData[idx].value).toString() == "NaN" || parseInt(that.GoodsListData[idx].value)<=0){

                that.CheckedListData.splice(i,1);
              }else{
                that.CheckedListData[i].value = that.GoodsListData[idx].value;
              }
            }else if(type == 'Date'){
              that.CheckedListData[i].DateInProduced = that.GoodsListData[idx].DateInProduced;
            }
            
            addFlag = false;
          }
        }

        if(addFlag){

          if(type == 'number'){

            if(parseInt(that.GoodsListData[idx].value).toString() == "NaN" || parseInt(that.GoodsListData[idx].value)<=0){
              that.GoodsListData[idx].value = '';
              return;
            }
          }
          
          that.CheckedListData.unshift(that.GoodsListData[idx])
        }
      },
      // 移除已检查库存中的商品
      removeGoods(idx){
        var that = this;

        for(var mh = 0;mh < that.GoodsListData.length; mh++){

          if(that.GoodsListData[mh].PRODUCT_ID == that.CheckedListData[idx].PRODUCT_ID){
            that.GoodsListData[mh].value = '';
            that.GoodsListData[mh].DateInProduced = '';
          }
        }
        this.CheckedListData.splice(idx,1);
        this.$nextTick(function() {
          var footBottom_height= $(".footBo_move").css("height");

        $(".footBo_move").animate({"marginTop": "-" + footBottom_height},300);
        })
      },
      // 选择弹出框出现  品牌
      SelectBoxShowBrand(){
      	this.SelectBoxFlagBrand=true;
      },
      // 选择弹出框出现  类型
      SelectBoxShowType(){
        this.SelectBoxFlagType=true;
      },
      // 选中 品牌
      SelectComitBrand(idx){
        this.GoodsBrand = this.SelectBoxListBrand[idx].PRODUCT_BRAND;
        this.BrandIdx = idx;
        this.SelectBoxListType = this.SelectBoxListBrand[idx].TYPE;

        if(this.SelectBoxListBrand[idx].TYPE[0]){
          this.goodsType = this.SelectBoxListBrand[idx].TYPE[0].PRODUCT_TYPE;
          this.TypeIdx = 0;
        }else{
          this.goodsType = "";
        }
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqGoodList();
        this.SelectBoxFlagBrand=false;
      },
      // 选中 类型
      SelectComitType(idx){
        var that = this;
        this.goodsType = this.SelectBoxListBrand[that.BrandIdx].TYPE[idx].PRODUCT_TYPE;
        this.TypeIdx = idx;
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqGoodList();
        this.SelectBoxFlagType=false;
      },
      // 取消按钮 弹出选择框  品牌
      SelectCancelBrand(){ 
         this.SelectBoxFlagBrand = false;
      },
      // 取消按钮 弹出选择框  分类
      SelectCancelType(){
         this.SelectBoxFlagType = false;
      },
      // 跳转到 建议订单 页面
      toSuggestOrder(){
        event.stopPropagation();
        var that = this,strProduct = [];

        this.$store.state.BackBottle = {
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//收货人信息
          DeliveryAddr_selected:[],//发货人信息
        }
        this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据

        for(var i = 0;i<that.CheckedListData.length;i++){

          if(that.CheckedListData[i].value && !that.CheckedListData[i].DateInProduced){

            that.$alert("库存数量有值时，生产日期不能为空", '提示', {
              confirmButtonText: '确定',
            })
            return;
          }
          if(!that.CheckedListData[i].value && that.CheckedListData[i].DateInProduced){

            that.$alert("生产日期有值时，库存数量不能为空", '提示', {
              confirmButtonText: '确定',
            })
            return;
          }
          if(that.CheckedListData[i].value){
            strProduct[strProduct.length]={
              "productId":that.CheckedListData[i].PRODUCT_ID,
              "productNo":that.CheckedListData[i].PRODUCT_CODE,
              "productName":that.CheckedListData[i].PRODUCT_NAME,
              "inventory":that.CheckedListData[i].value,
              "productionTime":that.CheckedListData[i].DateInProduced,
              "unit":that.CheckedListData[i].UNIT_OF_MEASURE_STR
            }
          }
        }
        var ProductData = {
          "visitId":that.$store.state.CurrentVisitId,//拜访id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "cellphone":that.$store.state.userInfo.cellphone,//手机号码
          "userId":that.$store.state.userInfo.user_id,//用户id
          "strProduct":strProduct,//货品
          "strCheckInventory":that.remark,//库存备注
        }
        console.log(ProductData)

        // 检查库存确认
        that.httpRequest("GetVisitCheckInventory.do",ProductData,function(ProductRes){
           console.log(ProductRes)

          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            // that.$router.push({
            //   name:"LeaveStore",
            //   query:{
            //     pageName:that.$route.query.pageName?that.$route.query.pageName:'',
            //   }
            // })
            that.$router.go(-1);
          }else{

            that.$router.push({
              name:"ChooseGoods",
              query:{ 
                CurrentStoresId:that.$store.state.CurrentStoresId,//门店id
                visitId:that.$store.state.CurrentVisitId,//拜访id
                whoPush:"CheckInventory",
                orderType:"SALE_OUT",
              }
            })
          }
        })
      },
      //购物车是否出现
      animat_cartShowIf(){

        let that = this;

        if($(".footBo_move").is(":animated")){
          return;
        }

        let footBottom_height= $(".footBo_move").css("height");

        // 如果购物车是呈现状态
        if( that.ShoppingCartDetailHide){

          $(".footBo_move").animate({"marginTop":0},500);
          $(".OutermostGray").fadeOut(500);
          that.ShoppingCartDetailHide = false;
        }else{

          $(".OutermostGray").fadeIn(500)
          that.ShoppingCartDetailHide = true;
          $(".footBo_move").animate({"marginTop": "-" + footBottom_height},500);
        } 
      },
      // 清除生产日期
      clearInput_check(index){
        this.GoodsListData[index].DateInProduced = '';
        
        var that = this;
        for(var mj = 0; mj <that.CheckedListData.length; mj++){

          if(that.GoodsListData[index].PRODUCT_ID == that.CheckedListData[mj].PRODUCT_ID){

            that.CheckedListData[mj].DateInProduced = '';
          }
        }
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.reqGoodList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqGoodList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CheckInventory{
  	overflow-x: hidden;
    height: 100%;
    padding:0 16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
  	.ScreeningNav{
      z-index: 50;
      border-top:none;
      .CustomerSearch{
        background-color:#fff;
        .TopSearchBox;
      }
  	}
    .container{
      height: 100%;
      margin-top: 0;
    }
  	.goodsList{
      height: 100%;
      overflow: scroll;
      padding-top: 273/50rem;
      box-sizing:border-box;
      .goodsListItem{
        padding:15/50rem 15/50rem;
        border-radius:15/50rem;
        box-sizing: border-box;
        background-color:#fff;
        margin-top: 20/50rem;
        &>div{
          font-size: 28/50rem;
          line-height: 50/50rem;
        }
        .inv_goodsInfo{
          display: flex;
          margin-bottom: 10/50rem;
          .inventoryLeft{
            flex-grow: 1;
          }
          .inventoryRight{
            width: 200/50rem;
            vertical-align: middle;
            flex-shrink:0;
            text-align: center;
            i{
              vertical-align: middle;
              color: red;
              font-size: 50/50rem;
              &:first-child{
                margin-right: 30/50rem;
              }
            }
          }
        }
        .InventoryQty{
          border-top: 1/50rem solid @PublicBorderColor;
          padding-top: 8/50rem;
          padding:20/50rem 0 10/50rem;
          &>div{
            line-height: 60/50rem;
          }
          ._inventoryQty{
            overflow: hidden;
            line-height: 60/50rem;
            span{
              float: left;
            }
            input{
              height: 60/50rem;
              width: 400/50rem;
              float: left;
              margin-right: 20/50rem;
            }
          }
          ._inventoryDate{
            line-height: 60/50rem;
            overflow: hidden;
            &>span{
              float: left;
            }
            &>div{
              float: left;
            }
            &>input{
              width: 100/50rem;
            }
            .DateInProduced{
              margin-right: 20/50rem;
            }
          }
        }
      }
      .classGoodNoData{
        padding: 20/50rem;
      }
  	}
    .CheckedInventory{
      .Checked_head{
        text-align: center;
        background-color:#f0f0f0;
        line-height: 60/50rem;
      }
      .CheckedList{
        max-height: 325/50rem;
        border-bottom:2/50rem solid @PublicBorderColor;
        overflow: scroll;
        &>div{
          line-height: 80/50rem;
          border-bottom:2/50rem solid @PublicBorderColor;
          display: flex;
          span{
            flex-grow: 1;
            padding-left:20/50rem;
          }
          i{
            flex-shrink:0;//宽度不会改变了
            width: 120/50rem;
            font-size: 50/50rem;
            color: red;
            text-align: center;
            font-weight: 900;
          }
        }
      }
    }
    .foot_cart{
      .foot_top{
        height: 80/50rem;
        .totalNum{
          overflow: hidden;
          line-height: 80/50rem;
          .icon-shoppingCart-full{
            font-size: 50/50rem;
            float: left;
            margin:0 20/50rem;
          }
          span{
            font-size: 28/50rem;
            float: left;
          }
        }
        .submit{
          line-height: 80/50rem;
        }
      }
      .foot_bottom{
        .footBo_move{
          transform:translate(0,-79/50rem);
          .footBottom_fixedBox{
            max-height: 500/50rem;
            .cartGoodsList{
              display: flex;
              padding:20/50rem 0;
              .cartListLeft_{
                flex-grow: 1;
                &>div{
                  line-height: 50/50rem;
                  &:first-child{
                    font-weight: 600;
                  }
                }
              }
              .cartListCenter_{
                text-align: center;
                min-width:130/50rem;
                flex-shrink:0;
                font-size: 32/50rem;
                line-height: 100/50rem;
              }
              .cartListRight_{
                text-align: center;
                min-width:130/50rem;
                line-height: 100/50rem;
                flex-shrink:0;
                i{
                  color: red;
                  font-weight: 900;
                  font-size: 50/50rem;
                }
              }
            }
          }
        }
      }
    }
  }
</style>


