<template>
  <div class="CheckDeliverPicture">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container">
		<div  v-for='(dataItem,index) in urlParts' :id="index"  :key='index'>
		  <div class="PictureContainer">
    		<img  class="picture" align="middle" :src=dataItem.Product_Url preview='2'>
		  </div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
	name:"CheckDeliverPicture",
	data(){
	  return{
		headerText:'查看交付照片' + this.$route.query.OMS_NO,
		urlParts:[],// 照片后缀
	  }
	},
	components:{
      componentHeader,
    },
	created(){

	  var that = this;

	  var postData = {
		strOrderIdx: this.$route.query.orderID,//配载单id
	  };

	  that.$emit('isLoading', true,"拼命加载中");

	  this.httpRequest("GetOrderPicture.do",postData,function(res){

		that.$emit('isLoading_',false);

		that.urlParts = res.data;

		that.$previewRefresh()
	  })
    },        
    methods:{
	  //返回上一页
	  f_Fanhui(){
  		this.$router.go(-1);
	  },
	}
  }
</script>
<style lang="less" scoped>
  .CheckDeliverPicture{
	.container{
	  margin-top: 120/50rem;
	  .PictureContainer{
		color: #FF3030;
		margin-top: 20/50rem;
		height: 360/50rem;
		text-align: center;
	  }
	  .picture{
		height: 100%;
	  }
	  margin-bottom: 20/50rem;
	}
  }
</style>


