<template>
  <div class="CustomerAllInventoryReg">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="CustomerInfo">
	  	<div>
	  	  <div>{{CustomerInfo.CUSTOMER_NAME}}</div>
	  	  <div><span>联 &nbsp;系 &nbsp;人：</span><span>{{CustomerInfo.CONTACT_NAME}} &nbsp; {{CustomerInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='CustomerInfo.CONTACT_TEL' @click="callPhone(CustomerInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
	  	  <div><span>详细地址：</span><span>{{CustomerInfo.CUSTOMER_ADDRESS}}</span></div>
	  	</div>
	  </div>
	  <div class="inventoryList">
	  	<div  class='inventoryListItem' @click='inventoryDetail(index)' v-for='(itemName,index) in listData'  :id="index"  :key='index'>
		    <div><span>客户名称：</span><span>{{itemName.NAME}}</span></div>
		  	<div><span>客户代码：</span><span>{{itemName.CODE}}</span></div>
		  	<div><span>库存月份：</span><span>{{itemName.INVENTORY_TIME}}</span></div>
		  	<div><span>填表日期：</span><span>{{itemName.ADDDATE}}</span></div>
	  	</div>
	  </div>
	  <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"客户所有库存登记",//页面标题
      	noDataShow:false,//没有数据图标显示
      	CustomerInfo:{},//客户信息
      	listData:[],//页面数据
      }
    },
    components:{
	  componentHeader,
	},
    created(){

    	var that = this;

    	this.CustomerInfo = this.$store.state.InventoryRegALL_CustomerInfo;

    	if(!this.CustomerInfo.CUSTOMER_ID){

    		that.$alert('这条记录没有id，请重新请求', '提示', {
	          confirmButtonText: '确定',
	        })
    		return;
    	}

    	var histData = {
          "strCustomerName":'',//搜索
          "strRegStartTime":'',//开始时间
          "strRegEndTime":'',//结束时间
          "currentPage":1,//
          "numberPerPage":15,//
          "types":'store',//类型：门店
          'businessId':that.$store.state.businessInfo.businessId,//业务ID
          'ospId':that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "orgnizationId":that.CustomerInfo.CUSTOMER_ID,//门店ID
          "userId":that.$store.state.userInfo.user_id,//用户id
          "cellphone":that.$store.state.userInfo.cellphone,//用户手机号
        }

        console.log(histData)
        // 获取库存登记列表
        that.httpRequest("GetRegisterList.do",histData,function(histRes){
          console.log(histRes)
          
          if(histRes.data.length){
            that.noDataShow = false;
            
          }else{
            // 没有数据显示
            that.noDataShow = true;
          }

          that.listData = histRes.data;
        })
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);

      	this.$store.state.InventoryRegALL_CustomerInfo = {};
      },
      // 跳转到详情页面
      inventoryDetail (idx){

        var that = this;
        this.$router.push({
          name:"InventoryRegDetail",
          query:{
            inventoryRegisterId : that.listData[idx].INVENTORY_REGISTER_ID,
            ObjectType:that.ObjectType
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .CustomerAllInventoryReg{
    overflow-x: hidden;
    .container{
      font-size: 28/50rem;
      .CustomerInfo{
        background-color:#fff;
        padding: 20/50rem;
        &>div{
          background-color: #f0f0f0;
          border-radius: 8/50rem;
          overflow: hidden;
          &>div{
            display: flex;
            line-height: 50/50rem;
            padding-left: 20/50rem;
            &:first-child{
              font-weight: 700;
              font-size: 32/50rem;
              margin:15/50rem 0;
            }
            span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
      }
      .inventoryList{
        border-top: 5/50rem solid #EFEFF2;
      }
      .inventoryListItem{
        width: 100%;
        padding: 10/50rem 25/50rem;
        box-sizing: border-box;
        border-bottom: 5/50rem solid #EFEFF2;
        &>div{
          display: flex;
          span:first-child{
            min-width: 130/50rem;
            flex-shrink:0;
          }
          span:nth-child(2){
            flex-grow: 1;
          }
        }
      }
      .inventoryListItem div{
        line-height: 60/50rem;  
      }
    }
  }
</style>


