<template>
  <div class="CreateNewCustomer">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="SupplierInfo">
        <div>供货商信息</div>
        <div>
          <span>供货商名称：</span>
          <span>{{SupplierName}}</span>
        </div>
      </div>
      <div class="CustomerInfo">
        <div>门店信息</div>
        <div>
          <span>门店名称：</span>
          <input placeholder="必填" v-model='CustomerName'></input>
        </div>
        <!-- <div>
          <span>门店代码：</span>
          <span>{{CustomerCode}}</span>
        </div> -->
        <div class="channel" @click='selectBoxShow()'>
          <span>渠道：</span>
          <span>{{Channel}}</span>
          <i class="iconfont icon-xiangyou"></i>
        </div>
        <div class="VisitingRoutes" @click='selectBoxShowRoutes()'>
          <span>拜访路线：</span>
          <span>{{VisitRouteName}}</span>
          <i class="iconfont icon-xiangyou"></i>
        </div>
        <div class="CustomerRemark">
          <span>备注：</span>
          <input v-model='CustomerRemark'></input>
        </div>
      </div>
      <div class="AddressInfo">
        <div>地址信息</div>
        <div>
           <span>收货人：</span>
           <input placeholder="必填" v-model='Consignee'></input>
        </div>
        <div>
           <span>联系方式：</span>
          <input placeholder="必填" v-model='ContactTel'></input>
        </div>
        <div class="lianxiren">
          <div>
            <i class="iconfont icon-lianxiren" @click='toChooseContact'></i>
            <span>选联系人</span>
          </div>
        </div>
        <div class="ChooseAddress" @click='toChooseAddress' >
          <span>所在地区：</span>
          <span>{{Location}}</span>
          <span><i class="iconfont icon-xiangyou"></i></span>
        </div>
        <div>
           <span>详细地址：</span>
           <input placeholder="必填" v-model='AddressDetail'></input>
        </div>
        <div  class="lnglat"  @click='toLnglat'>
          <span>经纬坐标：</span>
          <span>{{ lnglat }}</span>
          <span><i class="iconfont icon-xiangyou"></i></span>
        </div>
      </div>

      <div v-if="priceStrategys.length > 0" class="StrategyInfo">
        <div>价格策略</div>
        <div  class="priceStrategys"  v-for='(dataItem,index) in priceStrategys'  :id="index"  :key='index' @click='price_strategy_click(index)'>
         <i class="iconfont icon-weixuanzhong"></i><span>&nbsp;&nbsp;{{dataItem.POLICY_NAME}}</span>
        </div>
      </div>

      <div class="uploadImg">           
        <div class="LoadImg_head">请拍摄门店照片</div>
        <div class="Img">
          <el-upload  :action= "HOST + 'uploadStorePicture.do'"  :limit="1" :multiple="true" :data="uploadParam"    ref='upload_ref' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"      
          :on-error= "uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>

      <div class="StrategyInfo">
        <div>门店策略</div>
        <div  class="StrategyList"  v-for='(dataItem,index) in StrategyList'  :id="index"  :key='index' @click='fds(index)'>
         <i class="iconfont icon-1"></i><span>&nbsp;&nbsp;{{dataItem.STRATEGY_NAME}}</span>
        </div>
      </div>
      

      <div class="BtnComponent bottomBtn">
        <div @click='CustomerSave'>保存</div>
      </div>
      <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>
    <componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="cruuentSeleText" @selectCancel="selectCancel"></componentSelectBox>
    <div class="SelectMeng" v-if='RoutLineFlag'>
      <div class="SelectWarehouses SelectMengContent">
        <div class="SelectTitle">请选择{{cruuentSeleText}}</div>
        <div class="SelectItem">
          <div class="RoutLineInfo"  v-for='(dataItem,idx) in VisitLineList'  :id="idx"  :key='idx'>
            <span>{{dataItem.name}}</span>
            <input  class="border_w" type="checkbox" :value="idx" :checked="dataItem.checked"></input>
          </div>
        </div>
        <div class="selectBtn">
          <div @click="RoutLineConfirm">确定</div>
          <div @click="RoutLineCancel">取消</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
    data(){
      return{
      	headerText:"新建门店",//页面标题
        cruuentSeleText:"",//当前显示的弹出框名字
        SelectBoxList:[],//弹出框选择数据列表
        SelectBoxFlag:false,//渠道弹出选择框是否显示
        RoutLineFlag:false,//路线弹出选择框是否显示
        supplierList:[],//供货商列表
        SupplierName:'',//供货商名称
        suppliersId:'',//当前供应商id
        CustomerName:'',//客户名称
        // CustomerCode:'',//客户代码
        ChannelList:[],//渠道列表
        Channel:'',//渠道名称
        VisitLineList:[],//拜访线路列表
        VisitRouteName:'',//当前拜访线路名称
        VisitRouteNameInsert:'',//新增的拜访路线参数
        VisitRouteId:"",//当前拜访线路id
        CustomerRemark:'',//备注
        Consignee:'',//收货人
        ContactTel:'',//联系方式
        Location:'',//所在地区
        AddressDetail:'',//详细地址
        longitude:'',//经度
        latitude:'',//纬度
        lnglat:'',//显示经纬度
        province:'',//省
        cityChinese:'',//市
        districict:'',//区
        street:'',//街道
        ifTips:false,//提示信息是否显示
        tips_Msg:"",//提示信息
        selectedLine:[],
        selectedValue:[],
        StrategyList:[],//门店策略列表
        SumStrategyList:[],//选中的策略列表
        suppliersId:[],
        priceStrategys:[],//价格策略，像福侨有些门店【同一产品】不同门店享有【不同的价格】
        uploadParam:{},//上传图片时 额外的参数
        uploadFailNum:0,//上传失败个数
        uploadSuccessNum:0,//上传成功个数
        uploadTotalNum:0,//上传图片总数
        dialogImageUrl: '',//图片路径
        dialogVisible: false,//是否放大预览图片
        storeId:'',//门店ID
      } 
    },
    components:{
	    componentHeader,
      componentSelectBox
	  },
    mounted(){
      window.SetContactPeople = this.SetContactPeople;
      window.SetSendLocation = this.SetSendLocation;
    },
    created(){

      var that = this;
      that.SupplierName = that.$store.state.CustomerInfoOspInfo.name; //供应商名称
      that.CustomerName = that.$store.state.StoreInfo.CustomerName;//客户名称
      if(that.$store.state.StoreInfo.ChannelList){
        that.Channel = that.$store.state.StoreInfo.ChannelList.name;//渠道代码
      }
      that.VisitRouteId = that.$store.state.StoreInfo.VisitRouteId;//路线id
      that.VisitRouteName = that.$store.state.StoreInfo.VisitRouteName;//路线名称
      that.CustomerRemark = that.$store.state.StoreInfo.CustomerRemark;//备注
      that.Consignee = that.$store.state.StoreInfo.Consignee;//门店联系人
      that.ContactTel = that.$store.state.StoreInfo.ContactTel;//门店电话
      that.Location = that.$store.state.StoreInfo.Location;//所在地区
      that.AddressDetail = that.$store.state.StoreInfo.AddressDetail;//详细地址
      that.longitude = that.$store.state.StoreInfo.longitude;//经度
      that.latitude = that.$store.state.StoreInfo.latitude;//纬度
      that.lnglat = that.$store.state.StoreInfo.lnglat;//显示经纬度

      // var CodeData = {
      //   "businessId":that.$store.state.businessInfo.businessId,//业务id
      //   "businessCode":that.$store.state.businessInfo.businessCode,//业务代码
      // }
      // // 获取客户代码
      // that.httpRequest("GetStoresCode.do",CodeData,function(CodeRes){
         
      //   if(CodeRes.data.length){
      //     that.CustomerCode = CodeRes.data;
      //     that.$store.state.StoreInfo.CustomerCode = that.CustomerCode;
      //   }else{
      //     that.$alert('未获取到客户代码，请联系管理员', '提示', {
      //       confirmButtonText: '确定',
      //     })
      //   }
      // })
      if(that.$store.state.userInfo.business == 5){

        that.suppliersId = that.$store.state.CustomerInfoOspInfo.suppliersId
      }else{

        that.suppliersId = that.$store.state.CustomerInfoOspInfo.CUSTOMER_ID
      }

      var LineData = {
        "suppliersId":that.suppliersId,//供货商id
      }
      // 获取拜访线路
      that.httpRequest("GetVisitLine.do",LineData,function(LineRes){
         
       if(LineRes.data.length){
        that.VisitLineList = LineRes.data;
        for(var i = 0; i < that.VisitLineList.length; i++){
          if(LineRes.data[i].name == "全部"){
            that.VisitLineList.splice(i, 1)
          }
        }

       }else{
        that.$alert('未获取到拜访线路，请联系管理员', '提示', {
          confirmButtonText: '确定',
        })
       }
      })

      // 获取渠道
      that.httpRequest("findChannel.do", "",function(ChannelRes){
         
       if(ChannelRes.data.length){
        that.ChannelList = ChannelRes.data;
       }else{
        that.$alert('未获取到渠道，请联系管理员', '提示', {
          confirmButtonText: '确定',
        })
       }
      })

       var StrategyData = {
        "suppliersId":that.suppliersId,//供货商id
      }
      // 获取渠道
      that.httpRequest("GetStrategyByOSP.do",StrategyData,function(StrategyRes){
         
        if(StrategyRes.data.length){
          that.StrategyList = StrategyRes.data;
        }
      })
      that.getVisitLineInfo();

      var postData = {
        "businessId":that.$store.state.businessInfo.businessId,//业务id
      }
      // 获取价格策略
      that.httpRequest("GetStrategyPriceByCustomer.do",postData,function(res){
         
        if(res.data.length){
          that.priceStrategys = res.data
        }
      })
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
       this.$store.state.StoreInfo = {};
         this.$router.push({
          name:"CustomerList",
        })
      },
      price_strategy_click(index){

        let iconElement = $('.priceStrategys:eq('+index+')>i.iconfont')

        var selected = false
        if(iconElement.hasClass("icon-1")){
         selected = true
        }
        
        // 清空
        for(var i = 0 ; i < this.priceStrategys.length;i++){
          if($(".priceStrategys").eq(i).children(".iconfont").hasClass("icon-1")){
            var item = $('.priceStrategys:eq(' + i + ')>i.iconfont')
            item.removeClass("icon-1").addClass("icon-weixuanzhong")
          }
        }
        // 用户取消选择，不赋值
        if(selected){
          return
        }
        // 赋值
        if(iconElement.hasClass("icon-weixuanzhong")){
          iconElement.removeClass("icon-weixuanzhong").addClass("icon-1")
        }else if(iconElement.hasClass("icon-1")){
          iconElement.removeClass("icon-1").addClass("icon-weixuanzhong")
        }
        return;
      },
      fds(index){
        let iconElement = $('.StrategyList:eq('+index+')>i.iconfont');

        if(iconElement.hasClass("icon-1")){

          iconElement.removeClass("icon-1").addClass("icon-weixuanzhong")

        }else if(iconElement.hasClass("icon-weixuanzhong")){

          iconElement.removeClass("icon-weixuanzhong").addClass("icon-1")
        }
        return;

      },
      toLnglat(){
        this.saveInfo();
        if(this.$store.state.Device == "iOS"){
          // 苹果
          try { CallAndroidOrIOS("调用发送位置", ""); }
          catch(error) { }
        }else{
          this.$router.push({
            name:"SendLnglat",
            query:{
              whoPush:"CreateNewCustomer",
            }
          })
        }
      },
       //保存用户输入，防止push后信息丢失
      saveInfo(){
        var that = this;
        that.$store.state.StoreInfo.CustomerName = that.CustomerName;//门店名称
        that.$store.state.StoreInfo.Consignee = that.Consignee;//收货人
        that.$store.state.StoreInfo.ContactTel = that.ContactTel;//门店电话
        that.$store.state.StoreInfo.CustomerRemark = that.CustomerRemark;//备注
        that.$store.state.StoreInfo.Location = that.Location;//所在地区
        that.$store.state.StoreInfo.AddressDetail = that.AddressDetail;//详细地址
        that.$store.state.StoreInfo.longitude = that.longitude;//经度
        that.$store.state.StoreInfo.latitude = that.latitude;//纬度
        that.$store.state.StoreInfo.lnglat = that.lnglat;//显示经纬度
      },
      SetSendLocation(address, lng, lat){
        this.lnglat = address
        this.longitude = lng
        this.latitude = lat
      },
      SetContactPeople(name, tel){
        this.Consignee = name
        this.ContactTel = tel
      },
      toChooseContact(){
        // 安卓
        try { CallAndroidOrIOS.callAndroid("调用通讯录", ""); } 
        catch(error) { }
        // 苹果
        try { CallAndroidOrIOS("调用通讯录", ""); }
        catch(error) { }
      },
      // 排序，升序
      sortNumbers(array){
        return array.sort(
          function LM(a, b){
            return a - b;
        });
      },
      // 根据key，数组降序
       sortByKeyDesc(array, key){

        return array.sort(function(a,b){
            var x = a[key];
            var y = b[key];
            return((x<y)?-1:((x>y)?1:0));
        })
      },
      // 选择弹出框 显示
      selectBoxShow(){
        this.SelectBoxFlag=true;
        this.SelectBoxList = this.ChannelList;
        this.cruuentSeleText = '渠道';
      },
      // 弹出拜访路线选择
      selectBoxShowRoutes(){

        var that = this;
        this.selectedLine = this.$store.state.StoreInfo.VisitLineList;
        for(var m=0;m<that.VisitLineList.length;m++){
            that.VisitLineList[m].checked = '';//新增checked属性
        }
        if(that.VisitRouteName){
          for(var j = 0; j < that.selectedLine.length; j++){
           for(var k = 0; k < that.VisitLineList.length; k++){

            if(that.selectedLine[j].visitId == that.VisitLineList[k].visitId){

              that.VisitLineList[k].checked = 'checked';
            }
           }
          }
        }else{
          //拜访线路选择默认当前日期
          that.Date= new Date();
          var m = that.Date.getDay();
          var data;
          that.data = "星期" + "天一二三四五六".charAt(m);
          for(var i = 0; i < that.VisitLineList.length; i++){
            if(that.data == that.VisitLineList[i].name){
              that.VisitLineList[i].checked = 'checked';
            }
          }
        }
        that.RoutLineFlag=true;
        that.SelectBoxList = that.VisitLineList;
        that.cruuentSeleText = '拜访线路';
      },
      // 选中渠道
      selectedItem(index){
        var that = this;
        that.Channel = that.SelectBoxList[index].name;
        that.$store.state.StoreInfo.ChannelList = that.ChannelList[index];
        this.SelectBoxFlag = false;
      },
      //获取拜访id 拜访路线
      getVisitLineInfo(){
        var that = this;
        that.VisitRouteName = "";
        that.VisitRouteNameInsert = "";
        that.VisitRouteId = "";
        if(that.$store.state.StoreInfo.VisitLineList){
          that.selectedLine = that.$store.state.StoreInfo.VisitLineList;
        }
        for (var i = 0; i < that.selectedLine.length; i++) {
          that.VisitRouteName += that.selectedLine[i].name.substring(3,2)+" ";
          that.VisitRouteNameInsert += that.selectedLine[i].name+",";
          that.VisitRouteId += that.selectedLine[i].visitId+",";
        }
        //去掉字符串最后的标点
        that.VisitRouteName = that.VisitRouteName.substring(0, that.VisitRouteName.lastIndexOf(' '));
        that.VisitRouteNameInsert = that.VisitRouteNameInsert.substring(0, that.VisitRouteNameInsert.lastIndexOf(','));
        that.VisitRouteId = that.VisitRouteId.substring(0, that.VisitRouteId.lastIndexOf(','));
      },
      // 确定 拜访路线
      RoutLineConfirm(){
        var that = this;
        var checkedLine = [];
        for(var i =0;i<that.VisitLineList.length;i++){

          if($('.RoutLineInfo:eq('+i+') input.border_w').prop('checked')){
            checkedLine[checkedLine.length] = that.VisitLineList[i];
          }
        }
        that.$store.state.StoreInfo.VisitLineList = checkedLine;
        this.RoutLineFlag = false;
        this.getVisitLineInfo();
      },
      // 取消选择业务代码
      selectCancel(){
        this.SelectBoxFlag = false;
      },
      // 取消选择拜访路线
      RoutLineCancel(){
        this.selectedLine = [];
        this.RoutLineFlag = false;
      },
      // 跳转到 选择地址 页面
      toChooseAddress(){
        this.saveInfo();
        this.$router.push({
          name:"ChooseAddress",
        })
      },
       // 文件上传成功时的钩子
      uploadSuccess  (response, file, fileList){

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 图片上传成功
          console.log(response)
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 部分上传成功
          that.$alert("部分图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
        }
      },
      // 文件上传失败时的钩子
      uploadError  (err, file, fileList){
        var that = this;

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 全部上传失败

          that.uploadSuccessNum = 0;
          that.uploadFailNum = 0;
          that.uploadTotalNum = 0;
          that.$emit('isLoading', false);

          that.$alert("图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 部分上传失败
          that.$alert("部分图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
        }
      },
      // 预览放大图片
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 点击保存按钮
      CustomerSave:function(){
        var that = this;  
        var strategyIds = '';
        var policyPriceId = ""

        for(var i = 0 ; i < that.priceStrategys.length;i++){
          if($(".priceStrategys").eq(i).children(".iconfont").hasClass("icon-1")){
            policyPriceId = that.priceStrategys[i].POLICY_PRICE_ID + ",";
          }
        }

        for(var i = 0 ; i < that.StrategyList.length;i++){
          if($(".StrategyList").eq(i).children(".iconfont").hasClass("icon-1")){
            strategyIds += that.StrategyList[i].STRATEGY_DETAIL_ID + ",";
          }
        }
        if(strategyIds){
          strategyIds = strategyIds.substring(0, strategyIds.lastIndexOf(','));
        }

        if(!this.CustomerName){
          that.$alert("门店名称不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!$("div.uploadImg ul>li").length){

          that.$alert('请拍摄门店照片', '提示', {
            confirmButtonText: '确定'
          })
          return;
        }
         if(!this.Consignee){
          that.$alert("收货人不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
         if(!this.ContactTel){
          that.$alert("联系方式不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!this.AddressDetail){
          that.$alert("详细地址不能为空", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!that.Channel){
          that.$alert("请选择渠道", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }

        if(!(that.$store.state.StoreInfo.province || that.$store.state.StoreInfo.cityChinese || that.$store.state.StoreInfo.districict || that.$store.state.StoreInfo.street)){
          that.$alert("请选择所在区域", '提示', {
            confirmButtonText: '确定',
          })
          return;
        }

        if(!that.latitude){
          that.$alert("请选择经纬度", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }

        that.getVisitLineInfo();

        if(!that.VisitRouteId){
          that.$alert("请选择拜访路线", '提示', {
            confirmButtonText: '确定',
          })
          return;
        }

        var SubmitData = {
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "userId":that.$store.state.userInfo.user_id,
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "suppliersId":that.suppliersId,//供货商id
          "storesCode":"",//客户代码
          "storesName":that.CustomerName,//客户名称
          "storesAdress":that.AddressDetail,//详细地址
          "province":that.$store.state.StoreInfo.province?that.$store.state.StoreInfo.province:'',//省
          "cityChinese":that.$store.state.StoreInfo.cityChinese?that.$store.state.StoreInfo.cityChinese:'',//市
          "districict":that.$store.state.StoreInfo.districict?that.$store.state.StoreInfo.districict:'',//区
          "street":that.$store.state.StoreInfo.street?that.$store.state.StoreInfo.street:'',//街道
          "storesContactName":that.Consignee,//门店联系人
          "storesTel":that.ContactTel,//门店电话
          "storesNotes":that.CustomerRemark,//备注
          "visitRouteId":that.VisitRouteId?that.VisitRouteId:'',//路线id
          "visitRoute":that.VisitRouteNameInsert?that.VisitRouteNameInsert:'',//路线名称
          "channel":that.$store.state.StoreInfo.ChannelList.code?that.$store.state.StoreInfo.ChannelList.code:'',//渠道代码
          "longitude":that.longitude?that.longitude:'',//114.22,//经度
          "latitude":that.latitude?that.latitude:'',//22.44,//纬度
          "strategyIds":strategyIds,
          "policyPriceId":policyPriceId,//价格策略id
        }
        
        // 新增门店
        that.httpRequest("insterOrgn.do",SubmitData,function(Res){
          

          if(Res.status == 1){
            
            that.storeId =Res.data;
            
            //上传图片的参数
            that.uploadParam = {
              businessCode: that.$store.state.businessInfo.businessCode,//业务代码
              storeId: that.storeId,//生成的门店id
              storeName: that.$store.state.StoreInfo.CustomerName,//门店名称
              userId : that.$store.state.userInfo.user_id,//用户id
              cellphone : that.$store.state.userInfo.cellphone,//手机号码
            };
         
            // 上传图片
            that.$refs.upload_ref.submit();
            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){
              //下单成功后跳转到门店列表页面
              that.$router.push({
                  name:"CustomerList",//
                })
              },2000)
            that.$store.state.StoreInfo = {};
          }else{
            that.$alert('新增门店失败', '提示', {
              confirmButtonText: '确定',
            })
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateNewCustomer{
    overflow-x: hidden;
    padding-bottom:140/50rem;
    header{
      background-color:@PublicColor;
      color:#fff;
      border-bottom: none;
    }
    .SelectMeng{
      .SelectItem{
        max-height: 570/50rem;
      }
    }
    .RoutLineInfo{
      span{
        float: left;
        width: 50%;
      }
      input{
        background-color: pink;
        width: 50%;
      }
    }
    .container{
      font-size: 30/50rem;
      i.icon-xiangyou{
        float: right;
        margin-right: 15/50rem;
      }
      .SupplierInfo,.CustomerInfo,.StrategyInfo{
        &>div{
          line-height: 90/50rem;
          padding-left: 10/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &:nth-child(1){
            background-color: #E6E6E6;
            font-size: 26/50rem;
            line-height: 50/50rem;
            border-bottom:none;
          }
        }
      }
      .StrategyInfo{
        .StrategyList,.priceStrategys{
          &>i.iconfont{
            color:#1e8cf8;
            font-size: 3s0/50rem;
            &.icon-weixuanzhong{
              color: #888;
            }
          }
        }
      }
      .CustomerInfo{
        .channel{ 
          background-color: #E6E6E6;
          float: left;
          width: 48%;
          height: 92/50rem;
          margin-right: 10/50rem;
          span:first-child{float: left;}
          span:nth-child(2){
            width: 215/50rem;
            line-height: 35/50rem;
            font-size: 24/50rem;
            display: block;
            float: left;
            padding-top: 25/50rem;
          }
          i{float: right;}
        }
        .VisitingRoutes{ 
          background-color: #E6E6E6;
          width: 48%;
          height: 92/50rem;
          float: right;
          span:first-child{float: left;}
          span:nth-child(2){
            width: 150/50rem;
            line-height: 35/50rem;
            font-size: 25/50rem;
            display: block;
            float: left;
            padding-top: 25/50rem;
          }
          i{float: right;}
        } 
        .CustomerRemark{
          span{float: left;}
        }
      }
      .AddressInfo{
        overflow: hidden;
        position: relative;
        .ChooseAddress,.lnglat{
          flex-grow: 1;
          display: flex;
          span:first-child{
            float: left;
            width: 160/50rem;
          }
          span:nth-child(2){
            width: 550/50rem;
            line-height: 35/50rem;
            padding-top: 28/50rem;
            padding-bottom: 10/50rem;
          }
          span:nth-child(3){
            float: right;
          }
        }
        .coordinate{
          display:flex; 
          span:nth-child(1){
            flex:none;
          }
          span:nth-child(2){
            overflow:hidden;
            width: auto;
            line-height: 40/50rem;
            flex:1;
            padding-top: 28/50rem;
          }
          span:nth-child(3){
            width: 50/50rem;
            flex:none;
          }
        }
        &>div:not(.lianxiren){
          line-height: 90/50rem;
          padding-left: 10/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &:nth-child(1){
            border-bottom:none;
            line-height: 50/50rem;
            background-color: #E6E6E6;
            font-size: 26/50rem;
          }
          &:nth-child(2),&:nth-child(3){
            width: 75%;
          }
        }
        &>div.lianxiren{
          height: 180/50rem;
          width: 180/50rem;
          position: absolute;
          display: table;
          right: 0;
          top: 50/50rem;
          text-align: center;
          border-left:1/50rem solid @PublicBorderColor;
          border-bottom:1/50rem solid @PublicBorderColor;
          div{
            display: table-cell;
            vertical-align: middle;
          }
          i{
            font-size: 80/50rem;
            color: @PublicColor;
           }
          span{
            display: block; 
          }
        }
      }
      .uploadImg{
        padding: 20/50rem 20/50rem 20/50rem 10/50rem;
        .LoadImg_head{
          margin-bottom: 15/50rem;
          font-weight: 600;
        }
      }

      // .StrategyInfo{
      //   div:first-child{
      //       border-bottom:none;
      //       line-height: 50/50rem;
      //       background-color: #E6E6E6;
      //       font-size: 26/50rem;
      //     }

      // }
      .BtnComponent{
        width: 40%;
        left: 30%;
      }
      input{
        width:70%;
        height:50/50rem;
        border-style: none;
      }
    }
  } 
</style>


