<template>
  <div class="VisitRecord">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="CustomerName"></div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"拜访记录",//页面标题
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .VisitRecord{
    overflow-x: hidden;
  }
</style>


