<template>
  <div class="OutboundDetail">
  	<!-- 头部 -->
	  <componentHeader :headerText="$store.state.G_AppOrWX != 'WX'?'出库详情':'订单详情'" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
	  <div class='putDetails haveHeadcontainer'>
	    <div class='orderInfo'>
	      <div class='infoHead'>订单信息</div>
	      <div class='infoContainer'>
	        <div>{{$store.state.G_AppOrWX != "WX"?'出库单号':'订单编号'}}：<span>{{orderInfo.OUTNO}}</span></div>
          <div>创建时间：<span>{{orderInfo.ADDDATE}}</span></div>
          <div v-if='$store.state.G_AppOrWX != "WX"'>订单类型：<span>{{orderInfo.TYPE_text}}</span></div>
          <div>订单状态：<span>{{orderInfo.STATUS_text}}</span></div>
          <div v-if="orderInfo.REMARK">订单备注：<span>{{orderInfo.REMARK}}</span></div>
	        <div class="flexDiv">
            <div v-if='orderInfo.transferType=="PURCHASE_IN" || orderInfo.transferType=="SALE_OUT" || $store.state.G_AppOrWX == "WX"'>付款价格：<span>¥ {{orderInfo.PAY_PRICE}}</span></div>
	        	
            <div>下单数量：<span>{{orderInfo.orderQty}}</span></div>
            <div v-if='orderInfo.MJ_PRICE>0'>优惠：<span>¥{{orderInfo.MJ_PRICE}}</span></div>
	         <!--  <div>重量：<span>{{orderInfo.orderWt}}</span></div>
            <div>体积：<span>{{orderInfo.orderVol}}</span></div> -->
	        </div>

          <!-- 部分业务需要改订单数量和价格,这里记录汇总 开始 -->
          <div v-if='$store.state.businessInfo.isEdit == "T"' class="flexDiv">
            <div v-if='orderInfo.transferType=="PURCHASE_IN" || orderInfo.transferType=="SALE_OUT" || $store.state.G_AppOrWX == "WX"'>配送总价：<span>¥ {{orderInfo.TAKE_PRICE}}</span></div>

            <div>配送总量：<span>{{orderInfo.TAKE_NUM}}</span></div>
          </div>
          <!-- 部分业务需要改订单数量和价格,这里记录汇总 结束 -->

          <div v-if='orderInfo.REQUESTED_DELIVERY_DTE'>要求送货时间：<span>{{orderInfo.REQUESTED_DELIVERY_DTE?orderInfo.REQUESTED_DELIVERY_DTE.substring(0,10):orderInfo.REQUESTED_DELIVERY_DTE}}</span></div>
	      </div>
	    </div>

      <div class='orderInfo'>
        <div class='infoHead'>发货方</div>
        <div class='infoContainer customerInfo'>
          <div><div><span>客户名称：</span></div><div><span>{{orderInfo.consignorName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO">联系</span>人：</div><div><span v-if='orderInfo.consignorContact'>{{orderInfo.consignorContact}}&nbsp;&nbsp;</span><span>{{orderInfo.consignorTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consignorTelephone' @click="callPhone(orderInfo.consignorTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div><div><span>发货地址：</span></div><div><span>{{orderInfo.consignorAddress}}</span></div></div>
        </div>
      </div>

      <div class='orderInfo'>
        <div class='infoHead'>收货方</div>
        <div class='infoContainer customerInfo'>
          <div v-if='orderInfo.transferType=="TRANSFER_OUT"'><div><span class="ASD ASD_TWO">业务</span>员：</div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div v-else><div><span>门店名称：</span></div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div v-if='orderInfo.transferType=="TRANSFER_OUT"'><div><span>联系电话：</span></div><div><span>{{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-else><div><span class="ASD ASD_TWO">联系</span>人：</div><div><span>{{orderInfo.consigneeContact}}&nbsp;&nbsp;&nbsp;</span><span>{{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-if="orderInfo.consigneeAddress"><div><span>收货地址：</span></div><div><span>{{orderInfo.consigneeAddress}}</span></div></div>
        </div>
      </div>

      <!-- 选择出库对象 -->
     <!--  <div class='radio' v-if='orderInfo.transferType == "SALE_OUT" && orderInfo.STATUS == "NEW"'>
        <div class='infoHead'>请选择出库对象</div>
        <div class='infoContainer'>
          <el-radio v-model="OutboundObject" label="osp">{{orderInfo.consignorName}}</el-radio>
          <el-radio v-model="OutboundObject" label="user">{{orderInfo.createdUser}}</el-radio>
        </div>
      </div> -->
      <!-- 正常品 -->
      <componentCargoInfo :goodsArr='goodsArr' :TRANSFER_TYPE='orderInfo.transferType' titleHead='货物信息' @Comp_edit="Comp_edit"></componentCargoInfo>

      <!-- 赠品 -->
	    <componentCargoInfo :goodsArr='giftsArr' titleHead='赠品信息' @Comp_edit="Comp_edit"></componentCargoInfo>

      <div v-if='$store.state.G_AppOrWX != "WX" && orderInfo.STATUS == "FINISH"&&orderInfo.transferType == "SALE_OUT" && orderInfo.HAVE_RETURN !="T"' class="returnGoods" @click="returnOnclick">全部退货</div>
      <div v-if='$store.state.G_AppOrWX != "WX" && orderInfo.STATUS == "FINISH"&&orderInfo.transferType == "SALE_OUT"&& orderInfo.HAVE_RETURN !="T"' class="returnGoods" @click="PartReturn">部分退货</div>
      <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>

	  <!--取消/确认出库 按钮  -->
	  <div class='entryBtn' v-if='orderInfo.transferType != "TRANSFER_OUT"'>
	    <div v-if='orderInfo.STATUS == "NEW"' @click='cancelOutbound'>取消订单</div>
      <div v-if='orderInfo.STATUS == "NEW" && $store.state.G_AppOrWX != "WX" &&( $store.state.CurrentRole =="osp" ||($store.state.CurrentRole =="ospSalesman" && $store.state.userInfo.visitType == "车销")) ' @click='confirmOutbound'>确定出库</div>
      <div v-if='orderInfo.STATUS == "NEW" && $store.state.G_AppOrWX == "WX"' @click='pay'>付款</div>
      <div v-if='orderInfo.STATUS == "PAID" && $store.state.G_AppOrWX == "WX"' @click='confirmOutbound'>确认收货</div>
	  </div>

    <!-- 取消/确认调拔 按钮  -->
    <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType == "TRANSFER_OUT" && $store.state.G_AppOrWX != "WX"'>
      <div @click='cancelTransferIn'>取消调拔</div>
      <div v-if='$store.state.CurrentRole =="osp"' @click='confirmTransferOut'>确定调拔</div>
    </div>

    <!-- 恢复订单（仅把订单状态，从取消改成新建） 按钮  -->
    <div class='entryBtn' v-if='orderInfo.STATUS == "CANCEL" && $store.state.G_AppOrWX != "WX"'>
      <div @click='reStore'>恢复为新单</div>
    </div>

    <div class="selfconfirm" v-if="selfconfirm_show">
      <div class="selfconfirm_contianer">
        <div class="selfconfirm_head">提示</div>
        <div class="selfconfirm_content">{{confirmcontent}}</div>
        <div class="selfconfirm_btn">
          <div @click='selfconfirm_cancel()'>取消</div>
          <div @click='selfconfirm_confirm_()'>确认</div>
        </div>
      </div>
    </div>

  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentCargoInfo from '@/components/Widgets/componentCargoInfo'
  import $ from 'jquery'
  import html2canvas from 'html2canvas'
  export default{
  	data(){
  	  return{
  	  	TextContent:'打印',
  	  	orderInfo:{},
        goodsArr: [],//正常产品列表
        giftsArr:[],//赠品列表
        OutboundObject:'osp',//销售出库时 扣减库存对象
        ifTips:false,
        tips_Msg:"",
        selfconfirm_show:false,//自建confirm是否显示提示框
        confirmcontent:'',//自建confirm提示内容
  	  }
  	},
  	components:{
	    componentHeader,
      componentCargoInfo,
	  },
  	created(){

      this.request()
  	},
  	methods:{
  	  // 返回上一页
      f_Fanhui(){

        if(this.$route.query.whoPush && (this.$route.query.whoPush == 'ChooseGoods'|| this.$route.query.whoPush == 'orderConfirm')){
          this.$router.push({
            name:'QuerySalesOrder',
          })

          return;
        }
      	this.$router.go(-1);
      },
      request(){

        var that = this;

        if(that.$route.query.transferOutId){

          var LineData = {
            "transferOutId":that.$route.query.transferOutId,//出库单id
          }
          console.log(LineData)

          // 获取出库订单详情
          that.httpRequest("searchTransferOutDetail.do",LineData,function(SupRes){
            console.log(SupRes)
            if(SupRes.data.length){

              if(SupRes.data[0].orderInfo.STATUS == 'NEW'){

                SupRes.data[0].orderInfo.STATUS_text = '未确认';

              }else if(SupRes.data[0].orderInfo.STATUS == 'FINISH' &&SupRes.data[0].orderInfo.HAVE_RETURN != 'T'){

                SupRes.data[0].orderInfo.STATUS_text = '已确认';

              }else if(SupRes.data[0].orderInfo.STATUS == 'CANCEL'){

                SupRes.data[0].orderInfo.STATUS_text = '此出库单已取消';

              }else if(SupRes.data[0].orderInfo.HAVE_RETURN == 'T'){

                SupRes.data[0].orderInfo.STATUS_text = '已退货';
              }else{
                SupRes.data[0].orderInfo.STATUS_text = SupRes.data[0].orderInfo.STATUS;
              }

              // 销售出库、其它出库
              if(SupRes.data[0].orderInfo.transferType == 'SALE_OUT'){

                SupRes.data[0].orderInfo.TYPE_text = '销售出库';
              }else if(SupRes.data[0].orderInfo.transferType == 'OTHER_OUT'){

                SupRes.data[0].orderInfo.TYPE_text = '其它出库';
              }else if(SupRes.data[0].orderInfo.transferType == "TRANSFER_OUT"){

                SupRes.data[0].orderInfo.TYPE_text = '调拨出库';
              }else if(SupRes.data[0].orderInfo.transferType == "PURCHASE_RETURN_OUT"){

                SupRes.data[0].orderInfo.TYPE_text = '采购退货出库';
              }else{
                SupRes.data[0].orderInfo.TYPE_text = SupRes.data[0].orderInfo.transferType;
              }

              that.orderInfo = SupRes.data[0].orderInfo;
              
              var goodsArr = [],giftsArr = [];

              for(var i=0;i<SupRes.data[0].detail.length;i++){

               if(SupRes.data[0].detail[i].PRODUCT_TYPE == 'GF'){

                  giftsArr[giftsArr.length]=SupRes.data[0].detail[i];
                }else{
                  goodsArr[goodsArr.length]=SupRes.data[0].detail[i];
                }
              }
              that.goodsArr = goodsArr;
              that.giftsArr = giftsArr;

              // 后台未给赋默认值导致以下代码
              if(that.orderInfo.TAKE_PRICE == 0){
                that.orderInfo.TAKE_PRICE = that.orderInfo.PAY_PRICE
              }
              if(that.orderInfo.TAKE_NUM == 0){
                that.orderInfo.TAKE_NUM = that.orderInfo.orderQty
              }
              for(var i = 0; i < that.goodsArr.length; i++){
                if(that.goodsArr[i].TAKE_PO_NUM == 0){
                  that.goodsArr[i].TAKE_PO_NUM = that.goodsArr[i].pirchaseQuantity
                }
                if(that.goodsArr[i].TAKE_PRICE == 0){
                  that.goodsArr[i].TAKE_PRICE = that.goodsArr[i].payment
                }
              }
              for(var i = 0; i < that.giftsArr.length; i++){
                if(that.giftsArr[i].TAKE_PO_NUM == 0){
                  that.giftsArr[i].TAKE_PO_NUM = that.giftsArr[i].pirchaseQuantity
                }
                if(that.giftsArr[i].TAKE_PRICE == 0){
                  that.giftsArr[i].TAKE_PRICE = that.giftsArr[i].payment
                }
              }
            }else{
              // 没有数据
              that.$alert('没有查询到数据，请联系管理员', '提示', {
                confirmButtonText: '确定',
              })
           }
          })
        }
      },
      // 修改配送数量、价格
      Comp_edit(p, type){

        var that = this

        var inputValue = 0
        if(type == "数量"){
          inputValue = p.TAKE_PO_NUM
        }else if(type == "价格"){
          inputValue = p.TAKE_PRICE
        }

        this.$prompt('请输入' + type, '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^\d+(\.\d+)?$/,
          inputErrorMessage: '请输入数字',
          inputValue: inputValue,
          inputType: 'number',
          closeOnClickModal: false
        }).then(({ value }) => {

          if(type == "数量"){
            p.TAKE_PO_NUM = value
          }else if(type == "价格"){
            p.TAKE_PRICE = value
          }

          var total_qty = 0
          var total_price = 0
          for(var i = 0; i < that.goodsArr.length; i++){
            total_qty += parseInt(that.goodsArr[i].TAKE_PO_NUM)
            total_price +=  that.goodsArr[i].TAKE_PRICE * that.goodsArr[i].TAKE_PO_NUM
          }
          for(var i = 0; i < that.giftsArr.length; i++){
            total_qty += parseInt(that.giftsArr[i].TAKE_PO_NUM)
            total_price +=  that.giftsArr[i].TAKE_PRICE * that.giftsArr[i].TAKE_PO_NUM
          }
          console.log(total_qty)
          console.log(total_price)
          that.orderInfo.TAKE_NUM = total_qty
          that.orderInfo.TAKE_PRICE = total_price

          that.modify_qty_price(p)

        }).catch(() => { })

        // 自动聚集
        this.$nextTick(function(){

          $(".el-input__inner").select()
        })

      },
      modify_qty_price(p){

        var that = this

        var TransferOutDetails = [{"productId":p.productId, "transferOutDetailId":p.transferOutDetailId, "takeNum":p.TAKE_PO_NUM, "takePrice":p.TAKE_PRICE}]

        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id, //用户id
          "transferOutId":that.$route.query.transferOutId, //出库单id
          "takePoNumAll":that.orderInfo.TAKE_NUM,
          "takeAmount":that.orderInfo.TAKE_PRICE,
          "TransferOutDetails":TransferOutDetails
        }
        // 修改配送数量、价格
        that.httpRequest("updTranDelivery.do",postData,function(res){

          that.ifTips = true
          that.tips_Msg = "操作成功"

          setTimeout(function(){
            that.ifTips = false
            that.request()
          },1000)
        })
      },
      downloadURI(uri, name){
        var link = document.createElement("a")
        link.download = name
        link.href = uri
        link.click()
      },
      setImage(){

        html2canvas(document.body).then(function(canvas) {
          

          //生成base64图片数据  
         var dataUrl = canvas.toDataURL();  
        // console.log(dataUrl);  这里千万不要像这样打印出来否则HBuilder会报错的  可能输出的内容太多了吧  
         var b = new plus.nativeObj.Bitmap();  

          b.loadBase64Data(dataUrl,function(){  
              console.log("创建成功");  
          },function(){  
              console.log("创建失败");  
          }); 

          b.save('_www/img1.jpg',{overwrite:true},function(){  
              console.log("保存成功");  
          },function(){  
              console.log("保存失败");  
          });  

          plus.gallery.save( '_www/img1.jpg', function () {  
            console.log( "保存图片到相册成功" );  
          },function(){  
            console.log( "保存图片到相册失败" );
          })

        });

    　   

      },
      getBlob (canvas){ //获取blob对象
        var data = canvas.toDataURL("image/jpeg", 1);
        this.dataurl=data
        console.log(this.dataurl)
        data = data.split(',')[1];
        data = window.atob(data);
        var ia = new Uint8Array(data.length);
        for (var i = 0; i < data.length; i++) {
          ia[i] = data.charCodeAt(i);
        }
        return new Blob([ia], {
          type: "image/jpeg"
        });
      },
      // 打印
      f_Text(){

        this.setImage()
        return

        // 产品
        var p_org = this.goodsArr.concat(this.giftsArr)
        var p_deal = []
        for(var i = 0; i < p_org.length; i++){
          var p = {}
          p.name = p_org[i].productName
          p.qty = p_org[i].TAKE_PO_NUM
          p.uom = p_org[i].UNIT_STR
          p.price = p_org[i].TAKE_PRICE
          p_deal.push(p)
        }

        var post_json = {}

        // 抬头
        post_json.header = "销售单"
        
        // 客户信息
        post_json.cone_code = this.orderInfo.consigneeCode
        post_json.cone_tel = this.orderInfo.consigneeTelephone
        post_json.cone_name = this.orderInfo.consigneeName
        post_json.cone_address = this.orderInfo.consigneeAddress

        // 产品信息
        post_json.product = p_deal

        // 汇总信息
        post_json.total_qty = parseFloat(this.orderInfo.TAKE_NUM).toFixed(1);
        post_json.total_price = parseFloat(this.orderInfo.TAKE_PRICE).toFixed(2);
        post_json.cono_tel = this.orderInfo.consignorTelephone
        post_json.cono_name = this.orderInfo.consignorContact
        post_json.order_no = this.orderInfo.OUTNO

        var p_deal_json = JSON.stringify(post_json)
        console.log("产品：" + p_deal_json)

        // 安卓
        try { CallAndroidOrIOS.callAndroid("打印", p_deal_json) }
        catch(error) { }
        // 苹果
        try { CallAndroidOrIOS("打印", p_deal_json) }
        catch(error) { }
      },
      reStore(){

        var that = this;

        this.selfconfirm('确定要恢复成新单吗?',function(){

          var CancelData = {
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
            "transferOutId":that.$route.query.transferOutId,//出库单id
            "type":"reStore", //非必填，填了就是恢复订单（仅把订单状态，从取消改成新建） 
          }
          // 恢复订单（仅把订单状态，从取消改成新建）
          that.httpRequest("saveCancelTransferOut.do",CancelData,function(CancelRes){
            console.log(CancelRes)

            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      pay(){

        var that = this;
        
        var mch_create_ip = returnCitySN["cip"];
        var postData = {
          "cellphone":this.$store.state.userInfo.cellphone, //手机号
          "orderId":this.orderInfo.TRANSFER_ID,             //订单id
          "total_fee":this.orderInfo.PAY_PRICE,             //付款金额，单位：分
          "mch_create_ip":mch_create_ip,
          "sub_openid":this.$store.state.GopenId,
        }
        this.httpRequest("wxPay.do",postData,function(res){
          
          var pay_info = JSON.parse(res.data.pay_info)
          function onBridgeReady(){
            WeixinJSBridge.invoke(
              'getBrandWCPayRequest', {
                "appId":pay_info.appId,     //XXX动态获取初始化请求中返回的pay_info参数中appId值    
                "timeStamp":pay_info.timeStamp, //XXX动态获取初始化请求中返回的pay_info参数中timeStamp值     
                "nonceStr":pay_info.nonceStr,  //XXX动态获取初始化请求中返回的pay_info参数中nonceStr值   
                "package":pay_info.package,   //XXX动态获取初始化请求中返回的pay_info参数中package值  
                "signType":pay_info.signType,  //XXX动态获取初始化请求中返回的pay_info参数中signType值
                "paySign":pay_info.paySign    //XXX动态获取初始化请求中返回的pay_info参数中paySign值
              },
            function(res){
              if(res.err_msg == "get_brand_wcpay_request:ok"){
                alert("支付成功")
                // 使用以上方式判断前端返回,微信团队郑重提示：
                // res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
                var params = {
                  "appUsersId":that.$store.state.userInfo.user_id,//用户id
                  "transferOutId":that.$route.query.transferOutId,//出库单id
                }
                that.httpRequest("updTranState.do",params,function(res){

                  that.$router.push({
                    name:'QuerySalesOrder',
                  })
                })
              }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                alert("取消支付")
              }else if(res.err_msg == "get_brand_wcpay_request:fail"){
                alert("支付失败")
              }
            });
          }

          if(typeof WeixinJSBridge == "undefined"){
            if( document.addEventListener ){
              document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
            }else if (document.attachEvent){
              document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
              document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
            }
          }else{
            onBridgeReady();
          }
        })
      },
      // 自建confirm取消事件
      selfconfirm_cancel (){
        this.selfconfirm_show = false;
      },
      // 自建confirm确认事件
      selfconfirm_confirm(){

      },
      // 自建confirm确认事件
      selfconfirm_confirm_ (){

        this.selfconfirm_show = false;
        this.selfconfirm_confirm();
      },
      selfconfirm(text,selfconfirm_confirm){
        this.selfconfirm_show = true;
        this.confirmcontent = text;
        this.selfconfirm_confirm = selfconfirm_confirm;
      },
      //原单退货，全退
      returnOnclick(){

        var that = this;

        this.selfconfirm('确定要全部退货吗?',function(){
          var postData = {
            "transferOutId":that.$route.query.transferOutId,//出库单id
            "businessId":that.$store.state.businessInfo.businessId,//业务ID
            "userId":that.$store.state.userInfo.user_id,//用户id
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
          }

          // 销售退货，原单退、全退
          that.httpRequest("SalesReturnAll.do",postData,function(res){

            that.ifTips = true;
            that.tips_Msg = res.Msg;

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      // 部分退货
      PartReturn(){
        var that = this;
        // 收货
        var ReceiptInfo = {
          CUSTOMER_NAME:that.orderInfo.consignorName,
          CONTACT_NAME:that.orderInfo.consignorContact,
          CONTACT_TEL:that.orderInfo.consignorTelephone,
          CUSTOMER_ADDRESS:that.orderInfo.consignorAddress,
        }

        // 发货
        var DeliveryInfo = {
          CUSTOMER_NAME:that.orderInfo.consigneeName,
          CONTACT_NAME:that.orderInfo.consigneeContact,
          CUSTOMER_TEL:that.orderInfo.consigneeTelephone,
          CUSTOMER_ADDRESS:that.orderInfo.consigneeAddress,
        }
        this.$router.push({
          name:"ChooseGoods",
          query:{
            ReceiptInfo:ReceiptInfo,
            DeliveryInfo:DeliveryInfo,
            whoPush:"OutboundDetail",
            orderType:"SALE_RETURN_IN_PART",
            transferOutId:that.orderInfo.TRANSFER_ID,//销售单id
          }
        })
      },
  	  //确认出库事件
  	  confirmOutbound () {
  	    var that = this;

        if(this.orderInfo.transferType == 'SALE_OUT'){
          // var objectName;

          // if(that.OutboundObject == 'user'){
            // objectName = that.orderInfo.createdUser;
          // }else{
          //   objectName = that.orderInfo.consignorName;
          // }

          this.selfconfirm('确定要出库吗?',function(){
          // this.selfconfirm('确定要以 '+objectName+' 的库存出库吗?',function(){

            var ConfirmData = {
              "transferOutId":that.$route.query.transferOutId,//出库单id
              // "types":that.OutboundObject,//当出库类型时销售出库  用户选择扣减库存对象的type
              "types":'user',//当出库类型时销售出库  用户选择扣减库存对象的type
            }
            console.log(ConfirmData)

            // 获取出库订单列表
            that.httpRequest("saveConfirmTransferOut.do",ConfirmData,function(ConfirmRes){
              console.log(ConfirmRes)

              that.ifTips = true;
              that.tips_Msg = "操作成功";

              setTimeout(function(){
                that.$router.go(-1);
              },2000)
            })
          })
        }else{

          var msg = "确定要出库吗?"
          if(this.$store.state.G_AppOrWX == 'WX'){
            msg = "确定要收货吗？"

          }
          this.selfconfirm(msg,function(){

            var ConfirmData = {
              "transferOutId":that.$route.query.transferOutId,//出库单id
              "types":'osp',//当出库类型时销售出库  用户选择扣减库存对象的type
            }
            console.log(ConfirmData)

            // 获取出库订单列表
            that.httpRequest("saveConfirmTransferOut.do",ConfirmData,function(ConfirmRes){
              console.log(ConfirmRes)

              that.ifTips = true;
              that.tips_Msg = "操作成功";

              setTimeout(function(){
                that.$router.go(-1);
              },2000)
            })
          })
        }
  	  },
  	  //取消出库事件
  	  cancelOutbound (){
  	    var that = this;

        this.selfconfirm('确定要取消出库吗?',function(){

          var CancelData = {
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
            "transferOutId":that.$route.query.transferOutId,//出库单id
          }
          console.log(CancelData)

          // 获取出库订单列表
          that.httpRequest("saveCancelTransferOut.do",CancelData,function(CancelRes){
            console.log(CancelRes)

            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
  	  },
      // 确认调拔
      confirmTransferOut(){

        var that = this;
        this.selfconfirm('确定要调拔吗?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
            "transferId":that.$route.query.transferOutId, //调拔单id
            "appUsersId":that.$store.state.userInfo.user_id, //用户id
            "type":'OUT', // 调拔类型
          }
          that.httpRequest("saveTransferConfirm.do",ConfirmData,function(res){

            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      // 取消调拔
      cancelTransferIn() {

        var that = this;
        this.selfconfirm('确定取消调拔出库吗?',function(){
          var ConfirmData = {
            "transferId":that.$route.query.transferOutId, //调拔单id
            "appUsersId":that.$store.state.userInfo.user_id, //用户id
            "type":"OUT", // 调拔类型
          }

          // 取消调拔出库事件
          that.httpRequest("saveTransferCancel.do",ConfirmData,function(ConfirmRes){

            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .OutboundDetail{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    // .radio{
    //   .infoContainer{
    //     font-size: 33/50rem;
    //     padding:15/50rem 15/50rem;
    //     border-radius:15/50rem;
    //     box-sizing: border-box;
    //     background-color:#fff;
    //     margin-bottom: 20/50rem;
    //     &>div>div{
    //       float: left;
    //       width:344/50rem;
    //     }
    //   }
    //   .infoContainer>div{
    //     margin-bottom: 10/50rem;
    //     overflow: hidden;
    //   }
    // }
  }
  /*确认入库 按钮  */
  .entryBtn{
    font-size: 26/50rem;
    width:100%;
    position: fixed;
    bottom: 30/50rem;
    left: 0;
    text-align: center;
    overflow: hidden;
    display: flex;
    padding:0 20/50rem;
    box-sizing:border-box;
    text-align: center;
    &>div{
      width: 50%;
      margin: 0 auto;
      height: 68/50rem;
      background-color: @PublicColor;
      color: #fff;
      float: left;
      line-height: 68/50rem;
      text-align: center;
      border-radius:34/50rem;
      &:nth-child(2){
        margin-left: 20/50rem;
      }
    }
  }
  .returnGoods{
    float: right;
    margin-right: 10/50rem;
    margin-bottom: 30/50rem;
    padding-right: 35/50rem;
    padding-left: 35/50rem;
    padding-top: 10/50rem;
    padding-bottom: 10/50rem;
    border-radius: 10/50rem;
    color: #fff;
    font-size: 26/50rem;
    background-color: #999; 
  }
  .orderInfo{
    .infoContainer>div.flexDiv{
      // display: flex;
      // &>div{
      //   flex-grow: 1;
      // }
    }
    span{
      color: #666;
    }
    span.ASD{
      letter-spacing: 59.8/50rem;
      color: #000;
      margin-left: 0;
    }
    span.ASD_TWO{
      letter-spacing: 15/50rem;
    }
    .customerInfo>div{
      display: flex;
      &>div{
        float:none;
        width: auto; 
      }
      &>div:first-child{
        min-width: 130/50rem;
        flex-shrink:0;
        span{
          color: #000;
        }
      }
      &>div:nth-child(2){
        flex-grow: 1;
      }
      span{
        margin:0;
      }
    }
  }
</style>


