<template>
  <div class="PutInStorageDetails">
  	<!-- 头部 -->
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class='PutInOrderInfo'>
        <div class='infoHead'>订单信息</div>
        <div class='infoContainer'>
          <div><span>入库单号：</span><span>{{orderInfo.IN_NO}}</span></div>
          <div v-if="orderInfo.OMSNO">采购单号：<span>{{orderInfo.OMSNO}}</span></div>
          <div><span>创建时间：</span><span>{{orderInfo.ADDDATE}}</span></div>
          <div><span>订单类型：</span><span>{{orderInfo.TRANSFER_TYPE_text}}</span></div>
          <div><span>订单状态：</span><span>{{orderInfo.STATUS_text}}</span></div>
          <div v-if="orderInfo.REMARK"><div><span class="ASD">备</span>注：</div><div><span>{{orderInfo.REMARK}}</span></div></div>
          <div class="flexDiv">
            <div v-if='orderInfo.transferType =="PURCHASE_IN" || orderInfo.transferType =="SALE_OUT"'><span>付款价格：</span><span>¥{{orderInfo.PAY_PRICE}}</span></div>
            <div><span>下单数量：</span><span>{{orderInfo.orderQty}}</span></div>
            <div v-if='orderInfo.MJ_PRICE>0'><span>优惠：</span><span>¥{{orderInfo.MJ_PRICE}}</span></div>
           <!--  <div>重量：<span>{{orderInfo.orderWt}}</span></div>
            <div>体积：<span>{{orderInfo.orderVol}}</span></div> -->
          </div>
        </div>
      </div>

      <div class='PutInOrderInfo'>
        <div class='infoHead'>发货方</div>
        <div class='infoContainer'>
          <div><div><span class="ASD ASD_TWO">发货</span>方：</div><div><span>{{orderInfo.consignorName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO">联系</span>人：</div><span>{{orderInfo.consignorContact}}&nbsp; {{orderInfo.consignorTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consignorTelephone' @click="callPhone(orderInfo.consignorTelephone)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div v-if="orderInfo.consignorAddress"><span>发货地址：</span><span>{{orderInfo.consignorAddress}}</span></div>
        </div>
      </div>

      <div class='PutInOrderInfo'>
        <div class='infoHead'>收货方</div>
        <div class='infoContainer'>
          <div><div><span class="ASD ASD_TWO">收货</span>方：</div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO">联系</span>人：</div><div><span>{{orderInfo.consigneeContact}}&nbsp; {{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-if='orderInfo.consigneeAddress'><span>收货地址：</span><span>{{orderInfo.consigneeAddress}}</span></div>
        </div>
      </div>
      <!-- 正常品 -->
      <componentCargoInfo :goodsArr='goodsArr' :TRANSFER_TYPE='orderInfo.transferType' titleHead='货物信息'></componentCargoInfo>

      <!-- 赠品 -->
      <componentCargoInfo :goodsArr='giftsArr' titleHead='赠品信息'></componentCargoInfo>
      <!--确认入库 按钮  -->
      <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType != "TRANSFER_IN"'>
        <div @click='cancelOutbound'>取消入库</div>
        <div @click='confirmOutbound' v-if='$store.state.CurrentRole =="osp"'>确认入库</div>
      </div>
      <!-- 确认调拔 按钮  -->
      <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType == "TRANSFER_IN"'>
        <div @click='cancelTransferIn'>取消调拔</div>
        <div v-if='$store.state.CurrentRole =="osp"' @click='confirmTransferIn'>确认调拔</div>
      </div>
    </div>
	  
     <div class="selfconfirm" v-if="selfconfirm_show">
      <div class="selfconfirm_contianer">
        <div class="selfconfirm_head">提示</div>
        <div class="selfconfirm_content">{{confirmcontent}}</div>
        <div class="selfconfirm_btn">
          <div @click='selfconfirm_cancel()'>取消</div>
          <div @click='selfconfirm_confirm_()'>确认</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentCargoInfo from '@/components/Widgets/componentCargoInfo'
  export default{
  	data(){
  	  return{
  	  	headerText:'入库详情',
  	  	TextContent:'打印',
  	  	orderInfo:{},//订单信息
        goodsArr: [],//商品信息
        giftsArr:[],//赠品列表
        selfconfirm_show:false,//自建confirm是否显示提示框
        confirmcontent:'',//自建confirm提示内容
  	  }
  	},
  	components:{
	    componentHeader,
      componentCargoInfo
	  },
  	created(){
      
      var that = this;

      var LineData = {
        "transferId":that.$route.query.TRANSFER_ID,//入库id
      }
      console.log(LineData)

      // 获取出库订单列表
      that.httpRequest("searchTransferReceiptDetail.do",LineData,function(SupRes){
         console.log(SupRes)
         
         if(SupRes.data.length){

            if(SupRes.data[0].orderInfo.transferType == 'PURCHASE_IN'){

              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '采购入库';
            }else if(SupRes.data[0].orderInfo.transferType == 'OTHER_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '其它入库';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN_ALL'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '销售退货入库(全退)';

            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '销售退货入库';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN_PART'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '销售退货入库(部分)';
            }else if(SupRes.data[0].orderInfo.transferType == 'TRANSFER_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '调拨入库';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '销售出库';
            }else if(SupRes.data[0].orderInfo.transferType == 'TRANSFER_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '调拨出库';
            }else if(SupRes.data[0].orderInfo.transferType == 'PURCHASE_RETURN_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '采购退货出库';
            }

            if(SupRes.data[0].orderInfo.STATUS == 'NEW'){

              SupRes.data[0].orderInfo.STATUS_text = '新建';

            }else if(SupRes.data[0].orderInfo.STATUS == 'FINISH'){

              SupRes.data[0].orderInfo.STATUS_text = '已审核';

            }else if(SupRes.data[0].orderInfo.STATUS == 'CANCEL'){

              SupRes.data[0].orderInfo.STATUS_text = '此入库单已取消';
            }else{
              SupRes.data[0].orderInfo.STATUS_text = SupRes.data[0].orderInfo.STATUS;
            }

            that.orderInfo = SupRes.data[0].orderInfo;

            var goodsArr = [],giftsArr = [];

            for(var i=0;i<SupRes.data[0].detail.length;i++){

             if(SupRes.data[0].detail[i].PRODUCT_TYPE == 'GF'){

                giftsArr[giftsArr.length]=SupRes.data[0].detail[i];
              }else{
                goodsArr[goodsArr.length]=SupRes.data[0].detail[i];
              }
            }
            that.goodsArr = goodsArr;
            that.giftsArr = giftsArr;
         }else{
            // 没有数据
            that.$alert('没有查询到数据，请联系管理员', '提示', {
              confirmButtonText: '确定',
            })
         }
      })
  	},
  	methods:{
  	  // 返回上一页
      f_Fanhui(){
      	this.$router.push({
          name:"WarehousingDetail",
        })
      },
      // 打印
      f_Text(){

      },
      // 自建confirm取消事件
      selfconfirm_cancel (){
        this.selfconfirm_show = false;
      },
      // 自建confirm确认事件
      selfconfirm_confirm(){

      },
      // 自建confirm确认事件
      selfconfirm_confirm_ (){

        this.selfconfirm_show = false;
        this.selfconfirm_confirm();
      },
      selfconfirm(text,selfconfirm_confirm){
        this.selfconfirm_show = true;
        this.confirmcontent = text;
        this.selfconfirm_confirm = selfconfirm_confirm;
      },
  	  //确认入库事件
      confirmOutbound () {
        var that = this;

        this.selfconfirm('确定要入库吗?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
            "transferId":that.$route.query.TRANSFER_ID,//入库单id
            "businessId":that.$store.state.businessInfo.businessId,//业务id
          }
          console.log(ConfirmData)

          // 确认入库事件
          that.httpRequest("saveConfirmTransferReceipt.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '提示', {
              confirmButtonText: '确定',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      //取消入库事件
      cancelOutbound (){
        var that = this;

        this.selfconfirm('确定要取消入库吗?',function(){
          var CancelData = {
            "transferId":that.$route.query.TRANSFER_ID,//入库单id
          }
          console.log(CancelData)

          // 获取出库订单列表
          that.httpRequest("saveCancelTransferReceipt.do",CancelData,function(CancelRes){
             console.log(CancelRes)
             that.$alert(CancelRes.Msg, '提示', {
              confirmButtonText: '确定',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      // 确认调拔
      confirmTransferIn() {

        var that = this;
        this.selfconfirm('确定调拔入库吗?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
            "transferId":that.$route.query.TRANSFER_ID, //调拔单id
            "appUsersId":that.$store.state.userInfo.user_id, //用户id
            "type":"IN", // 调拔类型
          }
          console.log(ConfirmData)

          // 确认入库事件
          that.httpRequest("saveTransferConfirm.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '提示', {
              confirmButtonText: '确定',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      // 取消调拔
      cancelTransferIn() {

        var that = this

        this.selfconfirm('确定取消调拔入库吗?',function(){
          var ConfirmData = {
            "transferId":that.$route.query.TRANSFER_ID, // 调拔单id
            "appUsersId":that.$store.state.userInfo.user_id, //用户id
            "type":"IN", // 调拔类型
          }
          console.log(ConfirmData)

          // 取消调拔入库事件
          that.httpRequest("saveTransferCancel.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '提示', {
              confirmButtonText: '确定',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .PutInStorageDetails{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    .container{
      .PutInOrderInfo{
        .infoContainer{
          font-size: 28/50rem;
          padding:15/50rem 15/50rem;
          border-radius:15/50rem;
          box-sizing: border-box;
          background-color:#fff;
          margin-bottom: 20/50rem;
          &>div.flexDiv{
            display: flex;
            &>div{
              flex-grow: 1;
              span:first-child{
                color: #000;
              }
            }
          }
          &>div{
            line-height: 50/50rem;
            display: flex;
            &>div:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
              color: #000;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
        span{
          color: #555;
        }
        span.ASD{
          letter-spacing: 59.8/50rem;
          color: #000;
          margin-left: 0;
        }
        span.ASD_TWO{
          letter-spacing: 15/50rem;
        }
      }
    }
  }
 /*确认入库 按钮  */
  .entryBtn{
    font-size: 26/50rem;
    width:100%;
    position: fixed;
    bottom: 30/50rem;
    left: 0;
    text-align: center;
    overflow: hidden;
    display: flex;
    padding:0 20/50rem;
    box-sizing:border-box;
    text-align: center;
    &>div{
      width: 50%;
      margin: 0 auto;
      height: 68/50rem;
      background-color: @PublicColor;
      color: #fff;
      float: left;
      line-height: 68/50rem;
      text-align: center;
      border-radius:34/50rem;
      &:nth-child(2){
        margin-left: 20/50rem;
      }
    }
  }
</style>


