<template>
  <div>
    <footer>
      <div  :class="{active : active == dataItem.title}"   v-for='(dataItem,index) in footList'  :id="index"  :key='index' @click="toggle(index)" >
        <i class="iconfont" :class="dataItem.icon" ></i>
        <p>{{dataItem.title}}</p>
      </div>
    </footer>
  </div>
</template>
<script>
  export default {
    name:"FooterIndex",
    data(){
      return{
        footList:[
          {
            path: "Index",
            title: "首页",
            icon: "icon-shouye"
          },
          // {
          //   path: "SalesOrder",
          //   title: "下单",
          //   icon: "icon-xiadan"
          // },
          {
            path: "QuerySalesOrder",
            title: "查单",
            icon: "icon-chadan"
          },
          {
            path: "Home",
            title: "我的",
            icon: "icon-denglu"
          },
        ],
        active: '首页',
      }
    },
    created(){

        var that = this;
        // $store.state.CurrentRoleData.
        // 采购下单 销售下单都没有权限的时候 隐藏这个底部模块
        // if(!that.$store.state.CurrentRoleData.SalesOrder && !that.$store.state.CurrentRoleData.PurchaseOrder){

        //   for(var n = 0;n < that.footList.length; n++){
        //     if(that.footList[n].path == 'SalesOrder'){
        //       that.footList.splice(n,1);
        //     }
        //   }
        // }
        if(!that.$store.state.CurrentRoleData.QuerySalesOrder && !that.$store.state.CurrentRoleData.QueryPurchaseOrder){

          for(var m = 0;m < that.footList.length; m++){
            if(that.footList[m].path == 'QuerySalesOrder'){
              that.footList.splice(m,1);
            }
          }
        }
        // 只有采购下单时 改变下单底部按钮的默认路由
        // if(!that.$store.state.CurrentRoleData.SalesOrder && that.$store.state.CurrentRoleData.PurchaseOrder){
        //   for(var bn = 0;bn < that.footList.length; bn++){
        //     if(that.footList[bn].path == 'SalesOrder'){
        //       that.footList[bn].path = 'PurchaseOrder';
        //     }
        //   }
        // }
        if(!that.$store.state.CurrentRoleData.QuerySalesOrder && that.$store.state.CurrentRoleData.QueryPurchaseOrder){
          for(var cn = 0;cn < that.footList.length; cn++){
            if(that.footList[cn].path == 'QuerySalesOrder'){
              that.footList[cn].path = 'QueryPurchaseOrder';
            }
          }
        }

        for(var i = 0;i < that.footList.length; i++){

          // if(that.$route.name =="PurchaseOrder"){

          //    that.active = "下单"
          //    return;
          // }

          if(that.$route.name =="QueryPurchaseOrder"){

             that.active = "查单"
             return;
          }

          if(that.$route.name == that.footList[i].path){

            that.active = that.footList[i].title;
            return;
          }
        }

        if(this.$route.query.active){

          this.active = this.$route.query.active;
        }
    },
    methods:{
      toggle(index){
        this.active =  this.footList[index].title;

        this.$router.push({
          name:this.footList[index].path,
          query:{
            active:this.footList[index].title
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  @import "./publicLess.less";
  footer{
    position:fixed;
    z-index:99;
    background:#F8F8F8;
    bottom:0;
    left:0;
    height:111/50rem;
    width:100%;
    display:flex;
    div{
        color: #444;
        flex-grow:1;
        float:left;
        text-align:center;
        p{
            color:#A0A0A0;
            font-size:25/50rem;
            margin-bottom:10/50rem;
        }
        i{
            font-size: 40/50rem;
            
        }
        i.icon-chadan{
          font-size: 36/50rem;
        }
        // &:nth-child(1){
        //   i{
        //     font-size: 40/50rem;
        //   }
        // }
        // &:nth-child(3){
        //   i{
        //     font-size: 40/50rem;
        //   }
        // }
      }
    div.active{
        color: @PublicColor;
        p{
            color: @PublicColor;
        }
    }
  }
</style>


