<template>
  <div class="SalesOrder">
    <componentHeader  headerText="销售下单" :IfFanhuiShow='true'  @f_Fanhui='f_Fanhui'></componentHeader>
    <componentHeader v-if='$store.state.G_AppOrWX == "WX"' headerText="下单" :IfFanhuiShow='true'  @f_Fanhui='f_Fanhui'></componentHeader>
<!--     <header class="SalesOrderHeader" v-if='$store.state.G_AppOrWX != "WX" && $store.state.CurrentRoleData.PurchaseOrder'>
      <div class="activeBtn">销售单</div>
      <div @click="SelectPurchase">采购单</div>
    </header> -->
    <div class="container haveHeadcontainer">
      <div class="fixedHead">
        <div class="CustomerSearch">
          <div>
            <input v-model='searchValue' placeholder="按门店名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
        </div>
        <div class="selectCondition">
          <div @click='selectBoxShow("供货商")'>
            <span>供货商名称：</span>
            <i class="iconfont icon-xiangyou"></i>
            <span>{{SupplierName}}</span>
          </div>
        </div>
      </div>
      <div class="CustomerList">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class='customerList_box' v-for='(dataItem,index) in StoresListData'  :id="index"  :key='index' >
            <div  @click='Comp_CustomerSelected(index)'>
               <div class="CUSTOMER_NAME">
                <span>客户名称：</span>
                <span>{{dataItem.CUSTOMER_NAME}}</span>
              </div>
              
              <div>
                <span>客户代码：</span>
                <span>{{dataItem.CUSTOMER_CODE}}</span>
              </div>
              <div>
                <span>客户城市：</span>
                <span>{{dataItem.CUSTOMER_CITY_CHINESE}}</span>
              </div>
             <div>
                <span>客户类型：</span>
                <span>{{dataItem.CUSTOMER_TYPE}}</span>
              </div>
            </div>
            <i  class="iconfont icon-tiaozhuanqianwangyoujiantouxiangyouxiayibuxianxing"></i>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
    </div>
    <componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
    <!-- <componentFooter v-if='$store.state.G_AppOrWX != "WX"'></componentFooter> -->
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentFooter from '@/components/componentFooter'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
    data(){
      return{
        SupplierName:'',//供货商名称
        suppliersId:'',//当前供应商id
        supplierList:[],//供货商列表
        searchValue:'',//搜索的值
        StoresListData:[],//门店列表
        noDataShow:false,//页面没有数据图标
        SelectBoxFlag:false,//选择弹出框 隐藏
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentFooter,
      componentHeader,
      componentSelectBox
	  },
    created(){

      var that = this;
      var SupplierData = {
        "appUsersId":that.$store.state.userInfo.user_id,//用户id
        "businessId":that.$store.state.businessInfo.businessId,//业务id
      }
      console.log(SupplierData)

      if(that.$store.state.G_AppOrWX == 'WX'){
        var InterfaceName = 'wxSearchCustomerList.do';
      }else{
        var InterfaceName = 'searchCustomerList.do';
      }
      
      // 获取供货商
      this.httpRequest(InterfaceName,SupplierData,function(res){
        console.log(res)

        if(res.data.length){
          
          for(var k=0;k<res.data.length;k++){
            res.data[k].name = res.data[k].CUSTOMER_NAME;
          }
          var idx = that.$store.state.SalesOrder_supplierListIdx
          that.supplierList = res.data;
          that.SupplierName = res.data[idx].CUSTOMER_NAME;//供货商名称
          that.suppliersId = res.data[idx].CUSTOMER_ID;//供应商id
          that.reqStoresListInfo();

        }else{
          that.$alert('未获取到供应商，请联系管理员', '提示', {
            confirmButtonText: '确定',
          })
        }
      })
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoresListInfo();
      }
    },
    methods:{
      f_Fanhui(){
        this.$router.push({
          name:"Index"
        })
      },
      // 切换头部导航
      SelectPurchase(){
        this.$router.push({
          name:"PurchaseOrder"
        })
      },
      // 选中客户
      Comp_CustomerSelected(index){

        var that = this;
        this.$store.state.BackBottle = {
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//收货人信息
          DeliveryAddr_selected:[],//发货人信息
        }
        this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据
        var ReceiptInfo = this.StoresListData[index];//收货人信息

        var DeliveryInfo = this.supplierList[that.$store.state.SalesOrder_supplierListIdx];//发货人信息
        this.$router.push({
          name:"ChooseGoods",
          query:{
            ReceiptInfo:ReceiptInfo,
            DeliveryInfo:DeliveryInfo,
            whoPush:"SalesOrder",
            orderType:"SALE_OUT",
          }
        })
      },
      // 请求店铺列表
      reqStoresListInfo(load){
        var that = this;

        var ProductData = {
          "storeName":that.searchValue,//门店名称
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "suppliersId":that.suppliersId,//ospID
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        console.log(ProductData)

        if(that.$store.state.G_AppOrWX == 'WX'){
          var InterfaceName = 'wxSearchStore.do';
        }else{
          var InterfaceName = 'searchStore.do';
        }

        // 查询门店列表
        that.httpRequest(InterfaceName,ProductData,function(StoresRes){
          console.log(StoresRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(StoresRes.data.length){
              that.StoresListData = that.StoresListData.concat(StoresRes.data);
            }
          } else {
            that.StoresListData = StoresRes.data;
          }

          // 判断是否能上拉加载
          if (StoresRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.StoresListData.length){

            that.noDataShow = false;
           }else{
            that.noDataShow = true;
           }

          if(StoresRes.data.length == 1 && that.$store.state.G_AppOrWX == "WX"){

            that.Comp_CustomerSelected(0);
          }
        })
      },
      // 选择弹出框 显示
      selectBoxShow(text){
        this.BouncedName = text;
        this.SelectBoxFlag=true;

        this.SelectBoxList = this.supplierList;

      },
      // 选中某个状态
      selectedItem(index){

        var that = this;

        this.SupplierName = this.supplierList[index].name;
        this.suppliersId = this.supplierList[index].CUSTOMER_ID;
        this.$store.state.SalesOrder_supplierListIdx = index;
        this.SelectBoxFlag = false;
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoresListInfo();
      },
      // 取消按钮 弹出选择框
      selectCancel(){
        this.SelectBoxFlag = false;
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqStoresListInfo("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqStoresListInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SalesOrder{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .fixedHead{
        background-color:#fff;
        z-index: 50;
        width: 100%;
        position:fixed;
        top: 89/50rem;
        left: 0;
        .CustomerSearch{
          .TopSearchBox;
        }
        .selectCondition{
          div>span:nth-child(3){
            text-align: right;
            width: 430/50rem;
            overflow: hidden;
            text-overflow: ellipsis;/*文字隐藏后添加省略号*/
            white-space: nowrap;/*强制不换行*/
          }
        }
      }
      .CustomerList{
        height: 100%;
        color: #555;
        overflow: scroll;
        padding-top: 275/50rem;
        box-sizing: border-box;
        .customerList_box{
          width: 100%;
          background-color: #fff;
          font-size: 28/50rem;
          padding:20/50rem 62/50rem 20/50rem 20/50rem;
          box-sizing: border-box;
          border-bottom: 20/50rem solid #EEEFF2;
          position:relative;
          overflow: hidden;
          &>div>div{
            line-height: 46/50rem;
          }
          i{
            position: absolute;
            right: 20/50rem;
            top: 50%;
            font-size: 50/50rem;
            transform: translateY(-50%);
          }
          .CUSTOMER_NAME{
            span:nth-child(2){
              width: 520/50rem;
              float: right;
              display: block;
            }
          }
        }
      }
      .kong{
        height: 150/50rem;
      }
    }
  }
</style>


