<template>
  <div class="InventoryRegister">
	 <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfAddShow='true' @f_Add='f_Add'></componentHeader>

  	<div class='container haveHeadcontainer'>
      <div class="headFixed">
        <div class="CustomerSearch">
          <div>
            <input  v-model='searchValue'  placeholder="按客户名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
          <div class="MonthDisplay" @click="SelectMonthShow">{{MonthDisplay}}</div>
        </div>
        <div class="tabHead">
          <div><span class="tabHead_active" @click="SwitchBtnStore">门店</span></div>
          <div><span @click="SwitchBtnCustomer">客户</span></div>
        </div>
      </div>

      <div class="ListBox">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div  class='inventoryList' @click='inventoryDetail(index)' v-for='(itemName,index) in listData'  :id="index"  :key='index'>
            <div><span>客户名称：</span><span>{{itemName.NAME}}</span></div>
            <div><span>客户代码：</span><span>{{itemName.CODE}}</span></div>
            <div><span>库存月份：</span><span>{{itemName.INVENTORY_TIME}}</span></div>
            <div><span>填表日期：</span><span>{{itemName.ADDDATE}}</span></div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
  	</div>
    <div class="BtnComponent bottomBtn">
      <div @click="toSelectStore">选择具体门店</div>
    </div>
    <div class="SelectMeng" v-if="selectMonthHide">
      <div class="SelectWarehouses SelectMengContent">
        <div class="SelectTitle SelectMengTitle">请选择库存月份</div>
        <div class="SelectItem">
          <div>
            <span>开始月份</span>
            <el-date-picker v-model="StartMonth" type="month" :editable='false' placeholder="选择月" format="yyyy 年 MM 月" value-format="yyyy-MM"></el-date-picker>
          </div>
          <div>
            <span>结束月份</span>
            <el-date-picker v-model="EndMonth" type="month" :editable='false' placeholder="选择月" format="yyyy 年 MM 月" value-format="yyyy-MM"></el-date-picker>
          </div>
        </div>
        <div class="selectBtn">
          <div @click="selectMonthCancel">取消</div>
          <div @click="selectMonthConfirm">确定</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"库存登记",
      	listData:[],//页面数据列表
        MonthDisplay:"选择库存月份",//按钮显示内容
        selectMonthHide:false,//选择库存月份 弹出框是否显示
        StartMonth:'',//选择库存月份 开始月份
        EndMonth:'',//选择库存月份 结束月份
        searchValue:'',//搜索的值
        ObjectType:'store',//当前是门店还是客户
        noDataShow:false,//页面没有数据图标
        pickerOptions: {
          shortcuts: [{
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()]);
            }
          }, {
            text: '今年至今',
            onClick(picker) {
              const end = new Date();
              const start = new Date(new Date().getFullYear(), 0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setMonth(start.getMonth() - 6);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      this.SwitchBtnStore();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        var that = this;
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects(that.ObjectType);
      }
    },
    methods:{
      // 返回上一页
      f_Fanhui(){

        this.$router.push({
          name:"InventoryManage",
        })
      },
      // 切换门店/客户
      SwitchRegObjects(ObjectType,load){

        var that = this;

        this.ObjectType = ObjectType;

        if(ObjectType == 'store'){
          $('.tabHead>div:eq(0)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(1)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','block');

        }else if(ObjectType == 'customer'){
          $('.tabHead>div:eq(1)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(0)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','none');
        }
        
        var histData = {
          "strCustomerName":that.searchValue,//搜索
          "strRegStartTime":that.StartMonth,//开始时间
          "strRegEndTime":that.EndMonth,//结束时间
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
          "types":ObjectType,//类型：门店/客户
          'ospId':that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          'businessId':that.$store.state.businessInfo.businessId,//业务ID
          "orgnizationId":'',//门店ID
          "userId":that.$store.state.userInfo.user_id,//用户id
          "cellphone":that.$store.state.userInfo.cellphone,//用户手机号
        }

        console.log(histData)
        // 获取库存登记列表
        that.httpRequest("GetRegisterList.do",histData,function(histRes){
          console.log(histRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(histRes.data.length){
              that.listData = that.listData.concat(histRes.data);
            }
          } else {
            that.listData = histRes.data;
          }

          // 判断是否能上拉加载
          if (histRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
          
          if(that.listData.length){
            that.noDataShow = false;
            
          }else{
            // 没有数据显示
            that.noDataShow = true;
          }
        })
      },
      // 切换成 门店
      SwitchBtnStore(){
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('store');
      },
      // 切换成 客户
      SwitchBtnCustomer(){
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('customer');
      },
      // 跳转到 选择门店 页面
      toSelectStore(){

        this.$router.push({
          name:"SelectStores",
          query:{
            whoPush : 'InventoryRegister'
          }
        })
      },
      // 新增库存  跳转到 客户列表 页面
      f_Add(){

        this.$router.push({
          name:"AddInventory",
          query:{
            inventoryReg : 'inventoryReg'
          }
        })
      },
      // 选择库存月份 弹出框出现
      SelectMonthShow(){
        this.selectMonthHide = true;
      },
      // 选择库存月份  确认按钮
      selectMonthConfirm(){

        var that = this;

        // 两个都不选
        if(!this.StartMonth && !this.EndMonth){

          this.EndMonth = '';
          this.StartMonth = '';
          this.MonthDisplay = '选择库存月份';
          this.selectMonthHide = false;
          $('.MonthDisplay').css({'color':'#666','border-color':'#666'})
          return;
        }

            // color: @PublicColor;
            // border:1/50rem solid @PublicColor;
        // 如果只选了其中一个日期  则开始与结束日期相同
        if(this.StartMonth && !this.EndMonth){
          this.EndMonth = this.StartMonth;
        }
        if(!this.StartMonth && this.EndMonth){
          this.StartMonth = this.EndMonth;
        }

        let newArr = [this.StartMonth,this.EndMonth].sort(function sequence(a,b){
          return  Date.parse(a) - Date.parse(b);
        })

        this.StartMonth = newArr[0];
        this.EndMonth = newArr[1];

        this.MonthDisplay = this.StartMonth + '至' + this.EndMonth;

        // 如果开始与结束日期相同 则显示一个日期
        if(this.StartMonth == this.EndMonth){

          this.MonthDisplay = this.StartMonth;
        }

        $('.MonthDisplay').css({'color':'#1e8cf8','border-color':'#1e8cf8'})
        this.selectMonthHide = false;
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects(that.ObjectType);
      },
      // 选择库存月份  取消按钮
      selectMonthCancel(){
        this.selectMonthHide = false;
      },
      // 跳转到详情页面
      inventoryDetail (idx){

        var that = this;
        this.$router.push({
          name:"InventoryRegDetail",
          query:{
            inventoryRegisterId : that.listData[idx].INVENTORY_REGISTER_ID,
            ObjectType:that.ObjectType
          }
        })
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.SwitchRegObjects(that.ObjectType,"TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.SwitchRegObjects(that.ObjectType,"BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .InventoryRegister{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .headFixed{
        width: 100%;
        background-color:#fff;
        z-index: 50;
        position:fixed;
        top: 87/50rem;
        left: 0;
        .CustomerSearch{
          .TopSearchBox;
          display: flex;
          &>div{
            flex-grow: 1;
          }
          &>div:last-child{
            font-size: 25/50rem;
            text-align: center;
            margin-left: 20/50rem;
            line-height: 60/50rem;
            // background-color:#999;
            padding:0 5/50rem;
            color: #666;
            border:1/50rem solid #666;
            border-radius: 30/50rem;
          }
        }
        .tabHead{
          background-color: #fff;
          height: 70/50rem;
          line-height: 70/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &>div{
            // width: 50%;
            padding:0 60/50rem;
            text-align: center;
            float: left;
            span.tabHead_active{
              padding-bottom: 10/50rem;
              color: @PublicColor;
              border-bottom:3/50rem solid @PublicColor;
            }
          }
        }
      }
      .ListBox{
        overflow: scroll;
        padding-top: 263/50rem;
        height: 100%;
        box-sizing:border-box;
      }
    }
  }
  .iconfont {
    font-size:37/50rem;
    color: @PublicColor;
  }
  .inventoryList{
    width: 100%;
    padding: 10/50rem 25/50rem;
    box-sizing: border-box;
    font-size: 28/50rem;
    border-bottom: 20/50rem solid #EFEFF2;
  }
  .inventoryList div{
    line-height: 60/50rem;  
  }
  .BtnComponent{
    padding:0 50/50rem;
  }
  .SelectMeng{
    .SelectMengContent{
      .SelectItem{
        padding:20/50rem 0;
        color: #000;
        &>div{
          border-top:none;
          display: flex;
          &>span{
            width: 150/50rem;
          }
          &>div{
            flex-grow: 1;
            margin-right: 20/50rem;
          }
        }
      }
    }
  }
  .kong{
    // height: 300/50rem;
  }
</style>


