<template>
  <div class="AddFreeGift">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">

      <div class='calssType'>
        <div  class='goodsHead'>选择策略：</div>
        <div class='classList' @click='selectClass'  v-for='(itemName,idx) in GiftList'  :id="idx"  :key='idx'>
          <i class="iconfont" :class="{'icon-xuanzhong' : currentStrategyIdx == idx,'icon-weixuanzhong':currentStrategyIdx != idx}"></i>
          <span>{{itemName.strategyName}}</span>
          <div>已选：<span>{{itemName.HasChosenNum}}</span></div>
          <div>剩余：<span>{{parseInt(itemName.afterGiftNum) - parseInt(itemName.HasChosenNum)}}</span></div>
        </div>
      </div>
      <div v-if='GiftList[currentStrategyIdx].afterGiftNum>0'  class='gift'>
        <div class='goodsHead'>产品：</div>
        <div  class='selectGoodsList' v-for='(dataItem,index) in GiftList[currentStrategyIdx].gift'  :id="index"  :key='index'>
          <div class="selectGoodsListItem">
            <div class='LeftIcon'>
              <img :src="dataItem.PRODUCT_PIC?dataItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
            </div>
            <div class='listCenter'>
              <div class="goodsNumame">
                <span>{{dataItem.PRODUCT_NAME}}</span>
              </div>
              <div v-if='dataItem.PRODUCT_DESC' class="specifications">
                <span>规格：</span>
                <span>{{dataItem.PRODUCT_DESC}}</span>
              </div>
              <div v-if='dataItem.UNIT_OF_MEASURE_STR' class="specifications">
                <span>单位：</span>
                <span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
              </div>
            </div>
            <div class='listLast'>
              <i class="iconfont icon-Untitled-" :id="index"  @click='jian'></i>
              <span @click='entryShow($event, number[GiftList[currentStrategyIdx].strategyOrderId][dataItem.PRODUCT_ID])' :id='index' >{{number[GiftList[currentStrategyIdx].strategyOrderId][dataItem.PRODUCT_ID]}}</span>
              <i class="iconfont icon-jia1" :id="index"  @click='jia'></i>
            </div>
          </div>
          <div class="bottleFee" v-if='dataItem.GIFT_TYPE == "WINE"'>
            <div>
              <div><span>{{dataItem.bottleName}}</span></div>
              <div><span>单价：</span><span>¥ {{dataItem.bottlePrice}}</span>&nbsp;&nbsp;&nbsp; <span>总价：</span><span>¥ {{parseFloat(parseFloat(dataItem.bottlePrice) * number[GiftList[currentStrategyIdx].strategyOrderId][dataItem.PRODUCT_ID]).toFixed(2)}}</span></div>
            </div>
          </div>
        </div>
      </div>
      <div v-else class="noOptionalQty">{{GiftList[currentStrategyIdx].strategyName}}没有可选数量，请联系管理员</div>
	  </div>
    <!--购物车  -->
    <div class='foot_cart'>
      <div class='foot_top'   @click="animat_cartShowIf" >
        <div class='totalNum'>
          <div><i class="iconfont icon-shoppingCart-full"></i></div>
          <div>已选：{{SumHasChosenNum}}</div>
          <div>剩余：{{parseInt(SumChoosableNum) - parseInt(SumHasChosenNum)}}</div>
        </div>
        <div class='submit' @click='submit' >提交</div>
      </div>
      <div class='foot_bottom'>
        <div class="OutermostGray"  @click="animat_cartShowIf"></div>
        <div class="footBo_move">
          <div class="footBottom_head">已选商品详情：</div>
        <div class="footBottom_fixedBox">
          <div class='cartGoodsList' v-for='(cartItem,carindex) in footCartListData'  :id="carindex"  :key='carindex' >
            <div class='cartListLeft'>{{ cartItem.PRODUCT_NAME }}</div>
            <div class='listLast'><span>{{cartItem.number}}</span></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--输入框  -->
    <div class='entryNumber' v-if="isHidden">
      <div class='entry_contaniner'>
        <div class='entry_contaniner_header'>请输入产品数量</div>
        <div class='clearNum'>
          <input id='autofocus_input' v-model='inputValue' type='number'></input>
          <i @click='clearNum' v-if="inputValue" class='iconfont icon-guanbi'></i>
        </div>
      <div class='entryBtn'>
        <div class='en_cancel' @click='cancel_entry'>取消</div>
        <div class='en_ensure' @click='ensure_entry'>确定</div>
      </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"添加赠品",//页面标题
        GiftList:[],//页面赠品总数据
        currentStrategyIdx:0,//当前点击的策略idx
        number: {},
        isHidden:false,//输入框是否出现
        footCartListData:[],//购物车数据
        totalNum:0,//总数
        inputValue: "",//在输入框中输入的产品数量
        inputValue_old: "",//在输入框中输入的产品数量前的值
        inputNum:0,//输入框idx
        SumChoosableNum:0,//可以选择的总数量（固定不变的总数量）
        SumHasChosenNum:0,//已经选择的总数量
        whoPush:'',//哪个下单页面跳转
        orderType:'',//下单类型
        visitId:0,//线路id
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      var that = this;

      // 哪个页面
      if(this.$route.query.whoPush){

        this.whoPush = this.$route.query.whoPush;
      }

      // 下单类型
      if(this.$route.query.orderType){
        this.orderType = this.$route.query.orderType;
      }

      // 建议订单 拜访线路id
      if(this.$route.query.visitId){
        this.visitId = this.$route.query.visitId;
      }

      this.GiftList = this.$store.state.OptionsAvailableGift;//页面赠品总数据

      if(this.$store.state.OptionsGiftChosenCart.length){
        this.footCartListData = JSON.parse(JSON.stringify(that.$store.state.OptionsGiftChosenCart));//购物车数据
      }
      
      // 初始化
      for(var i=0;i<that.GiftList.length;i++){

        that.number[that.GiftList[i].strategyOrderId] = {};

        // 计算已选数量
        that.GiftList[i].HasChosenNum = 0;

        // 计算已选总数量
        that.SumHasChosenNum = 0;

        // 固定不变的可选总数量
        that.SumChoosableNum +=  that.GiftList[i].afterGiftNum;

        for(var j = 0;j<that.GiftList[i].gift.length;j++){
          that.GiftList[i].gift[j].number = 0;
          that.number[that.GiftList[i].strategyOrderId][that.GiftList[i].gift[j].PRODUCT_ID] = 0;
        }
      }

      // 如果购物车有值
      if(that.footCartListData.length){

        if(that.$store.state.OptionsGiftNumberList){
          that.number = JSON.parse(JSON.stringify(that.$store.state.OptionsGiftNumberList));
        }
        
        for (var val in that.number) {

          for(var mk=0;mk<that.GiftList.length;mk++){

            if(val == that.GiftList[mk].strategyOrderId){

              for (var valItem in that.number[val]) {
                // 计算每个策略的已选数量
                that.GiftList[mk].HasChosenNum += that.number[val][valItem];

                for(var dj = 0;dj<that.GiftList[mk].gift.length;dj++){

                  // 给有值的产品赋值数量
                  if(valItem == that.GiftList[mk].gift[dj].PRODUCT_ID){
                    that.GiftList[mk].gift[dj].number = that.number[val][valItem];
                  }
                }
              }
              break;
            }
          }

          for (var valItemALL in that.number[val]) {
            // 计算已选总数量
            that.SumHasChosenNum += that.number[val][valItemALL];
          }
        }
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        var that = this;

        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          // 合并销售单为采购单

          that.$router.push({
            name:'OrderConfirm',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          })
          return;
        }

        if(that.whoPush == 'CheckInventory' && that.orderType == "SALE_OUT" ){
          // 离店/查看拜访 跳转来编辑
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            
            that.$router.go(-1);
            return;
          }
        }

        this.$router.push({
          name:'OrderConfirm',
          query:{
            whoPush:that.whoPush,
            orderType:that.orderType,
            visitId:that.visitId
          }
        })
    	},
      selectClass (e){

        var that=this;

        this.currentStrategyIdx = e.currentTarget.id;
      },
      //提交按钮
      submit () {
        
        var that = this;
        event.stopPropagation();

        this.$store.state.OptionsGiftChosenCart = JSON.parse(JSON.stringify(that.footCartListData));
        this.$store.state.OptionsGiftNumberList = JSON.parse(JSON.stringify(that.number));

        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          // 合并销售单为采购单

          that.$router.push({
            name:'OrderConfirm',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          })
          return;
        }

        if(that.whoPush == 'CheckInventory' && that.orderType == "SALE_OUT" ){
          // 离店/查看拜访 跳转来编辑
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            
            that.$router.go(-1);
            return;
          }
        }
        
        this.$router.push({
          name:'OrderConfirm',
          query:{
            whoPush:that.whoPush,
            orderType:that.orderType,
            visitId:that.visitId
          }
        })
      },
      // 列表数量  减
      jian(e){

        event.stopPropagation();
        var that = this;

        var currSIdx = that.GiftList[that.currentStrategyIdx];

        if (that.number[currSIdx.strategyOrderId][currSIdx.gift[e.currentTarget.id].PRODUCT_ID]>0){

          that.number[currSIdx.strategyOrderId][currSIdx.gift[e.currentTarget.id].PRODUCT_ID] --;

          // 计算已选数量
          currSIdx.HasChosenNum --;

          // 计算已选总数量
          that.SumHasChosenNum --;

          // 购物车中的数量减少
          for (var i = 0; i < that.footCartListData.length;i++){

            if (that.footCartListData[i].PRODUCT_ID == that.GiftList[that.currentStrategyIdx].gift[e.currentTarget.id].PRODUCT_ID){

              that.footCartListData[i].number --;

              // 购物车物品数量如果小于零，该物品消失
              if (that.footCartListData[i].number <= 0) {

                that.footCartListData.splice(i, 1);
              }
            }
          }
        }
    },
    // 列表  加
    jia(e){

      event.stopPropagation();
      var that = this,currSIdx = this.GiftList[that.currentStrategyIdx];

      //如果添加赠品数量超过可选数量，则提示信息出现
      if ((parseInt(currSIdx.afterGiftNum) - parseInt(currSIdx.HasChosenNum)) <= 0) {

        that.$alert("超出当前赠品可选数额", '提示', {
          confirmButtonText: '确定',
        })
        return;
      }

      that.number[currSIdx.strategyOrderId][currSIdx.gift[e.currentTarget.id].PRODUCT_ID] ++;

      // 计算已选数量
      currSIdx.HasChosenNum ++;

      // 计算已选总数量
      that.SumHasChosenNum ++;

      var footCartListData = this.footCartListData;

      var isRepeat=false;

      if (that.footCartListData.length==0){

        that.footCartListData[0] = currSIdx.gift[e.currentTarget.id];
        that.footCartListData[0].number = 1;
       
      }else{
        for (var j = 0; j < that.footCartListData.length; j++) {

          if (that.footCartListData[j].PRODUCT_ID == that.GiftList[that.currentStrategyIdx].gift[e.currentTarget.id].PRODUCT_ID ) {

            isRepeat = true;
            that.footCartListData[j].number ++;
          }
        }

        if (!isRepeat){

          that.footCartListData[that.footCartListData.length] = that.GiftList[that.currentStrategyIdx].gift[e.currentTarget.id];

          that.footCartListData[that.footCartListData.length-1].number = 1;
        }
      }
    },
      //购物车是否出现
      animat_cartShowIf(){

        let that = this;

        if($(".footBo_move").is(":animated")){
          return;
        }

      let footBottom_height= $(".footBo_move").css("height");

      // 如果购物车是呈现状态
      if( that.ShoppingCartDetailHide){

        $(".footBo_move").animate({"marginTop":0},500);
        $(".OutermostGray").fadeOut(500);
        that.ShoppingCartDetailHide = false;
      }else{

        $(".OutermostGray").fadeIn(500)
        that.ShoppingCartDetailHide = true;
        $(".footBo_move").animate({"marginTop": "-" + footBottom_height},500);
      } 
      },
      //输入产品数量输入框   取消按钮
      cancel_entry(){

      this.isHidden = false;
      },
      // 输入产品数量输入框   确认按钮
      ensure_entry() {
        var that=this;
        event.stopPropagation();

        // 判断输入的是否为数字，不是数字则return
        if (parseInt(that.inputValue).toString() == "NaN" || parseInt(that.inputValue)<0) {

            this.isHidden = false;
      　    return;
    　　}

        var currSIdx = that.GiftList[that.currentStrategyIdx];


        if(parseInt(that.inputValue) > (parseInt(currSIdx.afterGiftNum))){

          that.$alert('输入的数量不可大于剩余数量', '提示', {
            confirmButtonText: '确定',
          })
          return;
        }

        var footCartListData = this.footCartListData;
    
        var isRepeat = false;

        if (footCartListData.length){
         
            for (var i = 0; i < footCartListData.length; i++) {

              // 购物车存在该商品
              if (footCartListData[i].PRODUCT_ID == currSIdx.gift[that.inputNum].PRODUCT_ID) {

                // 购物车数据改变
                footCartListData[i].number = footCartListData[i].number + parseInt(that.inputValue) - that.inputValue_old

                isRepeat = true;
              }
            }

            // 购物车不存在改商品
            if (!isRepeat && that.inputValue != 0) {

              footCartListData[footCartListData.length] = currSIdx.gift[that.inputNum];

              footCartListData[footCartListData.length - 1].number = parseInt(that.inputValue);
            } 

            // 计算已选总数量
            that.SumHasChosenNum = parseInt(that.SumHasChosenNum) - that.number[currSIdx.strategyOrderId][currSIdx.gift[that.inputNum].PRODUCT_ID] + parseInt(that.inputValue);

            // 计算已选数量
            currSIdx.HasChosenNum = parseInt(currSIdx.HasChosenNum) - that.number[currSIdx.strategyOrderId][currSIdx.gift[that.inputNum].PRODUCT_ID] + parseInt(that.inputValue);
            
            //页面上number重新显示
            that.number[currSIdx.strategyOrderId][currSIdx.gift[that.inputNum].PRODUCT_ID] = parseInt(that.inputValue);

            // 输入产品数量如果小于零，该物品在购物车中消失
            if (that.inputValue == 0) {

              for (var j = 0; j < footCartListData.length; j++) {

                if (footCartListData[j].PRODUCT_ID == currSIdx.gift[that.inputNum].PRODUCT_ID) {

                  footCartListData.splice(j, 1);
                }
              }
            }
        }else{

          if (that.inputValue != 0 ){

            footCartListData[0] =  currSIdx.gift[that.inputNum];

            footCartListData[0].number = parseInt(that.inputValue);

            // 计算已选总数量
            that.SumHasChosenNum = parseInt(that.inputValue);

            // 计算已选数量
            currSIdx.HasChosenNum = parseInt(that.inputValue);

            that.number[currSIdx.strategyOrderId][currSIdx.gift[that.inputNum].PRODUCT_ID] = parseInt(that.inputValue);
          }
        }

        this.isHidden = false;
        this.footCartListData = footCartListData;
      },
      //购物车内产品数量点击弹出  输入产品数量输入框
      entryShow(e, v){

        event.stopPropagation();

        var that=this;

        this.clearNum();
        this.isHidden = true;
        this.inputNum = e.currentTarget.id;//点击的输入框的idx

        // 自动聚集
        that.$nextTick(function(){
          that.inputValue_old = v
          if(v != 0){
            $("#autofocus_input").val(v)
          }
          $("#autofocus_input").focus()
          $("#autofocus_input").select()
        })
      },
      // 清除input框数据
      clearNum(){
        this.inputValue = "";
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .AddFreeGift{
    overflow-x: hidden;
    height: 100%;
    background-color:@GrayBackground;
    .goodsHead{
      width: 100%;
      line-height: 80/50rem;
      padding:0 20/50rem;
    }
    .calssType{
      margin-bottom: 50/50rem;
      .classList{
        width: 100%;
        line-height: 45/50rem;
        background-color: #fff;
        padding: 20/50rem 20/50rem;
        box-sizing:border-box;
        margin-bottom: 5/50rem;
        display: flex;
        &>i{
          margin-right: 20/50rem;
          font-size: 38/50rem;
          color: @PublicColor;
        }
        &>span{
          flex-grow: 1;
        }
        &>div{
          min-width: 170/50rem;
          margin-right: 10/50rem;
          text-align: center;
        }
      }
    }
    .gift{
      width: 100%;
      overflow-x: hidden;
      box-sizing:border-box;
      padding-bottom: 150/50rem;
      &>.selectGoodsList{
        width: 100%;
        background-color:#fff;
        margin-bottom: 5/50rem;
        // border-bottom: 3/50rem solid #f4f4f4;
        .selectGoodsListItem{
          width: 100%;
          box-sizing:border-box;
          font-size: 26/50rem;
          padding: 10/50rem 0;
          display: flex;
          .LeftIcon{
            width:120/50rem;
            position:relative;
            flex-shrink:0;
            img{
              width: 90/50rem;
              position:absolute;
              top:50%;
              left: 50%;
              transform: translate(-50%,-50%);
            }
          }
          .listCenter{
            flex-grow: 1;
            line-height: 50/50rem;
            .goodsNumame{
              font-weight: 600;
              font-size: 28/50rem;
            }
          }
          .listLast{
            line-height:100/50rem; 
          }
        }
      }
    }
    // 加减按钮样式
    .listLast{
      width:185/50rem;
      line-height:75/50rem; 
      text-align: center;
      margin-right: 20/50rem;
      overflow: hidden;
      float: right;
      &>span{
        font-size: 28/50rem;
        width:80/50rem;
        overflow: hidden;
        float: left;
        text-align: center;
      }
      &>i{
        width: 45/50rem;
        font-size: 50/50rem;
        float: left;
        color: @PublicColor;
      }
    }
    .noOptionalQty{
      width: 100%;
      padding:0 20/50rem;
      box-sizing:border-box;
      margin-top: 200/50rem;
      text-align: center;
    }
    /* 下单 购物车 */
    .foot_cart{
      .foot_top{
        .totalNum{
          font-size: 28/50rem;
          line-height: 100/50rem;
          &>div{
            &:first-child{
              width: 100/50rem;
            }
            width: 200/50rem;
          }
        }
      }
      .foot_bottom{
        .footBo_move{
          .footBottom_fixedBox{
            .cartGoodsList{
              width: 100%;
              display: flex;
              .cartListLeft{
                flex-grow: 1;
                height: 78/50rem;
                line-height: 78/50rem;
                float: left;
              }
              .listLast{
                margin-right: 0;
                min-width: 80/50rem;
                &>span{
                  float: right;
                }
              }
            }
          }
        }
      }
    }
  }
</style>


