<template>
  <div class="CheckSchedule">
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class='progressContainer haveHeadcontainer'>
      <div v-for='(item,idx) in data'  :id="idx"  :key='idx'>
      	<i class='iconfont icon-1'></i>
      	<span>{{item.types}}</span>
      	<span>{{item.time}}</span>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:'查看进度',
        data:[],//页面数据
        orderId:'',//订单id
      }
    },
    components:{
  	  componentHeader,
  	},
  	created(){
      
      var that = this;
      
      if(this.$route.query.orderId){
        this.orderId = this.$route.query.orderId;
      }

      var postData = {
        "orderId":this.orderId,
      }

      this.httpRequest("GetTMSOrderSpeed.do",postData,function(res){

        that.data = res.data;
      })
  	},
  	methods:{
	    //返回上一页
  	  f_Fanhui(){
  	  	this.$router.go(-1);
  	  },
	  }
  }
</script>
<style lang="less" scoped>
  .CheckSchedule{
  	.progressContainer{
  	  width:100%;
  	  height:100%;
  	  i{
  	  	color:#09BB07;
  	  	font-size: 45/50rem;
  	  	font-size: 40/50rem;
    		height: 40/50rem;
    		float: left;
    		padding: 0 30/50rem;
    		box-sizing: border-box;
  	  }
      &>div{
        width:100%;
        height: 90/50rem;
        border-bottom: 1/50rem solid #E8E8E9;
        overflow: hidden;
        line-height: 90/50rem;
        font-size: 30/50rem;
        &>span{
  	      float: left;
  	      margin-right: 30/50rem;
  	    }
      }
  	}
  }
</style>


