<template>
  <div class="OrderConfirm">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class="DeliveryInfo">
        <div class='infoHead_'>发货信息</div>
        <div class='infoContainer_'>
          <div><span>发货客户</span><span>{{DeliveryInfo.CUSTOMER_NAME}}</span></div>
          <div><span>发货人名</span><span>{{DeliveryInfo.CONTACT_NAME}}</span></div>
          <div><span>联系方式</span><span>{{DeliveryInfo.CUSTOMER_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='DeliveryInfo.CUSTOMER_TEL' @click="callPhone(DeliveryInfo.CUSTOMER_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div><span>发货地址</span><span>{{DeliveryInfo.CUSTOMER_ADDRESS }}</span></div>
        </div>
      </div>
      <div class="ReceiveInfo">
        <div class='infoHead_'>收货信息</div>
        <div class='infoContainer_'>
          <div><span>收货客户</span><span>{{ReceiptInfo.CUSTOMER_NAME}}</span></div>
          <div><span>收货人名</span><span>{{ReceiptInfo.CONTACT_NAME}}</span></div>
          <div><span>联系方式</span><span>{{ReceiptInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='ReceiptInfo.CONTACT_TEL' @click="callPhone(ReceiptInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div><span>收货地址</span><span>{{ReceiptInfo.CUSTOMER_ADDRESS}}</span></div>
        </div>
      </div>
      <div class='orderInfos'>
        <div class='infoHead_'>订单信息</div>
        <div class='orderList' v-for='(dataItem,idx) in submitArr'  :id="idx"  :key='idx'>
          <div class='LeftIcon'><img :src="dataItem.PRODUCT_PIC?dataItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
          </div>
          <div class='listRight'>
            <div class="PRODUCT_NAME"><span>{{dataItem.PRODUCT_NAME}}</span></div>
            <div class="listRightBottom">
              <div class="listRightBottomLeft">
                <div>
                  <span>原价：</span>
                  <span class="listRight_rmb">￥</span>
                  <span class="listRight_rmbNUM">{{dataItem.UNIT_PRICE}}</span>
                </div>
                <div>
                  <span>数量：</span>
                  <span class="listRight_rmbNUM">{{dataItem.number}} </span>
                  <span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
                </div>
              </div>
              <div class="listRightBottomRight">
                <div v-if='dataItem.promotionStrategy=="TJCL"' class="priceAdjust">
                  <span>现价：</span>
                  <i class="iconfont icon-Untitled-" :id="idx"  @click='jian'></i>
                  <div @click='entryShow' :id='idx' class="priceAdjust_price"><span class="listRight_rmb">￥</span><span class="listRight_rmbNUM">{{dataItem.priceInit}}</span></div>
                  <i class="iconfont icon-jia1" :id="idx"  @click='jia'></i>
                </div>
                <div v-else>
                  <span>现价：</span>
                  <span class="listRight_rmb">￥</span>
                  <span class="listRight_rmbNUM">{{dataItem.DIS_PRICE}}</span>
                  <i v-if='$store.state.businessInfo.isEdit == "T"' @click="modify_price(dataItem)" class="iconfont icon-xie"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='GiftInfo' v-if='giftInfo_IfShow'>
        <div class='infoHead_'>赠品</div>
        <div class="GiftContainer">
          <div class="SpecifyGiftList" v-if='SpecifyGift.length'>
            <div class='haveGift' v-for='(itemGift,idxGift) in SpecifyGift'  :id="idxGift"  :key='idxGift'>
              <div>
                <div class='LeftIcon'><img :src="itemGift.PRODUCT_PIC?itemGift.PRODUCT_PIC:'static/img/Order/goods.png'" alt=""></div>
                <div class='listCenter'>
                  <div class="PRODUCT_NAME"><span>{{itemGift.PRODUCT_NAME}}</span></div>
                  <div v-if='itemGift.PRODUCT_DESC'><span>规格：</span><span>{{itemGift.PRODUCT_DESC}}</span></div>
                </div>
                <div class='gift_right'>
                  {{itemGift.giveNum}} {{itemGift.UNIT_OF_MEASURE_STR}}
                </div>
              </div>
              <div class="bottleFee" v-if='itemGift.GIFT_TYPE == "WINE"'>
                <div>
                  <div><span>{{itemGift.bottleName}}</span></div>
                  <div><span>单价：</span><span>¥ {{itemGift.bottlePrice}}</span>&nbsp;&nbsp;&nbsp; <span>总价：</span><span>¥ {{parseFloat(parseFloat(itemGift.giveNum) * parseFloat(itemGift.bottlePrice)).toFixed(2)}}</span></div>
                </div>
              </div>
            </div>
          </div>
          <div class="OptionsGiftList"v-if='OptionsGiftChosenCart.length'>
            <div class='haveGift' v-for='(OpItemGift,OpIdxGift) in OptionsGiftChosenCart'  :id="OpIdxGift"  :key='OpIdxGift'>
              <div>
                <div class='LeftIcon'><img :src="OpItemGift.PRODUCT_PIC?OpItemGift.PRODUCT_PIC:'static/img/Order/goods.png'" alt=""></div>
                <div class='listCenter'>
                  <div class="PRODUCT_NAME"><span>{{OpItemGift.PRODUCT_NAME}}</span></div>
                  <div v-if='OpItemGift.PRODUCT_DESC'><span>规格：</span><span>{{OpItemGift.PRODUCT_DESC}}</span></div>
                </div>
                <div class='gift_right'>
                  {{OpItemGift.number}} {{OpItemGift.UNIT_OF_MEASURE_STR}}
                </div>
              </div>
              <div class="bottleFee" v-if='OpItemGift.GIFT_TYPE == "WINE"'>
                <div>
                  <div><span>{{OpItemGift.bottleName}}</span></div>
                  <div><span>单价：</span><span>¥ {{OpItemGift.bottlePrice}}</span>&nbsp;&nbsp;&nbsp; <span>总价：</span><span>¥ {{parseFloat(parseFloat(OpItemGift.number) * parseFloat(OpItemGift.bottlePrice)).toFixed(2)}}</span></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class='noneGift'>
          <span v-if='!SpecifyGift.length&&!OptionsAvailableGift.length'>无赠品</span>
          <div v-if='OptionsAvailableGift.length' class="addGift"><span @click='_addGift'>{{OptionsGiftChosenCart.length?'重新添加':'添加赠品'}}</span></div>
        </div>
      </div>

      <div class='total'>
        <div class='infoHead_'>总计</div>
          <div class="infoContainer_">
            <div><span>总数</span><span>{{totalNum}}</span></div>
            <div v-if="sumMjPrice_cru>0"><span>已优惠</span><span>¥{{sumMjPrice_cru}}</span></div>
            <div  v-if='giftInfo_IfShow'><span>付款</span><span>¥{{totalPayment}}</span></div>
            <div class="pay_typeInfo"  v-if='giftInfo_IfShow'><span>付款方式</span><span>{{PAY_TYPE}}
          <!--     <el-select  v-model="PAY_TYPE"   placeholder="请选择">
                <el-option
                  v-for="item in pay_typeList"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name">
                </el-option>
            </el-select> -->
            </span></div>
            <div v-if='MJ_hidden'><div>促销策略</div><div>{{MJ_REMARK}}</div></div>
            <div v-if='MJ_hidden'><div>满减总计</div><div>{{MJ_PRICE}}</div></div>
            <!-- <div>实际付款：<span>¥{{ACT_PRICE}}</span></div> -->
          </div>
      </div>
      <div class='arrDate' v-if='giftInfo_IfShow'>
          <div class='infoHead_'>选择送货时间</div>
          <el-date-picker
            v-model="REQUEST_DELIVERY" :editable='false' 
            type="date"
            placeholder="选择日期时间"
            align="right"
            :picker-options="pickerOptions"
            format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd">
          </el-date-picker>
      </div>
      <textarea class="Remark" placeholder='请输入备注信息' v-model="REMARK_VALUE" maxlength="-1"></textarea>
    </div>
    <div class="BtnComponent bottomBtn">
      <div class='onlyBtn' @click='orderSubmit'>确认</div>
    </div>

    <div v-if="ifTips" class="msg_tips">
      <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
      </div>
    </div>

    <!--输入框  -->
    <div class='entryNumber' v-if='isHidden'>
      <div class='entryContaniner'>
        <div class="entry_contaniner_header">请输入产品价格</div>
        <div>可调整价格上限：<span>{{submitArr[inputNum].priceMax}}</span></div>
        <div>可调整价格下限：<span>{{submitArr[inputNum].priceMin}}</span></div>
        <input  v-model="inputValue" type='number'></input>
        <div class='entryBtn'>
          <div class='en_cancel' @click='cancel_entry'>取消</div>
          <div class='en_ensure' @click='ensure_entry'>确定</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    data(){
      return{
        headerText:'订单确认',
        submitArr: [],//订单信息列表
        giftInfo_IfShow:true,//赠品一栏是否显示
        SpecifyGift:[],//指定赠品
        OptionsAvailableGift:[],//可选择赠品（手动赠品总数据）
        OptionsGiftChosenCart:[],//手动添加的赠品（已选择的）
        MJ_hidden: false,//满减策略信息 是否显示
        REMARK_VALUE: '',//客户填写的备注信息内容
        isHidden: false,//有单价策略调整时，调整弹出框是否显示
        inputValue: '',//有单价策略调整时，调整弹出框的值
        inputNum: 0,//有单价策略调整时，调整产品的idx
        totalNum:0,//总数量
        totalPrice:0,//总价格
        totalPayment:0,//总付款价格
        sumMjPrice_cru:0,//总优惠价格（优惠了多少钱）
        ifTips:false,//提示信息是否显示
        tips_Msg:"",//提示信息
        whoPush:"",//哪个页面
        orderType:'',//下单类型
        storeId:0,//门店ID
        visitId:0,//拜访id
        pickerOptions: {
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '后天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 2);
              picker.$emit('pick', date);
            }
          }]
        },
        REQUEST_DELIVERY:'',//送货时间
        PAY_TYPE:'',//付款方式
        // pay_typeList:[{'name':'先款后货'},{'name':'月付'},{'name':'货到付款'},{'name':'在线支付'}]
      }
    },
    components:{
      componentHeader,
    },
    mounted(){

      window.history.pushState(null, null, document.URL)
      window.addEventListener("popstate", this.onBrowserBack, false)
    },
    destroyed() {

      window.removeEventListener("popstate", this.onBrowserBack, false)
    },
    created(){

      var that = this,BackBottle = this.$store.state.BackBottle;

      this.ReceiptInfo = BackBottle.ConsigneeData_selected;//收货人
      
      this.DeliveryInfo = BackBottle.DeliveryAddr_selected;//发货人
      
      this.submitArr = BackBottle.g_submitArr; //页面传过来的购物车

      this.REQUEST_DELIVERY = this.$store.state.ORDER_REQUEST_DELIVERY;//要求送货时间
      this.REMARK_VALUE = this.$store.state.ORDER_REMARK_VALUE;//下单备注

      if(this.$store.state.OptionsGiftChosenCart){
        this.OptionsGiftChosenCart = that.$store.state.OptionsGiftChosenCart;//手动添加的赠品（选择后的）
      }
      
      console.log(this.submitArr)

      this.totalNum = BackBottle.totalNum;//总数量
      
      // 哪个页面
      if(this.$route.query.whoPush){
          this.whoPush = this.$route.query.whoPush;
      }

      // 下单类型
      if(this.$route.query.orderType){
          this.orderType = this.$route.query.orderType;
      }

      // 拜访id
      if(this.$route.query.visitId){

        this.visitId = this.$route.query.visitId;
      }

      // 计算总金额 正常商品价格 (其他出入库、调拨出入显示的总金额，非下单)
      for (var i = 0; i < that.submitArr.length; i++) {

          let mx = that.submitArr[i];
          that.totalPrice += parseFloat(mx.UNIT_PRICE) * mx.number;
      }
      this.totalPrice = this.totalPrice.toFixed(2);

      // 促销策略：采购单、销售单、建议订单
      if((that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN') || (that.whoPush == 'SalesOrder' && that.orderType == 'SALE_OUT') || (this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){

        //获取促销策略信息
        var OrderDetails=[],SalesPromotion_consigneeId=0;
        for (var m = 0; m < that.submitArr.length; m++) {
          OrderDetails[m] = {
            "UNIT_PRICE":that.submitArr[m].UNIT_PRICE,//原价
            "DIS_PRICE":that.submitArr[m].DIS_PRICE,//优惠后的价格
            "PRODUCT_ID":that.submitArr[m].PRODUCT_ID,//产品id
            "PO_QTY":that.submitArr[m].number,//下单数量
            "PO_UNIT":that.submitArr[m].UNIT_OF_MEASURE,//下单单位 英文 
            "PO_UNIT_STR":that.submitArr[m].UNIT_OF_MEASURE_STR,//下单单位 中文
            "FL_PRICE":that.submitArr[m].FL_PRICE,//返利
          }
        }

        // 销售下单
        if(that.whoPush == 'SalesOrder' && that.orderType == 'SALE_OUT'){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//收货id 销售单门店id 
        }
         // 采购下单
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN'){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//收货id  采购单ospid
        }
        // 建议下单
        if((that.whoPush == 'CheckInventory' && that.orderType == 'SALE_OUT')||(that.whoPush == 'HistoryVisit' && that.orderType == 'SALE_OUT')){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//收货id 销售单门店id 
        }

        var SalesPromotion_postData = {
          "businessId": that.$store.state.businessInfo.businessId,
          "consigneeId": SalesPromotion_consigneeId,//收货id 销售单门店id 
          "OrderDetails":OrderDetails,
        }

        // 合并销售单
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){

           SalesPromotion_postData.outOrderIds = that.$store.state.SumMergeOrderList;//销售出库单合集数组
        }

        console.log(SalesPromotion_postData)

        that.httpRequest("simulatedOrder.do",SalesPromotion_postData,function(promotionres){

          if(!that.$store.state.OptionsAvailableGift.length){
            that.submitArr = promotionres.data.detail; //页面传过来的购物车
          }
          
          // GIVE  指定赠品
          // STRAGY  可选择赠品
          that.SpecifyGift = promotionres.data.GIVE;
          that.OptionsAvailableGift = promotionres.data.STRAGY;
          that.PAY_TYPE = promotionres.data.PAY_TYPE;//付款方式
          
          that.totalNum =0;
          for(var gj=0;gj<that.submitArr.length;gj++){

            that.totalNum += parseInt(that.submitArr[gj].poQty);
          }

          for(var fh=0;fh < promotionres.data.GIVE.length;fh++){
            that.totalNum += parseInt(promotionres.data.GIVE[fh].giveNum);
          }

          for(var as=0;as < that.OptionsGiftChosenCart.length;as++){
            that.totalNum += parseInt(that.OptionsGiftChosenCart[as].number);
          }
          that.CalculateTotalPrice();
        })
      }else{

        // 判断赠品一栏是否显示  其他出入库、退货、调拨不用显示
        that.giftInfo_IfShow = false;
      }
    },
    methods:{
      onBrowserBack() {
        
        this.f_Fanhui()
      },
      //返回上一页 
      f_Fanhui(){

        var that = this;
        this.$store.state.OptionsGiftChosenCart = [];//手动选择赠品购物车数据
        this.$store.state.OptionsAvailableGift = [];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftNumberList = {};//添加赠品页面 number二维json 
        this.$store.state.ORDER_REQUEST_DELIVERY='';//要求送货时间
        this.$store.state.ORDER_REMARK_VALUE='';//下单备注
        // 部分退货
        if(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              transferOutId:that.$route.query.transferOutId
            }
          })
          return;
        }

        // 调拨
        if(that.whoPush == 'InventoryManage' && that.orderType == "TRANSFER" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              IFreceORdeliv_transition:that.$route.query.IFreceORdeliv_transition
            }
          })
          return;
        }
        // 建议订单 历史记录
        if(that.whoPush == 'HistoryVisit' && that.orderType == "SALE_OUT" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              orderConfirmattached:'orderConfirm',//传给选择订单页面的参数 判断路由跳转
            }
          })
          return;
        }

        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          // 合并销售单为采购单

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          })
          return;
        }
        // 离店/查看拜访 跳转来编辑
        if(that.whoPush == 'CheckInventory' && that.orderType == 'SALE_OUT'){
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            that.$router.go(-1);
            return;
          }
        }

        this.$router.push({
          name:'ChooseGoods',
          query:{
            whoPush:that.whoPush,
            orderType:that.orderType
          }
        })
      },
        //最终提交订单  确认按钮 点击事件
      orderSubmit () {
        var that = this;
        var submitArr = this.submitArr;
        var OrderDetails=[];

        // 销售出库、其它出库、销售退货入库

        // this.orderType == "SALE_OUT" || 销售出库

        // || this.orderType == "SALE_RETURN_IN"  销售退货入库

        // 其他出库  ||  销售退货（针对门店） || 调拨
        if( (this.whoPush == 'InventoryManage' && this.orderType == "OTHER_OUT") || (this.whoPush == 'InventoryManage' && this.orderType == "SALE_RETURN_IN") || (this.whoPush == 'InventoryManage' && this.orderType == "TRANSFER") ||(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART" )) {

            //最后提交的数组中  正常添加的商品
            for (var m = 0; m < submitArr.length; m++) {
              OrderDetails[m] = {
                "productId": "" + submitArr[m].PRODUCT_ID+"",
                "pirchaseQuantity":submitArr[m].number,//總數量
                "unitPrice":submitArr[m].UNIT_PRICE,
                "payment":submitArr[m].UNIT_PRICE,
                "amount":submitArr[m].number * submitArr[m].UNIT_PRICE?submitArr[m].number * submitArr[m].UNIT_PRICE:0,
                "unit":submitArr[m].UNIT_OF_MEASURE,//英文单位
              }
            }

            // 此处判断原因：其他出入库、没有发/收人
            var consignorId = 0,consigneeId = 0;
            if(that.DeliveryInfo.CUSTOMER_ID){
                // 发货人ID
                consignorId = that.DeliveryInfo.CUSTOMER_ID;
            }
            if(that.ReceiptInfo.CUSTOMER_ID){
                // 收货人id
                consigneeId = that.ReceiptInfo.CUSTOMER_ID;
            }

            // 调拨  有usersId这个字段，其他下单类型这个字段传0
            var usersId = 0;//车销人id
            if(that.whoPush == 'InventoryManage' && that.orderType == "TRANSFER"){

              usersId = that.$store.state.userInfo.user_id;

              // false 时发货人为osp  true时发货人为车销
              if(that.$route.query.IFreceORdeliv_transition){
                
                usersId = consignorId;
                consignorId = 0;// 发货人ID
              }else{
                usersId = consigneeId;
                consigneeId = 0;// 收货人id
              }
            }

            var transferOutId = '';//销售id,只有销售部分退货（指定订单）用得到
            if(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART"){
              transferOutId = that.$route.query.transferOutId;
            }

            var postData = {
              "appUsersId": that.$store.state.userInfo.user_id,//下单人
              "usersId":usersId,//车销人id,只有调拨用得到，其他下单类型此处传0
              "businessId": that.$store.state.businessInfo.businessId,
              "consignorId":consignorId,// 发货人ID
              "consigneeId": consigneeId,//收货人id
              "transferOutType":that.orderType,//下单类型
              "pirchaseQuantity":that.totalNum,//总数量
              "amount":that.totalPrice?that.totalPrice:0,
              "TransferOutDetails":OrderDetails,
              "visitId":visitId,//拜访id，只有建议下单用得到，其他传0
              "transferOutId":transferOutId,//销售id,只有销售部分退货（指定订单）用得到,其他下单类型传空''
              "remark": that.REMARK_VALUE,
            }

            this.httpRequest("saveTransferOut.do",postData,function(res){

              that.$store.state.BackBottle={
                g_submitArr:[],
                totalNum:0,
