      },
      //当有单价调整策略时，减
      jian (e){

        var that = this;

        if ((parseFloat(that.submitArr[e.currentTarget.id].priceInit) -1 ).toFixed(2) < parseFloat(that.submitArr[e.currentTarget.id].priceMin)){

          that.submitArr[e.currentTarget.id].priceInit = parseFloat(that.submitArr[e.currentTarget.id].priceMin).toFixed(2);

        }else{
          that.submitArr[e.currentTarget.id].priceInit =parseFloat(parseFloat(that.submitArr[e.currentTarget.id].priceInit) - 1).toFixed(2);
        }

        that.CalculateTotalPrice();
      },
      //当有单价调整策略时，加
      jia (e) {

        var that = this;

        if ((parseFloat(that.submitArr[e.currentTarget.id].priceInit) +1) > parseFloat(that.submitArr[e.currentTarget.id].priceMax)){

          that.submitArr[e.currentTarget.id].priceInit = parseFloat(that.submitArr[e.currentTarget.id].priceMax).toFixed(2);
        }else{
          that.submitArr[e.currentTarget.id].priceInit =parseFloat(parseFloat(that.submitArr[e.currentTarget.id].priceInit) + 1).toFixed(2);
        }

        that.CalculateTotalPrice();
      },
      //当有单价调整策略时点击价格  价格调整弹出框出现 
      entryShow (e){
        console.log(e.currentTarget.id)
        this.isHidden = true;
        this.inputNum = e.currentTarget.id;//点击的输入框的IDX
      },
      //当有单价调整策略时点击价格  价格调整弹出框确定按钮
      ensure_entry (){

        var that = this;

        // 判断输入的是否为数字，不是数字则return
        if (parseInt(that.inputValue).toString() == "NaN" || parseInt(that.inputValue)<0) {

          this.isHidden = false;
           return;
        }

        if(parseFloat(that.inputValue) >= parseFloat(that.submitArr[that.inputNum].priceMax)){

          that.submitArr[that.inputNum].priceInit = parseFloat(that.submitArr[that.inputNum].priceMax).toFixed(2);
          that.isHidden = false;
          that.CalculateTotalPrice();
          return;
        }

        if(parseFloat(that.inputValue) <= parseFloat(that.submitArr[that.inputNum].priceMin)){

          that.submitArr[that.inputNum].priceInit = parseFloat(that.submitArr[that.inputNum].priceMin).toFixed(2);
          that.isHidden = false;
          that.CalculateTotalPrice();
          return;
        }

        that.submitArr[that.inputNum].priceInit = parseFloat(that.inputValue).toFixed(2);
        that.isHidden = false;

        that.CalculateTotalPrice();
      },
      //当有单价调整策略时点击价格  价格调整弹出框取消按钮
      cancel_entry  () {
        this.isHidden = false;
      },
      // 计算总付款价和总优惠
      CalculateTotalPrice(){

        var sumPayPrice = 0,//订单的付款价总价
        sumUNIT_PRICE = 0,//订单的原价总价
        that = this,
        payment = 0;

        for (var mn = 0; mn < that.submitArr.length; mn++) {

          // 如果有调价策略  付款价用调价后的价格
          if(that.submitArr[mn].promotionStrategy=="TJCL"){

            payment = that.submitArr[mn].priceInit;

          }else{
            payment = that.submitArr[mn].DIS_PRICE;
          }

          sumPayPrice += (parseFloat(payment) * parseInt(that.submitArr[mn].number));//订单的付款价总价
       
          sumUNIT_PRICE += (parseFloat(that.submitArr[mn].UNIT_PRICE) * parseInt(that.submitArr[mn].number));//订单的原价总价
        }

        // 计算总价格  空瓶费  指定赠品 
        for(var n = 0; n < that.SpecifyGift.length; n++){

          if(that.SpecifyGift[n].GIFT_TYPE == "WINE"){

            let mf = that.SpecifyGift[n];

            sumPayPrice = parseFloat(sumPayPrice + parseFloat(mf.bottlePrice) * mf.giveNum).toFixed(2);
          }
        }

        // 计算总价格  空瓶费  手动选择赠品
        for(var n = 0; n < that.OptionsGiftChosenCart.length; n++){

          if(that.OptionsGiftChosenCart[n].GIFT_TYPE == "WINE"){

            let mr = that.OptionsGiftChosenCart[n];

            sumPayPrice = parseFloat(sumPayPrice+ parseFloat(mr.bottlePrice) * mr.number).toFixed(2);
          }
        }

        that.totalPayment = parseFloat(sumPayPrice).toFixed(2);//订单的付款价总价
        that.sumMjPrice_cru = parseFloat(sumUNIT_PRICE - sumPayPrice).toFixed(2)>=0?parseFloat(sumUNIT_PRICE - sumPayPrice).toFixed(2):0;//订单总共优惠了多少钱
      },
      // 修改价格
      modify_price(item){

        var that = this

        var inputValue = item.DIS_PRICE

        this.$prompt('请输入价格', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^\d+(\.\d+)?$/,
          inputErrorMessage: '请输入数字',
          inputValue: inputValue,
          inputType: 'number',
          closeOnClickModal: false
        }).then(({ value }) => {

          item.DIS_PRICE = value
          that.CalculateTotalPrice()
        }).catch(() => { })

        // 自动聚集
        this.$nextTick(function(){

          $(".el-message-box__input .el-input__inner").select()
        })
      },
    }
  }
</script>
<style lang="less" scoped>
