<template>
  <div class="ProductSalesRanking">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

    <div class="container haveHeadcontainer">
     
      <div class="DateDisplay">
        <div>
          <el-date-picker v-model="StartDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"  @change="ChangeStartDate" :editable="false"></el-date-picker>
        </div>
        <span>至</span>
        <div>
          <el-date-picker v-model="EndDate" type="date" placeholder="截止日期"  value-format="yyyy-MM-dd"  @change="ChangeEndDate" :editable="false"></el-date-picker>
        </div>
      </div>  
      <div class="ScreeningNav" >
        <div>
          <div class="StateSelect" @click='SelectBoxShow("产品品牌")'>
            <span>{{productType}}</span>
            <i class="iconfont icon-xiangxia"></i>
          </div>
          <div class="ConditionsSelectBtn">品牌筛选</div>
        </div>
      </div>
      <div class="reportSum" v-if='!noDataShow'>
        <div class="title"><span>品牌销售汇总 > </span><span>{{productType}}</span></div>
        <div class="QtySum"><span>销售数量：</span><span>{{orderQtySum}}</span></div>
        <div class="amountSum"><span>销售金额：</span><span>{{amountSum}}</span></div>
      </div> 
      <div class="reportInfo">
        <div class="ProductSalesRankingItem"  v-for='(dataItem,index) in ProductSalesRankingData'  :id="index"  :key='index'>
          <div class="CustomerName"><span>{{dataItem.ospName}}</span></div>
          <div><span>产品名称：</span><span>{{dataItem.productName}}</span></div>
          <div><span>销售数量：</span><span>{{dataItem.orderQty}}</span></div>
          <div><span>销售金额：</span><span>{{dataItem.amount}}</span></div>
        </div>
      </div>

      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>

    </div>
    <componentSelectBox v-if="SelectBoxHide" v-bind:SelectBoxList='SelectBoxList' @selectCancel='selectCancel' @selectedItem='selectedItem' :BouncedName='BouncedName'></componentSelectBox>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import $ from "jquery"
  export default{
    data(){
      return{
        headerText:"产品销量排行榜",//页面标题
        ProductSalesRankingData:[],
        StartDate:'',
        EndDate:'',
        noDataShow:false,//页面没有数据图标
        SelectBoxHide:false,//选择弹出框 是否显示
        productType:'全部',//品牌
        orderQtySum:'',//销售数量汇总
        amountSum:'',//销售金额汇总
        SelectBoxList:[],
      }
    },
    components:{
      componentHeader,
      componentSelectBox
    },
    created(){
      this.StartDate = this.getNowTime('getMonthFirstDay'); 
      this.EndDate = this.getNowTime('getDate'); 

      var that = this;
      //获取产品品牌
      var TypeData = {
        "businessId":that.$store.state.businessInfo.businessId,
        "appUsersType":that.$store.state.G_AppOrWX,//判断是app还是公众号
        "appUsersId": that.$store.state.userInfo.user_id,//用户id
      }
      // 请求商品品牌和分类
      that.httpRequest("searchProductType.do",TypeData,function(res){

        if(res.data.length){
          for(var i = 0; i < res.data.length; i++){
            if(res.data[i].PRODUCT_BRAND == "近期")
            {
              res.data.splice(i, 1);
            }
          }
          for(var j = 0; j < res.data.length; j++){
            var item = {"name" : res.data[j].PRODUCT_BRAND}
            that.SelectBoxList.push(item)
          }
        }
        that.productType = that.SelectBoxList[0].name;
      })
      this.GetProductSalesRankingData();

    },
    methods:{
      // 返回上一页
      f_Fanhui(){
       this.$router.go(-1);
      },

       ChangeStartDate(Value){
        this.StartDate = Value; 
        this.GetProductSalesRankingData();
      }, 

      ChangeEndDate(Value){
        if(this.StartDate > Value ){
          this.$alert('截止时间不可小于开始时间', '提示', {
            confirmButtonText: '确定',
          })
          this.EndDate = this.getNowTime('getDate'); 
        }else{
          this.EndDate = Value; 
          this.GetProductSalesRankingData();
        }
      },
      // 选择产品品牌 弹出框显示
      SelectBoxShow(){
        this.BouncedName = "产品品牌";
        this.SelectBoxHide = true;
      },
      // 选择状态弹出框 消失
      selectCancel(){
        this.SelectBoxHide = false;
      },
      // 选中某个状态
      selectedItem(index){
        this.productType = this.SelectBoxList[index].name;
        this.SelectBoxHide = false;
        this.GetProductSalesRankingData();
      },
      // 获取产品销量信息
      GetProductSalesRankingData(){
        var that = this;
        var storesData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "StartDate":that.StartDate,
          "EndDate":that.EndDate,
          "productType":that.productType,
        }
        // 获取销售业绩信息
        that.httpRequest("productSalesPerformanceRanking.do",storesData,function(saleRes){

          that.ProductSalesRankingData = saleRes.data;
        
          that.orderQtySum = 0;
          that.amountSum = 0;
          if(that.ProductSalesRankingData.length){
            that.noDataShow = false; 
            for(var i=0; i < that.ProductSalesRankingData.length;i++){
              that.orderQtySum += that.ProductSalesRankingData[i].orderQty;
              that.amountSum += that.ProductSalesRankingData[i].amount;
            }
          }else{
            that.noDataShow = true;
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ProductSalesRanking{
    overflow-x: hidden;
    .container{
      .DateDisplay{
        height: 100/50rem;
        line-height: 100/50rem;
        padding:0 60/50rem;
        background-color:#EEEFF2;
         &>div:first-child{
          float: left;
          width: 260/50rem;
         }
         span{ 
          float: left;
          width: 110/50rem; 
          text-align: center;
        }
         &>div:last-child{
           width: 260/50rem;
           float: right;
        }
      }
      .ScreeningNav{
         height: 80/50rem; 
         top: 190/50rem;
      }
      .reportSum{
        margin-top:100/50rem;
        padding-left: 30/50rem;
        color: #555;
        height: 125/50rem;
        border-bottom: 10/50rem solid #EEEFF2;
        .title{
          font-size: 28/50rem;
          font-weight: 550;
          line-height: 60/50rem;
          span:last-child{
            font-size: 30/50rem;
          }
        }
        .QtySum,.amountSum{
          width:45%;
          float: left;
          span:last-child{
            font-size: 34/50rem;
            font-weight: 550;
          }
        }
      }
      .reportInfo{
        .ProductSalesRankingItem{
          padding: 15/50rem 30/50rem;
          line-height: 45/50rem;
          border-bottom: 10/50rem solid #EEEFF2;
          color: #555;
          .CustomerName{
            line-height: 60/50rem;
            font-size: 32/50rem;
            font-weight: 550;
          }
        }
      }

      #main{
        margin: 60/50rem 0;
      }
    }
  }

  ::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
  }
</style>


