<template>
  <div class="AllOutgoingDetails">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="CustomerSearch">
        <div>
          <input v-model='searchValue' placeholder="按 出库单号 或 收货方名称 搜索">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <componentOutboundOrder :OutboundOrderList='OutboundOrderList'></componentOutboundOrder>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentOutboundOrder from '@/components/Widgets/componentOutboundOrder'
  export default{
    data(){
      return{
      	headerText:"出库列表",//页面标题
      	OutboundOrderList:[],//销售单列表
        searchValue:'',//搜索的值
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
  	  componentHeader,
      componentOutboundOrder
  	},
    created(){

      this.reqPutOutList();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqPutOutList();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.push({
          name:"InventoryManage",
        })
    	},
      // 请求出库列表
      reqPutOutList(load){
        var that = this;

        var LineData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          'storeName':that.searchValue,//搜索的值
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
          "userType":that.$store.state.G_AppOrWX,//判断是app还是公众号
        }
        console.log(LineData)

        // 获取出库订单列表
        that.httpRequest("searchTransferOut.do",LineData,function(SupRes){
          console.log(SupRes)

          for(var i =0;i<SupRes.data.length;i++){

            if(SupRes.data[i].STATUS == 'NEW'){

              SupRes.data[i].STATUS_color = '#FE5256';
              SupRes.data[i].STATUS_text = '未确认';

            }else if(SupRes.data[i].STATUS == 'FINISH'){

              SupRes.data[i].STATUS_color = '#28B45F';
              SupRes.data[i].STATUS_text = '已确认';

            }else if(SupRes.data[i].STATUS == 'CANCEL'){

              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = '此出库单已取消';
            }else{
              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = SupRes.data[i].STATUS;
            }

            // 销售出库、其它出库
            if(SupRes.data[i].TRANSFER_OUT_TYPE == 'SALE_OUT'){

              SupRes.data[i].TYPE_color = '#28B45F';
              SupRes.data[i].TYPE_text = '销售出库';
            }else if(SupRes.data[i].TRANSFER_OUT_TYPE == 'OTHER_OUT'){

              SupRes.data[i].TYPE_color = '#800080';
              SupRes.data[i].TYPE_text = '其它出库';
            }else if(SupRes.data[i].TRANSFER_OUT_TYPE == "TRANSFER_OUT"){

              SupRes.data[i].TYPE_color = '#28B45F';
              SupRes.data[i].TYPE_text = '调拨出库';
            }else if(SupRes.data[i].TRANSFER_OUT_TYPE == "PURCHASE_RETURN_OUT"){

              SupRes.data[i].TYPE_color = '#28B45F';
              SupRes.data[i].TYPE_text = '采购退货出库';
            }
          }
          
          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(SupRes.data.length){
              that.OutboundOrderList = that.OutboundOrderList.concat(SupRes.data);
            }
          } else {
            if(SupRes.data.length){
              that.OutboundOrderList = SupRes.data;
            }else{
              that.OutboundOrderList = [];
            }
          }

          // 判断是否能上拉加载
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.OutboundOrderList.length){
            that.noDataShow = false;
              
          }else{
            // 没有数据
            that.noDataShow = true;
          }
        })
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqPutOutList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqPutOutList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .AllOutgoingDetails{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .CustomerSearch{
        font-size: 32/50rem;
        position: fixed;
        z-index: 50;
        background-color: #fff;
        top: 89/50rem;
        left: 0;
        .TopSearchBox;
      }
      .ListBox{
        padding-top:189/50rem;
        height: 100%;
        box-sizing:border-box;
      }
    }
  }
  .kong{
    height: 100/50rem;
  }
</style>


