<template>
  <div class="LeaveStore">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
	  	<div class="stepTitle stepTitle_first" @click='PackUp("stepTitle_first","IntoTheShop")'><img src="static/img/Visit/IntoTheShop.png"><span>进店</span><i class="iconfont icon-xiangxia"></i></div>
	  	<div class="IntoTheShop">
	  	  <div class="IntoShopImg">
	  	  	<div  v-for='(IntoShopItem,IntoShopIdx) in leaveData.STORE'  :id="IntoShopIdx"  :key='IntoShopIdx'>
	  	  	  <img :src="IntoShopItem.IMAGES_URL" preview='99'>
	  	  	</div>
	  	  </div>
        <div class="save-img-prompt">* 长按图片保存到手机相册</div>
	  	  <div class="VisitAddress"><span>拜访位置：</span><span>{{leaveData.ACTUAL_VISITING_ADDRES}}</span></div>
	  	</div>
	  	<div class="stepTitle stepConfirm" @click='PackUp("stepConfirm","ConfirmInfo")'><img src="static/img/Visit/ConfirmInfo.png"><span>确认客户资料信息</span><i v-if="(!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo')" class="iconfont icon-xiangxia"></i><i v-else @click='ModifyData("stepConfirm")' class="iconfont icon-xiugai"></i></div>
	  	<div class="ConfirmInfo">
	  	  <div><span>客户代码：</span><span>RW00001</span></div>
	  	  <div><span>客户名称：</span><span>{{leaveData.PARTY_NAME}}</span></div>
	  	  <div><span>联&nbsp;系&nbsp;人&nbsp;：</span><span>{{leaveData.CONTACTS}}</span></div>
	  	  <div><span>联系电话：</span><span>{{leaveData.CONTACTS_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='leaveData.CONTACTS_TEL' @click="callPhone(leaveData.CONTACTS_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
	  	  <div><span>客户地址：</span><span>{{leaveData.PARTY_ADDRESS}}</span></div>
	  	  <div v-if="leaveData.REACH_THE_SITUATION"><span>备注：</span><span>{{leaveData.REACH_THE_SITUATION}}</span></div>
	  	</div>
	  	<div class="stepTitle stepCheck" @click='PackUp("stepCheck","CheckInventory")'><img src="static/img/Visit/CheckInventory.png"><span>检查库存</span><i v-if="(!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo')" class="iconfont icon-xiangxia"></i><i v-else @click='ModifyData("stepCheck")'  class="iconfont icon-xiugai"></i></div>
	  	<div v-if="leaveData.INVENTORY && leaveData.INVENTORY.length||leaveData.CHECK_INVENTORY" class="CheckInventory">

	  	  <div class="CheckedList"  v-for='(CheckedItem,CheckedIdx) in leaveData.INVENTORY'  :id="CheckedIdx"  :key='CheckedIdx'>
          <div>
            <div>{{CheckedItem.PRODUCT_NAME}}</div>
            <div><span>生产日期：</span><span>{{CheckedItem.PRODUCTION_TIME}}</span></div>
          </div>
	  	  	<span>{{CheckedItem.INVENTORY_QUANTITY}} {{CheckedItem.UNIT}}</span>
	  	  </div>
	  	  <div v-if="leaveData.CHECK_INVENTORY" class="CheckedRemark">备注：{{leaveData.CHECK_INVENTORY}}</div>
	  	</div>
	  	<div class="stepTitle stepSuggest" @click='PackUp("stepSuggest","SuggestOrder")'><img src="static/img/Visit/SuggestOrder.png"><span>建议订单</span><i v-if="(!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo')" class="iconfont icon-xiangxia"></i><i v-else @click='ModifyData("stepSuggest")'  class="iconfont icon-xiugai"></i></div>
	  	<div class="SuggestOrder" v-if="leaveData.ORDER && leaveData.ORDER.length">
	  	  <div class="SuggestOrderInfo"  v-for='(SuggestItem,SuggestIdx) in leaveData.ORDER'  :id="SuggestIdx"  :key='SuggestIdx' @click="toOutboundDetail(SuggestIdx)">
		  	  <div><span>出库单号：</span><span>{{SuggestItem.OMS_NO}}</span></div>
		  	  <div><span>门店名称：</span><span>{{SuggestItem.RECEIVE_PARTY_NAME}}</span></div>
		  	  <div><span>门店地址：</span><span>{{SuggestItem.RECEIVE_PARTY_ADDR1}}</span></div>
		  	  <div><span>下单人名：</span><span>{{SuggestItem.ADDWHO}}</span></div>

		  	  <div><span>下单日期：</span><span>{{SuggestItem.ORDER_RECEIVED_DTE}}</span></div>
		  	  <div><span>出库数量：</span><span>{{SuggestItem.PIRCHASE_QUANTITY}}</span></div>
		  	  <!-- 订单备注还是建议订单备注？订单是列表还是只会有一组值 -->
		  	  
		  	  <span v-if="$store.state.visitObject != 'OSP'" class="OrderStatus">销售出库：{{SuggestItem.STATS_text}}</span>
          <span v-if="$store.state.visitObject == 'OSP'" class="OrderStatus">采购订单</span>
	  	  </div>
	  	  <div v-if='leaveData.RECOMMENDED_ORDER'>备注：{{leaveData.RECOMMENDED_ORDER}}</div>
	  	</div>
	  	<div class="stepTitle stepVivid" @click='PackUp("stepVivid","VividDisplay")'><img src="static/img/Visit/VividDisplay.png"><span>生动化陈列</span><i v-if="(!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo')" class="iconfont icon-xiangxia"></i><i v-else @click='ModifyData("stepVivid")'  class="iconfont icon-xiugai"></i></div>
	  	<div class="VividDisplay" v-if='leaveData.SDXL && leaveData.SDXL.length'>
	  	  <div class="VividDisplay_item" v-for='(SDXLItem,SDXLIdx) in leaveData.SDXL'  :id="SDXLIdx"  :key='SDXLIdx'>
          <div class="VividType">
            <span>类型：{{SDXLItem.TYPE}}</span>
            <i v-if="!((!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo'))" @click='delVividType(SDXLIdx)' class="iconfont icon-trash"></i>
          </div>
  		  	<div class="VividImg">
  		  	  <div  v-for='(VividImgItem,VividImgIdx) in SDXLItem.IMAGES'  :id="VividImgIdx"  :key='VividImgIdx'>
  		  	  	<img :src="VividImgItem.IMAGES_URL" :preview = 'SDXLIdx'>
              <i v-if="!((!VisitCompleteBtnHide&&VISIT_STATES=='已拜访')||($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached == 'ModifyCustomerInfo'))" @click='delImage(SDXLIdx,VividImgIdx)' class="iconfont icon-guanbi"></i>
  		  	  </div>
  		  	</div>
          <div class="save-img-prompt">* 长按图片保存到手机相册</div>
	  	  </div>
	  		
	  	  <div v-if="leaveData.VIVID_DISPLAY_Text" class="VividRemark">备注：{{leaveData.VIVID_DISPLAY_Text}}</div>
	  	</div>	
	  	<div class="stepTitle stepVisit" @click='PackUp("stepVisit","VisitStatus")'><img src="static/img/Visit/VisitStatus.png"><span>拜访状态</span><i class="iconfont icon-xiangxia"></i></div>
	  	<div class="VisitStatus">
	  	 <div><span>拜访状态：</span><span>{{leaveData.VISIT_STATES}}</span></div>
	  	</div>
      <div class="VisitStatus">
       <div class="visit-results"><span>拜访结果：</span><span>{{leaveData.Visit_Results}}</span></div>
      </div>
	  	<div v-if="ifTips" class="msg_tips">
	      <div class="tips_content">
	        <i class="iconfont icon-chenggong1"></i>
	        <p>{{tips_Msg}}</p>
	      </div>
	    </div>
      <div v-if="VisitCompleteBtnHide" class="blank_"></div>
	  </div>

    <div v-if="VisitCompleteBtnHide" class="bottom-btn">
      <div class="next-btn" @click="VisitComplete">确认离店</div>
      <div class="edit-btn" @click="edit_btn">拜访备注</div>
    </div>

	  <div class="selfconfirm" v-if="selfconfirm_show">
      <div class="selfconfirm_contianer">
        <div class="selfconfirm_head">提示</div>
        <div class="selfconfirm_content">{{confirmcontent}}</div>
        <div class="selfconfirm_btn">
          <div @click='selfconfirm_cancel()'>取消</div>
          <div @click='selfconfirm_confirm_()'>确认</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"离店",//页面标题
      	leaveData:[],//离店信息
      	VisitCompleteBtnHide:true,//确认离店按钮是否显示
      	ifTips:false,
        tips_Msg:"",
        selfconfirm_show:false,//自建confirm是否显示提示框
        confirmcontent:'',//自建confirm提示内容
        VISIT_STATES:'',//判断是拜访中还是已拜访
      }
    },
    components:{
  	  componentHeader,
  	},
    created(){

      var that = this;

      if(that.$route.query.pageName && that.$route.query.pageName == '查看拜访'){
      	that.VisitCompleteBtnHide = false;//确认离店按钮是否显示
      	that.headerText = '查看拜访';
      }

      if(that.$route.query.VISIT_STATES){
        that.VISIT_STATES = that.$route.query.VISIT_STATES;
      }
      this.reqData();
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
        var that = this;
      	if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush == 'VividDisplay'){
      	  this.$router.push({
            name:"CustomerVisit",
          })
          return;
      	}else{
      	  this.$router.go(-1);
      	}
      },
      edit_btn(){

        var that = this

        this.$alert('<textarea class="textarea" rows="4" style="width:10.3rem;padding-left:0.3rem;padding-top:0.3rem;font-size:0.7rem;">', '拜访存在的问题、建议：', {
          dangerouslyUseHTMLString: true,
          showCancelButton: true
        })
        .then(({ value }) => {
          var r = $(".textarea").val()
          var leaveReqData = {
            "visitId": this.$store.state.CurrentVisitId,//拜访id
            "visitResults": r
          }
          // 上传拜访备注
          that.httpRequest("SavevisitResults.do",leaveReqData,function(leaveRes){
            that.$message({
              message: '提交成功，感谢您宝贵的建议！',
              type: 'success'
            })
            that.reqData()
          })
        })
        .catch(() => { /* 取消 */ })

        setTimeout(function (){
          $(".textarea").focus()
        }, 50)
      },
      reqData(){
        var that = this;
        var leaveReqData = {
          "visitId":this.$store.state.CurrentVisitId,//拜访id
          "cellphone":this.$store.state.userInfo.cellphone,//手机号码
          "userId":this.$store.state.userInfo.user_id,//用户id
        }

        // 获取离店信息
        that.httpRequest("GetVisitLeaveShopInfo.do",leaveReqData,function(leaveRes){
        
          console.log(leaveRes)
          that.leaveData = leaveRes.data;

          if(that.leaveData.ORDER.length){

            for(var j = 0;j<that.leaveData.ORDER.length;j++){

              if(that.leaveData.ORDER[j].STATS == 'NEW'){

                that.leaveData.ORDER[j].STATS_text = '未确认';

              }else if(that.leaveData.ORDER[j].STATS == 'FINISH'){

                that.leaveData.ORDER[j].STATS_text = '已确认';
              }else if(that.leaveData.ORDER[j].STATS == 'CANCEL'){

                that.leaveData.ORDER[j].STATS_text = '此出库单已取消';
              }
            }
          }
          // 刷新查看图片组件
          that.$previewRefresh()
        })
      },
      // 自建confirm取消事件
      selfconfirm_cancel (){
        this.selfconfirm_show = false;
      },
      // 自建confirm确认事件
      selfconfirm_confirm(){},
      // 自建confirm确认事件
      selfconfirm_confirm_ (){

        this.selfconfirm_show = false;
        this.selfconfirm_confirm();
      },
      selfconfirm(text,selfconfirm_confirm){
        this.selfconfirm_show = true;
        this.confirmcontent = text;
        this.selfconfirm_confirm = selfconfirm_confirm;
      },
      // 收起 展开
      PackUp(head_class,body_class){

      	if($('.'+ body_class).css("display")=="block"){
      	  $("."+head_class+" i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(-90deg)"})
	      $('.'+ body_class).hide(400);
      	}else{
      	  $("."+head_class+" i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(0deg)"})
	      $('.'+ body_class).show(400);
      	}
      },
      // 修改数据 跳转页面
      ModifyData(head_class){

        event.stopPropagation();

        var that = this;

        // 确认客户信息
        if(head_class=="stepConfirm"){

          that.$router.push({
            name:"CreateNewVisit",
            query:{
              VisitModifyData:'VisitModifyData',
              pageName:that.headerText
            }
          })
        }else if(head_class=="stepCheck"){
          // 检查库存
          that.$router.push({
            name:"CheckInventory",
            query:{
              VisitModifyData:'VisitModifyData',
              pageName:that.headerText
            }
          })
        }else if(head_class=="stepSuggest"){

          // 建议订单
          that.$router.push({
            name:"ChooseGoods",
            query:{
              VisitModifyData:'VisitModifyData',
              pageName:that.headerText,
              CurrentStoresId:that.$store.state.CurrentStoresId,//门店id
              visitId:that.$store.state.CurrentVisitId,//拜访id
              whoPush:"CheckInventory",
              orderType:"SALE_OUT",
            }
          })
          
        }else if(head_class=="stepVivid"){
          // 生动化陈列
          that.$router.push({
            name:"VividDisplay",
            query:{
              VisitModifyData:'VisitModifyData'
            }
          })
        }
      },
      // 跳转到出库明细页面/采购明细页面
      toOutboundDetail(idx){
      	var that = this;
        if(this.$store.state.visitObject == "OSP"){
          this.$router.push({
            name:"PurchaseOrderDetail",
            query:{
              orderId:that.leaveData.ORDER[idx].ORDER_ID
            }
          })
        }else{
          this.$router.push({
            name:"OutboundDetail",
            query:{
              transferOutId:that.leaveData.ORDER[idx].ORDER_ID
            }
          })
        }
      },
      // 确认离店
      VisitComplete(){

      	var that = this;

      	this.selfconfirm('确定要离店吗?',function(){
          var sureData = {
  	        "cellphone":that.$store.state.userInfo.cellphone,//手机号
  	        "userId":that.$store.state.userInfo.user_id,//用户id
  	        "visitId":that.$store.state.CurrentVisitId,//拜访id
  	      }
  	      console.log(sureData)
  	      // 确认门店信息
  	      that.httpRequest("GetVisitLeaveShop.do",sureData,function(sureRes){
  	        console.log(sureRes)

  	        that.ifTips = true;
            that.tips_Msg = "拜访完成";

            setTimeout(function(){
              //下单成功后跳转到采购单页面
              that.$router.push({
                name:"CustomerVisit",
              })
            },2000)
          })
        })
      },
      // 生动化陈列删除照片
      delImage(SDXLIdx,VividImgIdx){

        var that = this;

        this.selfconfirm('确定要删除图片吗?',function(){

          var delImgData = {
            "picId":that.leaveData.SDXL[SDXLIdx].IMAGES[VividImgIdx].IMAGES_ID,//图片id
            "visitId":that.$store.state.CurrentVisitId,//拜访id
            "type":"one",//表示删除一张图片
            "large":that.leaveData.SDXL[SDXLIdx].TYPE,//类型
          }
          console.log(delImgData)
          // 确认门店信息
          that.httpRequest("delPic.do",delImgData,function(delImgRes){
            console.log(delImgRes)

            that.ifTips = true;
            that.tips_Msg = "删除成功";

            setTimeout(function(){
              that.ifTips =false;
              that.reqData();
            },2000)
          })
        })
      },
      // 删除生动化陈列的类型
      delVividType(SDXLIdx){

        var that = this;

        this.selfconfirm('确定要删除该陈列类型吗?',function(){

          var delTypeData = {
            "picId":0,//图片id
            "visitId":that.$store.state.CurrentVisitId,//拜访id
            "type":"more",//表示删除一个类型
            "large":that.leaveData.SDXL[SDXLIdx].TYPE,//类型
          }
          console.log(delTypeData)
          // 确认门店信息
          that.httpRequest("delPic.do",delTypeData,function(delTypeRes){
            console.log(delTypeRes)

            that.ifTips = true;
            that.tips_Msg = "删除成功";

            setTimeout(function(){
              that.ifTips =false;
              that.reqData();
            },2000)
          })
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .LeaveStore{
    overflow: hidden;
    height: 100%;
    .container{
      color: #666;
      font-size: 30/50rem;
      overflow: scroll;
      overflow-x: hidden;
      height: calc(100% - 1.8rem);
      margin-top: 90/50rem;
      &>div:not(.stepTitle){
      	border-bottom:10/50rem solid #f0f0f0;
      	padding-left: 20/50rem;
      	padding-bottom: 20/50rem;
      	&>div{
      	  margin-top: 10/50rem;
      	}
      }
      .IntoTheShop{
        padding-right: 20/50rem;
      	img{
      		margin-right: 10/50rem;
      	}
      }
      .stepTitle{
      	color: #000;
      	overflow:hidden;
		    line-height: 80/50rem;
		    border-bottom: 5/50rem solid #f0f0f0;
		    position:relative;
    		img{
    		  float:left;
    		  margin:7.5/50rem  20/50rem;
    		  height: 65/50rem;
    		}
    		span{
    		  float: left;
    		  font-weight:700;
    		  font-size:30/50rem;
    		  line-height: 80/50rem;
    		}
    		i{
    		  position: absolute;
    		  right: 20/50rem;
          font-size: 40/50rem;
    		  top: 50%;
          transform: translateY(-50%) rotate(0deg);
    		  transition:all 0.4s;
          &.icon-xiugai{
            color: @PublicColor;
          }
    		}
      }
      .ConfirmInfo{
        padding-right: 20/50rem;
      }

      .ConfirmInfo,.SuggestOrderInfo,.IntoTheShop{
    		&>div{
    		  display: flex;
    		  &>span:first-child{
            min-width: 130/50rem;
            flex-shrink:0;
          }
          &>span:nth-child(2){
            flex-grow: 1;
          }
    		}
      }
  	  .VividImg,.IntoShopImg{
  	    overflow:hidden;
  	    &>div{
  	    	float: left;
          width: 200/50rem;
          position:relative;
  	      img{
  	        width: 200/50rem;
  	      }
          i{
            position:absolute;
            font-size: 40/50rem;
            top: 0;
            right: 0;
          }
  	    }
  	  }
      .save-img-prompt{
        color: green;
        font-size: 25/50rem;
      }
  	  .SuggestOrder{
        padding-right: 20/50rem;
  	  	&>.SuggestOrderInfo{
  	  		position:relative;
  	  		&>div{
  	  			margin-top: 10/50rem;
  	  		}
          &:not(:first-child){
            border-top:3/50rem solid #f0f0f0;
          }
  	  	}
    		.OrderStatus{
    			display: inline-block;
    		  position:absolute;
    		  right: 20/50rem;
    		  bottom:0;
    		  color: green;
    		}
    		.icon{
    			i{
    				font-size: 40/50rem;
    			}
    		}
  	  }
      .CheckInventory{
        padding-right: 20/50rem;
        box-sizing: border-box;
        .CheckedList{
          border-bottom:3/50rem solid #f0f0f0;
          display: flex;
          padding:10/50rem 0;
          &>div:nth-child(1){
            flex-grow: 1;
            &>div{
              line-height: 50/50rem;
            }
          }
          &>span:nth-child(2){
            min-width: 180/50rem;
            flex-shrink: 0;
            line-height: 100/50rem;
          }
        }
        &>div:last-child{
          border-bottom:none;
        }
      }
	    &>div.VividDisplay{
    		padding-left:0;
    		.VividDisplay_item{
    		  padding-left:20/50rem;
    		  border-bottom:3/50rem solid #f0f0f0;
          &>div.VividType{
            overflow: hidden;
            line-height: 70/50rem;
            &>span{
              float: left;
            }
            &>i.icon-trash{
              // color: #;
              margin-right: 20/50rem;
              font-size: 35/50rem;
              float: right;
            }
          }
    		  img{
      			margin-right: 10/50rem;
    		  }
    		}
    		.VividDisplay_item:last-child{
    			border-bottom:none;
    		}
  	  }
		  .AddSuggestOrder {
  			line-height: 310/50rem;
  			width: 100%;
  			height: 80/50rem;
        text-align: center;
  			.icon-jia{
	  			font-size: 120/50rem;
	  			color: @PublicColor;
	  		}
  		}
      .blank_{
        width: 100%;
        height: 120/50rem;
      }
    }
    .bottom-btn{
      width: 650/50rem;
      height: 70/50rem;
      position: absolute;
      bottom: 40/50rem;
      left: 50/50rem;
      &>div{
        width: 45%;
        height: 100%;
        background-color: #1e8cf8;
        border-radius: 35/50rem;
        font-size: 29/50rem;
        color: white;
        line-height: 70/50rem;
        text-align: center;
      }
      .next-btn{
        float: left;
      }
      .edit-btn{
        float: right;
      }
    }
    .visit-results{
      word-wrap:break-word;
    }
  }
</style>


