<template>
  <div class="componentReportBar">
  	<div class='reportContainer'>

      <div class='reportItem' @click='toDetails(index)' v-for='(item,index) in ReportList'  :id="index"  :key='index'>
        <div class="lineOne">
          <span>{{item.CUSTOMER_NAME}}</span>
          <span>{{item.qty}}&nbsp;<span>({{item.progressQty}}%)</span></span>
        </div>
        <div class="lineTwo">
          <div class="progressQty">{{ drawQty(index, item.progressQty) }}</div>
        </div>  
        <div class="lineThreePrompt">
          <span class="moneyCostomerName">{{item.CUSTOMER_NAME}}</span>
          <span>{{item.money}}&nbsp;<span>({{item.progressMoney}}%)</span></span>
        </div>
        <div class="lineThree">
          <div class="progressMoney">{{ drawMoney(index, item.progressMoney) }}</div>
        </div>
      </div>
      
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from "jquery"
  export default{
    data(){
      return{
      }
    },
    props:{
      ReportList:{
        type:Array,
        default: () => {
  	      return []
  	    }
      }
    },
    components:{},
    created(){

    },
    methods:{

      toDetails (index) {

        var that = this;

        this.$router.push({
          name:"CustomerSalesDetailReport",
          query:{
            "customerId":that.ReportList[index].customerId,
            "customerName":that.ReportList[index].CUSTOMER_NAME,
            "startDate":that.ReportList[0].startDate,
            "endDate":that.ReportList[0].endDate,
          }
        })
      },
      drawQty(index, progressQty){

        setTimeout(function (){

          $('.progressQty').eq(index).css("width", progressQty + '%');
        }, 30); 
      },
      drawMoney(index, progressMoney){

        setTimeout(function (){

          $('.progressMoney').eq(index).css("width", progressMoney + '%');

          // 设置颜色
          $('.colorQty').css('background-color','#DB7171')
          $('.progressQty').css('background-color','#DB7171')
          $('.colorMoney').css('background-color','#5899C3')
          $('.progressMoney').css('background-color','#5899C3')
          $('.moneyCostomerName').css('visibility','hidden')
        }, 30);
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .componentReportBar{
    overflow-x: hidden;
    .reportContainer{
      width:100%;
      font-size: 30/50rem;

      .reportItem{
        width:100%;
        color: #8B8B8D;
        padding: 20/50rem 30/50rem;
        box-sizing: border-box;
        border-bottom: 20/50rem solid #EEEFF2;
        background-color: #fff;
        .lineOne{
          width: 100%;
          height: 50/50rem;
          &>span:nth-child(1){
            float: left;
          }
          &>span:nth-child(2){
            float: right;
            &>span{
              font-size: 25/50rem;
            }
          }
        }
        .lineTwo{
          width: 100%;
          height: 50/50rem;
          .progressQty{
            height: 17/50rem;
            width: 0rem;
            background-color: red;
            border-radius: 10/50rem;
          }
        }
        .lineThreePrompt{
          width: 100%;
          height: 50/50rem;
          &>span:nth-child(1){
            float: left;
          }
          &>span:nth-child(2){
            float: right;
            &>span{
              font-size: 25/50rem;
            }
          }
        }
        .lineThree{
          width: 100%;
          height: 50/50rem;
          .progressMoney{
            height: 17/50rem;
            width: 0rem;
            background-color: red;
            border-radius: 10/50rem;
          }
        }
      }
      .float{
        float: right;
      }
    }
  }
</style>


