<template>
  <div class="InventoryDetails">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' @f_Text='f_Text' :TextContent='TextContent'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="orderInfo">
        <div class='infoHead'>产品信息</div>
        <div class="infoContainer">
          <div><span>产品编号：</span><span>{{ProductNo}}</span></div>
          <div><span>产品名称：</span><span>{{ProductName}}</span></div>
          <div><span>库存数量：</span><span>{{ProductTotalNum}}</span></div>
          <div><span>编辑时间：</span><span>{{DateEdit}}</span></div>
        </div>
      </div>
      <div class="goodsInfo">
        <div class='infoHead'>批次信息</div>
        <div class="batchInfo" v-for='(itemName,idx) in batchArr'  :id="idx"  :key='idx'>
          <div class="batchLeft">
            <div><span>库存批次：</span><span>{{itemName.InventoryBatch}}</span></div>
            <div><span>生产日期：</span><span>{{itemName.DateProduction}}</span></div>
            <div><span>操作日期：</span><span>{{itemName.DateOperation}}</span></div>
          </div>
          <div class="batchRight">
            <span>{{itemName.productNum}} {{itemName.productUom}}</span>
          </div>
        </div>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"库存详情",//页面标题
        TextContent:'流水',//
        ProductNo:'',//产品编号
        ProductName:'',//产品名称
        ProductTotalNum:'',//库存数量
        DateEdit:'',//编辑时间
        batchArr:[]
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	},
      f_Text(){
        this.$router.push({
          name:"AccessDetails",
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .InventoryDetails{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    .orderInfo>.infoContainer span{
      color: #000;
    }
  }
</style>


