<template>
  <div class="PurchaseOrder">
    <componentHeader  :headerText= "whoPush.length ? '门店列表' : '采购下单'" :IfFanhuiShow='true'  @f_Fanhui='f_Fanhui'></componentHeader>
  <!--   <header class="SalesOrderHeader" v-else>
      <div @click="SelectSales">销售单</div>
      <div class="activeBtn">采购单</div>
    </header> -->
    <div class="container haveHeadcontainer">
      <div class="fixedHead">
        <div class="CustomerSearch">
          <div>
            <input v-model="searchName"  placeholder="按供货商名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
        </div>
      </div>
      <div class="CustomerList">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <componentCustomer :ospList='ospList' @Comp_CustomerSelected="Comp_CustomerSelected"></componentCustomer>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <!-- 页面数据为空时 -->
      <div   v-if="noDataShow"   class="NoData" >
        <div>
          <i  class="iconfont icon-meiyouwuliuxinxi" ></i>
          <div>没有供货商信息，请联系管理员</div>
        </div>
      </div>
    </div>
    <!-- <componentFooter></componentFooter> -->
  </div>
</template>
<script type="text/javascript">
  import componentFooter from '@/components/componentFooter'
  import componentHeader from '@/components/componentHeader'
  import componentCustomer from '@/components/Widgets/componentCustomer'
  export default{
    data(){
      return{
        ConsigneeListData:[],//收货人信息列表
        ospList:[],
        searchName:'',
        businessCode:'',
        UsersId:'',
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        whoPush:"",//上一个界面
      }
    },
    components:{
	    componentCustomer,
      componentHeader,
      componentFooter,
	  },
    mounted(){

      window.history.pushState(null, null, document.URL)
      window.addEventListener("popstate", this.onBrowserBack, false)
    },
    destroyed() {

      window.removeEventListener("popstate", this.onBrowserBack, false)
    },
    created(){

      if(this.$route.query.whoPush){
        this.whoPush = this.$route.query.whoPush
      }

      this.loadData();
    },
    watch: {
      searchName(val, oldVal) {
        this.loadPage = 1;
        this.allLoaded = false;
        this.loadData();
      }
    },
    methods:{
      onBrowserBack() {
        
        this.f_Fanhui()
      },
      f_Fanhui(){
        this.$router.push({
          name:"Index"
        })
      },
      // 切换头部导航
      SelectSales(){
        this.$router.push({
          name:"SalesOrder"
        })
      },
      // 选中客户
      Comp_CustomerSelected(index){

        this.$store.state.BackBottle = {
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//收货人信息
          DeliveryAddr_selected:[],//发货人信息
        }
        this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据
        var ReceiptInfo = this.ospList[index];

        if(this.whoPush.length){
          this.$router.push({
            name:"FeeBill",
            query:{
              customerInfo:ReceiptInfo,
            }
          })
        }else{
          this.$router.push({
            name:"ChooseGoods",
            query:{ 
              ReceiptInfo:ReceiptInfo,
              whoPush:"PurchaseOrder",//采购单
              orderType:'PURCHASE_IN'
            }
          })
        }
      },
      // 请求客户列表
      loadData(load){

        var that = this;
        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户ID
          "businessId":that.$store.state.businessInfo.businessId,//业务ID
          "searchName":that.searchName,
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        this.httpRequest( "searchCustomerList.do",postData,function(res){

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(res.data.length){
              that.ospList = that.ospList.concat(res.data);
            }
          } else {
            that.ospList = res.data;
          }

          // 判断是否能上拉加载
          if (res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.ospList.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }
        })
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.loadData("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.loadData("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .PurchaseOrder{
    height: 100%;
    .container{
      margin-top: 0;
      height: 100%;
      .fixedHead{
        background-color:#fff;
        z-index: 50;
        width: 100%;
        position:fixed;
        top: 89/50rem;
        left: 0;
        .CustomerSearch{
          .TopSearchBox;
        }
      }
      .CustomerList{
        height: 100%;
        overflow: scroll;
        box-sizing: border-box;
        padding-top: 196/50rem;
      }
    }
  }
</style>


