<template>
  <div class="ChooseGoods">
    <componentHeader :headerText="headerText" :IfFanhuiShow= ' $store.state.G_AppOrWX == "WX" ? false :true' @f_Fanhui='f_Fanhui' :IfTextShow='IfTextShow' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
    <div class="noData" v-if="noDataShow">
      <i class="iconfont icon-meiyoushuju-copy"></i>
      <span>没有商品可选,请联系管理员</span>
    </div>
    <div class="container" v-if="!noDataShow">
      <div class="fixedTop">
        <div class="HorizontalNav">
          <div class="BrandItem" v-for='(BrandItem,BrandIdx) in HnavBrandList'  :id="BrandIdx"  :key='BrandIdx'    :class="{ Hnav_active:HnavBrandIndex == BrandIdx}" @click="BrandItemClick(BrandIdx)">{{BrandItem.PRODUCT_BRAND}}
          </div>
        </div>
        <div v-if="isShowSelectStore" class="selectStore">
          <div @click="toSelectStores">{{ returnOrtransrerText }}：{{SelectedInfoName}}</div>
        </div>
        <!-- 选择收发货人 -->
        <div v-if="isShowSelectReceiveAndDeliver" class="selectReceiveAndDeliver">
          <div class="select_Receive" @click='toSelectSalesMan("Receive")'>
            <div>
              <span>收货方：</span>
              <span>{{receORdeliv_consignee}}</span>
              <i v-if='!IFreceORdeliv_transition' class="iconfont icon-xiangyou"></i>
            </div>
          </div>
          <div class="select_transition" @click='receORdeliv_transition'>
            <i class="iconfont icon-jiaohuan"></i>
          </div>
          <div class="select_Deliver" @click='toSelectSalesMan("Deliver")'>
            <div>
              <span>发货方：</span>
              <span>{{receORdeliv_consignor}}</span>
              <i v-if='IFreceORdeliv_transition' class="iconfont icon-xiangyou"></i>
            </div>
          </div>
        </div>
        <div class="OtherNav">
          <div class="CSearch">
            <div>
              <i class="iconfont icon-sousuo"></i>
              <input v-model='searchValue' placeholder="按产品名称搜索">
            </div>
          </div>
          <div class="otherInfoBtn"><div @click='Address'>收货/发货</div></div>
        </div>
      </div>
      
      <div class="LowerBody">
        <div class="VerticalNav">
          <div v-for='(TypeItem,TypeIdx) in VnavTypeList'  :id="TypeIdx"  :key='TypeIdx'
          :class="{ Vnav_active:VnavTypeIdx == TypeIdx}" @click="TypeListClick(TypeIdx)">{{TypeItem.PRODUCT_TYPE}}</div>
          <span class="navKong"></span>
        </div>
        <div class="GoodsDisplay">
          <mt-loadmore  bottomPullText="" bottomDropText="上拉加载" bottomLoadingText=""  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class='selectGoodsList' v-for='(dataItem,index) in goodsList'  :id="index"  :key='index' @click='showPromotionInfo(index)'>
            <div class="selectGoodsListItem">
              <div class='LeftIcon'>
                <img :src="dataItem.PRODUCT_PIC?dataItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
              </div>
              <div class='listCenter'>
                <div class="goodsNumame">
                  <span>{{dataItem.PRODUCT_NAME}}</span>
                </div>
                <div class="specifications">
                  <span>规格：</span><span>{{dataItem.PRODUCT_DESC}}</span>
                </div>
                <div class="specifications">
                  <span>单位：</span><span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
                </div>
                <div class="moneyGroup">
                  <div>
                    <span>¥{{dataItem.DIS_PRICE}}</span>
                    <div v-if="whoPush == 'InventoryManage' && orderType == 'TRANSFER'" class="InventoryQtyClass">库存 : {{dataItem.DB_NUM}}</div>
                    <div v-if="orderType == 'SALE_OUT'" class="InventoryQtyClass">库存 : {{dataItem.KC_NUM}}</div>
                  </div>
                  <div class='listLast'>
                    <i class="iconfont icon-Untitled-" :id="index"  @click='jian'></i>
                    <span @click='entryShow($event, number[dataItem.PRODUCT_ID])' :id='index' data-element="页面" >{{number[dataItem.PRODUCT_ID]}}</span>
                    <i class="iconfont icon-jia1" :id="index"  @click='jia'></i>
                  </div>
                </div>
              </div>
            </div>
            <div class="promotionInfo" v-if='dataItem.CPCL.length'>
              <div class="promotionShow"><i class="iconfont icon-xiangxiaxialakuang"></i><span>促销信息</span></div>
              <div class="promotionHide">
                <div @click='promotionInstruction(index,PIindex)'  v-for='(PIItem,PIindex) in dataItem.CPCL'  :id="PIindex"  :key='PIindex'>
                  <div>
                    <div class="PI-icon-d">
                      <i class="iconfont icon-cuxiao"></i>
                    </div>
                    <div class="PI-name-d">
                      <span>{{ PIItem.name }}</span>
                      <span class="promotionDeShow" v-if='PIItem.detail.length' style="color: #666;">（需满足以下条件）</span>
                    </div>
                    <div class="PI-nav-d" v-if='PIItem.detail.length'>
                      <i class="iconfont icon-xiangxiaxialakuang"></i>
                    </div>
                  </div>
                  <div class="promotionDeHide">
                    <div class="promotionInstru" >
                    <div v-for='(DeItem,Deindex) in PIItem.detail' :id="Deindex" :key='Deindex'>
                      <span>{{DeItem.product_name}}</span> <span>&nbsp;&nbsp;&nbsp;{{DeItem.num}}</span><span>{{DeItem.unitStr}}</span>
                    </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </mt-loadmore>
        </div>
      </div>
    </div>
    <div class="SelectMeng" v-if='AddressShow'>
      <div class="AddressConfirm">
        <div class="AddressConfirmHead">
          <span>收货/发货</span>
          <i class="iconfont icon-guanbi" @click='AddressShowHide'></i>
        </div>
        <div class="Info">
          <div v-if='ReceiptInfo.CUSTOMER_NAME||ReceiptInfo.CONTACT_NAME||ReceiptInfo.CONTACT_TEL||ReceiptInfo.CUSTOMER_ADDRESS' class="ReceiveInfo">
            <div><span>收&nbsp;&nbsp;货&nbsp;&nbsp;方：</span><span>{{ReceiptInfo.CUSTOMER_NAME}}</span></div>
            <div><span>收货人名：</span><span>{{ReceiptInfo.CONTACT_NAME}}</span><span>&nbsp;&nbsp;&nbsp;&nbsp;{{ReceiptInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='ReceiptInfo.CONTACT_TEL' @click="callPhone(ReceiptInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span>收货地址：</span><span>{{ReceiptInfo.CUSTOMER_ADDRESS}}</span></div>
            <span></span>
            </div>
          <div v-if="DeliveryInfo.CUSTOMER_NAME||DeliveryInfo.CONTACT_NAME||DeliveryInfo.CUSTOMER_TEL||DeliveryInfo.CUSTOMER_ADDRESS" class="DeliveryInfo">
            <div><span>发&nbsp;&nbsp;货&nbsp;&nbsp;方：</span><span>{{DeliveryInfo.CUSTOMER_NAME}}</span></div>
            <div><span>发货人名：</span><span>{{DeliveryInfo.CONTACT_NAME}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span>{{DeliveryInfo.CUSTOMER_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='DeliveryInfo.CUSTOMER_TEL' @click="callPhone(DeliveryInfo.CUSTOMER_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span>发货地址：</span><span>{{DeliveryInfo.CUSTOMER_ADDRESS}}</span></div>
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <!-- 调拨 查看收货方/发货方全称 弹出框 -->
    <div class="SelectMeng" v-if='IFSelectReceiveMeng'>
      <div class="IFSelectReceiveMeng">
          <div>{{ReceiveORDeliverHeaderName}}</div>
          <div>{{ReceiveORDeliverName}}</div>
          <i class="iconfont icon-guanbi" @click='SelectReceiveMengHide'></i>
      </div>
    </div>
    <!--购物车  -->
    <div class='foot_cart'>
      <div class='foot_top'   @click="animat_cartShowIf" >
        <div class='totalNum'>
          <div><i class="iconfont icon-shoppingCart-full"></i></div>
          <div>
            <div>¥ {{totalPrice}}</div>
            <div>总数：{{totalNum}}</div>
          </div>
        </div>
        <div class='submit' @click='submitClick' >提交</div>
      </div>
      <div class='foot_bottom'>
        <div class="OutermostGray"  @click="animat_cartShowIf"></div>
        <div class="footBo_move">
        <div class="footBottom_head">已选商品详情：</div>
        <div class="footBottom_fixedBox">
          <div class='cartGoodsList' v-for='(cartItem,carindex) in submitArr'  :id="carindex"  :key='carindex' >
            <div class='cartListLeft'>{{ cartItem.PRODUCT_NAME }}</div>
            <div class='listLast'>
              <i class="iconfont icon-Untitled-" :id="carindex" @click='jian2'></i>
              <span @click='entryShow($event, cartItem.number)' :id="carindex"  data-element="购物车">{{cartItem.number}}</span>
              <i class="iconfont icon-jia1" :id="carindex" @click='jia2'></i>
            </div>
          </div>
        </div>
        </div>
      </div>
    </div>
    <!--输入框  -->
    <div class='entryNumber' v-if="isHidden">
      <div class='entry_contaniner'>
        <div class='entry_contaniner_header'>请输入产品数量</div>
        <div class='clearNum'>
          <input id='autofocus_input' bindinput='goodsNum' ref='autofocus_input'  v-model='inputValue' type='number'></input>
          <i @click='clearNum' v-if="inputValue" class='iconfont icon-guanbi'></i>
        </div>
        <div class='entryBtn'>
          <div class='en_cancel' @click='cancel_entry'>取消</div>
          <div class='en_ensure' @click='ensure_entry'>确定</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from "jquery"
  import componentHeader from '@/components/componentHeader'
  export default{
    name:"ChooseGoods",
      data(){
        return{
          headerText:"选择商品",
          AddressShow:false,//收货/发货是否出现
          goodsList: [],//数据列表
          HnavBrandList:[],//品牌数据列表  横向导航
          VnavTypeList:[],//分类数据列表   竖向导航
          HnavBrandIndex:0,//横向品牌下标值
          VnavTypeIdx:0,//纵向类型下标值
          totalNum:0, //总数量
          totalPrice:0,//总价
          isHidden:false,//输入产品数量弹出框是否显示
          cartShow:0,//购物车是否上拉
          submitArr:[],
          element:'',//点击“页面”的或者“购物车”的输入产品数量输入框
          inputFocus: false,//输入框是否聚焦
          inputValue: "",//在输入框中输入的产品数量
          ShoppingCartDetailHide:false,//购物车详情是否显示
          ReceiptInfo:{},//收货方信息
          DeliveryInfo:{},//发货方信息
          number:{},
          inputNum:0,//输入框idx
          firstReq:false,//第一次请求
          whoPush:"",//哪个页面过来的
          orderType:'',//是什么类型的下单
          isShowSelectStore:false, // 是否显示 选择门店/业务员
          SelectedInfoName:'',//显示的 门店/业务员 名称
          returnOrtransrerText:"",//销售退货、调拨入库、调拨出库
          isShowSelectReceiveAndDeliver:false,// 是否显示 选择收发货人 调拨
          receORdeliv_consignee:'',//收货人  调拨
          receORdeliv_consignor:'',//发货人  调拨
          IFreceORdeliv_transition:false,//调拨 发货人是osp
          IFSelectReceiveMeng:false,//调拨 查看收货方/发货方全称 弹出框是否显示
          ReceiveORDeliverName:'',//调拨 收货方/发货方全称
          ReceiveORDeliverHeaderName:'',//调拨 收货方/发货方全称 弹出框标题名字
          visitId:0,//拜访id
          CurrentStoresId:0,//门店ID
          IfTextShow:false,//建议订单时 跳过按钮显示
          TextContent:'跳过',//建议订单时 跳过按钮文字
          noDataShow:false,//页面数据
          // 上拉加载
          numberPerPage:15,//每页显示几条
          loadPage: 1 ,//当前第几页
          allLoaded:true,//上拉刷新还能上拉刷新
          autoFill:false,//
          searchValue:'',//搜索的值
          promotionName:'',//页面显示的促销名称
        }
      },
      components:{
      componentHeader,
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.resquestProductList();
      }
    },
    mounted(){

      window.history.pushState(null, null, document.URL)
      window.addEventListener("popstate", this.onBrowserBack, false)

      var LowerBodyHeight = parseFloat($('.fixedTop').css('height')) + parseFloat($('header').css('height')) + 'px';

      $('.LowerBody').css('paddingTop',LowerBodyHeight);
    },
    destroyed() {

      window.removeEventListener("popstate", this.onBrowserBack, false)
    },
    created(){

      var that = this;

      var BackBottleData = this.$store.state.BackBottle;

      // 全局的已经提交的数据
      this.submitArr = BackBottleData.g_submitArr;//购物车数据列表
      this.ReceiptInfo = BackBottleData.ConsigneeData_selected;//收货人信息
      this.DeliveryInfo = BackBottleData.DeliveryAddr_selected;//发货人信息
      this.totalNum = BackBottleData.totalNum;//总数量    

      //哪个页面过来的
      if(this.$route.query.whoPush){ 
        this.whoPush = this.$route.query.whoPush;
      }

      //是什么类型的下单
      if(this.$route.query.orderType){
          this.orderType = this.$route.query.orderType;
      }

       // 收货信息，采购订单/销售出库
      if(this.$route.query.ReceiptInfo){ 

        this.ReceiptInfo = this.$route.query.ReceiptInfo
      }

      // 发货信息，其它出库
      if(this.$route.query.DeliveryInfo){ 

        this.DeliveryInfo = this.$route.query.DeliveryInfo
      }

        // ***********************************************************
      // 采购下单
      if(this.whoPush == 'PurchaseOrder' && this.orderType == 'PURCHASE_IN'){
          this.headerText = '采购下单';
          var getParentData = {
          "customerId":that.ReceiptInfo.CUSTOMER_ID,
          }
          console.log(getParentData)
        that.httpRequest("getCustomerParent.do",getParentData,function(res){

          that.DeliveryInfo = res.data;
        })

        // 合并销售订单 为采购单
        if(this.$route.query.CombinedOrder && this.$route.query.CombinedOrder =='CombinedOrder'){
          var CombinedData = {
            "outOrderIds":that.$store.state.SumMergeOrderList,
            'consignee_id':that.ReceiptInfo.CUSTOMER_ID,//osp id
          }
          console.log(CombinedData)
          that.httpRequest("mergeSaleOrder.do",CombinedData,function(res){

            console.log(res)
            that.submitArr = res.data;
            var totalNum = 0;

            for(var tty =0 ;tty < res.data.length;tty++){

              totalNum = parseInt(totalNum) + parseInt(res.data[tty].number);
            }
            
            that.totalNum = totalNum;
          })
        }
      }
      // 销售下单
      if(this.whoPush == 'SalesOrder' && this.orderType == 'SALE_OUT'){

        if(that.$store.state.G_AppOrWX == 'WX'){
          this.headerText = '产品订购';

        }else{
          this.headerText = '销售下单';
        }
      }

      // 建议下单
      if((this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){
          
          this.headerText = '建议订单';//建议订单时 页面标题
          this.IfTextShow = true;//建议订单时 跳过按钮显示

          // 拜访id
        if(this.$route.query.visitId){ 

          this.visitId = this.$route.query.visitId;
        }

        if(!BackBottleData.totalNum){

          // 门店ID
          if(this.$route.query.CurrentStoresId){ 

            this.CurrentStoresId = this.$route.query.CurrentStoresId;
          }

          var getStoresData = {
            "cellphone":that.$store.state.userInfo.cellphone,//手机号
            "userId":that.$store.state.userInfo.user_id,//用户id
            "storesId":that.CurrentStoresId,//门店id
          }
          console.log(getStoresData)
          // 获取门店信息
          that.httpRequest("GetStoreOne.do",getStoresData,function(storesRes){
            console.log(storesRes)

            that.ReceiptInfo = storesRes.data;//收货人信息
            that.DeliveryInfo = storesRes.data.suppliers;//发货人信息
          })
        }
      }

      // 其他入库
      if(this.whoPush == 'InventoryManage' && this.orderType == 'OTHER_IN'){

        this.headerText = '其他入库';
        // 是否显示选择门店，销售退货、其他出入库时才用到
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "选择发货方";
        // 门店信息
        if(this.$route.query.CustomerInfo){
          this.DeliveryInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.DeliveryInfo.CUSTOMER_NAME;
      }

      // 其他出库
      if(this.whoPush == 'InventoryManage' && this.orderType == 'OTHER_OUT'){

        this.headerText = '其他出库';
        // 是否显示选择门店，销售退货、其他出入库时才用到
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "选择收货方";
        // 门店信息
        if(this.$route.query.CustomerInfo){
          this.ReceiptInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.ReceiptInfo.CUSTOMER_NAME;
      }
      
      // 销售退货(针对门店)
      if(this.whoPush == 'InventoryManage' && this.orderType == 'SALE_RETURN_IN'){
          
        this.headerText = '销售退货';

        // 是否显示选择门店，销售退货、其他出入库时才用到
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "选择门店";
        // 门店信息
        if(this.$route.query.CustomerInfo){
          this.DeliveryInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.DeliveryInfo.CUSTOMER_NAME;
      }

      // 销售退货(部分退货  针对销售订单)
      if(this.whoPush == 'OutboundDetail' && this.orderType == 'SALE_RETURN_IN_PART'){
      
        this.headerText = '部分退货';
      }
      // 调拨
      if(this.whoPush == 'InventoryManage' && this.orderType == 'TRANSFER'){
          
        this.headerText = '调拨';
        this.isShowSelectReceiveAndDeliver = true;

        // 已经选择过了
        if(that.$route.query.IFreceORdeliv_transition){
          this.IFreceORdeliv_transition = that.$route.query.IFreceORdeliv_transition;
        }else{
          var ProductData = {
            "storeName":'',//货品名称
            "cellphone":that.$store.state.userInfo.cellphone,//电话
            "userId":that.$store.state.userInfo.user_id,//用户id
            "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
            "currentPage":1,//当前页数
            "numberPerPage":5,//一页多少条
          }
          console.log(ProductData)
          // 查询业务员列表
          that.httpRequest("findUserList.do",ProductData,function(StoresRes){
            console.log(StoresRes)

            if(StoresRes.data.length==1){
                
              that.ReceiptInfo = {
                "CUSTOMER_ID":StoresRes.data[0].APP_USERS_ID,
                "CONTACT_NAME":StoresRes.data[0].USER_NAME,
                "CONTACT_TEL":StoresRes.data[0].CELLPHONE,
                "CUSTOMER_TEL":StoresRes.data[0].CELLPHONE,
              }
              that.receORdeliv_consignee = that.ReceiptInfo.CUSTOMER_NAME?that.ReceiptInfo.CUSTOMER_NAME:that.ReceiptInfo.CONTACT_NAME;//收货人  调拨
            }
          })
        }

        // false 时发货人为osp  true时发货人为车销
        if(this.IFreceORdeliv_transition){
          // 业务员信息
          if(this.$route.query.CustomerInfo){
            this.DeliveryInfo = this.$route.query.CustomerInfo;
          }
        }else{
          // 业务员信息
          if(this.$route.query.CustomerInfo){
            this.ReceiptInfo = this.$route.query.CustomerInfo;
          }
        }
        this.receORdeliv_consignee = this.ReceiptInfo.CUSTOMER_NAME?this.ReceiptInfo.CUSTOMER_NAME:this.ReceiptInfo.CONTACT_NAME;//收货人  调拨
        this.receORdeliv_consignor = this.DeliveryInfo.CUSTOMER_NAME?this.DeliveryInfo.CUSTOMER_NAME:this.DeliveryInfo.CONTACT_NAME;//发货人  调拨
      }

      var TypeData = {
        "businessId":this.$store.state.businessInfo.businessId,
        "appUsersType":this.$store.state.G_AppOrWX,//判断是app还是公众号
        "appUsersId": that.$store.state.userInfo.user_id,//用户id
      }
      console.log(TypeData)
      // 请求商品品牌和分类
      this.httpRequest("searchProductType.do",TypeData,function(res){

        that.firstReq = true;//是否是刚进页面
        that.noDataShow = false;//页面没有数据图标是否显示
        that.HnavBrandList = res.data; // 品牌、系列
        
        that.HnavBrandIndex = 0;
        that.VnavTypeIdx=0;

        if(res.data.length){

        that.VnavTypeList = res.data[0].TYPE;
         
        // 请求商品列表
        that.resquestProductList();

        }else{

        that.noDataShow = true;
        }
      })
    },
    methods:{
      onBrowserBack() {
        
        this.f_Fanhui()
      },
      // 返回
      f_Fanhui(){
          
        var that = this;

        this.$store.state.BackBottle={
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//收货人信息
          DeliveryAddr_selected:[],//发货人信息
        };//清空购物车数据
        this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据
        this.$store.state.OptionsGiftNumberList = {};//添加赠品页面 number二维json 
        this.$store.state.ORDER_REQUEST_DELIVERY='';//要求送货时间
        this.$store.state.ORDER_REMARK_VALUE='';//下单备注

        if(this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT'){
        //建议订单 检查库存跳转来

          // 离店/查看拜访 跳转来编辑
