      // 公共请求
      resquestProductList(load){
        var that = this;
        var transferOutId = '',consignee_id = '';

        // 销售退货(部分退货  针对销售订单)
        if(this.whoPush == 'OutboundDetail' && this.orderType == 'SALE_RETURN_IN_PART'){
          transferOutId = this.$route.query.transferOutId;
        }

        // 下 采购单、下建议订单 和下 销售单 时才用得到consignee_id
        // 采购单  销售单  赋值consignee_id
        if((this.whoPush == 'PurchaseOrder' && this.orderType == 'PURCHASE_IN')||(this.whoPush == 'SalesOrder' && this.orderType == 'SALE_OUT')){
          consignee_id = this.ReceiptInfo.CUSTOMER_ID;
        }

        // 建议下单  赋值consignee_id
        if((this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){
          consignee_id = this.CurrentStoresId;
        }

        var ProductData = {
          "productName":that.searchValue,
          "businessId":that.$store.state.businessInfo.businessId,
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "productBrand":that.HnavBrandList[that.HnavBrandIndex].PRODUCT_BRAND_ID,
          "productType":that.VnavTypeList[that.VnavTypeIdx].PRODUCT_TYPE_ID,
          "transferOutId":transferOutId,//销售单id 销售退货（根据订单 部分退货）所用
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
          'consignee_id':consignee_id,//收货人id
          "userType":that.$store.state.G_AppOrWX,//判断是app还是公众号
        }

        // 调拨
        if(that.whoPush == "InventoryManage" && that.orderType == "TRANSFER"){

          // false 时发货人为osp  true时发货人为车销
          if(that.IFreceORdeliv_transition){

            if(that.receORdeliv_consignor){
              ProductData.opeType = 'IN';//IN是从车销调到仓库   OUT是从仓库调到车销

              ProductData.dbUserId = that.DeliveryInfo.CUSTOMER_ID;//调拨userid  当类型为IN时 才有这个参数
              ProductData.superiorId = that.ReceiptInfo.CUSTOMER_ID;//ospid
            }
          }else{

            ProductData.opeType = 'OUT';//IN是从车销调到仓库   OUT是从仓库调到车销
            ProductData.superiorId = that.DeliveryInfo.CUSTOMER_ID;//ospid
          }
        }
        // 下销售单
        else if(that.orderType == "SALE_OUT"){
          ProductData.superiorId = that.DeliveryInfo.CUSTOMER_ID;//ospid（供货商id）
        }

        // 合并销售单
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
           ProductData.outOrderIds = that.$store.state.SumMergeOrderList;//销售出库单合集数组
           console.log(that.$store.state.SumMergeOrderList)
        }
        console.log(ProductData)
        that.httpRequest("searchProductList.do",ProductData,function(res){
          console.log(res.data)
          
          if(load && load == "BottomLoad"){

            that.$refs.loadmore.onBottomLoaded();
          }

          for(var i = 0;i<res.data.length;i++){

            that.number[res.data[i].PRODUCT_ID] = 0;
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(res.data.length){
              that.goodsList = that.goodsList.concat(res.data);
            }
          }
          else {
            that.goodsList = res.data;
          }

          // 判断是否能上拉加载
          if (res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          }
          else {
            that.allLoaded = false;
          }

          var BackBottleData = that.$store.state.BackBottle;

          // 刚进页面
          if(that.firstReq){

            that.firstReq = false;

            //计算总价格 
            for(var j = 0; j < that.submitArr.length; j++){

              that.totalPrice += that.submitArr[j].number * that.submitArr[j].DIS_PRICE;
            }

            // 改变页面数量
            for(var i = 0; i < that.goodsList.length; i++){

              for(var j = 0; j < that.submitArr.length; j++){

                if(that.submitArr[j].PRODUCT_ID == that.goodsList[i].PRODUCT_ID){

                  // 产品列表数据数量改变
                  that.number[that.goodsList[i].PRODUCT_ID] = that.submitArr[j].number;
                  break;
                }
              }
            }
            that.totalPrice = that.totalPrice.toFixed(2);

          }else{
            // 改变页面数量
            for(var i = 0; i < that.goodsList.length; i++){

              for(var j = 0; j < that.submitArr.length; j++){

                if(that.submitArr[j].PRODUCT_ID == that.goodsList[i].PRODUCT_ID){

                  // 产品列表数据数量改变
                  that.number[that.goodsList[i].PRODUCT_ID] = that.submitArr[j].number;
                  break;
                }
              }
            }
          }
        })
      },
