          }

           //其他入库
          if(that.whoPush == "InventoryManage" && that.$route.query.orderType=='OTHER_IN'){
            
            if(!that.SelectedInfoName) {
              that.$alert("请选择发货方", '提示', {
                confirmButtonText: '确定'
              })
              return;
            }
          }

           //其他出库
          if(that.whoPush == "InventoryManage" && that.$route.query.orderType=='OTHER_OUT' ){
            
            if(!that.SelectedInfoName) {
              that.$alert("请选择收货方", '提示', {
                confirmButtonText: '确定'
