          }

          that.$store.state.BackBottle.g_submitArr = that.submitArr;
          that.$store.state.BackBottle.totalNum = that.totalNum;// 总数量
          that.$store.state.BackBottle.ConsigneeData_selected = that.ReceiptInfo;//收货人信息
          that.$store.state.BackBottle.DeliveryAddr_selected = that.DeliveryInfo;//发货人信息

          var query;
