      //输入产品数量输入框   取消按钮
      cancel_entry(){

      this.isHidden = false;
      },
      // 输入产品数量输入框   确认按钮
      ensure_entry() {
        var self=this;

        // 判断输入的是否为数字，不是数字则return
        if (parseInt(self.inputValue).toString() == "NaN" || parseInt(self.inputValue)<0) {

          this.isHidden = false;
           return;
        }

        var number = this.number;
        var submitArr = this.submitArr;

        // 销售退货(部分退货  针对销售订单)
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){

          if(parseInt(self.inputValue)>self.goodsList[self.inputNum].KX_NUM){
              return;
          }
        }

        var isRepeat = false;

        var totalNum = this.totalNum;

        var totalPrice = this.totalPrice;

        if (submitArr.length){

          if (self.element == '页面') {

            for (var i = 0; i < submitArr.length; i++) {

              if (submitArr[i].PRODUCT_ID == self.goodsList[self.inputNum].PRODUCT_ID) {

                submitArr[i].number = parseInt(self.inputValue);

                isRepeat = true;
              }
            }

            if (!isRepeat && self.inputValue != 0) {

              submitArr[submitArr.length] = self.goodsList[self.inputNum];

              submitArr[submitArr.length - 1].number = parseInt(self.inputValue);
            } 

            // 产品总数量重新计算
            totalNum = parseInt(self.totalNum) - number[self.goodsList[self.inputNum].PRODUCT_ID] + parseInt(self.inputValue);

            //产品总价格重新计算
            totalPrice = parseFloat(self.totalPrice) + (parseInt(self.inputValue) - number[self.goodsList[self.inputNum].PRODUCT_ID]) * self.goodsList[self.inputNum].DIS_PRICE;
            
            //页面上number重新显示
            number[self.goodsList[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);

            // 输入产品数量如果小于零，该物品在购物车中消失
            if (self.inputValue == 0) {

              for (var j = 0; j < submitArr.length; j++) {

                if (submitArr[j].PRODUCT_ID == self.goodsList[self.inputNum].PRODUCT_ID) {

                  submitArr.splice(j, 1);
                }
              }
            }

          } else if (self.element == '购物车') {

            // 产品总数量重新计算
            totalNum = parseInt(self.totalNum) - submitArr[self.inputNum].number + parseInt(self.inputValue);

            //产品总价格重新计算
            totalPrice = parseFloat(self.totalPrice) + (parseInt(self.inputValue) - parseInt(submitArr[self.inputNum].number)) * submitArr[self.inputNum].DIS_PRICE;

            //页面上number重新显示
            number[submitArr[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);

            submitArr[self.inputNum].number = parseInt(self.inputValue);

            // 输入产品数量如果小于零，该物品在购物车中消失
            if (self.inputValue == 0) {  

              submitArr.splice(self.inputNum, 1);
            }
          }
      
        }else{

          if (self.inputValue != 0 ){

            submitArr[0] = self.goodsList[self.inputNum];

            submitArr[0].number = parseInt(self.inputValue);

            totalNum = parseInt(self.inputValue);

            totalPrice = totalNum * self.goodsList[self.inputNum].DIS_PRICE;

            number[self.goodsList[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);
          }
        }

        this.isHidden = false;
        this.number =number;
        this.submitArr = submitArr;
        this.totalNum = totalNum;
        this.totalPrice = totalPrice.toFixed(2);
      },
      //购物车内产品数量点击弹出  输入产品数量输入框
      entryShow(e, v){

        event.stopPropagation();

        var self=this;

        this.clearNum();

        if (e.currentTarget.dataset.element=='页面'){

          this.element = '页面';
