          this.element = '购物车';
        }

        this.isHidden = true;
        this.inputNum = e.currentTarget.id;//点击的输入框的idx

        // 自动聚集
        this.$nextTick(function(){
          if(v != 0){
            $("#autofocus_input").val(v)
          }
          $("#autofocus_input").focus()
          $("#autofocus_input").select()
        })
      },
      // 清除input框数据
      clearNum(){
        this.inputValue = "";
        // this.$refs['autofocus_input'].focus();
      },
      // 列表数量  减
      jian(e){

        event.stopPropagation();
      
        var self = this;
  
        if (self.number[self.goodsList[e.currentTarget.id].PRODUCT_ID]>0){

          var number = self.number;

          number[self.goodsList[e.currentTarget.id].PRODUCT_ID] = number[self.goodsList[e.currentTarget.id].PRODUCT_ID] - 1;

          var totalPrice = parseFloat(self.totalPrice) - parseFloat(self.goodsList[e.currentTarget.id].DIS_PRICE);

          var submitArr = self.submitArr;

          // 购物车中的数量减少
          for (var i = 0; i < submitArr.length;i++){
    
            if (submitArr[i].PRODUCT_ID == self.goodsList[e.currentTarget.id].PRODUCT_ID){
    
              submitArr[i].number = number[self.goodsList[e.currentTarget.id].PRODUCT_ID];

              // 购物车物品数量如果小于零，该物品消失
              if (number[self.goodsList[e.currentTarget.id].PRODUCT_ID] == 0) {
    
                submitArr.splice(i, 1);
              }
            }
          }

          self.number = number;
          self.totalNum = self.totalNum - 1;
          self.totalPrice =  totalPrice.toFixed(2);
          self.submitArr = submitArr;
        }
      },
      // 列表  加
      jia(e){
        event.stopPropagation();
        var self = this,
        number = this.number;

        // 销售退货(部分退货  针对销售订单)
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){
          if((number[self.goodsList[e.currentTarget.id].PRODUCT_ID] + 1) > parseInt(self.goodsList[e.currentTarget.id].KX_NUM)){
            return;
          }
        }

        number[self.goodsList[e.currentTarget.id].PRODUCT_ID] = parseFloat(number[self.goodsList[e.currentTarget.id].PRODUCT_ID])+1;

        var totalPrice = parseFloat(self.totalPrice) + parseFloat(self.goodsList[e.currentTarget.id].DIS_PRICE);

        var submitArr = self.submitArr;

        var isRepeat=false;

        if (submitArr.length==0){

          submitArr[0] = self.goodsList[e.currentTarget.id];
          submitArr[0].number = 1;
         
        }else{
          for (var j = 0; j < submitArr.length; j++) {

            if (submitArr[j].PRODUCT_ID == self.goodsList[e.currentTarget.id].PRODUCT_ID) {

              isRepeat = true;
              submitArr[j].number = number[self.goodsList[e.currentTarget.id].PRODUCT_ID];
            }
          }

          if (!isRepeat){

            submitArr[submitArr.length] = self.goodsList[e.currentTarget.id];

            submitArr[submitArr.length-1].number = 1;
          }
        }

        this.number = number;
        this.submitArr = submitArr;
        this.totalNum = self.totalNum + 1;
        this.totalPrice =  totalPrice.toFixed(2);
      },
      // 购物车 减
      jian2(e){ 

        event.stopPropagation();

        var self=this;

        var submitArr = this.submitArr;
        var g_number=this.number;

        submitArr[e.currentTarget.id].number--;

        // 总价  减少
        var totalPrice = parseFloat(self.totalPrice) - parseFloat(self.submitArr[e.currentTarget.id].DIS_PRICE);

        for (var i = 0; i < self.goodsList.length; i++) {

          if (self.goodsList[i].PRODUCT_ID == submitArr[e.currentTarget.id].PRODUCT_ID) {

            g_number[self.goodsList[i].PRODUCT_ID]--;
           
          }
        }

        // 购物车物品数量如果小于零，该物品消失
        if (submitArr[e.currentTarget.id].number == 0) {

          submitArr.splice(e.currentTarget.id, 1);

          self.$nextTick(function() {
            var footBottom_height= $(".footBo_move").css("height");

            $(".footBo_move").animate({"marginTop": "-" + footBottom_height},300);
          })
        } 

        this.number = g_number;
        this.totalNum = self.totalNum - 1;
        this.totalPrice = totalPrice.toFixed(2);
        this.submitArr = submitArr;
      },
      // 购物车 加
      jia2(e){

        event.stopPropagation();

        var self = this,
        submitArr = this.submitArr,
        g_number = this.number;

        // 销售退货(部分退货  针对销售订单)
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){

          if((submitArr[e.currentTarget.id].number +1)>submitArr[e.currentTarget.id].KX_NUM){
            return;
          }
        }

        submitArr[e.currentTarget.id].number++;

        // 总价增加
        var totalPrice = parseFloat(self.totalPrice) + parseFloat(self.submitArr[e.currentTarget.id].DIS_PRICE);

        for (var i = 0; i < self.goodsList.length; i++) {
          if (self.goodsList[i].PRODUCT_ID == submitArr[e.currentTarget.id].PRODUCT_ID) {

            g_number[self.goodsList[i].PRODUCT_ID]++;
          }
        }

        this.submitArr = submitArr;
        this.number = g_number;
        this.totalNum = self.totalNum + 1;
        this.totalPrice = totalPrice.toFixed(2);
      },
      // 展开、收起 促销信息
      showPromotionInfo(idx){
        if($('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').css('display') == 'none'){
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionShow .icon-xiangxiaxialakuang').css('transform','rotate(0deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').stop().show(500);
        }else{
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionShow .icon-xiangxiaxialakuang').css('transform','rotate(-90deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').stop().hide(500);
        }
      },
    
      // 展开策略说明
      promotionInstruction(idx,PIindex){
        event.stopPropagation();
        if($('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').css('display') == 'none'){
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .PI-nav-d .icon-xiangxiaxialakuang').css('transform','rotate(0deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').stop().show(500);
        }else{
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .PI-nav-d .icon-xiangxiaxialakuang').css('transform','rotate(-90deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').stop().hide(500);
        }
      },
      // 调拨 关闭查看收发货方全称弹出框
      SelectReceiveMengHide(){
        this.IFSelectReceiveMeng = false;
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){

          that.$refs.loadmore.onBottomLoaded();
          return;
        }else{

          that.loadPage += 1;
        }
        this.resquestProductList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ChooseGoods{
    height: 100%;
    overflow: hidden;
    .container{
      overflow: scroll;
      box-sizing:border-box;
      font-size: 28/50rem;
      height: 100%;
      .fixedTop{
        position:fixed;
        top:89/50rem;
        left: 0;
        width: 100%;
        z-index: 33;
        .selectStore{
          background-color: #EDEDED;
          padding-left: 20/50rem;
          font-size: 30/50rem;
          padding-top: 20/50rem;
          padding-bottom: 20/50rem;
        }
        .selectReceiveAndDeliver{
          width: 100%;
          height: 143/50rem;
          margin-bottom: 6/50rem;
          overflow: hidden;
          .select_transition{
            float: right;
            width: 123/50rem;
            position:relative;
            height: 143/50rem;
            background-color:#fff;
            i{
              color: @PublicColor;
              font-size: 50/50rem;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%,-50%);
            }
          }
          &>div:not(.select_transition){
            float: left;
            width:627/50rem;
            height: 70/50rem;
            line-height: 70/50rem;
            padding-left:20/50rem;
            box-sizing: border-box;
            border-bottom: 3/50rem solid #fff;
            &>div{
              position:relative;
              overflow: hidden;
              display: flex;
              span:nth-child(1){
                width: 130/50rem;
                flex-shrink:0;
              }
              span:nth-child(2){
                flex-grow: 1;
                overflow: hidden;
                text-overflow: ellipsis;/*文字隐藏后添加省略号*/
                white-space: nowrap;/*强制不换行*/
              }
              i{
                position:absolute;
                right: 20/50rem;
                top: 50%;
                transform: translateY(-50%);
              }
            }
          }
        }
      }
      .HorizontalNav{
        border-bottom:1/50rem solid #aaa;
        text-align: left;
        font-size: 28/50rem;
        height: 100/50rem;
        width: 100%;
        background-color:#F4F4F4;
        overflow-x:scroll;
        overflow-y:hidden;
        white-space:nowrap;
        &>div.BrandItem{
          display:inline-block;
          line-height: 100/50rem;
          padding:0 20/50rem;
        }
      }
      .Hnav_active{
        height: 95/50rem;
        color: @PublicColor;
        border-bottom:5/50rem solid @PublicColor;
      }
      .OtherNav{
        background-color:#f8f8f8;
        width: 100%;
        height: 80/50rem;
        line-height: 80/50rem;
        font-size: 29/50rem;
        display: flex;
        .CSearch{
          flex-grow: 1;
          &>div{
            position:relative;
            i{
              position:absolute;
              top: 0;
              left:45/50rem;
            }
            input{
              width: 500/50rem;
              padding-left: 70/50rem;
              box-sizing:border-box;
              line-height:60/50rem;
              margin-left: 20/50rem;
              border-radius:30/50rem;
            }
          }
        }
        .otherInfoBtn{
          flex-shrink:0;
          text-align: center;
          width: 200/50rem;
          margin-right: 20/50rem;
          height: 80/50rem;
          &>div{
            height: 60/50rem;
            background-color: #ddd;
            line-height: 60/50rem;
            border-radius: 30/50rem;
            margin-top: 10/50rem;
            font-size: 26/50rem;
          }
        }
      }
      .LowerBody{
        height: 100%;
        background-color:#F4F4F4;
        padding-top:270/50rem;
        box-sizing:border-box;
        overflow: hidden;
        .VerticalNav{
          overflow:scroll;
          height: 100%;
          width: 100/50rem;
          float: left;
          font-size: 28/50rem;
          background-color:#F4F4F4;
          &>div{
            text-align: center;
            line-height: 42/50rem;
            padding-top: 20/50rem;
            padding-bottom: 20/50rem;
          }
          &>.navKong{
            display: inline-block;
            height: 100/50rem;
          }
          .Vnav_active{
            background-color:#fff;
            color: @PublicColor;
            border-left: 5/50rem solid @PublicColor;
          }
        }
        // 选择商品
        .GoodsDisplay{
          width: 650/50rem;
          float: left;
          height: 100%;
          overflow-y: scroll;
          overflow-x: hidden;
          box-sizing:border-box;
          background-color:#fff;
          padding-bottom: 150/50rem;
          .selectGoodsList{
            border-bottom: 3/50rem solid #f4f4f4;
            .selectGoodsListItem{
              width: 100%;
              box-sizing:border-box;
              font-size: 26/50rem;
              padding: 10/50rem 0;
              display: flex;
              .LeftIcon{
                width:120/50rem;
                position:relative;
                flex-shrink:0;
                img{
                  width: 90/50rem;
                  position:absolute;
                  top:50%;
                  left: 50%;
                  transform: translate(-50%,-50%);
                }
              }
              .listCenter{
                flex-grow: 1;
                .goodsNumame{
                  font-size: 28/50rem;
                  font-weight: 600;
                  line-height: 60/50rem;
                }
                .specifications{
                  color: #777;
                  line-height: 40/50rem;
                }    
                .moneyGroup{
                  overflow: hidden;
                  line-height: 75/50rem;
                  &>div:first-child{
                    float: left;
                    color: red;
                    font-size: 35/50rem;
                  }
                  .InventoryQtyClass{
                    color: #666;
                    font-size: 26/50rem;
                    display: inline-block;
                    margin-left: 20/50rem;
                  }
                }
              }
            }
            .promotionInfo{
              width: 100%;
              line-height: 50/50rem;
              .promotionShow,.promotionDeShow{
                .icon-xiangxiaxialakuang{
                  transform: rotate(-90deg);
                  transition: all 0.5s;
                  color: #666;
                }
              }
              .promotionHide,.promotionDeHide{
                display: none;
                div{
                  .PI-icon-d{
                    float: left;
                    width: 120/50rem;
                  }
                  .PI-name-d{
                    word-wrap: break-word;
                    float: left;
                    width: calc(100% - 3.4rem);
                  }
                  .PI-nav-d{
                    height: 50/50rem;
                    float: right;
                    width: 50/50rem;
                  }
                  overflow: hidden;
                  margin-bottom: 5/50rem;
                  .icon-cuxiao{
                    color: @PublicColor;
                    margin-left: 60/50rem;
                    margin-right: 15/50rem;
                  }
                  .icon-xiangxiaxialakuang{
                    transform: rotate(-90deg);
                    transition: all 0.5s;
                    color: #666;
                    margin-left: 0;
                  }
                }
              }
              .promotionDeHide{
                div{
                  padding-left: 60/50rem;
