        .Info{
          width: 100%;
          font-size: 28/50rem;
          &>div{
            line-height: 50/50rem;
            border-top:3/50rem solid @PublicColor;
            padding-top:20/50rem;
            &>div{
              display: flex;
              span:first-child{
                min-width: 130/50rem;
                flex-shrink:0;
              }
