<template>
  <div class="AppQuestion">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="QuestionSearch">
        <div>
          <input  v-model="searchValue"  placeholder="按问题名称搜索">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="QuestionList">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="QuestionItem" v-for='(dataItem,index) in Questiondata' :id="index"  :key='index'>
            <div class="getIndex" @click='toQuestionInfo(index)'>
            <i class="iconfont icon-jiantouzuo"></i>{{dataItem.problem}}</div>
          </div>
        </mt-loadmore>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"常见问题",//页面标题
        searchValue:'',//搜索的值
        Questiondata:[],//常见问题列表
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqProblemList();
      }
    },
    created(){
     
      this.reqProblemList();
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	},
      // 请求常见问题列表
      reqProblemList(load){
        var that = this;
        var postData = {
          "strProblem":that.searchValue,
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        // 获取常见问题列表信息
        that.httpRequest("GetCommonProblemList.do",postData,function(Res){

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(Res.data.length){
              that.Questiondata = that.Questiondata.concat(Res.data);
            }
          } else {
            that.Questiondata = Res.data;
          }

          // 判断是否能上拉加载
          if (Res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.Questiondata.length){
            that.noDataShow = false;
           
          }else{

            that.noDataShow = true;
          }
        })
      },
      // 跳转到 问题详情 页面
      toQuestionInfo(index){
        var QuestionItem = this.Questiondata[index];
        this.$router.push({
          name:"Answer",
          query:{ 
            QuestionItem:QuestionItem,
            index:index
          }
        }) 
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.reqProblemList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqProblemList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .AppQuestion{
    overflow-x: hidden;
    height: 100%;
    .container{
      margin-top: 0;
      height: 100%;
      .QuestionSearch{
        .TopSearchBox;
        .headerFlex;
        top: 89/50rem;
      }
      .QuestionList{
        overflow: scroll;
        height: 100%;
        padding-top: 190/50rem;
        box-sizing:border-box;
        .QuestionItem{
          width: 100%;
          height: 80/50rem;
          line-height: 80/50rem;
          background-color: #fff;
          padding-left: 20/50rem;
          box-sizing: border-box;
          border-bottom:1/50rem solid @PublicBorderColor;
          overflow: hidden;
          .getIndex{
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
</style>


