<template>
  <div class="RecomRoute">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container" id="container"> </div>
    <div id="panel"></div>
    <div v-if="ifTips" class="msg_tips_text">
      <div class="tips_content">
        <p>{{tips_Msg}}</p>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">

  import componentHeader from '@/components/componentHeader'
  import $ from "jquery"
  export default{
    data(){
      return{
        headerText:"推荐线路", //页面标题
        lngLats:[],
        lineArr:[],
        map_g:"",
        suppliersId:"",
        VisitRouteId:"",
        CustomerListData:[],
        myInterval:"", // 等待标注计时器
        myInterval_onComplete:"", // 获取位置成功时，等待画线计时器
        myInterval_onError:"", // 获取位置失败时，等待画线计时器
        geoShopList:[], // 需要地址转坐标的门店
        geoTotalQty:0, // 有多少数量需要地址转坐标
        geoOkQty:0, // 完成多少个地址转坐标
        ifTips:false,
        tips_Msg:"",
      }
    },
    components:{
      componentHeader,
    },
    destroyed(){
      clearInterval(this.myInterval)
      clearInterval(this.myInterval_onComplete)
      clearInterval(this.myInterval_onError)
    },
    mounted(){

      var that = this

      // 加载地图
      var map = new AMap.Map('container', {
        resizeEnable: true,
      })
      that.map_g = map

      // 获取当前位置
      AMap.plugin('AMap.Geolocation', function() {
        var geolocation = new AMap.Geolocation({
          enableHighAccuracy: true,//是否使用高精度定位，默认:true
          timeout: 10000,          //超过10秒后停止定位，默认：5s
          buttonPosition:'RB',    //定位按钮的停靠位置
          buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
          // zoomToAccuracy: true,   //定位成功后是否自动调整地图视野到定位点
          zoom: 5,
        })
        map.addControl(geolocation)
        geolocation.getCurrentPosition(function(status,result){
          if(status=='complete'){
            that.onComplete(result)
            map.zoom = 5
          }else{
            that.onError(result)
          }
        })
      })

      if(that.$route.query.suppliersId){
        that.suppliersId = that.$route.query.suppliersId
      }

      if(that.$route.query.VisitRouteId){
        that.VisitRouteId = that.$route.query.VisitRouteId
      }

      var LineData = {
          "suppliersId":that.suppliersId,//供货商id
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "userId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "strLineId":that.VisitRouteId,//线路id
          "strStoreName":"",//门店名称
          "strStoreState":"全部",//拜访状态
          "currentPage":1,//当前页数
          "numberPerPage":999,//一页多少条
      }

      // 获取门店信息
      that.httpRequest("findStores.do",LineData,function(res){

        that.CustomerListData = res.data

        that.lngLats = []
        that.geoShopList = []
        that.geoTotalQty = 0
        that.geoOkQty = 0
        for(var i = 0; i < that.CustomerListData.length; i++){

          var lngLat = []

          if(that.CustomerListData[i].longitude == 0 || that.CustomerListData[i].longitude == "" ||
            that.CustomerListData[i].latitude == 0 || that.CustomerListData[i].latitude == "" ){
              that.geoTotalQty++
              that.geoShopList.push(that.CustomerListData[i])
          }
          if(that.CustomerListData[i].longitude == 0 || that.CustomerListData[i].longitude == "" ||
            that.CustomerListData[i].latitude == 0 || that.CustomerListData[i].latitude == "" ){
              var geocoder = new AMap.Geocoder({
                // city: "0755", //城市设为北京，默认：“全国”
              })
              geocoder.getLocation(that.CustomerListData[i].address1, function(status, result) {
              if (status === 'complete' && result.info === 'OK') {
                that.geoOkQty++
                lngLat = [result.geocodes[0].location.lng, result.geocodes[0].location.lat]
                that.lngLats.push(lngLat)
              }else{
                log.error('根据地址查询位置失败')
                that.geoOkQty++
                // GEO失败后，赋于深圳市中心坐标，保证坐标数组与门店数组一一对应关系
                lngLat = [114.085947,22.547]
                that.lngLats.push(lngLat)
              }
            })
          }else{

            lngLat = [that.CustomerListData[i].longitude, that.CustomerListData[i].latitude]
            that.lngLats.push(lngLat)
          }
        }

        if(that.geoTotalQty == 0){

          that.drawMarker(map, "that.lngLats")
        }else{
          // 等待地址转坐标geo队列完成
          that.myInterval = window.setInterval(() => {
            console.log("that.geoOkQty:" + that.geoOkQty + "  that.geoTotalQty：" + that.geoTotalQty)
            if(that.geoOkQty == that.geoTotalQty){
              console.log("geo完毕")
              clearInterval(that.myInterval)

              if(that.geoShopList.length <= 2){
                var name = ""
                for(var j = 0; j < that.geoShopList.length; j++) {
                  name = name + that.geoShopList[j].storesName + "，"
                }
                name = name.substr(0, name.length - 1)
                that.tips_Msg = name + "未维护坐标，线路可能不准确"
              }else{
                that.tips_Msg = "有" + that.geoShopList.length + "个门店未维护坐标，线路可能不准确"
              }
              that.ifTips = true
              setTimeout(function(){
                that.ifTips = false
              },5000)

              that.drawMarker(map, that.lngLats)
            }else{
              console.log("等待geo，延迟1秒")
            }
          }, 1000);
        }
      })

      // // 小车
      // var markerCar = new AMap.Marker({
      //   map: map,
      //   position: [114.039706,22.622864],
      //   icon: "https://webapi.amap.com/images/car.png",
      //   offset: new AMap.Pixel(-26, -13),
      //   autoRotation: true,
      //   angle:-90,
      // })
      // // 绘制轨迹
      // var polyline = new AMap.Polyline({
      //   map: map,
      //   path: that.lineArr,
      //   showDir:true,
      //   strokeColor: "#28F",  //线颜色
      //   // strokeOpacity: 1,     //线透明度
      //   strokeWeight: 6,      //线宽
      //   // strokeStyle: "solid"  //线样式
      // })
      // var passedPolyline = new AMap.Polyline({
      //   map: map,
      //   // path: lineArr,
      //   strokeColor: "#AF5",  //线颜色
      //   // strokeOpacity: 1,     //线透明度
      //   strokeWeight: 6,      //线宽
      //   // strokeStyle: "solid"  //线样式
      // })
      // markerCar.on('moving', function (e) {
      //   passedPolyline.setPath(e.passedPath);
      // })

      // markerCar.moveAlong(that.lineArr, 200);

      // map.setFitView();
    },
    created(){

    },
    methods:{
      // 返回上一页
      f_Fanhui(){
        
        if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush =='CreateNewVisit'){
          this.$router.push({
            name:"CustomerVisit",
          })
        }else{
          this.$router.go(-1)
        }
      },
      // 给地图打门店标注
      drawMarker(map, lngLats){
        var that = this;
        that.lineArr = that.lngLats
        // 加载标注
        AMapUI.loadUI(['overlay/SimpleMarker', 'overlay/SimpleInfoWindow'], function(SimpleMarker, SimpleInfoWindow) {

          for (var i = 0, len = that.lngLats.length; i < len; i++) {
            var marker = new SimpleMarker({
              iconStyle: {
                src: 'static/img/Visit/LM_visit_client.png',
                style: {
                  width: '35px',
                  height: '35px'
                }
              },
              //设置基点偏移
              offset: new AMap.Pixel(-17.5, -17.5),
              map: map,
              showPositionPoint: true,
              position: that.lngLats[i],
              zIndex: 100 + i
            })
            // 标注窗体加点击事件
            var infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, -20)})
            var info = []
            info.push("<div style='padding:7px 0px 0px 0px;'><b>" + that.CustomerListData[i].storesName + "</b></div>")
            info.push("<div style='margin-top:7px;'>" + that.CustomerListData[i].CONTACT_NAME + "   " + that.CustomerListData[i].CONTACT_TEL + "</div>")
            info.push("<div>" + that.CustomerListData[i].address1 + "</div>")
            // info.push("<a href='iosamap://navi?sourceApplication=xlwx&poiname=" + that.CustomerListData[i].address1 + "&lat=" + that.lngLats[i][1] + "&lon=" + that.lngLats[0] +"&dev=0&style=2'>导航</a>")
            info.push("<div id='nav' class=" + i + " style='width:40px; height:30px; color:blue;'>导航</div>")

            marker.content = info.join("")
            marker.on('click', markerClick)
            infoWindow.on('open',showInfoOpen)
            infoWindow.on('close',showInfoClose)
          }
          // 点击地图标注
          function markerClick(e) {
            that.markerClick(infoWindow, map, e)
          }
          // 点击地图标注窗体
          function showInfoOpen(){
            setTimeout(function (){
              var fds = document.getElementById("nav") 
              console.log(fds)
              if(fds){
                fds.onclick = () => {
                  var item = that.CustomerListData[fds.className]
                  var lnglat = that.lngLats[fds.className]
                  that.navOnclick(lnglat[0], lnglat[1], item.address1)
                }
              }
            }, 200)
          }
          // 关闭地图标注窗体
          function showInfoClose(){
            console.log("关闭窗体")
          }
          map.setFitView()
        })
      },
      navOnclick(lng, lat, address){

        // 安卓
        try { CallAndroidOrIOS.callAndroid("导航", lng, lat, address) } 
        catch(error) { }
        // 苹果
        try { CallAndroidOrIOS("导航", lng, lat, address) } 
        catch(error) { }
      },
      markerClick(infoWindow, map, e){

        infoWindow.setContent(e.target.content)
        infoWindow.open(map, e.target.getPosition())
      },
      onComplete(data){

        var that = this;
        console.log("获取【当前位置】成功", data)

        if(that.geoTotalQty == 0){
          that.drawDriving(data.position.lng, data.position.lat)
        }else{
          // 等待地址转坐标geo队列完成
          that.myInterval_onComplete = window.setInterval(() => {
            console.log("that.geoOkQty:" + that.geoOkQty + "  that.geoTotalQty：" + that.geoTotalQty)
            if(that.geoOkQty == that.geoTotalQty){
              console.log("onComplete geo完毕")
              clearInterval(that.myInterval_onComplete)
              that.drawDriving(data.position.lng, data.position.lat)
            }else{
              console.log("onComplete等待geo，延迟1秒")
            }
          }, 1000);
        }
      },
      onError(data){

        var that = this
        console.log("获取【当前位置】失败", data)
        if(that.geoTotalQty == 0){
          setTimeout(function (){
            that.drawDriving(102.799574,24.97502)
          }, 900)
        }else{
          // 等待地址转坐标geo队列完成
          that.myInterval_onError = window.setInterval(() => {
            console.log("that.geoOkQty:" + that.geoOkQty + "  that.geoTotalQty：" + that.geoTotalQty)
            if(that.geoOkQty == that.geoTotalQty){
              console.log("onError geo完毕")
              clearInterval(that.myInterval_onError)
              setTimeout(function (){
                that.drawDriving(102.799574,24.97502)
              }, 900)
            }else{
              console.log("onError等待geo，延迟1秒")
            }
          }, 1000);
        }
      },
      // 画线
      drawDriving(lng, lat){
        var that = this
        // 驾车线路
        var driving = new AMap.Driving({
          map: that.map_g,
          panel: "panel",
          hideMarkers:true,
        })
        // endLnglat为终点，lngLats被pop方法后变成途经点数组
        var lngLatsTemp = []
        var lngLatsTemp = lngLatsTemp.concat(that.lngLats)
        var endLnglat = lngLatsTemp.pop()

        // 根据起终点经纬度规划驾车导航路线
        driving.search(new AMap.LngLat(lng,lat), (endLnglat),{
          waypoints:lngLatsTemp
        }, function(status, result) {
          // result 即是对应的驾车导航信息，相关数据结构文档请参考  https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult
          if (status === 'complete') {
            console.log('绘制驾车路线完成')
          } else {
            console.log('获取驾车数据失败：' + result)
          }
        })
        that.map_g.setFitView()
      },
      // iOS/Android原生传过来的当前位置
      SetCurrAddress:function(address, lng, lat, bdcode) {

        this.CurrentLocation = address
        this.lng = lng
        this.lat = lat
      },
    }
  }
</script>
<style lang="less" scoped>
  .RecomRoute{
    width: 100%; 
    height: 100%;
    font-size: 33/50rem;
    .container{
      width: 100%; 
      height: 100%;
    }
    #panel {
      position: fixed;
      background-color: white;
      max-height: 90%;
      overflow-y: auto;
      top: 10px;
      right: 10px;
      width: 280px;
      margin-top: 80/50rem;
      visibility: hidden;
    }
    #panel .amap-lib-driving {
      border-radius: 4px;
      overflow: hidden;
    }
  }
</style>


