<template>
  <div class="InventoryRegDetail">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
  	  <div class='orderInfo'>
  	  	<div class='infoHead'>客户信息</div>
  	    <div class='infoContainer'>
  	      <div><span>业务名称：</span><span>{{Stock.businessName}}</span></div>
          <div><span>客户名称：</span><span>{{Stock.name}}</span></div>
  	      <div><span>客户代码：</span><span>{{Stock.code}}</span></div>
  	      <div><span>库存月份：</span><span>{{Stock.inventoryTime}}</span></div>
  	      <div><span>填表日期：</span><span>{{Stock.createTime}}</span></div>
  		  </div>
  		  <div class='infoHead'>库存信息</div>
    		<div class='infoContainer'  v-for='(itemName,index) in InventoryInfoList'  :id="index"  :key='index'>
    		  <div style="color: #000;font-weight: 900;">{{itemName.PRODUCTNAME}}</div>
          <div>
    		  	<span>产品代码：</span>
            <span>{{itemName.PRODUCTCODE}}</span>
    		  </div>
    		  <div>
    		  	<span>生产日期：</span>
    		  	<span>{{itemName.PRODUCETIME}}</span>
    		  </div>
    		  <div>
    		  	<span>到期日期：</span>
    		  	<span>{{itemName.ENDTIME}}</span>
    		  </div>
    		  <div>
    		  	<div>
    		  	  <span>库存数量：</span>
              <span>{{itemName.INVENTORY}}</span>
    		  	</div>
    		  	<div>
    		  	  <span>保质期：</span>
              <span>{{itemName.PERIODOFVALIDITY}}天</span>
    		  	</div>
    		  </div>
    		  <div>
    		  	<div>
    		  	  <span>产品货龄：</span>
              <span>{{itemName.PRODUCTHL}}</span>
    		  	</div>
    		  	<div>
    		  	  <span>填表时货龄：</span>
              <span>{{itemName.CREATEHL}}</span>
    		  	</div>
    		  </div>
    		  <div>
    		  	<div>
    		  	  <span>货龄状态：</span>
              <span>{{itemName.PRODUCTHLZT}}</span>
    		  	</div>
    		  	<div>
    		  	  <span>填表货龄状态：</span>
              <span>{{itemName.CREATEHLZT}}</span>
    		  	</div>
    		  </div>
    		</div>
	    </div>
	  </div>
    <div v-if="ObjectType == 'store'" class="BtnComponent bottomBtn">
      <div @click='viewAllData'>查看该客户所有库存登记</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"库存登记详情",//页面标题
      	InventoryInfoList:[],//库存信息
	      Stock:{},//客户信息
        ObjectType:'',//是门店还是客户
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
    	var that=this,inventoryRegisterId;

      if(this.$route.query.inventoryRegisterId){
        inventoryRegisterId  = this.$route.query.inventoryRegisterId;
      }else{
        that.$alert('这条记录没有id，请重新请求', '提示', {
          confirmButtonText: '确定',
        })
        return;
      }

      if(this.$route.query.ObjectType){
        that.ObjectType = that.$route.query.ObjectType;
      }

      var histData = {
        "inventoryRegisterId":inventoryRegisterId,
      }

      console.log(histData)
      // 获取库存登记列表
      that.httpRequest("GetRegister.do",histData,function(histRes){
        console.log(histRes)

        that.Stock = histRes.data.data;
        that.InventoryInfoList = histRes.data.detail;
      })

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	},
      // 查看该客户所有库存登记
      viewAllData(){

        var that = this;

        this.$store.state.InventoryRegALL_CustomerInfo = {
          "CUSTOMER_NAME":that.Stock.name,
          "CUSTOMER_ID":that.Stock.orgnizationId,
          "CONTACT_NAME":that.Stock.contactName,
          "CONTACT_TEL":that.Stock.smNo,
          "CUSTOMER_ADDRESS":that.Stock.address1,
        };
        this.$router.push({
          name:'CustomerAllInventoryReg',
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .InventoryRegDetail{
  	width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 180/50rem;
    .orderInfo>.infoContainer span{
      color: #000;
    }
    .infoContainer>div>span:nth-child(2){
      color: #555;
    }
    .infoContainer>div>div>span:nth-child(2){
      color: #555;
    }
    .BtnComponent{
      padding:0 50/50rem;
    }
  }
</style>


