<template>
  <div class="WXVerify">
  	<componentHeader :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	<div class="container haveHeadcontainer">
	  <div class="headTitle">
	  	<div>请输入验证码</div>
	  	<div>验证码已通过短信发送至 {{phoneNum}}</div>
	  </div>
	  <div class='identeContainer'>
	    <div class="mobilePhone">
	      <div class="mobilePhoneInput">
	      	<input placeholder='输入验证码' type="number" pattern="[0-9]*" ref="identifyingCode" @keyup.enter="toLogin" v-model="identifyingCode"></input>
	        <div class="button yanButton" @click='sendIdentifying'></div>
	      </div>
	      <div class="ErrMsg"><span>{{ErrMsg}}</span></div>
	    </div>
	    <div class="g_signBtn" v-if="LoginOrReg=='login'" :style="{color:identifyingCode.length>3?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toLogin'>登录</div>
	    <div class="g_signBtn" v-if="LoginOrReg=='register'" :style="{color:identifyingCode.length>3?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toRegister'>注册</div>
	    <div class="g_signBtn" v-if="LoginOrReg=='register'" :style="{color:identifyingCode.length>3?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toAssociatedStores'>关联门店</div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from "jquery"
  export default {
 	name:"WXVerify",
 	data(){
 	  return{
	    phoneNum:"",//手机号
	    identifyingCode:'',//验证码
	    openId:"",//
	    LoginBtnText:'登录',//按钮文字
	    LoginOrReg:'login',//判断是登录还是注册
	    ErrMsg:'',//错误信息
 	  }
 	},
	components:{
      componentHeader,
    },
    watch:{
	  identifyingCode(val){
	  	if(this.ErrMsg){
	  	  this.ErrMsg = '';
	  	}
	  }
	},
	mounted(){
	  var that = this;
	  this.$refs['identifyingCode'].focus();

	  	$(".yanButton").css("color","#555").html("重新发送(" + that.$store.state.MinuteNum + ")");

		  that.$store.state.CountDown = setInterval(function(){

		  that.$store.state.MinuteNum -- ;

		  $(".yanButton").html("重新发送(" + that.$store.state.MinuteNum + ")");

		  if(that.$store.state.MinuteNum  < 0 ){

			$(".yanButton").css("color"," #5965D8").html("重新获取验证码");

			that.$store.state.MinuteNum = 60;

			clearInterval(that.$store.state.CountDown);
		  }
		},1000)

	},
 	created(){

 	  var that = this;

 	  if(this.$route.query.phoneNum){
 	  	this.phoneNum = this.$route.query.phoneNum;
 	  }

 	  // 是注册还是登录
 	  if(this.$route.query.LoginOrReg){
 	  	this.LoginOrReg = this.$route.query.LoginOrReg;
 	  }

 	  //验证码
 	  if(this.$route.query.yzm){
 	  	this.identifyingCode = this.$route.query.yzm;
 	  }
	},
	// 页面销毁前
	beforeDestroy(){

	  var that = this;

	  clearInterval(that.$store.state.CountDown);
	},
	methods:{
	  f_Fanhui(){
	  	var that = this;

		this.$router.push({
          name:'WXLogin',
        })
	  },
      //获取验证码
  	  sendIdentifying (){
		var that = this;

	    if(that.$store.state.MinuteNum != 60){
	      return;
	    }
	    
	    var params = {
	      cellphone: that.phoneNum,
	    }
	    this.httpRequest("WXGetVerifyCode.do",params,function(res){

		  $(".button").css("color","#555").html("重新发送(" + that.$store.state.MinuteNum + ")")

			that.$store.state.CountDown = setInterval(function(){

			  that.$store.state.MinuteNum -- ;

			  $(".button").html("重新发送(" + that.$store.state.MinuteNum + ")")

			  if(that.$store.state.MinuteNum  < 0 ){

				$(".button").css("color"," #5965D8").html("重新获取验证码");

				that.$store.state.MinuteNum = 60;

				clearInterval(that.$store.state.CountDown);
			  }
		  },1000)
		})
	  },
	  // 验证码登录
      toLogin(){
        var that = this;

        if(that.identifyingCode.length<4){return;}

      	let regParams = {
          cellphone: that.phoneNum,//手机号
          type:'login',//类型
          openid:that.$store.state.GopenId,//openid
          yzm:that.identifyingCode,//验证码
        }

        this.httpRequest("wxXYVerCode.do",regParams,function(regRes){

          console.log(regRes);

          if(regRes.data.returnCode == 'YZSUCCESS'){
			// 改变登录状态
			that.$store.state.userInfo = regRes.data.user;

			if(regRes.data.business.length > 0){

			  that.$store.state.businessInfo = regRes.data.business[0];

	          that.$store.state.CurrentRole = 'storesStaff';

		      that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData['storesStaff'];

	          that.$router.push({
		        name:"Index",
		      })
		    }else{

	          that.$alert("该账号未绑定业务代码", '提示', {
		        confirmButtonText: '确定',
		      })
		    }
          }else{
          	that.ErrMsg = regRes.Msg;
          }
        })
      },
      // 跳转到注册页面
      toRegister(){
      	var that = this;
      	if(that.identifyingCode.length<4){return;}
      	let regParams = {
          cellphone: that.phoneNum,//手机号
          type:'register',//类型
          yzm:that.identifyingCode,//验证码
        }

        this.httpRequest("wxXYVerCode.do",regParams,function(regRes){
        	
          console.log(regRes);

          if(regRes.data.returnCode == 'YZSUCCESS'){

          	that.$store.state.WXRegIdentifyPhone = that.phoneNum;//手机号
          	that.$store.state.WXRegLoginOrReg = that.$route.query.LoginOrReg;//是注册还是登录
          	that.$store.state.WXRegCode = that.identifyingCode;//验证码

			that.$router.push({
  	          name:"CreateCustomerWeChat",
            })
          }else{
          	that.ErrMsg = regRes.Msg;
          }
        })
      },
      // 跳转到 关联门店 页面
      toAssociatedStores(){
      	// YZSUCCESS  验证码正确
      	// SUCCESS  登录成功
      	// CORRECT  未注册

      	var that = this;
      	if(that.identifyingCode.length<4){return;}

      	let identiParams = {
          cellphone: that.phoneNum,//手机号
          type:'associatedStores',//类型
          yzm:that.identifyingCode,//验证码
        }
        this.httpRequest("wxXYVerCode.do",identiParams,function(identiRes){
        	
          console.log(identiRes);

          if(identiRes.data.returnCode == 'YZSUCCESS'){
			that.$router.push({
  	          name:"WXAssociatedStores",
  	          query:{
  	          	phoneNum:that.phoneNum,//手机号
  	          	LoginOrReg : that.$route.query.LoginOrReg,//是注册还是登录
  	          	yzm:that.identifyingCode,//验证码
  	          }
            })
          }else{
          	that.ErrMsg = identiRes.Msg;
          }
        })
      },
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .WXVerify{
  	overflow:hidden;
  	header{
  	  color: #959595;
  	}
	.container{
	  padding-left:50/50rem;
	  padding-right:50/50rem;
	  font-size: 30/50rem;
	  .headTitle{
	  	margin: 40/50rem 0 80/50rem;
	  	&>div:first-child{
	  	  font-size: 50/50rem;
	  	}
	  	&>div:nth-child(2){
		  color: #636363;
		  margin-top: 5/50rem;
		  font-size: 28/50rem;
	  	}
	  }
	  .identeContainer{
	  	.mobilePhone{
	  	  height: 165/50rem;
	  	  .mobilePhoneInput{
	  	    position:relative;
	  	  	input{
		  	  width: 100%;
			  font-size: 30/50rem;
			  line-height: 75/50rem;
			  padding:0 180/50rem 0 10/50rem;
			  border-radius:0;
			  border:none;
			  border-bottom: 1/50rem solid #ccc;
		  	}
		  	.button{
			  position: absolute;
			  right: 0;
			  top: 50%;
			  transform: translateY(-50%);
			  height: 50/50rem;
			  line-height: 50/50rem;
			  box-sizing: border-box;
			  padding:0/50rem 15/50rem;
			  font-size: 28/50rem;
			  color: #555;
			}
	  	  }
	  	  .ErrMsg{
	  	  	span{
			  color: #ff4E32;
			  line-height: 70/50rem;
			  font-size: 28/50rem;
	  	    }
	  	  }
	  	}
	  	.g_signBtn{
	  	  margin-bottom:40/50rem;
	  	}
	  }
	}
  }
</style>


