<template>
  <div class="CustomerSalesDetailReport">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

    <div class="container haveHeadcontainer" id="container">
      <div class="totalQty">{{ customerName }}总销量：{{ totalQty }}
      </div>
      <div class='reportContainer'>
        <div class='reportItem' v-for='(item,index) in ReportList'  :id="index"  :key='index'>
          <div class="lineOne">
            <span>{{item.product_name}}</span>
            <span>{{item.progressQty}}%</span>
            <span>{{item.qty}}</span>
          </div>
          <div class="lineTwo">
            <div class="progressQty">{{ drawQty(index, item.qty, item.maxQty) }}</div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import echarts from 'echarts'
  import $ from "jquery"
  export default{
    data(){
      return{
        headerText: "品牌销量分析", //页面标题
        charts: '',
        totalQty: 0,
        ReportList: [],
        dateTopIndex: 1,
        customerName: "",
      }
    },
    components:{
     componentHeader,
    },
    created(){

      var that = this;

      var startDate
      if(this.$route.query.startDate){
        startDate = this.$route.query.startDate
      }

      var endDate
      if(this.$route.query.endDate){
        endDate = this.$route.query.endDate
      }

      var customerId = 0;
      if(this.$route.query.customerId){
        customerId = this.$route.query.customerId
      }

      if(this.$route.query.customerName){
        this.customerName = this.$route.query.customerName
      }

      var postData = {
        "appUsersId":that.$store.state.userInfo.user_id, //用户ID
        "businessId":that.$store.state.businessInfo.businessId, //业务ID
        "customerId":customerId,
        "startDate":startDate,
        "endDate":endDate,
      }
      that.httpRequest("customerSalesDetailReport.do",postData,function(res){
        that.ReportList = res.data;

        var maxQty = 0;
        var totalQty = 0;
        for(var i = 0; i < that.ReportList.length; i++){
          
          let qty = parseFloat(that.ReportList[i].qty)
          totalQty += qty
          if(maxQty < qty){
            maxQty = qty
          }
        }
        that.totalQty = totalQty;

        for(var i = 0; i < that.ReportList.length; i++){

          that.ReportList[i].progressQty = (that.ReportList[i].qty / totalQty * 100).toFixed(1);
          that.ReportList[i].maxQty = maxQty
        }
        that.sortByKeyDesc(that.ReportList, "qty")
      })
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      this.$router.go(-1);
      },
      drawQty(index, qty, maxQty){

        setTimeout(function (){
          
          var b = qty / maxQty;
          // 设置颜色
          $('.colorQty').css('background-color','#a1cb67')
          $('.progressQty').css('background-color','#a1cb67')
          $('.progressQty').eq(index).css("width", b * 100 + '%');
        }, 10);
      },
    }
  }

</script>
<style lang="less" scoped>
  .CustomerSalesDetailReport{
    overflow-x: hidden;
    header{
      background-color: #fff;
      color: #000;
      font-weight: 600;
      overflow: hidden;
    }

    .container{

      .totalQty{
        padding-left: 30/50rem;
        line-height: 100/50rem;
        background-color: #EEEFF2;
      }
      .reportContainer{
        width:100%;
        font-size: 30/50rem;
        .reportItem{
          width:100%;
          color: #8B8B8D;
          padding: 28/50rem 30/50rem 0 30/50rem;
          box-sizing: border-box;
          border-bottom: 20/50rem solid #EEEFF2;
          background-color: #fff;
          .lineOne{
            width: 100%;
            height: 50/50rem;
            &>span:nth-child(1){
              float: left;
            }
            &>span:nth-child(2){
              float: left;
              margin-left: 25/50rem;
            }
            &>span:nth-child(3){
              float: right;
            }
          }
          .lineTwo{
            width: 100%;
            height: 50/50rem;
            .progressQty{
              height: 17/50rem;
              width: 0rem;
              background-color: red;
              border-radius: 10/50rem;
            }
          }
        }
      }
      #main{
        margin: 60/50rem 0;
      }
    }
  }
  
</style>


