<template>
  <div class="RePassword">
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	<div class='container haveHeadcontainer'>
	  <div>
		<span>原密码：</span>
		<input placeholder='请输入原密码' ref="oldpwd"  @keyup.enter="getFocus('newpwd')"  v-model='oldpwd'></input>
		<i v-if="oldpwd" @click="clearInput('oldpwd')" class="iconfont icon-guanbi"></i>
	  </div>
	  <div>
		<span>新密码：</span>
		<input type='password'  ref="newpwd"    @keyup.enter="getFocus('reNewpwd')"  placeholder='请输入新密码'  v-model='newpwd'></input>
		<i v-if="newpwd" @click="clearInput('newpwd')" class="iconfont icon-guanbi"></i>
	  </div>
	  <div>
		<span>确认密码：</span>
		<input type='password'  ref="reNewpwd"    @keyup.enter="submit"  placeholder='请再次输入新密码'   v-model='reNewpwd'></input>
		<i v-if="reNewpwd" @click="clearInput('reNewpwd')" class="iconfont icon-guanbi"></i>
	  </div>

	  <div class='BtnComponent' @click='submit'>
		<div class="onlyBtn">提交</div>
	  </div>
	</div>
	<div v-if="ifTips" class="msg_tips">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
  	name:"RePassword",
  	data(){
  	  return{
  	  	headerText:"修改密码",//页面标题
  	  	oldpwd:"",//旧密码
  	    newpwd:"",//新密码
  	    reNewpwd:"",//确认新密码
  	    tips_Msg:"",//提示信息
  	  	ifTips:false
  	  }
	},
    components:{
	  componentHeader,
	},
	created(){

	},
	methods:{
	  // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);
      },
			submit (){

			    var that=this;

			    var cellphone = that.$store.state.userInfo.cellphone;

			    if (!this.oldpwd){

			    	that.$alert("请输入原密码！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    } else if (!this.newpwd){

			    	that.$alert("请输入新密码！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    } else if (!this.reNewpwd){

			    	that.$alert("请确认新密码！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    } else if (this.reNewpwd != this.newpwd){

			    	that.$alert("两次新密码不一致！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    } else if (this.newpwd.length<6){

			    	that.$alert("密码不能小于6位数！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    } else if (this.oldpwd == this.newpwd){

			    	that.$alert("新密码不能与原密码相同！", '提示', {
				        confirmButtonText: '确定',
				   	})

			      return;
			    }else{

			    	var params = {
				        "cellphone": cellphone,
				        "next_page": "xiugaimima",//判断是 忘记密码 页面 ，还是修改密码页面
				        "oldPassword": that.oldpwd,
				        "password": that.newpwd,
				    };

				    that.httpRequest( "updateUserPassword.do",params,function(res){

					    that.ifTips = true;
						that.tips_Msg = "修改成功";

						setTimeout(function(){

							that.ifTips = false;

							that.setCookie(cellphone,"",10*365*60);

							that.$router.push({
								name:"Login",
								query:{
								}
							})
						},1500)
		            })
				}
		    }
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .RePassword{
	overflow: hidden;
	.container{
	  width:100%;
	  padding: 30/50rem 40/50rem 0;
	  box-sizing:border-box;
	  &>div{
	  	width: 100%;
	    font-size: 28/50rem;
	    overflow: hidden;
	    margin-bottom: 25/50rem;
	    position: relative;
	    display: flex;
	    span{
	  	  min-width:155/50rem;
	  	  flex-shrink:0;
		  height: 70/50rem;
		  line-height: 70/50rem;
		  text-align: right;
		}
		input{
		  flex-grow: 1;
		  height: 70/50rem;
		  border: 2/50rem solid #E8E8E8;
		  border-radius: 10/50rem;
		  font-size: 26/50rem;
		  padding:0 10/50rem;
		  box-sizing: border-box;
		  outline: none;
		}
		i{
		  font-size: 42/50rem;
		  position:absolute;
		  top:10/50rem;
		  right: 10/50rem;
		}
	  }
	}
  }
</style>


