<template>
  <div class="MessageDetail">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="detail">
        <div class="time">{{MessageItem.appTime}}</div>
        <div class="info">{{MessageItem.appContent}}</div>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"",//页面标题
        ReceiveTime:"",//消息接收时间
        MessageInfo:" ",//消息内容
        msg_id:"",//消息ID
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      var that = this;
      if(that.$route.query.MessageItem){ 
        that.MessageItem = that.$route.query.MessageItem;
        that.headerText = that.MessageItem.title;
      }
      var postData = {
        "msg_id":that.MessageItem.msg_id,
      }
      // 修改消息状态
      that.httpRequest("updataMsgToRead.do",postData,function(Res){
        
        // that.Messagedata = Res.data;
      })
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		//this.$router.go(-1);
        this.$router.push({
          name:"MessageList",
        }) 

    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .MessageDetail{
    overflow-x: hidden;
    .detail{
      .time{
        width: 100%;
        height: 70/50rem;
        line-height:90/50rem;
        font-size: 26/50rem;
        margin-left: 45/50rem;
        color: #6D6D6D;

        // background-color: pink;

      }
      .info{
        width: 80%;
        margin-left: 20/50rem;
        padding-left: 20/50rem;
        padding-top: 15/50rem;
        padding-right: 20/50rem;
        padding-bottom: 20/50rem;
        background-color: #F0F0F0;
        border-radius: 20/50rem;
        font-size: 30/50rem;


      }

    }
  }
</style>


