<template>
  <div class="componentCustomer">
	<div class="customerListBox">
	  <div class='customerListDetail' v-for='(dataItem,index) in ospList'  :id="index"  :key='index' >
	  	<div  @click='toSelectGoods(index)'>
  		 <div>
		    <span>客户名称：</span>
		    <span>{{dataItem.CUSTOMER_NAME}}</span>
		  </div>
		 
		  <div>
		    <span>客户代码：</span>
		    <span>{{dataItem.CUSTOMER_CODE}}</span>
		  </div>
		  <div>
		    <span>客户城市：</span>
		    <span>{{dataItem.CUSTOMER_CITY_CHINESE}}</span>
		  </div>
		  <div>
		    <span>客户类型：</span>
		    <span>{{dataItem.CUSTOMER_TYPE}}</span>
		  </div>
		</div>
		<i  class="iconfont icon-xiangyou"></i>
	  </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default{
	data(){
	  return{
	  }
	},
	props:{
	  ospList:{
	  type:Array,
    	default: () => {
			return []
		}
	  },//客户信息列表
	},
	created(){
	},
	methods:{
	  toSelectGoods(index){
		this.$emit('Comp_CustomerSelected',index);
	  },
	}
  }
</script>
<style lang="less" scoped>
 @import "../publicLess.less";
  .componentCustomer{
	height:100%;
	.customerListBox{
	  width: 100%;
	  .customerListDetail{
	    width: 100%;
	    background-color: #fff;
	    font-size: 28/50rem;
          padding:20/50rem 62/50rem 20/50rem 20/50rem;
	    box-sizing: border-box;
	    border-bottom: 20/50rem solid #EEEFF2;
	    position:relative;
	    overflow: hidden;
	    &>div>div{
            line-height: 46/50rem;
          }
		i{
		  position: absolute;
		  right: 20/50rem;
		  top: 50%;
		  font-size: 50/50rem;
		  transform: translateY(-50%);
		}
	  }
	}
	.NoData{
		text-align: center;
		color: #A8AFB3;
	}
  }
</style>


