<template>
  <div  class="Login">
    <!-- 头部 -->
	<!-- <componentHeader :headerText="headerText"></componentHeader> -->

	<div class="container haveHeadcontainer">
	  <div class="LoginLogo">
	  	<img src="static/img/Logo/logo.png" alt="">
	  </div>
	  <div class="loginContainer">
		<div>
		  <i class="iconfont icon-denglu"></i>
		  <input class='userName'  ref="userName"  v-model="userName"  placeholder='请输入手机号' ></input>
		  <i v-if="userName" @click="clearInput('userName')" class="iconfont icon-guanbi"></i>
		</div>
	  	<div>
		  <i class="iconfont icon-mima" ></i>
		  <input class='pswd' type='password'  ref="password"   v-model="password"   placeholder='请输入密码' ></input>
		  <i v-if="password" @click="clearInput('password')" class="iconfont icon-guanbi"></i>
		</div>
		<div class="BtnComponent" @click='login'>
		  <div>登  录</div>
		</div>
		<div class='repswd'>
		  <span @click='repwd'>忘记密码</span>
		  <!-- <span @click='reg'>注册</span> -->
		</div>
	  </div>
	  <div class="ThirdPartyLogin" ref="ThirdPartyLogin">
		<div class="ThirdParty_title">
		  <div></div>
		  <div>第三方登录</div>
		  <div></div>
		</div>
		<div class="ThirdParty_content" @click="ThirdPartyLogin"><i class="iconfont icon-weixin"></i></div>
	  </div>
	</div>
    <div class="verseion">{{$store.state.App_Version}}</div>
	<div class="register">门店注册请移步至“卖货易”微信公众号</div>
 
	<componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='BusinessList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
  </div>
</template>
<script type="text/javascript">
  import $ from "jquery"
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default {
    name:"Login",
	data(){
	  return{
	    headerText:"登录",
	    userName:"",//用户名
	    password:"",//密码
	    BusinessList:[],//业务代码
	    SelectBoxFlag:false,//弹出选择框是否显示
      	BouncedName:'业务代码',//弹框名字
	  }
    },
    components:{
	  componentHeader,
	  componentSelectBox
	},
	mounted() {
	  this.$refs['userName'].focus();
	  $("input").attr("maxlength","50");
	  window.WXBind_NO_Ajax = this.WXBind_NO_Ajax;
	  window.WXBind_YES_Ajax = this.WXBind_YES_Ajax;
	  window.VersionShow = this.VersionShow;
	  window.SetUserNameAndPassword = this.SetUserNameAndPassword;
	  this.tellServerAddress(this.HOST);
	  this.loginVueMounted();
	  window.WXInstall_Check_Ajax = this.WXInstall_Check_Ajax;
	  window.Device_Ajax = this.Device_Ajax;
	},
	created(){

	  if(this.$store.state.G_AppOrWX == "WX"){
	  	this.$router.push({
          name:"WXLogin",
        })
	  }

	  this.getCookie("e");

	},
	methods:{
	  // 检查手机是否安装微信，以免App Store审核被拒
	  WXInstall_Check_Ajax:function(isInstall) {

		if(isInstall == "NO") {

		  this.$refs['ThirdPartyLogin'].remove();
		}
	  },

	  loginVueMounted:function(){
		// 安卓
		try {
		  CallAndroidOrIOS.callAndroid("登录页面已加载", "");
		} 
		catch(error) {
		}
		// 苹果
		try {
		  CallAndroidOrIOS("登录页面已加载");
		}
		catch(error) {
		}
	  },
	  tellServerAddress:function(a){

	    console.log("tellServerAddress：");

		// 安卓
		try {
		  CallAndroidOrIOS.callAndroid("服务器地址", a);

		} 
		catch(error) {
		}
		// 苹果
		try {
		  CallAndroidOrIOS("服务器地址", a);
		}
		catch(error) {
		}
	  },
	  WXBind_NO_Ajax:function(openid) {
		this.$store.state.WX_Openid = openid;
		this.$router.push({
		  name:"Binding",
		  query:{
		  }
		})
	  },
	  WXBind_YES_Ajax:function(data) {

		var str = decodeURIComponent(data);
		var obj = JSON.parse(str);
		this.userName = obj.data.user.cellphone;

		this.loginSuccess(obj);
	  },
	  // 显示版本号
	  VersionShow:function(v) {
		this.$store.state.App_Version = v;
	  },
	  SetUserNameAndPassword:function(u, p) {
		this.userName = u;
		this.password = p;
	  },
	  Device_Ajax:function(d) {
		this.$store.state.Device = d;
	  },
	  // 点击登录按钮
	  login:function(){

		var that = this;

		// 初始化用户习惯
		that.$store.state.QuerySalesOrder_supplierListIdx = 0;
		that.$store.state.CustomerVisit_supplierListIdx = 0;
		that.$store.state.SalesOrder_supplierListIdx = 0;
		
		if(!this.userName){

			that.$alert("用户名不能为空", '提示', {
				confirmButtonText: '确定',
			})
					
			return;

		}else if(!this.password){

			that.$alert("密码不能为空", '提示', {
				confirmButtonText: '确定',
			})
					
			return;
		}

		var postData = {
			"cellphone":this.userName,//手机号
			"password":this.password,//密码
			"wxOpenid":"",//openid
		}

		this.httpRequest("login.do",postData,function(res){

		    that.loginSuccess(res);
		})
	  },
	  // 登录功能，保存帐号密码、有多个务代码需要弹选择框
	  loginSuccess(res) {

	  	var that = this;
		that.$store.state.userInfo = res.data.user;
		that.KeepUserNameAndPassord(that.userName, that.password);
		that.setCookie(that.userName,that.password,10*365*60);
		
		that.BusinessList = res.data.business;
		if(res.data.business.length == 1){

			that.$store.state.businessInfo = that.BusinessList[0];
			for(var mk = 0;mk < that.$store.state.RoleTotalData.length; mk++){

	          if(that.$store.state.userInfo.business == that.$store.state.RoleTotalData[mk].business){
	          	that.$store.state.CurrentRole = that.$store.state.RoleTotalData[mk].EnglishRoleName;
	          }
	        }
			for(var li in that.$store.state.RoleModuleTotalData){

		      if(that.$store.state.CurrentRole == li){
		        that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData[li];
		      }
		    }
	    
	        that.SelectBoxFlag = false;
	        that.$router.push({
				name:"Index",
				query:{
				}
			})
		}else if(res.data.business.length > 1){

	        that.SelectBoxFlag = true;
	    }else{

	        that.$alert("该账号未绑定业务代码", '提示', {
		        confirmButtonText: '确定',
		    })
		}
	  },
	  // 选中某个业务代码
      selectedItem(index){
      	var that =this;

        this.$store.state.businessInfo = this.BusinessList[index];

        for(var mk = 0;mk < that.$store.state.RoleTotalData.length; mk++){

          if(that.$store.state.userInfo.business == that.$store.state.RoleTotalData[mk].business){
          	that.$store.state.CurrentRole = that.$store.state.RoleTotalData[mk].EnglishRoleName;
          }
        }

        for(var li in that.$store.state.RoleModuleTotalData){

	      if(that.$store.state.CurrentRole == li){
	        that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData[li];
	      }
	    }

        this.SelectBoxFlag = false;
        this.$router.push({
			name:"Index",
			query:{
			}
		})
      },
      // 取消选择业务代码
      selectCancel(){
	  	this.SelectBoxFlag = false;
	  },
	  // 跳转到 忘记密码 页面 (验证手机号)
	  "repwd":function(){

		this.$router.push({
			name:"Identifying",
			query:{
				page:"ForgotPwd"
			}
		})
	  },
	  // 跳转到 注册 页面 (验证手机号)
	 //  "reg":function(){

		// this.$router.push({
		// 	name:"Identifying",
		// 	query:{
		// 		page:"Reg"
		// 	}
		// })
	 //  },
	  // 第三方登录
	  ThirdPartyLogin:function(){
		// 安卓
		try {
		  CallAndroidOrIOS.callAndroid("微信登录","");
		} 
		catch(error) {
		}
		// 苹果
		try {
		  CallAndroidOrIOS("微信登录");
		}
		catch(error) {
		}
	  },
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Login{
	overflow: hidden;
	header{
	  background-color:@PublicColor;
	  color: #fff;
	  border-bottom: none;
	}
	.container{
	  .LoginLogo{
	    padding:40/50rem 0;
	    text-align: center;
	    img{
	      width: 200/50rem;
	      border-radius:50%;
	    }
	  }
	  .loginContainer{
		width:638/50rem;
		margin:60/50rem auto 0;
		&>div{
		  width:638/50rem;
		  height:70/50rem;
		  position: relative;
		  margin-bottom: 50/50rem;
		  .icon-denglu,.icon-mima{
	    	position: absolute;
	    	top:50%;
	    	transform: translateY(-50%);
	    	left:20/50rem;
	    	color: @PublicColor;
			font-size: 45/50rem;
			font-weight: 500;
		  }
		  .icon-guanbi{
	    	position:absolute;
			right:20/50rem;
			font-size: 40/50rem;
			top: 50%;
	    	transform: translateY(-50%);
		  }
		}
	    .userName,.pswd{
		  width:638/50rem;
		  height:70/50rem;
		  border-radius: 10/50rem;
		  padding-left: 82/50rem;
		  padding-right: 80/50rem;
		  font-size: 26/50rem;
		  box-sizing: border-box;
		}
		.BtnComponent{
		  margin-bottom: 20/50rem;
		}
		div.repswd{
		  font-size: 28/50rem;
		  color: #777;
		  text-align: right;
		}
	  }
	}
  }
  .SelectMeng{
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba(0,0,0,0.3);
	z-index: 999;
	.SelectWarehouses{
      width: 680/50rem;
      margin:0 ;
      position:absolute;
      top:50%;
      left: 50%;
      transform: translate(-50%,-50%);
      background-color: #fff;
	  &>div{
	    width: 100%;
	  }
	  .SelectItem{
	    color: #2F84F0;
	    font-size: 29/50rem;
	    overflow: scroll;
	    max-height: 486/50rem;
	    &>div{
	      height: 80/50rem;
	      line-height: 80/50rem;
	      border-top: 1/50rem solid #D9D7D9;
	    }
	  }
	  .SelectTitle{
	    height: 114/50rem;
	    line-height: 114/50rem;
	    font-weight: 600;
	    font-size: 34/50rem;
	  }
	  .cancel{
	    height: 80/50rem;
	    line-height: 80/50rem;
	    color: #2F84F0;
	    font-size: 34/50rem;
	    font-weight: 600;
	    border-top:  1/50rem solid #D9D7D9;
	  }
	}
  }
  .ThirdPartyLogin{
	width: 100%;
	overflow: hidden;
	// margin-top: 290/50rem;//
	.ThirdParty_title{
	  margin: 0 auto;
	  height: 50/50rem;
	  &>div{
	  	float: left;
	  	height: 50/50rem;
	  	font-size:30/50rem;
	  	color: @PublicColor;
	  }
	  &>div:nth-child(1),&>div:nth-child(3){
		height: 3/50rem;
		margin-top: 22.5/50rem;
		width:247.5/50rem;
	    background:-webkit-gradient(linear, left top, right top, from(#fff), to(@PublicColor));
	  }
	  &>div:nth-child(3){
		background:-webkit-gradient(linear, right top, left top, from(#fff), to(@PublicColor));
	  }
	  &>div:nth-child(2){
	  	width: 255/50rem;
	  	text-align: center;
	  }
	}
	.ThirdParty_content{
	  width: 80/50rem;
	  height: 80/50rem;
	  border-radius: 50%;
	  border:1/50rem solid #469E3D;
	  color:#469E3D;
	  margin: 30/50rem auto 30/50rem;
	  text-align: center;
	  line-height: 73/50rem;
	  i{
	  	font-size: 45/50rem;
	  }
	}
  }
  @media screen and (max-width: 374px) {	
	.ThirdPartyLogin{
	  .ThirdParty_content{
		border:3/50rem solid #469E3D;
	  }
	}
  }
  .verseion{
	width:200/50rem;
	height: 40/50rem;
	color: #ccc;
	font-size: 29/50rem;
	text-align: center;
	position: fixed;
	bottom: 0;
	right: 0;
  }
  .register{
	color: #888;
	text-align: center;
	font-size: 28/50rem;
  }
</style>


