<template>
  <div class="CustomerList">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfAddShow='true' @f_Add='f_Add'></componentHeader>
	  
	  <div class="container haveHeadcontainer">
      <div class="headFixed">
        <div class="CustomerSearch">
          <div>
            <input v-model='searchValue' placeholder="按门店名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
        </div>
        <div class="InventoryTotal">
          <div class="totalNum">
            <div class="totalNumber">{{totalNum}}</div>
            <div class="totalNumBot">
              <div>
                <i class="iconfont icon-yonggongzongliang"></i>
                <span>门店总量</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      <div class="CustomerListData">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="CustomerListItem" @click='toModify(index)' v-for='(dataItem,index) in CustomerListData'  :id="index"  :key='index'>
            <div class="CustomerName"><span>{{dataItem.storesName}}</span></div>
            <div><span>{{dataItem.address1}}</span></div>
            <div class="TheContact">
              <div>
                <i class="iconfont icon-lianxiren"></i>
                <span>{{dataItem.CONTACT_NAME}}</span>
              </div>
              <div>
                <span>{{dataItem.CONTACT_TEL}}&nbsp;&nbsp;</span>
                <i v-if='dataItem.CONTACT_TEL' @click="callPhone(dataItem.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i>
              </div>
            </div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
	  	
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"门店列表",//页面标题
      	CustomerListData:[],//客户列表数据
        searchValue:'',//搜索的值
        noDataShow:false,//没有数据图标 是否显示
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        totalNum:0,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      this.reqFindStores();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){

        this.reqFindStores();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        this.$router.push({
          name:"Index",
        })
    	},
      // 获取门店信息
      reqFindStores(load){

        var that = this;

        var i_do = "selectStores.do"
        var suppliersId = that.$store.state.CustomerInfoOspInfo.CUSTOMER_ID
        if(that.$store.state.userInfo.business == 5){
          i_do = "selectOSP.do"
          suppliersId = that.$store.state.CustomerInfoOspInfo.suppliersId
        }

        var storesData = {
          "suppliersId":suppliersId,//供货商id
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "userId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "strStoreName":that.searchValue,//门店名称
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }

        // 获取门店信息
        that.httpRequest(i_do,storesData,function(storesRes){

          that.totalNum = storesRes.data.storesNum;

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(storesRes.data.storesArr.length){
              that.CustomerListData = that.CustomerListData.concat(storesRes.data.storesArr);
            }
          } else {
            that.CustomerListData = storesRes.data.storesArr;
          }

          // 判断是否能上拉加载
          if (storesRes.data.storesArr.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.CustomerListData.length){
            that.noDataShow = false;
            
          }else{
            // 没有数据
            that.noDataShow = true;
          }
        })
      },
      // 新建客户  跳转到 新建客户 页面
      f_Add(){

        this.$router.push({
          name:"CreateNewCustomer",
        })
      },
      // 跳转到 修改客户信息页面
      toModify(index){
        var storeInfo = this.CustomerListData[index];
        this.$router.push({
          name:"ModifyCustomerInfo",
          query:{ 
            storeInfo:storeInfo,
          }
        })
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqFindStores("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }
        this.reqFindStores("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerList{
    overflow-x: hidden;
    height: 100%;
  	.container{
      height: 100%;
      margin-top: 0;
      .headFixed{
        width: 100%;
        position:fixed;
        background-color:#fff;
        top: 89/50rem;
        left: 0;
        z-index: 50;
        .CustomerSearch{
          .TopSearchBox;
        }
        .InventoryTotal{
          padding:10/50rem 0;
          border-bottom:5/50rem solid #f0f0f0;
          background-color: #fff;
          .totalNum{
            text-align: center;
            .totalNumber{
              line-height: 60/50rem;
              font-size: 44/50rem;
              font-weight: 600;
            }
            .totalNumBot{
              text-align: center;
              div{
                overflow: hidden;
                line-height: 60/50rem;
                font-size: 32/50rem;
                display: inline-block;
                i{
                  font-size: 45/50rem;
                  margin-right: 10/50rem;
                  float: left;
                }
                span{
                  float: left;
                }
              }
            }
          }
        }
      }
      .CustomerListData{
        overflow: scroll;
        height: 100%;
        padding-top: 340/50rem;
        box-sizing:border-box;
        .CustomerListItem{
          padding:10/50rem 20/50rem;
          box-sizing: border-box;
          border-bottom: 5/50rem solid @PublicBorderColor;
          &>div:not(.CustomerName){
            line-height: 45/50rem;
            color: #666;
            font-size: 28/50rem;
            overflow: hidden;
            i{
              margin-right: 8/50rem;
              color: @PublicColor;
              font-size: 38/50rem;
            }
            i.icon-dianhua-copy{
              font-size: 35/50rem;
            }
            &>i{
              float: left;
            }
            &>span{
              float: left;
            }
          }
          .CustomerName{
            line-height: 60/50rem;
            font-weight: 550;
            font-size: 32/50rem;
          }
          .TheContact{
            margin-top: 12/50rem;
            display: flex;
            line-height: 50/50rem;
            &>div:first-child{
              overflow: hidden;
              min-width: 240/50rem;
              .icon-lianxiren{
                float: left;
                margin-right: 15/50rem;
              }
              span{
                float: left;
              }
            }
          }
        }
      }
  	}
    .kong{
      height: 50/50rem;
    }
  }
</style>


