<template>
  <header>
	<div v-if="IfFanhuiShow" @click="Fanhui" class="fanhui">
	  <i class="iconfont icon-fanhui"></i>
	</div>
	<span>{{headerText}}</span>
	<div v-if="IfSousuoShow" @click="Sousuo"  class="sousuo">
	  <i class="iconfont icon-sousuo"></i>
	</div>
	<div v-if="IfAddShow" @click="Add"  class="add">
	  <i class="iconfont icon-jia"></i>
	</div>
	<div v-if="IfChakanShow" @click="Chakan"  class="chakan">
	  <i class="iconfont icon-chakan"></i>
	</div>
	<div v-if="IfXiaoxiShow" @click="Xiaoxi"  class="xiaoxi">
	  <i class="iconfont icon-xiaoxi"></i>
	</div>
	<div v-if="IfTextShow" @click="Text"  class="text">
	  <span>{{TextContent}}</span>
	</div>
  </header>
</template>
<script type="text/javascript">
  export default{
	data(){
	  return{

	  }
	},
	props:{
	  headerText:'',//标题名称
	  IfSousuoShow:false,//搜索图标是否显示
	  IfFanhuiShow:false,//返回图标是否显示
	  IfAddShow:false,//添加图标是否显示
	  IfChakanShow:false,//查看图标是否显示
	  IfXiaoxiShow:false,//消息图标是否显示
	  IfTextShow:false,//文字 是否显示
	  TextContent:'',//文字内容
	},
	methods:{
	  // 返回上一页
	  Fanhui(){

	    if(this.IfFanhuiShow){

	      this.$emit('f_Fanhui');
	    }
	  },
	  // 搜索
	  Sousuo(){

	    if(this.IfSousuoShow){

	      this.$emit('f_Sousuo');
	    }
	  },
	  // 添加
	  Add(){
		 if(this.IfAddShow){

	      this.$emit('f_Add');
	    }
	  },
	  // 查看
	  Chakan(){
		 if(this.IfChakanShow){

	      this.$emit('f_Chakan');
	    }
	  },
	   // 消息
	  Xiaoxi(){
		 if(this.IfXiaoxiShow){

	      this.$emit('f_Xiaoxi');
	    }
	  },
	  // 文字
	  Text(){
		if(this.IfTextShow){

	      this.$emit('f_Text');
	    }
	  }
	}
  }
</script>
<style lang="less" scoped>

  @import "./publicLess.less";
  header{
  	.headerFlex;
	color: #fff;
	font-size: 32/50rem;
	// height: 90/50rem;
	line-height: 90/50rem;
	background-color:@PublicColor;
	div{
	  width: 100/50rem;
	  // height: 90/50rem;
	  position:absolute;
	  top: 0;
	  left: 0;
	  &.sousuo,&.add,&.text,&.chakan,&.xiaoxi{
		left: initial;
		right: 0;
	  }
	  &.text{
	  	width: auto;
	  	font-size: 30/50rem;
	  	font-weight: 400;
	  	span{
	  		margin-right: 20/50rem;
	  	}
	  }
	  .icon-fanhui{
		font-size: 48/50rem;
	  }
	  .icon-chakan{
		font-size: 55/50rem;
	  }
	  .icon-xiaoxi{
		font-size: 45/50rem;
	  }
	  .icon-sousuo{
		font-size: 45/50rem;
	  }
	  .icon-jia{
	  	font-size: 45/50rem;
	  }
	}
  }
</style>


