<template>
  <div class="CustomerSalesReport">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">

      <div class="dateContainer">
        <div class="dateInfo">
          <div class="dataType" @click='dateOnclick(index, $event)' v-for='(item,index) in dateTopList'  :id="index"  :key='index'>
            {{ item.a }}
          </div>
        </div>

        <div class="dateScroll">
          <div class="dateContentSize"> 
            <div class="fds" @click='monthOnclick(index, $event)' v-for='(item,index) in dateTopList[dateTopIndex].b'  :id="index"  :key='index'>
              {{ item.text }} 
            </div>
          </div>
          
        </div>
      </div>

      <div class="reportInfo">
        <div class="left">
          <div class="lineOne" @click='qtyDesc()'>
            <div class="colorQty"></div>
            <div class="prompt">销量</div>
            <div class="prompt3">{{ totalQty }}</div>
          </div>
          <div class="lineTwo" @click='moneyDesc()'>
            <div class="colorMoney"></div>
            <div class="prompt">销售额(元)</div>
            <div class="prompt2">(点击按销售额降序)</div>
            <div class="prompt3">{{ totalMoney }}</div>
          </div>
        </div>
      </div>
      <div class="componentReportBar"></div>
	  	<div>
        <componentReportBar :ReportList='ReportList'></componentReportBar>
      </div>
	  </div>

  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentReportBar from '@/components/Widgets/componentReportBar'
  import $ from "jquery"
  export default{
    data(){
      return{
      	headerText:"客户销量、销售额报表",//页面标题
        charts: '',
        ReportList:[],
        totalQty: 0,   //总数量
        totalMoney: 0, //总金额
        dateTopList: [{'a':'月','b':[{'text':'2018-12月'}],'c':0}, {'a':'季','b':[{'text':'一季度'},{'text':'二季度'},{'text':'三季度'},{'text':'四季度'}],'c':0}, {'a':'年','b':[{'text':'去年'},{'text':'今年'}],'c':1}],
        dateTopIndex: 0,
        lastDateDiv: {},
        firstReq: true, //首次请求
      }
    },
    components:{
	    componentHeader,
      componentReportBar
	  },
    created(){

      this.getData()
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
    	 this.$router.go(-1);
      },
      getData(){

        var that = this;
        
        // 配置json
        for(var i = 0; i < that.dateTopList.length; i++){
          var date = new Date();
          var year = date.getFullYear();
          var a = that.dateTopList[i].a;
          var b = that.dateTopList[i].b;
          if(a == "周"){
            for(var j = 0; j < b.length; j++){

            }
          }else if(a == "月"){

            if(that.firstReq) {
              let currMonth = date.getMonth()+1;
              for(var k = 0; k < currMonth; k++){
                let m = k + 1
                var item= {'text':m+'月'};
                that.dateTopList[0].b.push(item)
              }
              that.dateTopList[0].c = currMonth
            }

            for(var j = 0; j < b.length; j++){

              var c = b[j]
              let text = c.text.replace("月","")
              var month = 0
              var day = 0
              // 把 2018-12月 或 9月 标准化出来
              if(text.indexOf("20") != -1){
                if(text.length >= 4){
                  year = text.substring(0, 4)
                  month = text.replace(year + "-","")
                  day = that.getDayWithYearMonth(year, month)
                }
              }else{
                year = date.getFullYear();
                month = text
                day = that.getDayWithYearMonth(year, month)
              }
              c.start = year + "-" + month + "-1"
              c.end = year + "-" + month + "-" + day
              c.start = c.start + " 00:00:00"
              c.end = c.end + " 23:59:59"
            }
          }else if(a == "季"){
            year = date.getFullYear();
            if(that.firstReq) {
              let currMonth = date.getMonth()+1;
              if(currMonth <= 3 ){
                 that.dateTopList[1].c = 0
              }else if(currMonth > 3 && currMonth <= 6 ){
                 that.dateTopList[1].c = 1
              }else if(currMonth > 6 && currMonth <= 9 ){
                 that.dateTopList[1].c = 2
              }else if(currMonth > 9 && currMonth <= 12 ){
                 that.dateTopList[1].c = 3
              }
            }
            for(var j = 0; j < b.length; j++){
              var c = b[j]
              let text = c.text.replace("季度","")
              if(text == "一"){
                c.start = year + "-1-1"
                c.end = year + "-3-" + that.getDayWithYearMonth(year, 3)
              }else if(text == "二"){
                c.start = year + "-4-1"
                c.end = year + "-6-" + that.getDayWithYearMonth(year, 6)
              }else if(text == "三"){
                c.start = year + "-7-1"
                c.end = year + "-9-" + that.getDayWithYearMonth(year, 9)
              }else if(text == "四"){
                c.start = year + "-10-1"
                c.end = year + "-12-" + that.getDayWithYearMonth(year, 12)
              }
              c.start = c.start + " 00:00:00"
              c.end = c.end + " 23:59:59"
            }
          }else if(a == "年"){
            for(var j = 0; j < b.length; j++){
              var c = b[j]
              year = date.getFullYear();
              if(c.text == "去年"){
                year = year - 1
              }
               if(c.text == "今年"){
                year = year
              }
              c.start = year +"-1-1" 
              c.end = year +"-12-31" 
              c.start = c.start + " 00:00:00"
              c.end = c.end + " 23:59:59"
            }
          }
        }

        let dateTopItem = that.dateTopList[that.dateTopIndex]
        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id, //用户ID
          "businessId":that.$store.state.businessInfo.businessId, //业务ID
          "startDate":dateTopItem.b[dateTopItem.c].start, //开始时间
          "endDate":dateTopItem.b[dateTopItem.c].end, //结束时间 
        }

        that.httpRequest("customerSalesReport.do",postData,function(res){
          that.ReportList = []
          if(res.data.length){
            that.ReportList = res.data;
          }
          var totalQty = 0;
          var maxQty = 0;
          var totalMoney = 0;
          var maxMoney = 0;

          for(var i = 0; i < that.ReportList.length; i++) {

            that.ReportList[i].qty = that.ReportList[i].salesQty
            that.ReportList[i].money = that.ReportList[i].salesAmount
            that.ReportList[i].startDate = dateTopItem.b[dateTopItem.c].start
            that.ReportList[i].endDate = dateTopItem.b[dateTopItem.c].end
            let qty = parseFloat(that.ReportList[i].qty)
            let money = parseFloat(that.ReportList[i].money)
            totalQty += qty
            totalMoney += money
            if(maxQty < qty){
              maxQty = qty
            }
            if(maxMoney < money){
              maxMoney = money
            }
          }

          that.totalQty = totalQty
          that.totalMoney = totalMoney.toFixed(0)

          for(var i = 0; i < that.ReportList.length; i++) {

            that.ReportList[i].progressQty = totalQty ? (that.ReportList[i].qty / totalQty * 100).toFixed(1) : 0
            that.ReportList[i].maxQty = maxQty
            that.ReportList[i].progressMoney = totalMoney ? (that.ReportList[i].money / totalMoney * 100).toFixed(1) : 0
            that.ReportList[i].maxMoney = maxMoney
          }

          that.ReportList = that.sortByKeyDesc(that.ReportList,'qty')

          var x = that.dateTopList[that.dateTopIndex].b.length * 80;
          // 首次进入页面才执行
          if(that.firstReq){
            that.firstReq = false
            setTimeout(function (){
              // 设置滚动宽度
              $('.dateContentSize').css("width", x + 'px');
              // 默认选择月
              $('.dataType').eq(that.dateTopIndex).css('background-color','white')
              $('.dataType').eq(that.dateTopIndex).css('border-top','1px solid #D9D9D9')
              $('.dataType').eq(that.dateTopIndex).css('border-left','1px solid #D9D9D9')
              $('.dataType').eq(that.dateTopIndex).css('border-right','1px solid #D9D9D9')
              $('.dataType').eq(that.dateTopIndex).css('border-radius','5px 5px 0px 0px')
              $('.fds').eq(that.dateTopList[that.dateTopIndex].c)[0].scrollIntoView();
              document.documentElement.scrollTop = 0;
              // 默认当前月
              $('.fds').eq(that.dateTopList[that.dateTopIndex].c).css('border-bottom','1px solid green')
            }, 10);
          }
        })
      },
      monthOnclick(index, e){
        var crrentDiv = e.target;
        var divs = $('.fds')
        $(divs).css('border-bottom','0px solid green')
        $(crrentDiv).css('border-bottom','1px solid green')
        this.dateTopList[this.dateTopIndex].c = index;
        this.getData();
      },
      dateOnclick(index, e){
        var that = this;
        this.dateTopIndex = index;
        var crrentDiv = e.target;
        var divs = $('.dataType')
        $(divs).css('background-color','#EEEFF2')
        $(divs).css('border-top','1px solid #EEEFF2')
        $(divs).css('border-left','1px solid #EEEFF2')
        $(divs).css('border-right','1px solid #EEEFF2')
        $(crrentDiv).css('background-color','white')
        $(crrentDiv).css('border-top','1px solid #D9D9D9')
        $(crrentDiv).css('border-left','1px solid #D9D9D9')
        $(crrentDiv).css('border-right','1px solid #D9D9D9')
        $(crrentDiv).css('border-radius','5px 5px 0px 0px')

        // 设置滚动宽度
        var x = that.dateTopList[that.dateTopIndex].b.length * 80;
        $('.dateContentSize').css("width", x + 'px');

        setTimeout(function (){
          $('.fds').eq(that.dateTopList[that.dateTopIndex].c)[0].scrollIntoView();
          document.documentElement.scrollTop = 0;
        }, 10);

        var divfds = $('.fds')
        $(divfds).css('border-bottom','0px solid green')
        setTimeout(function (){
            $('.fds').eq(that.dateTopList[index].c).css('border-bottom','1px solid green')
        }, 10);
        this.getData();
      },
      qtyDesc(){

        this.ReportList = this.sortByKeyDesc(this.ReportList,'qty')
      },
      moneyDesc(){
        
        this.ReportList = this.sortByKeyDesc(this.ReportList,'money')
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerSalesReport{
    overflow-x: hidden;
    header{
      background-color: #fff;
      color: #000;
      font-weight: 600;
      overflow: hidden;
    }
    .container{
      .dateContainer{
        .dateInfo{
          width: 1500/50rem;
          height: 70/50rem;
          background-color: #EEEFF2;
          border-top: 15/50rem solid #EEEFF2;
          &>div{
            float: left;
            width: 750/3/50rem;
            height: 100%;
            text-align: center;
            line-height: 65/50rem;
            box-sizing: border-box;
            border-top: 1px solid #EEEFF2;
            border-left: 1px solid #EEEFF2;
            border-right: 1px solid #EEEFF2;
          }
        }
        .dateScroll{
          overflow: scroll;
          .dateContentSize{
            width: 2000px;
            height: 80/50rem;
            &>div{
              width: 80px;
              float: left;
              height: 80/50rem;
              text-align: center;
              line-height: 80/50rem;
              box-sizing: border-box;
            }
          }
        }
      }
      .componentReportBar{
       border-bottom: 20/50rem solid #EEEFF2;
        background-color: #fff;
      }
      .reportInfo{
        height: 150/50rem;
        margin-left: 30/50rem;
        margin-right: 30/50rem;
        .left{
          width: 100%;
          height: 100%;
          float: left;
          .lineOne{
            width: 100%;
            height: 35/50rem;
            float: left;
            margin-top: 30/50rem;
            .colorQty{
              width: 35/50rem;
              height: 35/50rem;
              float: left;
              margin-right: 10/50rem;
              margin-top: 3/50rem;
            }
          }
          .lineTwo{
            width: 100%;
            height: 35/50rem;
            float: left;
            margin-top: 30/50rem;
            .colorMoney{
              width: 35/50rem;
              height: 35/50rem;
              float: left;
              margin-right: 10/50rem;
              margin-top: 6/50rem;
            }
            .prompt2{
              float: left;
              color: #C1C1C1;
              margin-left: 5/50rem;
            }
          }
          .prompt{
            float: left;
          }
          .prompt3{
            float: right;
            margin-left: 5/50rem;
          }
        }
        .right{
          width: 180/50rem;
          height: 100%;
          float: right;
        }
      }

    	#main{
    		margin: 60/50rem 0;
    	}
    }
  }

  ::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
  }
</style>


