<template>
  <div class="CustomerVisit">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfChakanShow='true' @f_Chakan='f_Chakan' ></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="FixedHead">
	  	<div class="CustomerSearch">
	  	  <div>
	  	    <input v-model='searchValue' placeholder="按客户名称搜索">
	  	    <i class="iconfont icon-sousuo"></i>
	  	  </div>
	    </div>
	    <div class="selectCondition">
  		  <div @click='selectBoxShow("供货商")'>
  		    <span>供货商名称：</span>
  		    <i class="iconfont icon-xiangyou"></i>
  		    <span>{{SupplierName}}</span>
  		  </div>
  		  <div @click='selectBoxShow("拜访线路")'>
  		    <span>拜访线路：</span>
  		    <i class="iconfont icon-xiangyou"></i>
  		    <span>{{VisitRouteName}}</span>
  		  </div>
        <div @click='selectBoxShow("拜访状态")'>
          <span>拜访状态：</span>
          <i class="iconfont icon-xiangyou"></i>
          <span>{{VisitStateName}}</span>
        </div>
	    </div>
	  </div>
    <div class="CustomerList">
      <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
        <div class="CustomerItem" @click='toCreateNewVisit(index)'  v-for='(dataItem,index) in CustomerListData'  :id="index"  :key='index'>
          <div class="firmName">
            <span>{{dataItem.storesName}}</span>
            <span class="vi_status" :style='{backgroundColor:dataItem.color}'>{{dataItem.visitState}}</span>
          </div>
          <div class="firmAddr"><span>{{dataItem.address1}}</span></div>
          <div v-if="dataItem.lastvisitTime"><span>上次拜访时间：</span><span>{{dataItem.lastvisitTime}}</span></div>
          <div class="HaveBeenVisit">
            <div>已拜访{{dataItem.visitCount}}次</div>
            <div>
              <i class="iconfont icon-xie"></i>
              <span>拜访</span>
            </div>
            <div @click='toHistoryVisit(index)'>
              <i class="iconfont icon-chakan"></i>
              <span>历史拜访</span>
            </div>
          </div>
        </div>
      </mt-loadmore>
      <div class="kong"></div>
    </div>
    <div class="noData" v-if="noDataShow">
      <i class="iconfont icon-meiyoushuju-copy"></i>
      <span>没有数据</span>
    </div>
	</div>
  <div class="SelectMeng" v-if='StatisticsShow'>
    <div class="CustomerVisitStatistics">
      <div class="StatisticsHead">
        <div><span>客户拜访统计</span></div>
        <div><span>{{CurrentDate}}</span></div>
      </div>
      <div>
        <div>
          <div>{{TargetVisits}}</div>
          <div>目标拜访量</div>
        </div>
        <div>
          <div>{{TodayVisits}}</div>
          <div>今日拜访量</div>
        </div>
        <div>
          <div>{{VisitsRate}}</div>
          <div>今日达成率</div>
        </div>
      </div>
      <div>
        <div class="monthVisit">月已拜访&nbsp; <span>{{MonthVisits}} </span>/<span>{{MonthTargetVisits}}</span>
          <span>{{VisitsRateMonth}}</span>
        </div>
        <div class="progressBorder">
           <div class="progressMonth">{{ drawLine(this.progressMonth)}}</div>
        </div>
      </div>
      <p><span @click='StatisticsHide'>关闭</span></p>
    </div>
  </div>
	<componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
  <div class="BtnComponent bottomBtn" v-if='!noDataShow'>
    <div @click="toRecomRoute">推荐线路</div>
  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import componentReportBar from '@/components/Widgets/componentReportBar'
  import $ from "jquery"
  export default{
    data(){
      return{
      	headerText:"客户拜访",//页面标题
      	SupplierName:'',//供货商名称
        suppliersId:'',//当前供应商id
      	VisitRouteName:'',//当前拜访线路名称
        VisitRouteId:"",//当前拜访线路id
        VisitStateName:'',//拜访状态
      	SelectBoxFlag:false,//弹出选择框是否显示
      	BouncedName:'',//弹框名字
        searchValue:'',//搜索的值
        cruuentSeleText:"",//当前显示的弹出框名字
        StatisticsShow:false,//拜访统计是否出现
        CurrentDate:'',//当前日期
      	CustomerListData:[],//门店列表
        SelectBoxList:[],//弹出框选择数据列表
        supplierList:[],//供货商列表
        VisitLineList:[],//拜访线路列表
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        TargetVisits:'',//目标拜访量
        TodayVisits:'',//今日拜访量
        VisitsRate:'',//今日达成率
        MonthTargetVisits:'',//本月目标拜访量
        MonthVisits:'',//本月实际拜访量
        progressMonth:'',
        VisitsRateMonth:'',//本月拜访率
      }
    },
    components:{
	    componentHeader,
	    componentSelectBox,
      componentReportBar
	  },
    created(){
      var that = this;

      var i_do = "findSuppliers.do"
      if(this.$store.state.visitObject == "OSP"){
        i_do = "findClassA.do"
      }

      var VisitStateList =  that.$store.state.VisitStateList;//拜访状态列表

      // 拜访状态
      if(that.$store.state.VisitConditionUserHabits.VisitState){

        this.VisitStateName = that.$store.state.VisitConditionUserHabits.VisitState;
      }else{
        this.VisitStateName = VisitStateList[0].name;//拜访状态
      }

      var SupplierData = {
        "cellphone":that.$store.state.userInfo.cellphone,//手机号
        "userId":that.$store.state.userInfo.user_id,//用户id
        "businessId":that.$store.state.businessInfo.businessId,//业务ID
      }
      // 获取供货商
      this.httpRequest(i_do,SupplierData,function(res){
        if(res.data.length){
          var idx = that.$store.state.CustomerVisit_supplierListIdx;
          that.supplierList = res.data;
          that.SupplierName = res.data[idx].name;//供货商名称
          that.suppliersId = res.data[idx].suppliersId;//供应商id

          that.getVisitLine();
        }else{
          that.$alert('未获取到供应商，请联系管理员', '提示', {
            confirmButtonText: '确定',
          })
        }
      })
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoreInfo();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){

        this.$store.state.VisitConditionUserHabits = {
          VisitLine:'',//拜访线路
          VisitState:'',//拜访状态
        }//拜访筛选条件用户习惯记录清空

    	  this.$router.push({
          name:"Index",
        })
    	},
      toRecomRoute(){

        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName;//拜访线路
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//拜访状态

        this.$router.push({
          name:"RecomRoute",
          query:{
            suppliersId:this.suppliersId,
            VisitRouteId:this.VisitRouteId,
          }
        })
      },
      drawLine(progress){
        setTimeout(function (){
          var a = (progress  * 100).toFixed(1);
          if(a > 100){ a = 100;}
          $('.progressMonth').css("width", a +'%');
        }, 30);
      },
      // 获取拜访线路
      getVisitLine(){

        var that = this;
        var LineData = {
          "suppliersId":that.suppliersId,//供货商id
        }
        // 获取拜访线路
        that.httpRequest("GetVisitLine.do",LineData,function(LineRes){
           
           if(LineRes.data.length){
            that.VisitLineList = LineRes.data;

            if(that.$store.state.VisitConditionUserHabits.VisitLine){

              that.VisitRouteName = that.$store.state.VisitConditionUserHabits.VisitLine;
            }else{
              that.Date= new Date();
              var m = that.Date.getDay();
              that.VisitRouteName = "星期" + "天一二三四五六".charAt(m);//拜访线路显示当前日期
            }
            
            for(var i=0; i<LineRes.data.length; i++){
              if(that.VisitRouteName == LineRes.data[i].name){
                that.VisitRouteId = LineRes.data[i].visitId;
              }
            }
            // 获取门店信息
            that.reqStoreInfo();

           }else{

            that.$alert('未获取到拜访线路，请联系管理员', '提示', {
              confirmButtonText: '确定',
            })
          }
        })
      },
      // 搜索门店
      reqStoreInfo(load){

        var that = this,
            VisitStateList = this.$store.state.VisitStateList;

        var LineData = {
          "suppliersId":that.suppliersId,//供货商id
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "userId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "strLineId":that.VisitRouteId,//线路id
          "strStoreName":that.searchValue,//门店名称
          "strStoreState":that.VisitStateName,//拜访状态
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }

        var i_do = "findStores.do"
        if(this.$store.state.visitObject == "OSP"){
          i_do = "findClassB.do"
        }

        // 获取门店信息
        that.httpRequest(i_do,LineData,function(SupRes){
          // console.log(SupRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          for(var i=0;i<SupRes.data.length;i++){

            for (var j = 0;j < VisitStateList.length; j++) {
              
              if(SupRes.data[i].visitState == VisitStateList[j].name){
                SupRes.data[i].color = VisitStateList[j].color;
              }
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(SupRes.data.length){
              that.CustomerListData = that.CustomerListData.concat(SupRes.data);
            }
          } else {
            that.CustomerListData = SupRes.data;
          }
          // 判断是否能上拉加载
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
           
          if(that.CustomerListData.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }
        })
      },
    	//拜访记录 出现
    	f_Chakan(){
        var that = this;
        if(!this.CurrentDate){
          this.CurrentDate = this.getNowTime('getDate');
        }
         //获取拜访完成率
       var VisitData = {
          "appUsersId": that.$store.state.userInfo.user_id,//用户id
          "businessId": that.$store.state.businessInfo.businessId,//业务ID
          "orgnizationId": that.suppliersId,
        }
        this.httpRequest("visitCountReport.do",VisitData,function(res){
        console.log(res.data)
          if(res.data){
            that.TargetVisits = res.data.targetSum;//目标拜访量
            that.TodayVisits = res.data.visitSum;//今日拜访量
            that.MonthTargetVisits = res.data.targetSumMonth;//本月目标拜访量
            that.MonthVisits = res.data.visitSumMonth;//本月实际拜访量

            if(res.data.targetSum == 0 || res.data.targetSumMonth == 0){
              that.VisitsRate = "100%";
              that.progressMonth = 1;
            }else{
              that.VisitsRate = (that.TodayVisits/that.TargetVisits  * 100).toFixed(1)+'%';//今日拜访完成率
              that.progressMonth = that.MonthVisits/that.MonthTargetVisits;
              that.VisitsRateMonth = (that.progressMonth  * 100).toFixed(1)+'%';//本月拜访完成率
            }
           
            that.StatisticsShow = true;

          }else{
            that.$alert('未获取到数据，请联系管理员', '提示', {
              confirmButtonText: '确定',
            })
          }
        })
    	},
      // 拜访统计 消失
      StatisticsHide(){
        this.StatisticsShow = false;
      },
      // 选择弹出框 显示
    	selectBoxShow(text){
    		this.BouncedName = text;
    		this.SelectBoxFlag=true;

        if(text == '供货商'){
          this.SelectBoxList = this.supplierList;
          this.cruuentSeleText = '供货商';

        }else if(text == '拜访线路'){
          this.SelectBoxList = this.VisitLineList;
          this.cruuentSeleText = '拜访线路';

        }else if(text == '拜访状态'){
          this.SelectBoxList = this.$store.state.VisitStateList;
          this.cruuentSeleText = '拜访状态';
        }
    	},
      // 选中某个状态
      selectedItem(index){

        var that = this;
        if(this.cruuentSeleText == '供货商'){
          this.SupplierName = this.supplierList[index].name;
          this.suppliersId = this.supplierList[index].suppliersId;
          this.loadPage = 1;
          this.allLoaded = false;
          this.$store.state.CustomerVisit_supplierListIdx = index;
          // 获取拜访线路、门店信息
          that.getVisitLine();

        }else if(this.cruuentSeleText == '拜访线路'){
          this.VisitRouteName = this.VisitLineList[index].name;
          this.VisitRouteId = this.VisitLineList[index].visitId;
          this.loadPage = 1;
          this.allLoaded = false;
          // 获取门店信息
          that.reqStoreInfo();

        }else if(this.cruuentSeleText == '拜访状态'){
          this.VisitStateName = this.$store.state.VisitStateList[index].name;
          this.loadPage = 1;
          this.allLoaded = false;
          // 获取门店信息
          that.reqStoreInfo();
        }

        this.SelectBoxFlag = false;
      },
    	// 取消按钮 弹出选择框
    	selectCancel(){
    	  this.SelectBoxFlag = false;
    	},
      // 跳转到 新建拜访 页面
      toCreateNewVisit(index){

        var that = this;
  
        this.$store.state.CurrentStoresId = this.CustomerListData[index].storesId;//将当前拜访的门店id存到全局
        
        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName ;//拜访线路
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//拜访状态

        if(that.CustomerListData[index].visitState == "拜访中"){

          var histData = {
            "storesId":this.$store.state.CurrentStoresId,//门店id
            "startTime":'',//开始时间
            "endTime":'',//结束时间
            "currentPage":1,//当前页数
            "numberPerPage":1,//每页显示多少行数据
          }
          // 获取历史拜访数据列表
          that.httpRequest("GetVisitHistory.do",histData,function(histRes){

            var m = histRes.data[0];

            if(m.VISIT_STATES != "拜访中"){

              that.$alert('请进入"历史拜访"完成上次任务', '提示', {
                confirmButtonText: '确定',
              })
              return;
            }

            // 保存全局 当前拜访id
            that.$store.state.CurrentVisitId = m.PRATY_VISIT_ID;

            if(m.VISIT_STEPS == '进店'){
              that.$router.push({
                name:"CreateNewVisit",
              })
            }else if(m.VISIT_STEPS == '确认信息'){
              that.$router.push({
                name:"CheckInventory",
              })
            }else if(m.VISIT_STEPS == '检查库存'){
              that.$router.push({
                name:"ChooseGoods",
                query:{ 
                  CurrentStoresId:that.$store.state.CurrentStoresId,//门店id
                  visitId:that.$store.state.CurrentVisitId,//拜访id
                  whoPush:"HistoryVisit",
                  orderType:"SALE_OUT",
                }
              })
            }else if(m.VISIT_STEPS == '建议订单'){
               that.$router.push({
                name:"VividDisplay",
              })
            }else if(m.VISIT_STEPS == '生动化陈列'){
              that.$router.push({
                name:"LeaveStore",
              })
            }else{
             
              that.$router.push({
                name:"LeaveStore",
                query:{
                  pageName:'查看拜访',
                }
              })
            }
          })
        }else{

          that.$router.push({
            name:"IntoTheShop",
            query:{
              LineId:that.VisitRouteId
            }
          })
        }
      },
      // 跳转到 历史拜访 页面
      toHistoryVisit(index){
        event.stopPropagation();

        var cruuentCont = this.CustomerListData[index];

        this.$store.state.CurrentStoresId = cruuentCont.storesId;//将当前查看的门店id存到全局

        this.$store.state.CurrentStoresHistoryInfo = {
          storesName:cruuentCont.storesName,//门店名称
          lastvisitTime:cruuentCont.lastvisitTime,//上次拜访时间
          visitCount:cruuentCont.visitCount,//拜访次数
        }

        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName ;//拜访线路
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//拜访状态

        this.$router.push({
          name:"HistoryVisit",
        })
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.reqStoreInfo("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqStoreInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerVisit{
  	overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
    }
  	.FixedHead{
      background-color:#fff;
      z-index: 50;
  	  width: 100%;
  	  position:fixed;
  	  top: 89/50rem;
  	  left: 0;
  	  .CustomerSearch{
  	    .TopSearchBox;
  	  }
      .selectCondition{
        &>div{
          height: 80/50rem;
          &>span:nth-child(3){
            text-align: right;
            width: 430/50rem;
            overflow: hidden;
            text-overflow: ellipsis;/*文字隐藏后添加省略号*/
            white-space: nowrap;/*强制不换行*/
          }
        }
      }
  	}
  	.CustomerList{
      height: 100%;
      overflow: scroll;
      padding-top: 430/50rem;
      box-sizing:border-box;
      .CustomerItem{
        border-bottom:10/50rem solid #f0f0f0;
        font-size: 28/50rem;
        padding-left: 20/50rem;
        box-sizing:border-box;
        color: #666;
        overflow: hidden;
        &>div{
          line-height: 55/50rem;
          overflow: hidden;
        }
        .firmName{
          line-height: 65/50rem;
          &>span{
            &:first-child{
              font-size: 30/50rem;
              color: #000;
              font-weight: 600;
            }
            &.vi_status{
              float: right;
              background-color: #5965D8;
              padding:0  25/50rem;
              margin-top: 7.25/50rem;
              height: 50/50rem;
              line-height: 50/50rem;
              color: #fff;
              border-radius: 25/50rem 0 0 25/50rem;
            }
          }
        }
        .firmAddr{
          padding-right:20/50rem;
          line-height: 45/50rem;
        }
        .HaveBeenVisit{
          margin-top: 5/50rem;
          line-height: 60/50rem;
          &>div{
            float: right;
            overflow: hidden;
            margin-right: 25/50rem;
            i{
              float: left;
              font-size: 30/50rem;
            }
            span{
              float: left;
              margin-left: 10/50rem;
            }
            &:first-child{
              float: left;
            }
            &:last-child{
              i{
                font-size: 40/50rem;
              }
            }
          }
        }
      }
  	}
    .SelectMeng{
      font-size: 32/50rem;
      .CustomerVisitStatistics{
        width: 710/50rem;
        margin:0;
        position:absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        .monthVisit{
          font-size: 28/50rem;
          padding: 20/50rem 25/50rem 0 25/50rem;
          color: #666;
          span:nth-child(3){
            float: right;
          }
        }
        .progressBorder{
          height: 12/50rem;
          border:1/50rem solid  @PublicColor;
          margin: 8/50rem  20/50rem  0  20/50rem;
          padding: 5/50rem 5/50rem 5/50rem 5/50rem;
          border-radius: 10/50rem;
          .progressMonth{
            height: 12/50rem;
            background-color:  @PublicColor;
            border-radius: 10/50rem;
          }
        }
        
        p{
          text-align: center;
          span{
            font-size: 34/50rem;
            font-weight: 600;
            padding:20/50rem 30/50rem;
            display: inline-block;
          }
          i{
            font-size: 45/50rem;
          }
        }
      }
    }
    .BtnComponent{
      padding:0 250/50rem;
    }
  }
</style>


