<template>
  <div class="QueryPurchaseOrder">
    <componentHeader v-if='!$store.state.CurrentRoleData.QuerySalesOrder' headerText="采购订单"></componentHeader>
	  <header class="SalesOrderHeader" v-else>
      <div @click="SelectSales">销售订单</div>
      <div class="activeBtn">采购订单</div>
    </header>
  	<div class="container haveHeadcontainer">
      <div class="ScreeningNav">
        <div>
          <div class="StateSelect" @click='SelectBoxShow("订单状态")'>
            <span>{{SelectBox.name}}</span>
            <i class="iconfont icon-xiangxia"></i>
          </div>
          <div class="ConditionsSelectBtn">条件筛选</div>
        </div>
      </div>
      <div class='searchContainer'>
        <div class='searchResult'>
          <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
            <div @click='toDetail(Purchaseindex)' class='searchList' v-for='(PurchaseItem,Purchaseindex) in PurchaseArr'  :id="Purchaseindex"  :key='Purchaseindex'>
              <div class='list_left'>
                <div><span>订单编号：</span><span>{{PurchaseItem.OMS_NO}}</span><span></span></div>
                <div><span>创建时间：</span><span>{{PurchaseItem.ADDDATE}}</span><span></span></div>
                <div><span>客户名称：</span><span>{{PurchaseItem.RECEIVE_PARTY_NAME}}</span><span></span></div>
              </div>
              <div class='list_right'>
                <span>{{PurchaseItem.ORDER_STATUS}}</span>
              </div>
            </div>
          </mt-loadmore>
          <div v-if="PurchaseArr&&PurchaseArr.length" class='searchTotal'>共计{{PurchaseArr.length}}条记录</div>
          <div class="kong"></div>
        </div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
    <componentSelectBox v-if="SelectBoxHide" v-bind:SelectBoxList='SelectBoxList' @selectCancel='selectCancel' @selectedItem='selectedItem' :BouncedName='BouncedName'></componentSelectBox>
    <componentFooter></componentFooter>
  </div>
</template>
<script type="text/javascript">
  import componentFooter from '@/components/componentFooter'
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'

  export default{
    data(){
      return{
        PurchaseArr:[],//采购订单 列表
        SelectBox:{},
        SelectBoxHide:false,//选择弹出框 是否显示
        SelectBoxList:[{
          name:'全部',
          state:'',
        },{
          name:'新建',
          state:'NEW',
        },{
          name:'已审核',
          state:'AUDIT',
        },{
          name:'待装车',
          state:'UNPLANED',
        },{
          name:'在途',
          state:'SHIPPED',
        },{
          name:'已完成',
          state:'DELIVERED',
        },{
          name:'已取消',
          state:'CANCEL',
        }],//订单状态
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentFooter,
      componentHeader,
      componentSelectBox
	  },
    created(){

      this.SelectBox = (this.SelectBoxList.length) ? this.SelectBoxList[0] : {};
      this.requsterList();
    },
    methods:{
      // 请求数据
      requsterList(load){
        var that = this;
        var postData = {
          "tmsOrderStatus":this.SelectBox.state,
          "businessId":this.$store.state.businessInfo.businessId,
          "appUsersId":this.$store.state.userInfo.user_id,
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        this.httpRequest( "getPurchaseOrderList.do",postData,function(res){
          console.log(res.data)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(res.data.length){
              that.PurchaseArr = that.PurchaseArr.concat(res.data);
            }
          } else {
            that.PurchaseArr = res.data;
          }

          // 判断是否能上拉加载
          if (res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.PurchaseArr.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }
        })
      },
      // 切换头部导航
      SelectSales(){
        this.$router.push({
          name:"QuerySalesOrder"
        })
      },
      // 选择订单状态 弹出框显示
      SelectBoxShow(text){
        this.BouncedName = text;
        this.SelectBoxHide = true;
      },
      // 选择状态弹出框 消失
      selectCancel(){
        this.SelectBoxHide = false;
      },
      // 选中某个状态
      selectedItem(index){
        this.SelectBox = this.SelectBoxList[index];
        this.SelectBoxHide = false;
        this.requsterList();
      },
      //跳转到订单详情页
      toDetail (idx){

        var that = this;

        this.$router.push({
          name:"PurchaseOrderDetail",
          query:{
            orderId:that.PurchaseArr[idx].ORDER_ID
          }
        })
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.requsterList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.requsterList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .QueryPurchaseOrder{
    overflow:hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .searchContainer{
        width: 100%;
        height: 100%;
        overflow: scroll;
        box-sizing:border-box;
        padding-top:170/50rem;
        .searchActive{
          color: #A71E23;
          border-bottom: 1px solid #A71E23;
        }
        .searchTotal{
          color: #3C3C3C;
          font-size: 28/50rem;
          text-align: center;
          font-weight: 600;
          width: 100%;
          height:80/50rem;
          line-height: 80/50rem;
        }
        .searchList{
          width:100%;
          display: flex;
          position: relative;
          background-color: #fff;
          border-top: 1px solid #ECECF2;
          border-bottom: 1px solid #ECECF2;
          padding: 20/50rem 0/50rem 20/50rem 20/50rem;
          box-sizing: border-box;
          overflow: hidden;
          &>.list_left{
            flex-grow: 1;
            font-size: 28/50rem;
            &>div{
              display: flex;
              &>span:nth-child(2){
                flex-grow: 1;
              }
              &>span:first-child{
                min-width:155/50rem;
                flex-shrink: 0;
              }
              &>span:nth-child(3){
                min-width:155/50rem;
                flex-shrink: 0;
              }
            }
          }
          &>.list_right{
            position:absolute;
            right: 0;
            top: 0;
            padding:20/50rem 0;
            font-weight: 600;
            height: 100%;
            min-width:155/50rem;
            box-sizing: border-box;
            flex-shrink: 0;
            font-size: 30/50rem;
            span{
              position:absolute;
              top: 50%;
              text-align: center;
              width: 100%;
              left: 0;
              transform: translateY(-50%);
            }
          }
        }
      }
    }
  }
</style>


