<template>
  <div class="AddInventory">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <mt-loadmore  bottomPullText="" bottomDropText="上拉加载" bottomLoadingText=""  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
      <div class="dateHead">
        <div>
          <span>库存月份：</span>
          <el-date-picker v-model="InventoryMonth" :editable='false' type="month" placeholder="选择月" format="yyyy 年 MM 月" value-format="yyyy-MM"></el-date-picker>
        </div>
        <div><span>填表日期：</span><span>{{CurrentDate}}</span></div>
      </div>
      <div class="orderInfo">
        <div class='infoHead'>客户信息</div>
        <div v-if="selectCustomerFlag" class='infoContainer'>
          <div><span>客户名称：</span><span>{{CustomerInfo.CUSTOMER_NAME}}</span></div>
          <!-- <div>客户代码：<span>{{CustomerInfo.CustomerCode}}</span></div> -->
          <!-- <div>客户城市：<span>{{CustomerInfo.CustomerCity}}</span></div> -->
          <div><span>联&nbsp;系&nbsp;人&nbsp;：</span><span>{{CustomerInfo.CONTACT_NAME}}&nbsp;&nbsp;&nbsp;{{CustomerInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='CustomerInfo.CONTACT_TEL' @click="callPhone(storesData.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div><span>详细地址：</span><span>{{CustomerInfo.CUSTOMER_ADDRESS}}</span></div>
        </div>
        <div v-if="selectCustomerFlag" class="reselect"><span @click="ReSelect">重新选择</span></div>
        <div v-if="!selectCustomerFlag" class="BtnComponent">
          <div @click="toSelectStores">选择门店</div>
          <div @click="SelectOsp">选择客户</div>
        </div>
      </div>
      <div class="inv_goods">
        <div class='infoHead'>货物信息</div>
          <div class='infoContainer' v-for='(itemName,index) in ProductList'  :id="index"  :key='index'>
            <div class="inv_goodsInfo">
              <div class="inventoryLeft">
                <div style="font-weight: 900;">{{itemName.PRODUCT_NAME}}</div>
                <div><span>规格：</span><span>{{itemName.PRODUCT_DESC}}</span></div>
                <div><span>原价：</span><span>{{itemName.UNIT_PRICE}}</span></div>
              </div>
              <div class="inventoryRight">
                <i class="iconfont icon-Untitled-" @click='deleteOne(index)'></i>
                <i class="iconfont icon-jia1" @click='setNew(index)'></i>
              </div>
            </div>
            <div class="InventoryQty" v-for='(QtyName,QtyIdx) in itemName.QtyList'  :id="QtyIdx"  :key='QtyIdx'>
              <div class="_inventoryQty">
                <span>库存数量：</span>
                <input type="number" pattern="[0-9]*" @blur="onblur(index,QtyIdx)"  v-model='QtyName.InventoryQty'>
                <span>{{itemName.UNIT_OF_MEASURE_STR}}</span>
              </div>
              <div class="_inventoryDate">
                <span>生产日期：</span>
                <el-date-picker v-model="QtyName.DateInProduced" :editable='false' type="date" placeholder="选择日期" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" :picker-options="expireTimeOption"></el-date-picker>
                <span class="DateInProduced">{{QtyName.DateInProduced}}</span>
                <i v-if='QtyName.DateInProduced' @click="clearInput_check(index,QtyIdx)" class="iconfont icon-guanbi"></i>
              </div>
            </div>
          </div>
      </div>
      </mt-loadmore>
      <div class="kong"></div>
	  </div>
    <div class="noData" v-if="noDataShow">
      <i class="iconfont icon-meiyoushuju-copy"></i>
      <span>没有数据</span>
    </div>
    <div class="BtnComponent bottomBtn">
      <div @click='preview' class="onlyBtn">预览</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"登记库存",//页面标题
        InventoryMonth:'',//库存月份
        CurrentDate:'',//当前日期
        CustomerInfo:{},//客户信息
      	ProductList:[],//全部货物列表
        finalGoodsList:[],//登记好了的货物列表
        selectCustomerFlag:false,//是否选择了门店/客户
        custoType:'',//客户类型
        AddInvPagesData:[],//保存了该页面全局的已经选好的数据
        // 上拉加载
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        noDataShow:false,//没有数据
        expireTimeOption: {
          disabledDate(date) {
              return date.getTime() >= Date.now();
          }
        },//生产日期不可大于当前日期
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      this.CurrentDate = this.getNowTime('getDate');
      var that = this;

      var AddInvPagesData = this.$store.state.AddInventory_pagesData;
      this.AddInvPagesData = AddInvPagesData;

      if(AddInvPagesData.InventoryMonth){
        this.InventoryMonth = AddInvPagesData.InventoryMonth;//库存月份
      }
      if(AddInvPagesData.CustomerInfo){
        this.CustomerInfo = AddInvPagesData.CustomerInfo;//客户信息
      }
      if(AddInvPagesData.selectCustomerFlag){
        this.selectCustomerFlag = AddInvPagesData.selectCustomerFlag;//是否选择了门店/客户
      }

      if(this.$route.query.CustomerInfo&&this.$route.query.CustomerInfo.CUSTOMER_NAME){
        this.custoType = 'store';//客户类型
        this.selectCustomerFlag = true;
        this.CustomerInfo = this.$route.query.CustomerInfo;
      }

      this.reqGoodsList();
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        // 清空
        this.$store.state.AddInventory_pagesData = {};
    		this.$router.push({
          name:"InventoryRegister",
        })
    	},
      // 请求列表
      reqGoodsList(load){
        var that = this;
        var ProductData = {
          "productName":"",//货品名称
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "productBrand":'',//货品类型
          "productType":'',//货品分类
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
          'consignee_id':'',//收货人id
          "userType":that.$store.state.G_AppOrWX,//判断是app还是公众号
        }
        console.log(ProductData)
        // 查询商品列表
        that.httpRequest("searchProductList.do",ProductData,function(ProductRes){
          console.log(ProductRes)

          if(load && load == "BottomLoad"){

            that.$refs.loadmore.onBottomLoaded();
          }

          for(var i = 0;i < ProductRes.data.length;i++){
            ProductRes.data[i].QtyList = [{InventoryQty:'',DateInProduced:''}]
          }

          if(ProductRes.data.length){
            
            var AddInvPagesData = that.AddInvPagesData;
            if(that.AddInvPagesData.ProductList){

              for(var j=0;j<AddInvPagesData.ProductList.length;j++){

                for(var k = 0;k<ProductRes.data.length;k++){

                  if(AddInvPagesData.ProductList[j].productId == ProductRes.data[k].PRODUCT_ID){

                    if(!ProductRes.data[k].QtyList[0].InventoryQty&&!ProductRes.data[k].QtyList[0].produceTime){
                      ProductRes.data[k].QtyList[0].InventoryQty = AddInvPagesData.ProductList[j].inventory;
                      ProductRes.data[k].QtyList[0].DateInProduced = AddInvPagesData.ProductList[j].produceTime;
                    }else{
                      ProductRes.data[k].QtyList[ProductRes.data[k].QtyList.length] = {
                        InventoryQty:AddInvPagesData.ProductList[j].inventory,
                        DateInProduced:AddInvPagesData.ProductList[j].produceTime
                      }
                    }
                  }
                }
              }
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(ProductRes.data.length){
              that.ProductList = that.ProductList.concat(ProductRes.data);
            }
          } else {
            that.ProductList = ProductRes.data;
          }

          // 判断是否能上拉加载
          if (ProductRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.ProductList.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }

          
        })
      },
      // 重新选择客户信息
      ReSelect(){
        this.selectCustomerFlag = false;
        this.CustomerInfo = {};
      },
      // 新增一组库存数量
      setNew(idx){
        this.ProductList[idx].QtyList.push({InventoryQty:'',DateInProduced:''})
      },
      // 删除一组库存数量
      deleteOne(idx){

        if(this.ProductList[idx].QtyList.length>1){
          this.ProductList[idx].QtyList.pop()
        }
      },
      // 选择osp 进行库存登记
      SelectOsp(){
        this.selectCustomerFlag = true;
        var ospInfo = this.$store.state.InventoryOspInfo;

        this.custoType = 'customer';//客户类型
        this.$store.state.AddInventory_pagesData.custoType= 'customer';//客户类型
        this.CustomerInfo = {
          "CUSTOMER_NAME":ospInfo.CUSTOMER_NAME,
          "CONTACT_NAME":ospInfo.CONTACT_NAME,
          "CONTACT_TEL":ospInfo.CONTACT_TEL,
          "CUSTOMER_ADDRESS":ospInfo.CUSTOMER_ADDRESS,
        };
      },
      // 跳转到 选择门店页面
      toSelectStores(){

        var that = this,AddInvPagesData = this.$store.state.AddInventory_pagesData,finalGoodsList=[];

        AddInvPagesData.InventoryMonth = that.InventoryMonth;//库存月份

         // 汇总已操作数据
        for(var j=0;j<that.ProductList.length;j++){

          for(var k = 0;k<that.ProductList[j].QtyList.length;k++){

            if(that.ProductList[j].QtyList[k].DateInProduced || that.ProductList[j].QtyList[k].InventoryQty){

              finalGoodsList[finalGoodsList.length] = {
                "productId":that.ProductList[j].PRODUCT_ID,
                "inventory":that.ProductList[j].QtyList[k].InventoryQty,
                "produceTime":that.ProductList[j].QtyList[k].DateInProduced,
              }
            }
          }
        }
        this.$store.state.AddInventory_pagesData.ProductList = finalGoodsList;
        this.$router.push({
          name:"SelectStores",
          query:{
            whoPush:"AddInventory"
          }
        })
      },
      // 预览 跳转到 确认添加库存 页面
      preview(){

        var that = this,finalGoodsList=[];
        if(!this.InventoryMonth){
          that.$alert('请选择库存月份', '提示', {
            confirmButtonText: '确定',
          })
          return;
        }
        // ?======请选择客户信息
        if(!that.selectCustomerFlag){
          that.$alert('请选择客户信息', '提示', {
            confirmButtonText: '确定',
          })
          return;
        }

        // 汇总已操作数据
        for(var j=0;j<that.ProductList.length;j++){

          for(var k = 0;k<that.ProductList[j].QtyList.length;k++){

            if(that.ProductList[j].QtyList[k].InventoryQty <= 0){
              that.ProductList[j].QtyList[k].InventoryQty = '';
            }

            if(that.ProductList[j].QtyList[k].InventoryQty&&!that.ProductList[j].QtyList[k].DateInProduced){
              that.$alert('库存数量有值时，生产日期不能为空', '提示', {
                confirmButtonText: '确定',
              })
              return;
            }

            if(that.ProductList[j].QtyList[k].DateInProduced&&!that.ProductList[j].QtyList[k].InventoryQty){
              that.$alert('生产日期有值时，库存数量不能为空', '提示', {
                confirmButtonText: '确定',
              })
              return;
            }

            if(that.ProductList[j].QtyList[k].DateInProduced&&that.ProductList[j].QtyList[k].InventoryQty){

              finalGoodsList[finalGoodsList.length] = {
                "productId":that.ProductList[j].PRODUCT_ID,
                "productName":that.ProductList[j].PRODUCT_NAME,
                "unitPrice":that.ProductList[j].UNIT_PRICE,
                "productDesc":that.ProductList[j].PRODUCT_DESC,
                "inventory":that.ProductList[j].QtyList[k].InventoryQty,
                "produceTime":that.ProductList[j].QtyList[k].DateInProduced,
                "UNIT_OF_MEASURE_STR":that.ProductList[j].UNIT_OF_MEASURE_STR,
              }
            }
          }
        }

        if(!finalGoodsList.length){
          that.$alert('没有操作库存数量哦', '提示', {
            confirmButtonText: '确定',
          })
          return;
        }

        var AddInvPagesData = that.$store.state.AddInventory_pagesData;

        AddInvPagesData.InventoryMonth = that.InventoryMonth;//库存月份
        AddInvPagesData.ProductList = finalGoodsList;//操作了的数据数组
        AddInvPagesData.CustomerInfo = that.CustomerInfo;//客户信息
        AddInvPagesData.selectCustomerFlag = that.selectCustomerFlag;//是否选择了门店/客户
        AddInvPagesData.fillingDate= that.CurrentDate;//是否选择了门店/客户
        AddInvPagesData.custoType= that.custoType;//客户类型
        this.$router.push({
          name:"AddInventoryConfirmInfo"
        })
      },
      // input失去焦点
      onblur(index,QtyIdx){
        if(this.ProductList[index].QtyList[QtyIdx].InventoryQty <= 0){
          this.ProductList[index].QtyList[QtyIdx].InventoryQty = '';
        }
      },
      // 清除生产日期
      clearInput_check(index,QtyIdx){
        this.ProductList[index].QtyList[QtyIdx].DateInProduced = '';
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
          return;
        }else{
         
          that.loadPage += 1;
        }

        this.reqGoodsList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .AddInventory{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    .container{
      height: 100%;
      padding-bottom:80/50rem;
      overflow: scroll;
      .reselect{
        width: 100%;
        overflow: hidden;
        span{
          float: right;
          font-size: 26/50rem;
          color: #0076DF;
        }
      }
      .dateHead{
        &>div{
          line-height: 75/50rem;
          &:last-child{
            &>span:nth-child(2){
              margin-left: 15/50rem;
            }
          }
        }
      }
      .orderInfo{
        .infoContainer{
          &>div{
            display: flex;
            line-height: 50/50rem;
            margin-bottom: 0;
            span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
              color: #000;
            }
            span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
      }
      .inv_goods{
        .infoContainer{
          padding:15/50rem 15/50rem;
          border-radius:15/50rem;
          box-sizing: border-box;
          background-color:#fff;
          margin-bottom: 20/50rem;
          &>div{
            font-size: 28/50rem;
            line-height: 50/50rem;
          }
          .inv_goodsInfo{
            display: flex;
            margin-bottom: 10/50rem;
            .inventoryLeft{
              flex-grow: 1;
            }
            .inventoryRight{
              width: 200/50rem;
              vertical-align: middle;
              flex-shrink:0;
              text-align: center;
              i{
                vertical-align: middle;
                color: red;
                font-size: 50/50rem;
                &:first-child{
                  margin-right: 30/50rem;
                }
              }
            }
          }
          .InventoryQty{
            border-top:  1/50rem solid @PublicBorderColor;
            padding-top: 8/50rem;
            padding:20/50rem 0 10/50rem;
            &>div{
              line-height: 70/50rem;
            }
            ._inventoryQty{
              overflow: hidden;
              line-height: 60/50rem;
              span{
                float: left;
              }
              input{
                height: 60/50rem;
                width: 400/50rem;
                float: left;
                margin-right: 20/50rem;
              }
            }
            ._inventoryDate{
              line-height: 60/50rem;
              overflow: hidden;
              &>span{
                float: left;
              }
              &>div{
                float: left;
              }
              &>input{
                width: 100/50rem;
              }
              .DateInProduced{
                margin-right: 20/50rem;
              }
            }
          }
        }
        span{
          color: #000;
        }
      }
      .BtnComponent{
        margin-top: 10/50rem;
        margin-bottom: 30/50rem;
      }
    }
  }
</style>


