<template>
  <div class="CustomerSalesReport">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
	  	<div>
          <div>
            <div style="width:100%; height: 350px;">
              <div id="main"  style="float: left;width:100%;height: 300px;"></div>
              <div id="pie"  style="float: left;width:100%;height: 300px;"></div>
              <div id="bar"  style="float: left;width:100%;height: 300px;"></div>
          </div>
        </div>
      </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import echarts from 'echarts'
  export default{
    data(){
      return{
      	headerText:"客户与销量报表",//页面标题
      	isCollapsed: false,
        charts: ''
      }
    },
    components:{
	   componentHeader,
	  },
    mounted() {
      this.$nextTick(function() {
        this.drawLine('main')
        // this.drawLine2('echarts')
        this.drawPie('pie')
        this.drawBar('bar')
      })
    },
    created(){

    },
    computed: {
      menuitemClasses: function() {
        return [
          'menu-item',
          this.isCollapsed ? 'collapsed-menu' : ''
        ]
      }
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
    	this.$router.go(-1);
      },
      drawBar(id){
      	this.charts = echarts.init(document.getElementById(id));
      	this.charts.setOption(
      	  {
      	  	xAxis: {
		      type: 'category',
		      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
		    },
		    yAxis: {
		      type: 'value'
		    },
		    series: [{
		      data: [120, 200, 150, 80, 70, 110, 130],
		      type: 'bar'
		    }]
      	  }
      	)
      },
      drawPie(id){
      	this.charts = echarts.init(document.getElementById(id));
      	this.charts.setOption({
	      legend: {},
	      tooltip: {
	        trigger: 'axis',
	        showContent: false
	      },
	      dataset: {
	        source: [
	          ['product', '2012', '2013', '2014', '2015', '2016', '2017'],
	          ['娃哈哈', 41.1, 30.4, 65.1, 53.3, 83.8, 98.7],
	          ['怡宝', 86.5, 92.1, 85.7, 83.1, 73.4, 55.1],
	          ['雪花', 24.1, 67.2, 79.5, 86.4, 65.2, 82.5],
	          ['青岛', 55.2, 67.1, 69.2, 72.4, 53.9, 39.1]
	        ]
	      },
	      series: [
	        {
	          type: 'pie',
	          radius: '50%',
	          center: ['50%', '50%'],
	          label: {
	            formatter: '{b}: {@2012} ({d}%)'
	          },
	          encode: {
	            itemName: 'product',
	            value: '2012',
	            tooltip: '2012'
	          }
	        }
	      ]
	    });
      },
      drawLine(id) {
        this.charts = echarts.init(document.getElementById(id))
        this.charts.setOption({
          title: {
            text: '每月销售量'
          },
          xAxis: {
            type: 'category',
            data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
          },
          tooltip: {
              trigger: 'axis'
          },
          grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
          },
          yAxis: {
              type: 'value',
              axisLabel: {
                  formatter: '{value} 台'
              }
          },
          series: [{
              name: '销售量',
              type: 'line',
              data: [108, 133, 136, 101, 155, 113, 125, 100, 181, 128, 79, 131]
          }]
        })
      },
      // drawLine2(id) {
      //   this.charts = echarts.init(document.getElementById(id))
      //   this.charts.setOption({
      //     title: {
      //         text: '每月销售额'
      //     },
      //     xAxis: {
      //       type: 'category',
      //       data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月']
      //     },
      //     tooltip: {
      //         trigger: 'axis'
      //     },
      //     grid: {
      //         left: '3%',
      //         right: '4%',
      //         bottom: '3%',
      //         containLabel: true
      //     },
      //     yAxis: {
      //       type: 'value',
      //       axisLabel: {
      //         formatter: '{value} 万'
      //       }
      //     },
      //     series: [{
      //       name: '销售额',
      //       type: 'line',
      //       stack: '总量',
      //       itemStyle: {
      //         normal: {
      //           color: '#99CCFF',
      //           lineStyle: {
      //             color: '#99CCFF'
      //           }
      //         }
      //       },
      //       data: [11, 15, 14, 14, 17, 12, 13, 12, 20, 15, 9, 12]
      //     }]
      //   })
      // }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerSalesReport{
    overflow-x: hidden;
    header{
      background-color: #fff;
      color: #000;
      font-weight: 600;
      overflow: hidden;
    }
    .container{
    	#main{
    		margin: 60/50rem 0;
    	}
    }
  }
</style>


