<template>
  <div class="ModifyCustomerInfo">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  <div class="CustomerItemInfo">
	  <div class="container haveHeadcontainer">
  	  <div class="ModifyMainHead">
  	  	<div>{{CustomerNAME}}</div>
  	  	<div>
  	  	  <div v-if='CustomerInfoData.lastvisitTime'><span>最后拜访时间：</span><span>{{CustomerInfoData.lastvisitTime}}</span></div>
  	  	  <div>
  	  		<span>拜访次数：</span><span>{{CustomerInfoData.visitCount}}</span>
  	  	  </div>
  	  	</div>
      </div>
  	  <div class="CustomerInfo">
  	  	<div><span>门店级别</span><span>{{CustomerInfoData.CUSTOMER_GRADE}}</span></div>
      <!--   v-model='CustomerAddr_Show' -->
  	  	<div class="CustomerAddr">
          <span>门店地址</span>
          <div class="addr_">
            <textarea  rows='3' disabled>{{ADDRESS}}</textarea>
          </div>
          <div>
            <i @click='ChangeAddress' class="iconfont icon-xiugai"></i>
          </div>
        <!-- <i class="iconfont icon-guanbi"></i> -->
  	  	</div>
  	  	<div><span>跟进人</span><span>{{ $store.state.userInfo.userName }}</span></div>
  	  </div>
  	  <!-- 联系人 -->
  	  <div class="TheContact">
  	  	<div><span>联系人</span><i  @click='ChangeContacts'  class="iconfont icon-xiugai"></i></div>
  	  	<div>
          <div>
  	  		<div>  {{CONTACT_NAME}}</div>
  	  		<div>  {{CONTACT_TEL}}</div>
  	  		<div><i @click="callPhone(CONTACT_TEL)" class="iconfont icon-dianhua2"></i></div>
          <!--  v-if="dataItem.issueAddrLiat[0].issuePhone"  -->
  	  	  </div>
        </div>
  	  </div>
  	  <!-- 拜访记录 -->
  	  <div class="VisitRecord" @click="toHistoryVisit">
  	  	<div><span>拜访记录</span></div>
  	  	<div><i class="iconfont icon-tiaozhuanqianwangyoujiantouxiangyouxiayibuxianxing"></i></div>
  	  </div>
    </div>
	
    <div class="SelectMeng" v-if='ModifyAddressHide'>
      <div class="SelectWarehouses SelectMengContent">
        <!-- <div class="SelectTitle SelectMengTitle">更新客户地址</div> -->
        <div class="SelectMengItem">
     <!--      <div><span>客户地址：</span>
          <input type="" v-model='CustomerAddress'></div> -->
          <div class="ModifyPrompt">确认将当前位置更新为客户地址吗？</div>
          <div>
            <span @click='getCurrAddress()'><i class="iconfont icon-dizhi"></i></span>
            <div class="addr_">
              <textarea v-model="V_ADDRESS">{{ADDRESS}}</textarea>
            </div>

            <!-- <span>{{dataItem.ADDRESS1}}</span> -->
          </div>
          
        </div>
        <div class="selectBtn">
          <div @click="ModifyAddressConfirm">确定</div>
          <div @click="ModifyAddressCancel">取消</div>
        </div>
      </div>
    </div>

    <div class="SelectMeng" v-if='ModifyContactsHide'>
      <div class="SelectWarehouses SelectMengContent">
        <!-- <div class="SelectTitle SelectMengTitle">更新客户地址</div> -->
        <div class="SelectMengItem">
     <!--      <div><span>客户地址：</span>
          <input type="" v-model='CustomerAddress'></div> -->
          <div class="ModifyPrompt">确认更新客户联系人吗？</div>
          <div>
           <span> 姓名：</span><input class="name" v-model="V_CONTACT_NAME"></input>
          </div>
          <div>
           <span> 电话：</span><input class="tel" v-model="V_CONTACT_TEL"></input>
            <!-- <span>{{dataItem.ADDRESS1}}</span> -->
          </div>
          
        </div>
        <div class="selectBtn">
          <div @click="ModifyContactsConfirm">确定</div>
          <div @click="ModifyContactsCancel">取消</div>
        </div>
      </div>
    </div>

  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"修改门店信息",//页面标题
      	//CustomerAddr_Show:'广东省深圳市宝安区西乡街道明月花都',//客户地址
        ModifyAddressHide:false,//修改地址
        ModifyContactsHide: false,//修改联系人
        CustomerInfoData:{},
        ADDRESS:"",
        V_ADDRESS:"",
        CONTACT_NAME:"",
        V_CONTACT_NAME:"",
        CONTACT_TEL:"",
        V_CONTACT_TEL:"",
        CustomerNAME:"",
        storesId:"",
      }
    },
    components:{
	   componentHeader,
	  },  
    mounted(){

      window.SetCurrAddress = this.SetCurrAddress;
    },
    created(){

      if(this.$route.query.storeInfo){

        this.storesId = this.$route.query.storeInfo.storesId;//门店id
      }

      if(!this.$route.query.storeInfo.storesId){
        this.storesId = this.$store.state.CurrentStoresId;
      }

      this.GetDate();
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);
      },
      // iOS/Android原生传过来的当前位置
      SetCurrAddress:function(address, lng, lat, bdcode) {

        this.V_ADDRESS = address;
      },
      getCurrAddress(){

        this.TelliOSORAndroidVueOfGetCurrentLocMounted();
      },
      //请求数据
      GetDate(){
        let that = this;
        var storesInfoData = {
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "userId":that.$store.state.userInfo.user_id,//用户id
          "storesId":this.storesId,//门店id
        }

        // 获取门店信息
        that.httpRequest("GetStoreOne.do",storesInfoData,function(storesInfoRes){
          that.CustomerInfoData = storesInfoRes.data;
          that.ADDRESS = that.CustomerInfoData.ADDRESS1;
          that.CustomerNAME = that.CustomerInfoData.NAME;
          that.CONTACT_NAME = that.CustomerInfoData.CONTACT_NAME;
          that.CONTACT_TEL = that.CustomerInfoData.CONTACT_TEL;
        })
      },
      // 修改地址
      ChangeAddress(){
        this.ModifyAddressHide = true;
        this.V_ADDRESS = this.ADDRESS;
      },
      // 确认修改 客户地址
      ModifyAddressConfirm(){
        var that = this;
       
        if(!that.V_ADDRESS){
          that.$alert("门店地址不能为空！", '提示', {
            confirmButtonText: '确定',
          })
          return;
        }
        that.Submit(that.V_ADDRESS,that.CONTACT_NAME,that.CONTACT_TEL);
      },
      // 取消修改 客户地址
      ModifyAddressCancel(){
        this.ModifyAddressHide = false;
      },
      //修改联系人
      ChangeContacts(){
        this.ModifyContactsHide = true;
        this.V_CONTACT_NAME = this.CONTACT_NAME;
        this.V_CONTACT_TEL = this.CONTACT_TEL;
      },
      // 确认修改 联系人
      ModifyContactsConfirm(){
        var that = this;
        if(!that.V_CONTACT_NAME){
          that.$alert("联系人姓名不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!that.V_CONTACT_TEL){
          that.$alert("联系人电话不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        that.Submit(that.ADDRESS,that.V_CONTACT_NAME,that.V_CONTACT_TEL);
      },
      // 取消修改 联系人
      ModifyContactsCancel(){
        this.ModifyContactsHide = false;
      },

      Submit(ADDRESS,CONTACT_NAME,CONTACT_TEL){

        var that = this;
        that.ModifyAddressHide = false;
        that.ModifyContactsHide = false;
          var storesUpdateData = {
                "cellphone":that.$store.state.userInfo.cellphone,//手机号
                "userId":that.$store.state.userInfo.user_id,//用户id
                "storesId":that.storesId,//门店id
                "storesName":that.CustomerNAME,//门店名称            
                "storesAdress":ADDRESS,
                "storesContactName":CONTACT_NAME,
                "storesTel":CONTACT_TEL,

              }
        // 修改联系人信息
        that.httpRequest("updateCustomerInfo.do",storesUpdateData,function(storesUpdateRes){

          if(storesUpdateRes.status==1){

            that.$alert("更新成功", '提示', {
              confirmButtonText: '确定',
            }) 
            that.GetDate();

          }else {

            that.$alert("更新失败", '提示', {
              confirmButtonText: '确定',
            }) 
          }
        }) 
      },

      // 跳转到 历史拜访 页面
      toHistoryVisit(){
        event.stopPropagation();
        var cruuentCont = this.CustomerInfoData;
        this.$store.state.CurrentStoresId = cruuentCont.STORESID;//将当前查看的门店id存到全局

        this.$store.state.CurrentStoresHistoryInfo = {
          storesName:cruuentCont.NAME,//门店名称
            lastvisitTime:cruuentCont.lastvisitTime,//上次拜访时间
            visitCount:cruuentCont.visitCount,//拜访次数
        }

        this.$router.push({
          name:"HistoryVisit",
          query:{
            ModifyCustomerattached:'ModifyCustomerInfo'
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  @GrayWordColor:#666;
  .ModifyCustomerInfo{
  	overflow-x: hidden;
  	header{
  	  background-color:#fff;
  	  color: #000;
  	}
    .CustomerItemInfo{
      .haveHeadcontainer{
        font-size: 28/50rem;
      }
      .CustomerInfo{
        border-bottom:10/50rem solid @PublicBorderColor;
        &>div{
          display: flex;
          line-height: 80/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &.CustomerAddr{
            line-height: normal;
            &>span{
              line-height: 80/50rem;
            }
            &>div{
              &>i{
                color: @PublicColor;
                width: 100/50rem;
                font-size: 40/50rem;
                display: block;
                height: 100/50rem;
                line-height: 100/50rem;
                text-align: center;
              }
            }
          }
          &>span{
            &:first-child{
              flex-shrink:0;
              width: 135/50rem;
              color: @GrayWordColor;
              padding-left: 20/50rem;
            }
          }
          &>div.addr_{
            flex-grow: 1;
            position:relative;
            textarea{
              padding-top: 20/50rem;
              background-color:#fff;
              height: 200/50rem;
              color: #000;
              line-height: 50/50rem;
              width: 100%;
              font-size: 28/50rem;
              border:none;
              outline: none;
            }
            i{
              position:absolute;
              right: 30/50rem;
              top: 0;
            }
          }
          &>i{
            color: @PublicColor;
            width: 70/50rem;
            font-size: 40/50rem;
          }
        }
      }
      .TheContact>div:first-child,.VisitRecord>div{
        color: @GrayWordColor;
        padding-left:20/50rem;
        box-sizing:border-box;
        line-height: 70/50rem;
        border-bottom:1/50rem solid @PublicBorderColor;
      }
      .TheContact>div:last-child>div>div:last-child,.VisitRecord>div:last-child{
        position: absolute;
        top: 0;
        right: 0;
        width: 100/50rem;
        text-align: center;
        line-height: 130/50rem;
        height: 130/50rem;
        i{
          font-size: 45/50rem;
          color: @PublicColor;
        }
      }
      .TheContact{
        border-bottom:10/50rem solid @PublicBorderColor;
        &>div{
          &:first-child{ 
            position:relative;
            &>i{
              position:absolute;
              top: 0;
              right: 0;
              color: @PublicColor;
              width: 70/50rem;
              font-size: 40/50rem;
            }
          }
          &:last-child{
            &>div{
              border-bottom:1/50rem solid @PublicBorderColor;
              font-size: 28/50rem;
              color: @GrayWordColor;
              padding:10/50rem 20/50rem;
              box-sizing:border-box;
              position:relative;
              &>div{
                line-height: 55/50rem;
                height: 55/50rem;
                &:first-child{
                font-size: 30/50rem;
                  color: #000;
                }
              }
            }
          }
        }
      }
      .VisitRecord{
        position:relative;
        &>div{
          line-height: 90/50rem;
          &:last-child{
            line-height: 90/50rem;
            height: 90/50rem;
            i{
              color: @GrayWordColor;
            }
          }
        }
      }
      .SelectMeng{
        .SelectMengContent{
          .SelectMengItem{
            font-size: 32/50rem;
            padding:20/50rem;
            box-sizing: border-box;
            color: #000;
            height: 320/50rem;
           .name{
              width: 300/50rem;
              height: 60/50rem;
              margin-bottom: 20/50rem;
              font-size: 30/50rem;
            }
            .tel{
              width: 300/50rem;
              height: 60/50rem;
              font-size: 30/50rem;
            }
            &>div{
              overflow: hidden;
              .addr_{
                flex-grow: 1;
                position:relative;
                float: right;
                width: 478/50rem;
                margin-right: 18/50rem;
                textarea{
                  background-color:#fff;
                  color: #000;
                  line-height: 55/50rem;
                  width: 478/50rem;
                  height: 180/50rem;
                  font-size: 30/50rem;
                  padding-left: 10/50rem;
                  padding-right: 10/50rem;
                  box-sizing:border-box;
                  outline: none;
                  border-color:@PublicBorderColor ;
                  border-radius: 4%;
                }
              }
              &.ModifyPrompt{
                margin: 30/50rem 0;
                font-weight: 600;
                text-align: center;
              }
              i{
                font-size: 45/50rem;
                color: @PublicColor;   
                left: 2/50rem;        
              }
            }
          }
        }
      }
    }
  }
</style>


