<template>
  <div class="VividDisplay">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

  	<div class="container haveHeadcontainer">
      <div class="VividList" v-for='(dataItem,index) in VividList' :id="index"  :key='index'>
        <div class="selectDown">
          <i v-if='index!=0' @click='deleteIcon(index)' class="iconfont icon-Untitled-"></i>
          <span>生动化陈列：</span>
          <!-- -->
          <el-select  v-model="dataItem.DisplayType"   @change='selectChange($event,index)'  placeholder="请选择">
            <!--  v-model="VividList[0].DisplayType"-->
            <el-option
              v-for="item in SelectBoxList"
              :key="item.name"
              :label="item.name"
              :value="item.name"
              :disabled="item.disabled">
            </el-option>
          </el-select>
        </div>
        <div class="uploadImg">
          <el-upload  :action = " HOST_xsy + 'GetVisitVividDisplay.do'" :auto-upload="false"  :data="uploadParamsList[index]"  list-type="picture-card"   ref="updom"  :limit="6" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
      <div class="addBtn" @click="addBtn"><span>新增生动化陈列</span></div>
      <div class="remark">
  	  	<div><span>备注：</span></div>
  	    <textarea v-model='remark' placeholder="请输入备注信息">
  	    </textarea>
  	  </div>
  	  <div class="BtnComponent bottomBtn">
  	  	<div  @click="toLeaveStore">确认</div>
  	  </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"生动化陈列",//页面标题
        thisClickSeleIdx:0,//当前点击的生动化idx
        VividList:[{
          IDX:0,
          DisplayType:'',//生动化陈列类型
        }],//页面生动化陈列列表
        SelectBoxList:[],//生动化陈列类型列表
      	dialogImageUrl: '',//图片路径
        dialogVisible: false,//是否放大预览图片
        uploadParams:{},//上传图片时 额外的参数
        uploadParamsList:[{}],
        remark:'',//生动化陈列备注
        uploadFailNum:0,//上传失败个数
        uploadSuccessNum:0,//上传成功个数
        uploadTotalNum:0,//上传图片总数
      }
    },
    components:{
  	  componentHeader,
  	},
    mounted(){

      $('.el-upload__input').attr('capture','camera');
    },
    created(){
      // 请求生动化陈列类型
      var that = this;

      that.uploadParamsList[0] = {
        visitId :  that.$store.state.CurrentVisitId,//拜访id
        userId :  that.$store.state.userInfo.user_id,//用户id
        cellphone :  that.$store.state.userInfo.cellphone,//手机号码
        businessCode : that.$store.state.businessInfo.businessCode,
      };
    
      var vividTypeData = {
        "types":"SDXL",
      }
      console.log(vividTypeData)
      // 获取生动化陈列类型
      that.httpRequest("findKVByType.do",vividTypeData,function(vividTypeRes){
         console.log(vividTypeRes)
         
         that.SelectBoxList = vividTypeRes.data;
      })
    },
    methods:{
      // 返回上一页
      f_Fanhui(){

        if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush == 'OrderConfirm'){
          this.$router.push({
            name:"CustomerVisit",
          })
        }else{
          this.$router.go(-1);
        }
      },
      // 选择
      selectChange(e,index){
        let that = this;

        for(var dl=0;dl<that.SelectBoxList.length;dl++){
          that.SelectBoxList[dl].disabled = false;
        }

        for(var gl=0;gl<that.VividList.length;gl++){
          if(that.VividList[gl].DisplayType){
            for(var hl=0;hl<that.SelectBoxList.length;hl++){
              if(that.VividList[gl].DisplayType == that.SelectBoxList[hl].name){
                that.SelectBoxList[hl].disabled = true;
                continue;
              }
            }
          }
        }
      },
      // 新增 生动化陈列
      addBtn(){
        // 新增之前判断上面图片是否选择、生动化陈列是否选择
        let that = this;

        for(var i=0;i<that.VividList.length;i++){
          if(!that.VividList[i].DisplayType){
            that.$alert('请选择以上生动化陈列再新增', '提示', {
              confirmButtonText: '确定',
            })
            return;
          }
        }

        let AddFlag = false;
        for(var dl=0;dl<that.SelectBoxList.length;dl++){
          if(that.SelectBoxList[dl].disabled == false){
            AddFlag = true;
          }
        }

        if(!AddFlag){
          that.$alert('已经没有类型可选', '提示', {
            confirmButtonText: '确定',
          })
          return;
        }
        
        this.VividList.push({
          IDX:that.VividList.length,
          DisplayType:'',
        })

        that.$nextTick(function(){
          for(var mj=0 ; mj < that.VividList.length ; mj++ ){
            $('.VividList:eq('+mj+') .el-upload__input').attr('capture','camera');
          }
        })
 
        that.uploadParamsList[that.uploadParamsList.length]={
          visitId :  that.$store.state.CurrentVisitId,//拜访id
          userId :  that.$store.state.userInfo.user_id,//用户id
          cellphone :  that.$store.state.userInfo.cellphone,//手机号码
          businessCode : that.$store.state.businessInfo.businessCode,
        };
      },
      // 移除 生动化陈列
      deleteIcon(idx){
        let that = this;
        event.stopPropagation();
        this.VividList.splice(idx,1);
        for(var dl=0;dl<that.SelectBoxList.length;dl++){
          that.SelectBoxList[dl].disabled = false;
        }

        for(var gl=0;gl<that.VividList.length;gl++){
          if(that.VividList[gl].DisplayType){
            for(var hl=0;hl<that.SelectBoxList.length;hl++){
              if(that.VividList[gl].DisplayType == that.SelectBoxList[hl].name){
                that.SelectBoxList[hl].disabled = true;
                continue;
              }
            }
          }
        }

        that.uploadParamsList.splice(idx, 1);
      },
      // 跳转到 离店 页面
      toLeaveStore(){

        var that = this;

        for(var k = 0;k<that.VividList.length;k++){

          if(!that.VividList[k].DisplayType){

            that.$alert('请选择生动化陈列类型', '提示', {
              confirmButtonText: '确定'
            })

            return;
          }

          if(!$(".container .uploadImg:eq("+k+") li").length){

            that.$alert('请上传照片', '提示', {
              confirmButtonText: '确定'
            })
            return;
          }
        }

        this.uploadTotalNum = $(".container li").length;
        
        that.uploadParamsList.forEach(function(item,index,arr){

          item.vividDisplay = encodeURIComponent(that.VividList[index].DisplayType);
          item.vividDisplayText = encodeURIComponent(that.remark);
        })

        console.log(that.uploadParamsList)

        for(var j = 0;j<that.VividList.length;j++){
          that.$refs.updom[j].submit();
        }
      },
      // 改变拜访状态  跳转到下一页
      ChangeVisitStatus(){
        
        var that = this;

        that.uploadSuccessNum = 0;
        that.uploadFailNum = 0;
        that.uploadTotalNum = 0;

        var postData = {
          "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
          "types":'SDXL',
          "visitId":that.$store.state.CurrentVisitId,//拜访id
          "actualVisitingAddress":'',//当前位置 进店用得到
          "remarks": that.remark,//备注
        }

        console.log(postData)

        this.httpRequest("GetVisitPicStatus.do",postData,function(res){

          console.log(res)
          that.$router.push({
            name:"LeaveStore",
            query:{
              visit_whoPush:"VividDisplay"
            }
          })
        })
      },
      // 文件上传成功时的钩子
      uploadSuccess  (response, file, fileList){

        console.log(response)

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 全部上传成功
          that.ChangeVisitStatus();
          return;
        }else if((that.uploadSuccessNum + that.uploadFailNum) == parseInt(that.uploadTotalNum)){

          that.ChangeVisitStatus();
        }
      },
      // 文件上传失败时的钩子
      uploadError  (err, file, fileList){
        console.log(err)

        var that = this;

        this.$emit('isLoading', false);

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 全部上传失败

          that.uploadFailNum = 0;
          that.uploadSuccessNum = 0;
          that.uploadTotalNum = 0;

          that.$emit('isLoading', false);
          
          that.$alert("上传失败", '提示', {
            confirmButtonText: '确定'
          })
          return;
        }else if((that.uploadFailNum + that.uploadSuccessNum) == parseInt(that.uploadTotalNum)){

          that.ChangeVisitStatus();
        }
      },
      // 预览放大图片
      handlePictureCardPreview (file) {

        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";

  .VividDisplay{
    overflow-x: hidden;
    .container{
      padding-bottom: 130/50rem;
      .VividList{
        .selectDown{
          display: flex;
          line-height: 90/50rem;
          span{
            width: 230/50rem;
            text-align: center;
          }
          div{
            flex-grow: 1;
            padding-right: 20/50rem;
            font-size: 54/50rem;

          }
          .icon-Untitled-{
            margin-left: 20/50rem;
            width: 60/50rem;
            font-weight: 600;
            font-size: 45/50rem;
            color: red;
          }
        }
      }
      .uploadImg{
        margin: 20/50rem;
      }
      .addBtn{
        text-align: center;
        span{
          display: inline-block;
          padding: 0 50/50rem;
          line-height: 70/50rem;
          border-radius: 35/50rem;
          background-color:@PublicColor;
          color: #fff;
        }
      }
      .remark{
        textarea{
          height: 120/50rem;
          margin-top: 10/50rem;
          font-size: 30/50rem;
        }
      }
    }
  }
</style>


