<template>
  <div class="UserInfo">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="UserInfo">
        <div class="_head"><i class="iconfont icon-geren"></i><span>个人信息</span></div>
        <div>
          <span>姓名</span>
          <span>{{UserName}}</span>
        </div>
         <div>
          <span>手机号</span>
          <span>{{UserTel}}</span>
        </div>
        <div>
          <span>身份证号</span>
          <input>{{UserIDNum}}</input>
          <i v-if="UserIDNum" @click="clearInput('UserIDNum')" class="iconfont icon-guanbi"></i>
        </div>
      </div>

      <div class="ShopInfo">
        <div class="_head"><i class="iconfont icon-dianpu"></i><span>店铺信息</span></div>
        <div>
          <span>车牌号</span>
          <input v-model='VehicleID'></input>
        </div>
        <div>
          <span>车辆型号</span>
          <input v-model='VehicleModel'></input>
        </div>
         <div>
          <span>车长</span>
          <input v-model='VehicleLength'></input>
        </div>
        <div>
          <span>车宽</span>
          <input v-model='VehicleWidth'></input>
        </div>
         <div>
          <span>车高</span>
          <input v-model='VehicleHeight'></input>
        </div>
        <div>
          <span>载重吨</span>
          <input v-model='VehicleWeight'></input>
          <i v-if="VehicleWeight" @click="clearInput('VehicleWeight')" class="iconfont icon-guanbi"></i>
        </div>
        <div>
          <span>载重体积</span>
          <input v-model='VehicleVolume'></input>
          <i v-if="VehicleVolume" @click="clearInput('VehicleVolume')" class="iconfont icon-guanbi"></i>
        </div>
      </div>

      <div class="UploadData uploadImg ">
        <div class="_head"><i class="iconfont icon-shangchuan"></i><span>上传资料</span></div>
        <div class="UploadUserIDCard">
          <div class="tittleImg">身份证正面照片</div>
          <el-upload  :action = " HOST + 'uploadPicture.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="CarUpload" :limit="4" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
        <div class="UploadUserIDCards">
          <div class="tittleImg">身份证反面照片</div>
          <el-upload  :action = " HOST + 'uploadPicture.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="CarUpload" :limit="4" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
        <div class="UploadBusiness">
          <div class="tittleImg">营业执照照片</div>
          <el-upload  :action = " HOST + 'uploadPicture.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="CarUpload" :limit="4" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
	  </div>
    <div class="BtnComponent bottomBtn">
     <div class="onlyBtn" @click='CustomerSave'>保存</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"个人资料",//页面标题
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerVisit{
    overflow-x: hidden;
  }
  .container{
    font-size:32/50rem;
    &>div{
      border-bottom: 8/50rem solid @PublicBorderColor;
      &:last-child{
        border-bottom: none;
      }
      &>div{
        padding-left: 20/50rem;
        line-height: 90/50rem;
        box-sizing: border-box;
        border-bottom:1/50rem solid @PublicBorderColor;
        &._head{
          font-size: 32/50rem;
          font-weight:600;
          line-height: 80/50rem;
          border-bottom:5/50rem solid @PublicBorderColor;
          overflow: hidden;
          i{
            float: left;
            color: @PublicColor;
            font-size: 45/50rem;
            font-weight: 500;
          }
          span{
            float: left;
          }
        }
      }
    }
    input{
      width:70%;
      height:50/50rem; 
      font-size: 25/50rem;
      border-style: none;
    }
    .icon-guanbi{
        //position:absolute;
        right:20/50rem;
        font-size: 140/50rem;
        top: 17/50rem;
    }

    .UploadData{
       &>div{
        &:not(._head){
          padding-bottom: 20/50rem;
        }
        }
        .tittleImg{
          font-size: 28/50rem;
          color: #757575;
          border-bottom: none;
        }
    }
  }
</style>


