<template>
  <div class="CreateCustomerWeChat">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="CustomerInfo">
        <div>
          <div>手机电话</div>
          <div>{{ContactTel}}</div>
        </div>
        <div>
          <div>门店名称</div>
          <div><input placeholder="必填" v-model='CustomerName'></input></div>
        </div>
        <div>
          <div>收货人</div>
          <div><input placeholder="必填" v-model='Consignee'></input></div>
        </div>
        <div @click='toChooseAddress'>
          <div>所在地区</div>
          <div><span>{{Location}}</span><div><i class="iconfont icon-xiangyou"></i></div></div>
        </div>
        <div>
          <div>详细地址</div>
          <div><input placeholder="必填" v-model='AddressDetail'></input></div>
        </div>
        <div  class="lnglat"  @click='toLnglat'>
          <div>经纬坐标</div>
          <div><span>{{lnglat}}</span><div><i class="iconfont icon-xiangyou"></i></div></div>
        </div>
      </div>
      <div class="BusinessLicense">
        <div class="uploadImg">
          <div>门店营业执照</div>
          <el-upload  :action = " HOST_xsy + 'wxInsterOrgnUpload.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="upload_ref" :limit="2" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
      <div class="IDcard">
        <div class="uploadImg">
          <div>门店法人身份证正反面</div>
          <el-upload  :action = " HOST_xsy + 'wxInsterOrgnUpload.do'" :auto-upload="false"  :data="uploadParams1"  list-type="picture-card"  ref="upload_ref1" :limit="2" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
      <div class="BtnComponent bottomBtn">
        <div @click='CustomerSave'>保存</div>
      </div>
       <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
    data(){
      return{
      	headerText:"注册门店",//页面标题
        ContactTel:'',//手机电话
        storeID:'',//门店ID
        CustomerName:'',//门店名称
        Consignee:'',//收货人
        Location:'',//所在地区
        AddressDetail:'',//详细地址
        longitude:'',//经度
        latitude:'',//纬度
        lnglat:'',//显示经纬度
        province:'',//省
        cityChinese:'',//市
        districict:'',//区
        street:'',//街道
        dialogImageUrl: '',//图片路径
        dialogVisible: false,//是否放大预览图片
        uploadParams:{},//上传图片时 额外的参数
        uploadParams1:{},//上传图片时 额外的参数
        uploadSuccessNum:0,//上传成功数量
        uploadTotalNum:0,//上传图片总数
        uploadFailNum:0,//上传失败数量
        ifTips:false,//提示信息是否显示
        tips_Msg:"",//提示信息
        LoginOrReg:'',//是注册还是登录
        yzm:'',//验证码内容
        phoneNum:"",//手机号
      } 
    },
    components:{
	    componentHeader,
	  },
    mounted(){
      window.SetSendLocation = this.SetSendLocation;
    },
    created(){

      var that = this;
      that.ContactTel = that.$store.state.WXRegIdentifyPhone;//手机号码
      that.CustomerName = that.$store.state.StoreInfo.CustomerName;//门店名称
      that.Consignee = that.$store.state.StoreInfo.Consignee;//收货人
      that.Location = that.$store.state.StoreInfo.Location;//所在地区
      that.AddressDetail = that.$store.state.StoreInfo.AddressDetail;//详细地址
      that.longitude = that.$store.state.StoreInfo.longitude;//经度
      that.latitude = that.$store.state.StoreInfo.latitude;//纬度
      that.lnglat = that.$store.state.StoreInfo.lnglat;//显示经纬度
      that.LoginOrReg = that.$store.state.WXRegLoginOrReg;//是注册还是登录
      that.yzm = that.$store.state.WXRegCode;//验证码
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        var that = this;
        this.$store.state.StoreInfo = {};
        this.$router.push({
          name:'WXVerify',
          query:{
            phoneNum:that.ContactTel,
            LoginOrReg : that.LoginOrReg,//是注册还是登录
            yzm:that.yzm,//是注册还是登录
          }
        })
      },
      toLnglat(){
        this.saveInfo();
        this.$router.push({
          name:"SendLnglat",
           query:{
            whoPush:"CreateCustomerWeChat",
          }
        })
      },
      //保存用户输入，防止push后信息丢失
      saveInfo(){
        var that = this;
        that.$store.state.StoreInfo.CustomerName = that.CustomerName;//门店名称
        that.$store.state.StoreInfo.Consignee = that.Consignee;//收货人
        that.$store.state.StoreInfo.Location = that.Location;//所在地区
        that.$store.state.StoreInfo.AddressDetail = that.AddressDetail;//详细地址
        that.$store.state.StoreInfo.longitude = that.longitude;//经度
        that.$store.state.StoreInfo.latitude = that.latitude;//纬度
        that.$store.state.StoreInfo.lnglat = that.lnglat;//显示经纬度
      },
      // 跳转到 选择地址 页面
      toChooseAddress(){
        this.saveInfo();
        this.$router.push({
          name:"ChooseAddress",
          query:{
            create_whoPush:"WeChat"
          }
        })
      },
      // 文件上传成功时的钩子
      uploadSuccess  (response, file, fileList){

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 全部上传成功

          that.$store.state.StoreInfo = {};

          that.$alert('提交资料成功，审核通过后我们将通知您！', '提示', {
            confirmButtonText: '确定',
            callback(){
              that.$router.push({
                name:"WXLogin",
              })
            }
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 部分上传成功
          that.$alert("部分图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
        }
      },
      // 文件上传失败时的钩子
      uploadError  (err, file, fileList){
        var that = this;

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 全部上传失败

          that.uploadSuccessNum = 0;
          that.uploadFailNum = 0;
          that.uploadTotalNum = 0;
          that.$emit('isLoading', false);

          that.$alert("图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 部分上传失败
          that.$alert("部分图片上传失败", '提示', {
            confirmButtonText: '确定'
          })
        }
      },
      // 预览放大图片
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 点击保存按钮
      CustomerSave:function(){
        var that = this;  
        if(!this.CustomerName){
          that.$alert("门店名称不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!this.Consignee){
          that.$alert("收货人不能为空！", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
         if(!this.Location){
          that.$alert("所在地区不能为空", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }

        if(!this.AddressDetail){
          that.$alert("详细地址不能为空", '提示', {
            confirmButtonText: '确定',
          })  
          return;
        }
        if(!$("div.BusinessLicense div.uploadImg ul>li").length){
          that.$alert('请拍摄门店营业执照', '提示', {
            confirmButtonText: '确定'
          })
          return;
        }

        if($("div.IDcard div.uploadImg ul>li").length < 2 ){
          that.$alert('请拍摄身份证正反面照片', '提示', {
            confirmButtonText: '确定'
          })
          return;
        }

        that.uploadTotalNum = $("div.uploadImg ul>li").length;

        var SubmitData = {
          "cellphone":that.$store.state.userInfo.cellphone,//手机号
          "storesName":that.CustomerName,//门店名称
          "storesAdress":that.AddressDetail,//详细地址
          "province":that.$store.state.StoreInfo.province,//省
          "cityChinese":that.$store.state.StoreInfo.cityChinese,//市
          "districict":that.$store.state.StoreInfo.districict,//区
          "street":that.$store.state.StoreInfo.street,//街道
          "storesContactName":that.Consignee,//门店联系人
          "storesTel":that.ContactTel,//门店电话
          "longitude":that.longitude,//经度
          "latitude":that.latitude,//纬度
        }
        // 公众号注册门店
        that.httpRequest("wxInsterOrgn.do",SubmitData,function(Res){
          
          that.uploadParams.types =  "BusinessLicense";//图片类型
          that.uploadParams1.types =  "IDcard";//图片类型
          that.uploadParams.storeId =  Res.data;//门店id
          that.uploadParams1.storeId =  Res.data;//门店id
          
          // 上传图片
          that.$refs.upload_ref.submit();
          that.$refs.upload_ref1.submit();
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateCustomerWeChat{
    overflow-x: hidden;
    padding-bottom: 150/50rem;
    .container{
      font-size: 28/50rem;
      .CustomerInfo{
        &>div{
          line-height: 60/50rem;
          padding:10/50rem 20/50rem;
          box-sizing:border-box;
          border-bottom:2/50rem solid @PublicBorderColor;
          display: flex;
          &>div{
            &:nth-child(1){
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &:nth-child(2){
              color: #666;
              flex-grow: 1;
              padding-left:20/50rem;
              box-sizing:border-box;
              overflow: hidden;
              display: flex;
              line-height: 60/50rem;
              input{
                flex-grow: 1;
                line-height: 60/50rem;
                border:none;
              }
              span{
                flex-grow: 1;
              }
              div{
                width: 50/50rem;
                flex-shrink:0;
                position: relative;
                i.icon-xiangyou{
                  position:absolute;
                  top: 50%;
                  left: 60%;
                  transform: translate(-50%,-50%);
                  font-size: 40/50rem;
                }
              }
            }
          }
        }
      }
      .uploadImg{
        border-bottom:1/50rem solid @PublicBorderColor;
        padding: 25/50rem 20/50rem;
        box-sizing:border-box;
        &>div{
          padding-bottom:15/50rem;
        }
      } 
      .BtnComponent{
        width: 40%;
        left: 30%;
      }
    }
  }  
</style>


