<template>
  <div class="WXLogin">
	<div class="WXLoginContainer">
	  <div class="LoginLogo">
	  	<img src="static/img/Logo/logo.png" alt="">
	  </div>
	  <div v-if='Way_Verify' class='identeContainer'>
	    <div class="mobilePhone">
	      <div class="mobilePhoneInput"></div>
	      <div class="mobilePhoneInput">
	      	<input placeholder='输入手机号' ref="phoneNum" v-model="phoneNum" ></input>
		    <i v-if="phoneNum" @click="clearInput('phoneNum')" class="iconfont icon-guanbi"></i>
	      </div>
	      <div class="ErrMsg"><span v-show='ErrMsg'>{{ErrMsg}}</span></div>
	    </div>
	    <div class="g_signBtn" :style="{color:phoneNum.length>0?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toWXVerify'>获取验证码</div>
	    <div class="g_signBtn hollow" @click='ChangeModule("Verify")'>用账号密码登录</div>
	  </div>
	  <div v-else class='identeContainer'>
	  	<div class="mobilePhone">
	  	  <div class="mobilePhoneInput">
	      	<input placeholder='门店代码' ref="storeCode" v-model="storeCode" ></input>
		    <i v-if="storeCode" @click="clearInput('storeCode')" class="iconfont icon-guanbi"></i>
	      </div>
	      <div class="mobilePhoneInput">
	      	<input placeholder='输入登录密码' type="password" ref="loginPswd" v-model="loginPswd" ></input>
		    <i v-if="loginPswd" @click="clearInput('loginPswd')" class="iconfont icon-guanbi"></i>
	      </div>
	      <div class="ErrMsg"><span v-show='SCErrMsg'>{{SCErrMsg}}</span></div>
	  	</div>
	  	<div class="g_signBtn" :style="{color:storeCode.length>0 && loginPswd.length>0?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toLogin'>登录</div>
	    <div class="g_signBtn hollow" @click='ChangeModule("StoreCode")'>用短信验证码登录/注册</div>
	  </div>
	</div>
  	<div class="verseion">0.{{0.1 * $store.state.version}}</div>
  </div>
</template>
<script type="text/javascript">
  import $ from "jquery"
  export default {
 	name:"WXLogin",
 	data(){
 	  return{
	    phoneNum:"",//手机号
	    openId:"",//
	    Way_Verify:false,//默认使用账号密码方式登录
	    storeCode:'',//门店代码
	    loginPswd:'',//登录密码
	    ErrMsg:'',//错误信息
	    SCErrMsg:'',//账号密码登录 错误信息
	    UserLoginHabits:'accountPassword',//用户登录习惯
 	  }
 	},
	mounted(){
	},
	watch:{
	  phoneNum(val){
	  	if(this.ErrMsg){
	  	  this.ErrMsg = '';	
	  	}
	  },
	  loginPswd(val){
	  	if(this.SCErrMsg){
	  	  this.SCErrMsg = '';	
	  	}
	  },
	  storeCode(val){
	  	if(this.SCErrMsg){
	  	  this.SCErrMsg = '';	
	  	}
	  }
	},
 	created(){

 	  var that = this;

 	  //手机键盘隐藏(简单粗暴版)
	  $('body').on('touchend', function(el) {
		if(el.target.tagName != 'INPUT') {
		  $('input').blur()
		}
	  })
	  if(this.$store.state.G_AppOrWX == "APP"){
	  	this.$router.push({
          name:"Login",
        })
	  }

	  this.getWXCookie("e");
	  this.getWXPhoneCookie("e");
	  this.getWXUserLoginHabitsCookie("e");
 	  this.getWXCookie_openid("e")

	  if(this.UserLoginHabits=='accountPassword'){
	  	this.Way_Verify = false;
	  }else if(this.UserLoginHabits=='VerificationCode'){
	  	this.Way_Verify = true;
	  }

	  if(this.storeCode.length && this.loginPswd.length){

	  	this.toLogin();
	  }

	  // if(!this.$store.state.GLoginFlag){
	  	if(!that.$store.state.GopenId.length){

		  let code = this.getUrlKey("code");

		  if(code){

			console.log(code)

			let LoginData = {
			  code: code
			}  
			this.httpRequest("wechatGetOpenId.do",LoginData,function(regRes){
			  console.log(regRes)

			  that.openId = regRes.data.openId;
			  that.$store.state.GopenId = regRes.data.openId;
			  that.setWXCookie_openid(regRes.data.openId)

		        console.log(regRes);

		        if(regRes.data.returnCode == "SUCCESS"){
				  // 改变登录状态
				  that.$store.state.userInfo = regRes.data.user;

				  if(regRes.data.business.length > 0){

					that.$store.state.businessInfo = regRes.data.business[0];

			        that.$store.state.CurrentRole = 'storesStaff';

				    that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData['storesStaff'];

			        that.login_click();
				  }else{

			        that.$alert("该账号未绑定业务代码", '提示', {
				      confirmButtonText: '确定',
				    })
				  }
		        }
			})
		  }else{
			this.getCodeApi("123");
		  }
		}else{
		  that.openId = that.$store.state.GopenId;
		}
	  // }
	},
	methods:{
      //获取验证码 跳转到验证码页面 
  	  toWXVerify (){
		var that = this;
	    
	    if (!that.phoneNum.length) {

	      return;

	    }else if(that.phoneNum.length != 11){

	      that.ErrMsg = '手机号输入有误';
	    }else{

	      var params = {
	        cellphone: that.phoneNum,
	      }
	      this.httpRequest("WXGetVerifyCode.do",params,function(res){

	      	console.log(res)
		    // that.ErrMsg = '验证码获取次数已超限，请明天再尝试';

		    that.setWXPhoneCookie(that.phoneNum,30);
		    that.setWXUserLoginHabitsCookie('VerificationCode',30);
		    that.$store.state.MinuteNum = 60;

			that.$router.push({
			  name:"WXVerify",
			  query:{
			    phoneNum:that.phoneNum,//手机号码
			    LoginOrReg:res.data,//判断是登录还是注册
			  }
			})
		  })
	    }
	  },
	  // 账号密码登录
      toLogin(){
        var that = this;

        if (!that.storeCode.length || !that.loginPswd.length) {return;}

	  	let LoginParams = {
	      storeCode:that.storeCode,//门店代码
	      storePswd:that.loginPswd,//验证码
	      openId:that.openId,//openId
	    }
	    this.httpRequest("wxLogin.do",LoginParams,function(LoginRes){
	        	
	      console.log(LoginRes);

	      if(LoginRes.data.returnCode == 'SUCCESS'){

	        that.setWXCookie(that.storeCode,that.loginPswd,30);
  			
		    that.setWXUserLoginHabitsCookie('accountPassword',30);

	        that.$store.state.userInfo = LoginRes.data.user;

			if(LoginRes.data.business.length > 0){

			  that.$store.state.businessInfo = LoginRes.data.business[0];

	          that.$store.state.CurrentRole = 'storesStaff';

		      that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData['storesStaff'];

		      that.login_click();
		    }else{

	          that.$alert("该账号未绑定业务代码", '提示', {
		        confirmButtonText: '确定',
		      })
		    }
	      }else{
	        that.SCErrMsg = LoginRes.Msg;
	      }
	    })
      },
      // 切换登录方式
      ChangeModule(theWay){

      	if( theWay == 'Verify'){
      	  this.Way_Verify = false;

      	}else if( theWay =='StoreCode'){
      	  this.Way_Verify = true;
      	}
      },
      getUrlKey:function(name){//获取url 参数
		return decodeURIComponent((new RegExp('[?|&]'+name+'='+'([^&;]+?)(&|#|;|$)').exec(location.href)||[,""])[1].replace(/\+/g,'%20'))||null;
	  },
	  //获取code
	  getCodeApi(state){
	  	var url = "";
	  	var nav = this.$route.query.nav;
	  	if(nav == "order"){

	 	  url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxce01bf1ba861a973&redirect_uri=http://k56.kaidongyuan.com/wechat?nav=order&response_type=code&scope=snsapi_base&state=144&connect_redirect=1#wechat_redirect';
	 	}else if(nav == "queryOrder"){

	 	  url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxce01bf1ba861a973&redirect_uri=http://k56.kaidongyuan.com/wechat?nav=queryOrder&response_type=code&scope=snsapi_base&state=144&connect_redirect=1#wechat_redirect';
	 	}else if(nav == "my"){

	 	  url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxce01bf1ba861a973&redirect_uri=http://k56.kaidongyuan.com/wechat?nav=my&response_type=code&scope=snsapi_base&state=144&connect_redirect=1#wechat_redirect';
	 	}else{

		  url='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxce01bf1ba861a973&redirect_uri=http://k56.kaidongyuan.com/wechat&response_type=code&scope=snsapi_base&state=144&connect_redirect=1#wechat_redirect';
	 	}
		 window.location.replace(url);
	  },
	  login_click(){

	  	var nav = this.$route.query.nav;

	 	if(nav == "order"){

	 	  this.$router.push({
		    name:"SalesOrder",
		  })
	 	}else if(nav == "queryOrder"){

	 	  this.$router.push({
		    name:"QuerySalesOrder",
		  })
	 	}else if(nav == "my"){

	 	  this.$router.push({
		    name:"Home",
		  })
	 	}else{

		  this.$router.push({
		    name:"Index",
		  })
	 	}
	  },
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .WXLogin{
	.WXLoginContainer{
	  font-size: 30/50rem;
	  .LoginLogo{
	  	padding:50/50rem 0;
	  	text-align: center;
	  	img{
	  	  width: 200/50rem;
		  border-radius:50%;
	  	}
	  }
	  .identeContainer{
	  	padding:0 50/50rem;
	  	.mobilePhone{
	  	  height: 270/50rem;
	  	  .mobilePhoneInput{
	  	  	height: 75/50rem;
	  	    position:relative;
			margin-top: 20/50rem;
	  	  	input{
		  	  width: 100%;
			  font-size: 30/50rem;
			  line-height: 75/50rem;
			  padding:0 80/50rem 0 10/50rem;
			  border-radius:0;
			  border:none;
			  border-bottom: 1/50rem solid #ccc;
		  	}
		  	i{
		  	  position: absolute;
		  	  right: 20/50rem;
			  top: 50%;
			  transform: translateY(-50%);
			  font-size: 35/50rem;
			  color: #CCCCCC;
		  	}
	  	  }
	  	  .ErrMsg{
	  	  	span{
			  color: #ff4E32;
			  line-height: 70/50rem;
			  font-size: 28/50rem;
	  	    }
	  	  }
	  	}
	  	.hollow{
	  	  margin-top: 40/50rem;
	  	  background-color: #fff;
	  	  border:1/50rem solid @PublicColor;
	  	  color: @PublicColor;
	  	}
	  }
	}
	.verseion{
	  width:130/50rem;
	  height: 40/50rem;
	  color: #ccc;
	  font-size: 29/50rem;
	  text-align: center;
	  position: fixed;
	  bottom: 0;
	  right: 0;
	}
  }
</style>


