<template>
  <div class="Binding">
	<!-- 头部 -->
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true'></componentHeader>
	<div class="container">
	  <div>
		<span>快速绑定账号</span>
	  </div>
	  <div>
		<i class="iconfont icon-denglu"></i>
		<input class='userName'  ref="userName"   style="text-transform:none;"   v-model="userName"  placeholder='请输入手机号' ></input>
		<i v-if="userName" @click="clearInput('userName')" class="iconfont icon-cuowu"></i>
	  </div>
	  <div>
		<i class="iconfont icon-mima" ></i>
		<input class='pswd' type='password'  ref="password"  style="text-transform:none;"  v-model="password" placeholder='请输入密码' ></input>
		<i v-if="password" @click="clearInput('password')" class="iconfont icon-cuowu"></i>
	  </div>
	  <button class='bind' @click='bind'>关联</button>
	</div>
	<componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='BusinessList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
  	name:"Binding",
  	data(){
  	  return{
		headerText:'微信登录绑定',
		userName:"",//用户名
		password:"",//密码
		WarehousesList:[],//仓库列表
		selectMShow:false,//选择仓库蒙版是否出现
  		SelectBoxFlag:false,//弹出选择框是否显示
		BouncedName:'业务代码',//弹框名字
  		BusinessList:[],//业务代码
  	  }
  	},
	components:{
      componentHeader,
      componentSelectBox
	},
	methods:{
  	  // 返回上一页
      f_Fanhui(){
        this.$router.go(-1);
      },
	  toLogin:function(){
		this.$router.push({
		  name:"Login",
		})
	  },
	  // 绑定
	  bind:function(){

		var that = this;
		
		if(!this.userName){

		  that.$alert("用户名不能为空", '提示', {
		    confirmButtonText: '确定'
		  })
		  return;

		}else if(!this.password){

		  that.$alert("密码不能为空", '提示', {
		    confirmButtonText: '确定'
		  })
		  return;
		}

		var postData = {
		  "wxOpenid":this.$store.state.WX_Openid,
		  "cellphone":this.userName,
		  "password":this.password,
		}

		this.httpRequest("bindingWX.do",postData,function(res){

		  that.$store.state.userInfo = res.data.user;
		  that.KeepUserNameAndPassord(that.userName, that.password);
		  that.setCookie(that.userName,that.password,10*365*60);
			
		  that.BusinessList = res.data.business;
		  if(res.data.business.length == 1){

			that.$store.state.businessInfo = that.BusinessList[0];

			for(var mk = 0;mk < that.$store.state.RoleTotalData.length; mk++){

	          if(that.$store.state.userInfo.business == that.$store.state.RoleTotalData[mk].business){
	          	that.$store.state.CurrentRole = that.$store.state.RoleTotalData[mk].EnglishRoleName;
	          }
	        }
			for(var li in that.$store.state.RoleModuleTotalData){

		      if(that.$store.state.CurrentRole == li){
		        that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData[li];
		      }
		    }

	        that.SelectBoxFlag = false;
	        that.$router.push({
			  name:"Index",
			  query:{
			  }
			})
		  }else if(res.data.business.length > 1){

	        that.SelectBoxFlag = true;
		  }else{

	        that.$alert("该账号未绑定业务代码", '提示', {
		      confirmButtonText: '确定',
		    })
		  }
		})
	  },
	  // 选中某个业务代码
      selectedItem(index){

        this.$store.state.businessInfo = this.BusinessList[index];

        for(var mk = 0;mk < that.$store.state.RoleTotalData.length; mk++){

          if(that.$store.state.userInfo.business == that.$store.state.RoleTotalData[mk].business){
          	that.$store.state.CurrentRole = that.$store.state.RoleTotalData[mk].EnglishRoleName;
          }
        }

        for(var li in that.$store.state.RoleModuleTotalData){

	      if(that.$store.state.CurrentRole == li){
	        that.$store.state.CurrentRoleData = that.$store.state.RoleModuleTotalData[li];
	      }
	    }

        this.SelectBoxFlag = false;
        this.$router.push({
	      name:"Index",
	      query:{
	      }
	    })
      },
      // 取消选择业务代码
      selectCancel(){
	    this.SelectBoxFlag = false;
	  },
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Binding{
	.container{
	  font-size: 30/50rem;
	  width: 100%;
	  float: left;
	  margin-top: 100/50rem;
	  &>div{
		width: 100%;
		position: relative;
		span{
		  margin: 20/50rem auto 40/50rem;
		  margin-left: 58/50rem;
		  margin-right: 10/50rem;
		  display: block;
		}
		.userName,.pswd{
		  width:638/50rem;
		  height:70/50rem;
		  border-radius: 10/50rem;
		  padding-left: 82/50rem;
		  font-size: 30/50rem;
		  box-sizing: border-box;
		  margin: 20/50rem auto;
		  display: block;
		}
		i.icon-denglu,i.icon-mima{
		  position: absolute;
		  top:15/50rem;
		  left:80/50rem;
		  color: #265AC3;
		  font-size: 40/50rem;
		  font-weight: 500;
		}
		i.icon-cuowu{
		  position:absolute;
		  right:70/50rem;
		  font-size: 37/50rem;
		  top: 15/50rem;
		}
	  }
	  .bind{
		width:640/50rem;
		height:73/50rem;
		line-height: 73/50rem;
		font-size: 30/50rem;
		background-color:@PublicColor;
		color: #fff;
		margin: 20/50rem auto;
		margin-top: 80/50rem;
		border:none;
		border-radius: 36.5/50rem;
		outline: none;
		display: block;
	  }
	}
  }	
</style>


