<template>
  <div class='LogisticsInfo'>
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class="haveHeadcontainer">
  	  <div class='orderInfo'>
	    <div class='infoHead'>订单信息</div>
	    <div class='infoContainer'>
	      <div><span>客户名称：</span><span>{{data.CONSIGNEE_NAME}}</span></div>
	      <div><span>收货地址：</span><span>{{data.CONSIGNEE_ADDRESS}}</span></div>
	      <div>
          <div><span>下单总量：</span><span>{{data.ORDER_QTY}} 件</span></div>
          <div><span>发货总量：</span><span>{{data.ISSUE_QTY}} 件</span></div>
        </div>
		    <div>
          <div><span>下单总重：</span><span>{{data.ORDER_WT}} 吨</span></div>
          <div><span>发货重量：</span><span>{{data.ISSUE_WT}} 吨</span></div>
        </div>
		    <div>
          <div><span>下单体积：</span><span>{{data.ORDER_VOL}} m³</span></div>
          <div><span>发货体积：</span><span>{{data.ISSUE_VOL}} m³</span></div>
        </div>
		  </div>
	  </div>
	  <div class='orderInfo'>
	    <div class='infoHead'>原单/拆单</div>
   		  <div class='infoContainer billList' @click='tologisticsDetail(idx)' v-for='(item,idx) in data.DETAIL'  :id="idx"  :key='idx'>
            <div><span>订单编号：</span><span>{{item.OMS_NO}}</span></div>
            <div><span>配载单号：</span><span>{{item.SHIPMENT_NO}}</span></div>
            <div><span>配载时间：</span><span>{{item.SHIP_TIME}}</span></div>
            <div><span>发运时间：</span><span>{{item.ACTUAL_DEPT_TIME}}</span></div>
  	        <div>
              <div>数量：<span>{{item.ORDER_QTY}} 件</span></div>
  	          <div>流程节点：<span>{{item.NODE_STATUS}}</span></div>
  	        </div>
  	        <div>
              <div>重量：<span>{{item.ORDER_WT}} 吨</span></div>
  	          <div>司机交付：<span>{{item.DRIVER_STATUS}}</span></div>
  	        </div>
            <div>体积：<span>{{item.ORDER_VOL}} m³</span></div>
            <div class='entryBtn'>
              <div class='progress' :id="idx" @click='toProgress(idx)'>查看进度</div>
              <div class='line' :id="idx"  @click='toLine(idx)'>查看路线</div>
              <div class='sign' :id="idx" @click='toSign(idx)'>查看签名</div> 
      		</div>
      		<i class='iconfont icon-tiaozhuanqianwangyoujiantouxiangyouxiayibuxianxing'></i>
	      </div>
	      <span v-if="!data.DETAIL.length">无</span>
	  </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
  	data(){
  	  return{
  	  	headerText:'物流信息',
        data:{"DETAIL":""},
        idx:'',
  	  }
  	},
    components:{
	    componentHeader,
	  },
  	created(){

      var that = this;

      if(this.$route.query.orderId){
        this.orderId = this.$route.query.orderId;
      }

      var postData = {
        "sellOrderId":this.orderId,
      }
      this.httpRequest("GetInformation.do",postData,function(res){

        that.data = res.data;
      })
  	},
  	methods:{

  	  //返回上一页
  	  f_Fanhui(){
  	  	this.$router.go(-1);
  	  },

  	  // 页面跳转  物流信息详情页面
  	  tologisticsDetail(index){
  	  	this.$router.push({
          name:"LogisticsInfoDetail",
          query:{
            orderId:this.data.DETAIL[index].ORDER_ID
          }
        })
  	  },

  	  // 页面跳转 查看进度
  	  toProgress(index){
  	  	event.stopPropagation();
  	  	this.$router.push({
          name:"CheckSchedule",
          query:{
            orderId:this.data.DETAIL[index].ORDER_ID
          }
        })
  	  },

  	  // 页面跳转  查看签名
  	  toSign(index){
  	  	event.stopPropagation();
        let orderID = this.data.DETAIL[index].DELIVERY_ID
        let OMS_NO = this.data.DETAIL[index].OMS_NO
          this.$router.push({
          name:"CheckDeliverPicture",
          query:{
            orderID:orderID,
            OMS_NO:OMS_NO
          }
        })
  	  },

      // 页面跳转 查看轨迹
      toLine(index){
        event.stopPropagation();
        var item = this.data.DETAIL[index]
        if(item.DRIVER_STATUS == "已交付"){
          this.ViewTrajectoryInMain(item.SHIPMENT_ID, item.SHIPMENT_NO, "交付");
        }else{
          this.ViewTrajectoryInMain(item.SHIPMENT_ID, item.SHIPMENT_NO, "在途");
        }
      }
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .LogisticsInfo{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    .haveHeadcontainer{
      .orderInfo {
        .infoContainer{
          &>div{
            display: flex;
            margin-bottom: 0;
            line-height: 50/50rem;
            span{
              margin-left: 0;
            }
            span:first-child{
              color: #000;
            }
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
      }
  	  .billList{
        position: relative;
        i{
    		  font-size:50/50rem;
    		  margin-bottom: 25/50rem;
    		  position: absolute;
    		  top:50%;
    		  margin-top: -25/50rem;
    		  right:15/50rem;
    		  color: #444;
    		}
	    .entryBtn{
	      display: flex;
	      margin-top: 20/50rem;
	      &>div{
	        flex-grow: 1;
	        height:50/50rem;
	        background-color: @PublicColor;
	        color: #fff;
	        text-align: center;line-height: 50/50rem;
	        border-radius: 10/50rem;
	      }
	      .progress{
	        margin-right: 27/50rem;
	      }
	      .sign{
	        margin-left: 27/50rem;
	      }
      	}
      }
    }
  }
</style>


