<template>
  <div class="SelectStores">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="CustomerSearch">
        <div>
          <input  v-model='searchValue' placeholder="按门店名称搜索">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="StoresList">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="CustomerListItem" @click="Rebound(index)" v-for='(dataItem,index) in StoresListData'  :id="index"  :key='index'>
            <div><span>门店名称：</span><span class="CUSTOMER_NAME">{{dataItem.CUSTOMER_NAME}}</span></div>
            <div><span>联&nbsp;系&nbsp;人&nbsp;：</span><span>{{dataItem.CONTACT_NAME}}</span></div>
            <div><span>联系电话：</span><span>{{dataItem.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='dataItem.CONTACT_TEL' @click="callPhone(dataItem.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span>详细地址：</span><span>{{dataItem.CUSTOMER_ADDRESS}}</span></div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"选择门店",//页面标题
      	StoresListData:[],//门店列表
        searchValue:'',//搜索的值
        noDataShow:false,//页面没有数据图标
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      this.reqStoresListInfo();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoresListInfo();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        var that = this;

        if(this.$route.query.whoPush){ 

          if(this.$route.query.whoPush_order && this.$route.query.orderType){

            // 选择商品  下单类型：销售退货（针对门店）
            if(this.$route.query.whoPush_order =='InventoryManage' && this.$route.query.orderType=='SALE_RETURN_IN'){

              this.$router.push({
                name:that.$route.query.whoPush,
                query:{
                  whoPush:that.$route.query.whoPush_order,
                  orderType:that.$route.query.orderType,
                }
              })
              return;
            }

            // 选择商品  下单类型：其他入库 其他出库
            if(this.$route.query.whoPush_order =='InventoryManage' && (this.$route.query.orderType=='OTHER_IN' || this.$route.query.orderType=='OTHER_OUT')){
              this.$router.push({
                name:that.$route.query.whoPush,
                query:{
                  whoPush:that.$route.query.whoPush_order,
                  orderType:that.$route.query.orderType,
                }
              })
              return;
            }
          }
        }

    		this.$router.go(-1);
    	},
      // 请求店铺列表
      reqStoresListInfo(load){
        var that = this;

        var ProductData = {
          "storeName":that.searchValue,//货品名称
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        console.log(ProductData)
        // 查询门店列表
        that.httpRequest("searchStore.do",ProductData,function(StoresRes){
          console.log(StoresRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(StoresRes.data.length){
              that.StoresListData = that.StoresListData.concat(StoresRes.data);
            }
          } else {
            that.StoresListData = StoresRes.data;
          }

          // 判断是否能上拉加载
          if (StoresRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.StoresListData.length){

            that.noDataShow = false;
           }else{
            that.noDataShow = true;
           }
        })
      },
    	// 回跳页面
    	Rebound(idx){

    	  var that = this;

    	  if(this.$route.query.whoPush){ 

          // 库存登记
          if(this.$route.query.whoPush=='InventoryRegister'){

            this.$store.state.InventoryRegALL_CustomerInfo = {
              "CUSTOMER_NAME":that.StoresListData[idx].CUSTOMER_NAME,
              "CUSTOMER_ID":that.StoresListData[idx].CUSTOMER_ID,
              "CONTACT_NAME":that.StoresListData[idx].CONTACT_NAME,
              "CONTACT_TEL":that.StoresListData[idx].CONTACT_TEL,
              "CUSTOMER_ADDRESS":that.StoresListData[idx].CUSTOMER_ADDRESS,
            };
            this.$router.push({
              name:'CustomerAllInventoryReg',
            })
            return;
          }

          if(this.$route.query.whoPush_order && this.$route.query.orderType){

            var CustomerInfo = {
              "CUSTOMER_NAME":that.StoresListData[idx].CUSTOMER_NAME,
              "CUSTOMER_ID":that.StoresListData[idx].CUSTOMER_ID,
              "CONTACT_NAME":that.StoresListData[idx].CONTACT_NAME,
              "CONTACT_TEL":that.StoresListData[idx].CONTACT_TEL,
              "CUSTOMER_TEL":that.StoresListData[idx].CONTACT_TEL,
              "CUSTOMER_ADDRESS":that.StoresListData[idx].CUSTOMER_ADDRESS,
            };
            // 选择商品  下单类型：销售退货（针对门店）
            if(this.$route.query.whoPush_order =='InventoryManage' && this.$route.query.orderType=='SALE_RETURN_IN'){

              this.$router.push({
                name:that.$route.query.whoPush,
                query:{
                  CustomerInfo:CustomerInfo,
                  whoPush:that.$route.query.whoPush_order,
                  orderType:that.$route.query.orderType,
                }
              })
              return;
            }

            // 选择商品  下单类型：其他入库 其他出库
            if(this.$route.query.whoPush_order =='InventoryManage' && (this.$route.query.orderType=='OTHER_IN' || this.$route.query.orderType=='OTHER_OUT')){
              this.$router.push({
                name:that.$route.query.whoPush,
                query:{
                  CustomerInfo:CustomerInfo,
                  whoPush:that.$route.query.whoPush_order,
                  orderType:that.$route.query.orderType,
                }
              })
              return;
            }
          }
          
    			this.$router.push({
  		      name:that.$route.query.whoPush,
  		      query:{
  		        CustomerInfo:{
  		          "CUSTOMER_NAME":that.StoresListData[idx].CUSTOMER_NAME,
                "CUSTOMER_ID":that.StoresListData[idx].CUSTOMER_ID,
  		          "CONTACT_NAME":that.StoresListData[idx].CONTACT_NAME,
  		          "CONTACT_TEL":that.StoresListData[idx].CONTACT_TEL,
  		          "CUSTOMER_ADDRESS":that.StoresListData[idx].CUSTOMER_ADDRESS,
  		        }
  		      }
  		    })
  		  }
    	},
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqStoresListInfo("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqStoresListInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SelectStores{
    overflow-x: hidden;
    height: 100%;
  	.container{
      height: 100%;
      margin-top: 0;
      .CustomerSearch{
        font-size: 32/50rem;
        position: fixed;
        z-index: 50;
        background-color: #fff;
        top: 89/50rem;
        left: 0;
        .TopSearchBox;
      }
      .StoresList{
        height: 100%;
        overflow: scroll;
        padding-top: 202/50rem;
        box-sizing:border-box;
        .CustomerListItem{
          padding:10/50rem 20/50rem;
          box-sizing: border-box;
          border-bottom: 3/50rem solid @PublicBorderColor;
          &>div{
            display: flex;
            line-height: 55/50rem;
            color: #000;
            font-size: 28/50rem;
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:nth-child(2){
              flex-grow: 1;
              color: #777;
            }
            &>span.CUSTOMER_NAME{
              font-weight:900;
              color: #000;
            }
          }
        }
      }
  	}
    .kong{
      height: 100/50rem;
    }
  }
</style>


