<template>
  <div class="FeeBill">
    <componentHeader  headerText= '账单列表' :IfFanhuiShow='true'  @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class="v-f-parent">
        <div class="v-f-bill" v-for='(item,index) in bill_list'  :id="index"  :key='index'  @click='toDetail(index)'>
          <p><span>账单名称：</span>{{ item.FEE_NAME }}</p>
          <p><span>账单月份：</span>{{ item.FEE_DATE }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    name:"FeeBill",
    data(){
      return{
        customerInfo:{},//客户信息
        bill_list:[],//费用账单列表
      }
    },
    created(){

      var that = this;

      if(this.$route.query.customerInfo){
        this.customerInfo = this.$route.query.customerInfo
      }
      
      var postData = {
        "businessId":that.$store.state.businessInfo.businessId,//业务id
        "appUsersId":that.$store.state.userInfo.user_id,//用户id
      }

      this.httpRequest("getAppBillFeeList.do",postData,function(res){

        that.bill_list = res.data
      })
    },
    components:{
      componentHeader,
    },
    methods:{
      f_Fanhui(){
        this.$router.push({
          name:"PurchaseOrder",
          query:{
            whoPush:"FeeBill",
          },
        })
      },
      toDetail(index){
        this.$router.push({
          name:"FeeBillDetail",
          query:{
            monthDate:this.bill_list[index].FEE_DATE,
            customerInfo:this.customerInfo,
          },
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  .FeeBill{
    height: 100%;
    overflow: hidden;
    .container{
      overflow: scroll;
      margin-top: 90/50rem;
      height: calc(100% - 45px);
      .v-f-parent{
        padding-top: 30/50rem;
        margin-left: 30/50rem;
        margin-right: 30/50rem;
        .v-f-bill{
          background-color: #eee;
          margin-bottom: 30/50rem;
          padding: 15/50rem;
          font-size: 28/50rem;
          border-radius: 10/50rem;
          p{
            padding: 5/50rem;
          }
        }
      }
    }
  }
</style>


