<template>
  <div class="componentCargoInfo" v-if="goodsArr.length">
  	<div class='goodsInfoData'>
	  <div class='infoHead'>{{titleHead}}</div>
      <div class="goodsContent">
        <div class='goodsArr' v-for='(itemName,idx) in goodsArr'  :id="idx"  :key='idx'> 
          <!--货物信息列表  -->
          <div>
            <div class='LeftIcon'>
              <img :src="itemName.PRODUCT_PIC?itemName.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
            </div>
            <div class='RightBox'>
              <div>{{itemName.productName}}</div>
              <div><span>产品代码：</span><span>{{itemName.productCode}}</span></div>
              <div>
                <div><span>数量：</span><span>{{itemName.pirchaseQuantity}}&nbsp;{{itemName.UNIT_STR}}</span></div>
                <!--  {{itemName.unit}} -->
                <div v-if='titleHead != "赠品信息"&&(TRANSFER_TYPE=="PURCHASE_IN" || TRANSFER_TYPE=="SALE_OUT")'><span>付款单价：</span><span>¥ {{itemName.payment}}</span></div>
              </div>

              <!-- 部分业务需要改订单数量和价格 开始 -->
              <div v-if='$store.state.businessInfo.isEdit == "T"' class="delivery-qty-price">
                <div>
                  <span>配送数量：</span><span>{{itemName.TAKE_PO_NUM}}&nbsp;{{itemName.UNIT_STR}}</span>
                  <i @click="Comp_edit(itemName, '数量')" class="iconfont icon-xie"></i>
                </div>

                <div v-if='titleHead != "赠品信息"&&(TRANSFER_TYPE=="PURCHASE_IN" || TRANSFER_TYPE=="SALE_OUT")'>
                  <span>配送单价：</span><span>¥ {{itemName.TAKE_PRICE}}</span>
                  <i v-if='titleHead != "赠品信息"&&(TRANSFER_TYPE=="PURCHASE_IN" || TRANSFER_TYPE=="SALE_OUT")' @click="Comp_edit(itemName, '价格')" class="iconfont icon-xie"></i>
                </div>
              </div>
              <!-- 部分业务需要改订单数量和价格 结束 -->

              <div v-if='titleHead != "赠品信息"&& itemName.MJ_PRICE>0 && (TRANSFER_TYPE=="PURCHASE_IN" || TRANSFER_TYPE=="SALE_OUT")'><span>优惠单价：</span><span>¥ {{itemName.MJ_PRICE}}</span></div>
            </div>
          </div>
          <div class="bottleFee" v-if='itemName.PRODUCT_TYPE == "GF" &&itemName.GIFT_TYPE == "WINE"'>
            <div>
              <div><span>{{itemName.BOTTLE_NAME}}</span></div>
              <div><span>单价：</span><span>¥ {{itemName.BOTTLE_PRICE}}</span>&nbsp;&nbsp;&nbsp; <span>总价：</span><span>¥ {{parseFloat(parseFloat(itemName.BOTTLE_PRICE) * itemName.pirchaseQuantity).toFixed(2)}}</span></div>
            </div>
          </div>
        </div>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  export default{
  	data(){
  	  return{
  	  }
  	},
  	props:{
      goodsArr:{
    		type:Array,
    	    default: () => {
    		  return []
    		}
  	  },//客户信息列表
      titleHead:'',//列表头名字
      TRANSFER_TYPE:'',//订单类型
  	},
  	methods:{
      Comp_edit(itemName, type){
        this.$emit('Comp_edit', itemName, type);
      }
  	}
  }
</script>
<style lang="less" scoped>
  @import "../publicLess.less";
  .OutboundDetail,.PutInStorageDetails{
   .goodsInfoData{
      background-color:#F4F4F4;
      .goodsContent{
        padding: 0 8/50rem;
        box-sizing: border-box;
        padding-bottom: 12/50rem;
        .goodsArr{
          font-size: 28/50rem;
          width:100%;
          background-color: #fff;
          border-radius: 10/50rem;
          margin-bottom: 10/50rem;
          &>div{
            display: flex;
            .LeftIcon{
              width:120/50rem;
              position:relative;
              flex-shrink:0;
              img{
                width: 90/50rem;
                position:absolute;
                top:50%;
                left: 50%;
                transform: translate(-50%,-50%);
              }
            }
            .RightBox{
              padding:16/50rem;
              flex-grow: 1;
              &>div{
                margin-bottom:10/50rem;
                display: flex;
                span{
                  color: #555;
                }
                &>div{
                  float: left;
                  width: 50%;
                }
                &>span:first-child{
                  min-width: 130/50rem;
                  flex-shrink:0;
                }
                &>span:nth-child(2){
                  flex-grow: 1;
                }
              }
              .delivery-qty-price{
                font-size: 25/50rem;
                i{
                  padding: 15/50rem;
                }
              }
            }
          }
        }
      }
    }
  }
</style>


