<template>
  <div class="MessageList">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="MessageListData">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="Message" v-for='(dataItem,index) in Messagedata' :id="index"  :key='index'>
            <div class="icon"> 
               <i class="iconfont" :class="dataItem.read == 1 ? 'icon-yiduxiaoxi-' : 'icon-weiduxiaoxi'"></i>
            </div>
            <div class="MessageColumn" @click='toMessageInfo(index)'> 
              <div class="one">
                <div class="MessageItem" >{{dataItem.title}}</div>
                <div class="ReceiveTime">{{dataItem.appTime}}</div>
              </div>
              <div class="two">
                <div class="MessageInfo">{{dataItem.appContent}}</div>
              </div>
            </div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{ 
    data(){
      return{
      	headerText:"消息",
        Messagedata:[],//消息列表
        noDataShow:false,//没有数据
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        nowDate:'',//当前日期
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      this.nowDate = this.getNowTime('getDate'); 
      this.reqMessageListInfo();
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
       this.$router.push({
          name:"Index",
       })
    	},
      // 请求消息列表
      reqMessageListInfo(load){
        var that = this;
        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户ID
          "businessId":that.$store.state.businessInfo.businessId,//业务ID
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        // 获取消息列表
        that.httpRequest("earlyWarningPush.do",postData,function(Res){

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          for( var i=0; i<Res.data.length; i++){
            if(Res.data[i].appTime.substring(0,10) == that.nowDate){
              
              Res.data[i].appTime = Res.data[i].appTime.substring(11,16);
            }
            else{
              Res.data[i].appTime = Res.data[i].appTime.substring(5,10);
              
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(Res.data.length){
              that.Messagedata = that.Messagedata.concat(Res.data);
            }
          } else {
            that.Messagedata = Res.data;
          }

          // 判断是否能上拉加载
          if (Res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
           
          if(that.Messagedata.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }
        })
      },
      //跳转到消息明细
      toMessageInfo(index){
        var MessageItem = this.Messagedata[index];
        this.$router.push({
          name:"MessageDetail",
          query:{ 
            MessageItem:MessageItem,
          }
        }) 
      },
      // 下拉刷新
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.reqMessageListInfo("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqMessageListInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .MessageList{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .MessageListData{
        height: 100%;
        overflow: scroll;
        padding-top: 89/50rem;
        box-sizing:border-box;
        .Message{
          width: 100%;
          overflow: hidden;
          padding:10/50rem 0;
          border-bottom:3/50rem solid @PublicBorderColor;
          .icon{
            width: 12%;
            height: 100/50rem;
            text-align: center;
            i{
              line-height: 100/50rem;
              font-size: 65/50rem;
              color:#F56464;
            }
            .icon-yiduxiaoxi-{
              font-size: 43/50rem;
            }
            float: left;
          }
          .MessageColumn{
            width: 88%;
            float: left;
            .one{
              height: 55/50rem;
              overflow: hidden;
              .MessageItem{
                float: left;
                width: 80%;
                height: 55/50rem;
                line-height: 55/50rem;
                font-size: 30/50rem;
              }
              .ReceiveTime{
                float: right;
                width: 20%;
                height: 55/50rem;
                line-height: 55/50rem;
                text-align: center;
                font-size: 28/50rem;
                color: #6D6D6D;
              }
            }
            .two{
              height: 50/50rem;
              .MessageInfo{
                float: left;
                width: 96%;
                height: 50/50rem;
                line-height: 50/50rem;
                font-size: 25/50rem;
                color: #6D6D6D;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
              }
            }
          }
        }
      }  
    }
    .kong{
      height: 80/50rem;
    }
  }
</style>


