<template>
  <div class="ospPerformanceRanking">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

    <div class="container haveHeadcontainer">
     
      <div class="DateDisplay">
        <div>
          <el-date-picker v-model="StartDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"  @change="ChangeStartDate" :editable="false"></el-date-picker>
        </div>
        <span>至</span>
        <div>
          <el-date-picker v-model="EndDate" type="date" placeholder="截止日期"  value-format="yyyy-MM-dd"  @change="ChangeEndDate" :editable="false"></el-date-picker>
        </div>
      </div>   
      <div class="reportInfo">
        <div class="ospPerformanceRankingItem"  v-for='(dataItem,index) in ospPerformanceRankingData'  :id="index"  :key='index'>      
          <div class="CustomerName"><span>{{dataItem.ospName}}</span></div>
          <div><span>销售金额：</span><span>{{dataItem.amount}}</span></div>
          <div><span>单店VPO：</span><span>{{dataItem.vpo}}</span></div>
          <div><span>订单份数：</span><span>{{dataItem.orderCount}}</span></div>
          <div><span>订单数量：</span><span>{{dataItem.orderQty}}</span></div>
        </div>
      </div>

      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from "jquery"
  export default{
    data(){
      return{
        headerText:"OSP业绩排行榜",//页面标题
        ospPerformanceRankingData:[],
        StartDate:'',
        EndDate:'',
        noDataShow:false,//页面没有数据图标
      }
    },
    components:{
      componentHeader,
    },
    created(){
       this.StartDate = this.getNowTime('getMonthFirstDay'); 
       this.EndDate = this.getNowTime('getDate'); 
       this.GetospPerformanceRankingData();
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
       this.$router.go(-1);
      },

       ChangeStartDate(Value){
        this.StartDate = Value; 
        this.GetospPerformanceRankingData();
      }, 

      ChangeEndDate(Value){
        if(this.StartDate > Value ){
          this.$alert('截止时间不可小于开始时间', '提示', {
            confirmButtonText: '确定',
          })
          this.EndDate = this.getNowTime('getDate'); 
        }else{
          this.EndDate = Value; 
          this.GetospPerformanceRankingData();
        }
      },

      // 获取客户拜访信息
      GetospPerformanceRankingData(){
        var that = this;
        var storesData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "StartDate":that.StartDate,
          "EndDate":that.EndDate,
        }
        // 获取销售业绩信息
        that.httpRequest("ospPerformanceRanking.do",storesData,function(saleRes){

          that.ospPerformanceRankingData = saleRes.data;
          if(that.ospPerformanceRankingData.length){
            that.noDataShow = false;
          }else{
            that.noDataShow = true;
          }
        })


      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ospPerformanceRanking{
    overflow-x: hidden;
    .container{
      .DateDisplay{
        height: 100/50rem;
        line-height: 100/50rem;
        padding:0 60/50rem;
        background-color:#EEEFF2;
         &>div:first-child{
          float: left;
          width: 260/50rem;
         }
         span{ 
          float: left;
          width: 110/50rem; 
          text-align: center;
        }
         &>div:last-child{
           width: 260/50rem;
          float: right;

        }
      }
      .reportInfo{
        .ospPerformanceRankingItem{
          padding: 15/50rem 30/50rem;
          line-height: 45/50rem;
          border-bottom: 10/50rem solid #EEEFF2;
          color: #555;
          .CustomerName{
            line-height: 60/50rem;
            font-size: 32/50rem;
            font-weight: 550;
          }
        }
      }

      #main{
        margin: 60/50rem 0;
      }
    }
  }

  ::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
  }
</style>


