<template>
  <div class='LogisticsInfoDetail'>
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class="container haveHeadcontainer">
  	  <div class='orderInfo'>
        <div class='infoHead'>订单信息</div>
        <div class="infoContainer">
          <div>订单编号：<span>{{data.OMS_NO}}</span></div>
          <div>配载单号：<span>{{data.SHIPMENT_NO}}</span></div>
          <div>配载时间：<span>{{data.SHIP_TIME}}</span></div>
          <div>发运时间：<span>{{data.ACTUAL_DEPT_TIME}}</span></div>
          <div>承运商名：<span>{{data.trans_party_name}}</span></div>
          <div>司机车牌：<span>{{data.vehicle_Code}}</span></div>
          <div>司机姓名：<span>{{data.driver_Name}}</span>&nbsp;&nbsp;&nbsp;<span>{{data.mobile_Number}}&nbsp;&nbsp;&nbsp;<i v-if='data.mobile_Number' @click="callPhone(data.mobile_Number)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div  class='bottom'><div>下单数量：<span>{{data.ORDER_QTY}} 件</span></div><div>订单流程：<span>{{data.NODE_STATUS}}</span></div></div>
          <div  class='bottom'><div>下单总重：<span>{{data.ORDER_WT}} 吨</span></div><div>司机交付：<span>{{data.DRIVER_STATUS}}</span></div></div>
          <div  class='bottom'><div>下单体积：<span>{{data.ORDER_VOL}} m³</span></div><div>付款方式：<span>{{ }}</span></div></div>
        </div>
      </div>
      <div class='orderInfo LogisticsGoodsInfo'>
        <div class='infoHead'>货物信息</div>
        <div>
          <div class='infoContainer' v-for='(item,idx) in data.PRODUCT'  :id="idx"  :key='idx'>
  		    <div class="PRODUCT_NAME">{{item.PRODUCT_NAME}}</div>
  		    <div>产品编号：<span>{{item.PRODUCT_NO}}</span></div>
          <div>产品数量：<span>{{item.ISSUE_QTY}} {{item.SECONDARY_UNIT_STR}}</span></div>
          </div>  
        </div>
      </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
  	data(){
  	  return{
  	  	headerText:'物流信息详情',
        data:{},
  	  	ORD_NO: '', //订单号
        TMS_SHIPMENT_NO:'', //装运编号
        TMS_DATE_LOAD: '',//装运时间
        TMS_DATE_ISSUE: '', // 出库时间
        TMS_FLEET_NAME: '', //承运商名
        TMS_DRIVER_NAME: '',// 司机姓名
        TMS_DRIVER_TEL:'',// 司机号码
        ORD_ISSUE_QTY: 0,// 下单数量
        ORD_ISSUE_WEIGHT: 0,// 下单总重
        ORD_ISSUE_VOLUME: 0,// 下单体积
        TMS_PLATE_NUMBER: '',// 车牌号码
        ORD_WORKFLOW: '',// 订单流程
        ORD_STATE: '',// 订单状态
        DRIVER_PAY:'',// 付款方式
        OrderDetails: [], //货物信息数组
  	  }
  	},
  	components:{
	  componentHeader,
	},
  	created(){

      var that = this;

      if(this.$route.query.orderId){
        this.orderId = this.$route.query.orderId;
      }

      var postData = {
        "orderId":this.orderId,
      }

      this.httpRequest("GetTMSOrderInfo.do",postData,function(res){

        that.data = res.data;
      })
  	},
  	methods:{
  	  //返回上一页
  	  f_Fanhui(){
  	  	this.$router.go(-1);
  	  },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .LogisticsInfoDetail{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    .LogisticsGoodsInfo{
      div:not(.infoHead){
        color: #666;
        .PRODUCT_NAME{
          color: #000;
          font-weight: 900;
        }
      }
    }
    .bottom{
      &>div:first-child{
        float: left;
        width: 50%;
      }
      &>div:nth-child(2){
        float: right;
        width: 50%;
      }
    }
  }
</style>


