<template>
  <div class="AddInventoryConfirmInfo">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

  	<div class="container haveHeadcontainer">
  	  <div class="dateHead">
          <div><span>库存月份：</span><span>{{InventoryMonth}}</span></div>
          <div><span>填表日期：</span><span>{{CurrentDate}}</span></div>
        </div>
        <div class="orderInfo">
          <div class='infoHead'>客户信息</div>
          <div class='infoContainer'>
             <div><span>客户名称：</span><span>{{CustomerInfo.CUSTOMER_NAME}}</span></div>
            <!-- <div>客户代码：<span>{{CustomerInfo.CustomerCode}}</span></div> -->
            <!-- <div>客户城市：<span>{{CustomerInfo.CustomerCity}}</span></div> -->
            <div><span>联&nbsp;系&nbsp;人&nbsp;：</span><span>{{CustomerInfo.CONTACT_NAME}}&nbsp;&nbsp;&nbsp;{{CustomerInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='CustomerInfo.CONTACT_TEL' @click="callPhone(storesData.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span>详细地址：</span><span>{{CustomerInfo.CUSTOMER_ADDRESS}}</span></div>
          </div>
        </div>
        <div class="orderInfo">
          <div class='infoHead'>货物信息</div>
          <div class='infoContainer' v-for='(itemName,index) in ProductList'  :id="index"  :key='index'>
            <div class="productName" style="color: #000;font-weight: 900;">{{itemName.productName}}</div>
            <div><span>规&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;格：</span><span>{{itemName.productDesc}}</span></div>
            <div><span>原&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;价：</span><span>¥ {{itemName.unitPrice}}</span></div>
            <div><span>库存数量：</span><span>{{itemName.inventory}} {{itemName.UNIT_OF_MEASURE_STR}}</span></div>
            <div><span>生产日期：</span><span>{{itemName.produceTime}}</span></div>
          </div>
        </div>
  	</div>
    <div v-if="ifTips" class="msg_tips">
      <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
      </div>
    </div>
	  <div class="BtnComponent bottomBtn">
      <div @click='AddConfirm' class="onlyBtn">提交</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"确认登记库存",//页面标题
      	InventoryMonth:'',//库存月份
      	CurrentDate:'',//当前日期
        CustomerInfo:{},//客户信息
      	ProductList:[],//产品列表
        custoType:'',//客户类型
        ifTips:false,
        tips_Msg:"",
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      var AddInvPagesData = this.$store.state.AddInventory_pagesData;
      if(AddInvPagesData.InventoryMonth){
        this.InventoryMonth = AddInvPagesData.InventoryMonth;
      }
      if(AddInvPagesData.CustomerInfo){
        this.CustomerInfo = AddInvPagesData.CustomerInfo;
      }
      if(AddInvPagesData.ProductList){
        this.ProductList = AddInvPagesData.ProductList;
      }
      if(AddInvPagesData.fillingDate){
        this.CurrentDate = AddInvPagesData.fillingDate;
      }
      if(AddInvPagesData.custoType){
        this.custoType = AddInvPagesData.custoType;
      }
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);
      },
      // 提交 确认添加库存
      AddConfirm(){
      	var that = this;

        this.$confirm('确定要提交吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {

          var  ospId,orgnizationId;

          if(that.custoType == 'store'){

            ospId = that.$store.state.InventoryOspInfo.CUSTOMER_ID;//ospID
            orgnizationId = that.CustomerInfo.CUSTOMER_ID;//门店id
            // ospId = that.CustomerInfo.CUSTOMER_ID;
          }else{
            ospId = '';
            orgnizationId = that.$store.state.InventoryOspInfo.CUSTOMER_ID;//ospID
          }
           var ProductData = {
            "businessId":that.$store.state.businessInfo.businessId,//业务id
            "businessCode":that.$store.state.businessInfo.businessCode,//业务代码
            "ospId":ospId,//ospID
            "orgnizationId":orgnizationId,//门店ID
            "inventoryTime":that.InventoryMonth,//库存月份
            "strInventory":that.ProductList,//数据集合
            "type":that.custoType,//客户类型
            "userId":that.$store.state.userInfo.user_id,//用户id
            "cellphone":that.$store.state.userInfo.cellphone,//用户手机号
          }
          console.log(ProductData)
          // 添加库存
          that.httpRequest("saveRegister.do",ProductData,function(ProductRes){
            console.log(ProductRes);

            that.$store.state.AddInventory_pagesData = {};

            that.ifTips = true;
            that.tips_Msg = "操作成功";

            setTimeout(function(){

              that.$router.push({
                name:"InventoryRegister"
              })
            },2000)

          })
        }).catch(() => {});
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .AddInventoryConfirmInfo{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    .container{
  	  padding-bottom:80/50rem;
      .orderInfo{
        .infoContainer{
          &>div:not(.productName){
            margin-bottom: 0;
            display: flex;
            line-height: 50/50rem;
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
              color: #000;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
            span{
              margin-left: 0;
            }
          }
          .productName{
            line-height: 50/50rem;
          }
        }
      }
  	  .dateHead{
        &>div{
          margin-bottom: 15/50rem;
        }
      }
    }
  }
</style>


