<template>
  <div class="HistoryVisit">
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class="container haveHeadcontainer">
  	  <div class="ModifyMainHead">
  	  	<div>{{storesName}}</div>
  	  	<div>
  	  	  <div v-if='lastvisitTime'><span>最后拜访时间：</span><span>{{lastvisitTime}}</span></div>
  	  	  <div>
  	  		<span>拜访次数：</span><span>{{visitCount}}</span>
  	  	  </div>
  	  	</div>
  	  </div>
  	  <div class="VisitList">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
    	  	<div class="VisitListItem" v-for='(dataItem,index) in VisitHistListData'  :id="index"  :key='index'  @click='toLeaveStore(index)'>
    	  	  <div>{{dataItem.VISIT_DATE}}</div>
    	  	  <div class="visitAddress">
    	  	  	<i class="iconfont icon-dizhi"></i>
    			    <span>{{dataItem.ACTUAL_VISITING_ADDRESS}}</span>
    	  	  </div>
            <div class="VISIT_STATES" v-if='!($route.query.ModifyCustomerattached && $route.query.ModifyCustomerattached=="ModifyCustomerInfo")'>
              <span :style="{color:dataItem.VISIT_STATES=='已拜访'?'rgb(40, 180, 95)':'rgb(228, 196, 121)'}">{{dataItem.VISIT_STATES}}</span>
              <div v-if="dataItem.VISIT_STATES=='拜访中'" @click='ContinueVisit(index)'><i class="iconfont icon-xie"></i><span>继续拜访</span></div>
            </div>
    	  	  <div class="xiangyou"><i class="iconfont icon-xiangyou"></i></div>
    	  	</div>
        </mt-loadmore>
        <div class="kong"></div>
  	  </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"历史拜访",//页面标题
      	VisitHistListData:[],//历史拜访列表
        storesName:'',//门店名称
        lastvisitTime:'',//上次拜访时间
        visitCount:0,//拜访次数
        noDataShow:false,//没有数据图标 是否显示
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
      componentHeader,
    },
    mounted(){

      var topHeight = parseFloat($('.ModifyMainHead').css("height")) + parseFloat($('header').css("height"));
      $('.VisitList').css('paddingTop',topHeight + 'px');
    },
    created(){
      var that = this;

      if(this.$store.state.CurrentStoresHistoryInfo.storesName){
        that.storesName = this.$store.state.CurrentStoresHistoryInfo.storesName;//门店名称
      }
      if(this.$store.state.CurrentStoresHistoryInfo.lastvisitTime){
        that.lastvisitTime = this.$store.state.CurrentStoresHistoryInfo.lastvisitTime;//上次拜访时间
      }
      if(this.$store.state.CurrentStoresHistoryInfo.visitCount){
        that.visitCount = this.$store.state.CurrentStoresHistoryInfo.visitCount;//拜访次数
      }

      this.reqVisitList();
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
        var that = this;
      	
        // 从历史拜访进入建议下单 返回后
       if(this.$route.query.orderConfirmattached && this.$route.query.orderConfirmattached == 'orderConfirm'){
        that.$router.push({
          name:"CustomerVisit"
        })
        return;
       }else{

        that.$router.go(-1);
       }
      },
      // 请求历史拜访记录
      reqVisitList(load){

        var that = this;

        var histData = {
          "storesId":this.$store.state.CurrentStoresId,//门店id
          "startTime":'',//开始时间
          "endTime":'',//结束时间
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//每页显示多少行数据
        }

        console.log(histData)
        // 获取历史拜访数据列表
        that.httpRequest("GetVisitHistory.do",histData,function(histRes){
          console.log(histRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(histRes.data.length){
              that.VisitHistListData = that.VisitHistListData.concat(histRes.data);
            }
          } else {
            that.VisitHistListData = histRes.data;
          }

          // 判断是否能上拉加载
          if (histRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
          
          if(that.VisitHistListData.length){
            that.noDataShow = false;

          }else{
            // 没有数据显示
            that.noDataShow = true;
          }
        })
      },
      // 跳转到 离店 页面
      toLeaveStore(index){

        var that = this;
        // 保存全局 当前拜访id
        this.$store.state.CurrentVisitId = this.VisitHistListData[index].PRATY_VISIT_ID;
      	this.$router.push({
  	  	  name:"LeaveStore",
          query:{
            pageName:'查看拜访',
            VISIT_STATES:that.VisitHistListData[index].VISIT_STATES,
            ModifyCustomerattached:that.$route.query.ModifyCustomerattached&&that.$route.query.ModifyCustomerattached=='ModifyCustomerInfo'?that.$route.query.ModifyCustomerattached:''
          }
  	  	})
      },
      // 继续拜访
      ContinueVisit(index){
        event.stopPropagation();
        var that = this;
        // 新建 进店 检查库存 建议订单 生动化陈列 离店

        this.$store.state.BackBottle = {
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//收货人信息
          DeliveryAddr_selected:[],//发货人信息
        }
        this.$store.state.OptionsAvailableGift=[];//手动选择赠品页面总数据
        this.$store.state.OptionsGiftChosenCart=[];//手动选择赠品购物车数据
        // 保存全局 当前拜访id
        this.$store.state.CurrentVisitId = this.VisitHistListData[index].PRATY_VISIT_ID;

        if(that.VisitHistListData[index].VISIT_STATES == '拜访中'){

           if(that.VisitHistListData[index].VISIT_STEPS == '进店'){
             that.$router.push({
              name:"CreateNewVisit",
            })
          }else if(that.VisitHistListData[index].VISIT_STEPS == '确认信息'){
             that.$router.push({
              name:"CheckInventory",
            })
          }else if(that.VisitHistListData[index].VISIT_STEPS == '检查库存'){
            that.$router.push({
              name:"ChooseGoods",
              query:{ 
                CurrentStoresId:that.$store.state.CurrentStoresId,//门店id
                visitId:that.$store.state.CurrentVisitId,//拜访id
                whoPush:"HistoryVisit",
                orderType:"SALE_OUT",
              }
            })
          }else if(that.VisitHistListData[index].VISIT_STEPS == '建议订单'){
             that.$router.push({
              name:"VividDisplay",
            })
          }else if(that.VisitHistListData[index].VISIT_STEPS == '生动化陈列'){
             that.$router.push({
              name:"LeaveStore",
            })
          }else{
            that.toLeaveStore(index);
          }

        }else{
          that.toLeaveStore(index);
        }
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqVisitList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqVisitList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .HistoryVisit{
    overflow-x: hidden;
    height: 100%;
    background-color: @GrayBackground;
    header{
  	  background-color:#fff;
  	  color: #000;
  	}
    .container{
      height: 100%;
      margin-top: 0;
      .ModifyMainHead{
        .headerFlex;
        text-align: left;
        top: 89/50rem;
      }
    }
  	.VisitList{
      height: 100%;
      box-sizing: border-box;
      overflow: scroll;
  	  .VisitListItem{
  	  	background-color:#fff;
  	  	margin-bottom: 10/50rem;
  	  	padding: 20/50rem 20/50rem 0;
  	  	overflow: hidden;
  	  	position:relative;
        .visitAddress{
          width: 650/50rem;
          margin-bottom: 5/50rem;
        }
        .VISIT_STATES{
          line-height: 80/50rem;
          height: 80/50rem;
          font-size: 28/50rem;
          &>span{
            line-height: 80/50rem;
            float: left;
          }
          &>div{
            line-height: 80/50rem;
            overflow: hidden;
            float: right;
            i{
              margin-right: 10/50rem;
              float: left;
            }
            span{
              float: left;
            }
          }
        }
  	  	.xiangyou{
  	  	  position: absolute;
  	  	  top: 0;
  	  	  height: 100%;
  	  	  right: 40/50rem;
    		  i{
    		  	position:absolute;
    		  	top: 50%;
    		  	left: 50%;
    		  	transform: translate(-50%;-50%);
      	  	font-size: 40/50rem;
    		  }
  	  	}
    		&>div:not(.xiangyou){
    		  line-height: 60/50rem;
    		  i{
    		  	font-size: 40/50rem;
    		  }
    		}
  	  }
  	}
  }
  .kong{
    height: 80/50rem;
  }
</style>


