<template>
  <div class="SuggestOrder">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="remark">
	  	<div><span>建议订单：</span></div>
	    <textarea v-model="remark" placeholder="请输入备注信息">
	    </textarea>
	  </div>
	  <div class="BtnComponent bottomBtn">
	  	<div @click='toChooseGoods'>帮门店下单</div>
	  	<div @click="toVividDisplay">下一步</div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"建议订单",//页面标题
        remark:'',//建议订单备注
      }
    },
    components:{
  	  componentHeader,
  	},
    created(){
        
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);
      },
      // 帮门店下单 跳转下单页面
      toChooseGoods(){
        this.$router.push({
          name:"ChooseGoods",
        })
      },
      // 下一步
      toVividDisplay(){

        var that = this;

         var ProductData = {
          "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
          "visitId":that.$store.state.CurrentVisitId,//拜访id
          "strRecommendedOrder":that.remark,//备注
        }
        console.log(ProductData)
        // 检查库存确认
        that.httpRequest("GetVisitRecommendedOrder.do",ProductData,function(ProductRes){
           console.log(ProductRes)
           // that.GoodsListData = ProductRes.data;

           that.$router.push({
             name:"VividDisplay",
           })
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SuggestOrder{
  	overflow-x: hidden;
  }
</style>


