<template>
  <div class="SelectSalesman">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  
	  <div class="container haveHeadcontainer">
      <div class="CustomerSearch">
        <div>
          <input  v-model='searchValue' placeholder="按业务员姓名搜索">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="CustomerListItem" @click="Rebound(index)" v-for='(dataItem,index) in StoresListData'  :id="index"  :key='index'>
            <div><span>业务员姓名：</span><span class="CUSTOMER_NAME">{{dataItem.USER_NAME}}</span></div>
            <!-- <div><span>联&nbsp;系&nbsp;人&nbsp;：</span><span>{{dataItem.CONTACT_NAME}}</span></div> -->
            <div><span>联&nbsp;系&nbsp;电&nbsp;话：</span><span>{{dataItem.CELLPHONE}}</span>&nbsp;&nbsp;&nbsp;<i v-if='dataItem.CELLPHONE' @click="callPhone(dataItem.CELLPHONE)" class="iconfont icon-dianhua-copy"></i></div>
            <!-- <div><span>详细地址：</span><span>{{dataItem.CUSTOMER_ADDRESS}}</span></div> -->
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"选择业务员",//页面标题
      	StoresListData:[],//业务员列表
        searchValue:'',//搜索的值
        noDataShow:false,//页面没有数据图标
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      this.reqStoresListInfo();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){

        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoresListInfo();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){

        var that = this;
        if(this.$route.query.whoPush && this.$route.query.whoPush_order =='InventoryManage' && this.$route.query.orderType=='TRANSFER' ){
          this.$router.push({
            name:that.$route.query.whoPush,
            query:{
              whoPush:that.$route.query.whoPush_order,
              orderType:that.$route.query.orderType,
              IFreceORdeliv_transition:that.$route.query.IFreceORdeliv_transition
            }
          })
        }else{
          this.$router.go(-1);
        }
    	},
      // 请求业务员列表
      reqStoresListInfo(load){
        var that = this;

        var ProductData = {
          "storeName":that.searchValue,//货品名称
          "cellphone":that.$store.state.userInfo.cellphone,//电话
          "userId":that.$store.state.userInfo.user_id,//用户id
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        console.log(ProductData)
        // 查询业务员列表
        that.httpRequest("findUserList.do",ProductData,function(StoresRes){
          console.log(StoresRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(StoresRes.data.length){
              that.StoresListData = that.StoresListData.concat(StoresRes.data);
            }
          } else {
            that.StoresListData = StoresRes.data;
          }

          // 判断是否能上拉加载
          if (StoresRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.StoresListData.length){

            that.noDataShow = false;
           }else{
            that.noDataShow = true;
           }
        })
      },
    	// 回跳页面
    	Rebound(idx){

    	  var that = this;

    	  if(this.$route.query.whoPush){

          // 选择商品  下单类型：调拨
          if(this.$route.query.whoPush && this.$route.query.whoPush_order =='InventoryManage' && this.$route.query.orderType=='TRANSFER' ){

             this.$router.push({
              name:that.$route.query.whoPush,
              query:{
                CustomerInfo:{
                  // "CUSTOMER_NAME":that.StoresListData[idx].CUSTOMER_NAME,
                  "CUSTOMER_ID":that.StoresListData[idx].APP_USERS_ID,
                  "CONTACT_NAME":that.StoresListData[idx].USER_NAME,
                  "CONTACT_TEL":that.StoresListData[idx].CELLPHONE,
                  "CUSTOMER_TEL":that.StoresListData[idx].CELLPHONE,
                  // "CUSTOMER_ADDRESS":that.StoresListData[idx].CUSTOMER_ADDRESS,
                },
                whoPush:that.$route.query.whoPush_order,
                orderType:that.$route.query.orderType,
                IFreceORdeliv_transition:that.$route.query.IFreceORdeliv_transition
              }
            })
            return;
          }

    		  this.$router.push({
  		      name:that.$route.query.whoPush,
  		      query:{
  		        CustomerInfo:{
  		          "CUSTOMER_NAME":that.StoresListData[idx].CUSTOMER_NAME,
                "CUSTOMER_ID":that.StoresListData[idx].CUSTOMER_ID,
  		          "CONTACT_NAME":that.StoresListData[idx].CONTACT_NAME,
  		          "CONTACT_TEL":that.StoresListData[idx].CONTACT_TEL,
  		          "CUSTOMER_ADDRESS":that.StoresListData[idx].CUSTOMER_ADDRESS,
  		        }
  		      }
  		    })
  		  }
    	},
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqStoresListInfo("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqStoresListInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SelectSalesman{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .CustomerSearch{
        font-size: 32/50rem;
        position: fixed;
        z-index: 50;
        background-color: #fff;
        top: 89/50rem;
        left: 0;
        .TopSearchBox;
      }
      .ListBox{
        height: 100%;
        overflow: scroll;
        box-sizing:border-box;
        padding-top: 206/50rem;
        .CustomerListItem{
          padding:10/50rem 20/50rem;
          box-sizing: border-box;
          border-bottom: 3/50rem solid @PublicBorderColor;
          &>div{
            line-height: 55/50rem;
            color: #000;
            font-size: 28/50rem;
            &>span:nth-child(2){
              color: #777;
            }
            &>span.CUSTOMER_NAME{
              font-weight:900;
              color: #000;
              font-size: 30/50rem;
            }
          }
        }
      }
    }
    .kong{
      height: 100/50rem;
    }
  }
</style>


