<template>
  <div class="AccessDetails">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="batchInfo" v-for='(itemName,idx) in AccessRecordsArr'  :id="idx"  :key='idx'>
	    <div class="batchLeft">
	      <div>
	      	<span>库存批次：</span>
	      	<span>{{itemName.InventoryBatch}}</span>
	      </div>
	      <div>
	      	<span>生产日期：</span>
	      	<span>{{itemName.DateProduction}}</span>
	      </div>
	      <div>
	      	<span>操作日期：</span>
	      	<span>{{itemName.DateOperation}}</span>
	      </div>
	    </div>
	    <div class="batchRight">
	    	<span v-if="itemName.outboundFlag">-</span><span>{{itemName.productNum}} {{itemName.productUom}}</span>
	    </div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"出入详情",//页面标题
      	AccessRecordsArr:[]
      }
    },
    components:{
	  componentHeader,
	},
    created(){

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .AccessDetails{
    overflow-x: hidden;
    .batchInfo{
    	border-bottom: 10/50rem solid @GrayBackground;
    }
  }
</style>


