<template>
  <div class="SalesPerformanceReport">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

    <div class="container haveHeadcontainer">

      <div class="DateDisplay">
        <div>
          <el-date-picker v-model="StartDate" type="date" placeholder="开始日期" value-format="yyyy-MM-dd"  @change="ChangeStartDate" :editable="false"></el-date-picker>
        </div>
        <span>至</span>
        <div>
          <el-date-picker v-model="EndDate" type="date" placeholder="截止日期"  value-format="yyyy-MM-dd"  @change="ChangeEndDate" :editable="false"></el-date-picker>
        </div>
      </div>  
      <div class="reportSum" v-if='!noDataShow'>
        <div class="title">业绩汇总</div>
        <div class="reportItem" >
          <div>
            <div class="one"><span>下单份数：</span><span>{{orderCountSum}}</span></div>
            <div class="two"><span>订单销售额：</span><span>{{amountSum}}</span></div>
          </div>
          <div>
            <div class="three"><span>订单数量：</span><span>{{pirchaseQTYSum}}</span></div>
            <div class="four"><span>下单客户数：</span><span>{{customerCountSum}}</span></div>
          </div>
          </div>
      </div>  

      <div class="reportInfo">
        <div class="SalesPerformanceItem"  v-for='(dataItem,index) in SalesPerformanceData'  :id="index"  :key='index'>
          <div class="CustomerName"><span>{{dataItem.NAME}}</span></div>
          <div><span>下单份数：</span><span>{{dataItem.orderCount}}</span></div>
          <div><span>订单数量：</span><span>{{dataItem.pirchaseQTY}}</span></div>
          <div><span>订单销售额：</span><span>{{dataItem.amount}}</span></div>
          <div><span>下单客户数：</span><span>{{dataItem.customerCount}}</span></div>
          <div><span>平均单店销售额：</span><span>{{dataItem.avgAmount}}</span></div>
        </div>
      </div>

      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from "jquery"
  export default{
    data(){
      return{
        headerText:"销售业绩报表",//页面标题
        SalesPerformanceData:[],
        StartDate:'',
        EndDate:'',
        noDataShow:false,//页面没有数据图标
        orderCountSum:'',//下单份数汇总
        pirchaseQTYSum:'',//订单数量汇总
        amountSum:'',//订单销售额汇总
        customerCountSum:'',//下单客户数汇总
      }
    },
    components:{
      componentHeader,
    },
    created(){
       this.StartDate = this.getNowTime('getMonthFirstDay'); 
       this.EndDate = this.getNowTime('getDate'); 
       this.GetSalesPerformance();
       
       if(this.SalesPerformanceData.length){
        console.log(111)
        
       }
       
    },
    methods:{
      // 返回上一页
      f_Fanhui(){
       this.$router.go(-1);
      },

      ChangeStartDate(Value){
        this.StartDate = Value; 
        this.GetSalesPerformance();
      }, 

      ChangeEndDate(Value){
        if(this.StartDate > Value ){
          this.$alert('截止时间不可小于开始时间', '提示', {
            confirmButtonText: '确定',
          })
          this.EndDate = this.getNowTime('getDate'); 
        }else{
          this.EndDate = Value; 
          this.GetSalesPerformance();
        }
      },

      // 获取门店信息
      GetSalesPerformance(){
        var that = this;
        var storesData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id 
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "StartDate":that.StartDate,
          "EndDate":that.EndDate,
        }
        // 获取销售业绩信息
        that.httpRequest("salesPerformanceSummary.do",storesData,function(saleRes){

          that.SalesPerformanceData = saleRes.data;
          that.orderCountSum = 0;
          that.pirchaseQTYSum = 0;
          that.amountSum = 0;
          that.customerCountSum = 0;
          if(that.SalesPerformanceData.length){
            that.noDataShow = false;
            for(var i=0; i < that.SalesPerformanceData.length;i++){
              that.orderCountSum += that.SalesPerformanceData[i].orderCount;
              that.pirchaseQTYSum += that.SalesPerformanceData[i].pirchaseQTY;
              that.amountSum += that.SalesPerformanceData[i].amount;
              that.customerCountSum += that.SalesPerformanceData[i].customerCount;
            }
          }else{
            that.noDataShow = true;
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SalesPerformanceReport{
    overflow-x: hidden;
    .container{
      .DateDisplay{
        height: 100/50rem;
        line-height: 100/50rem;
        padding:0 60/50rem;
        background-color:#EEEFF2;
         &>div:first-child{
          float: left;
          width: 260/50rem;
         }
         span{ 
          float: left;
          width: 110/50rem; 
          text-align: center;
        }
         &>div:last-child{
          float: right;
          width: 260/50rem;
        }
      }
      .reportSum{
        width: 100%;
        height: 150/50rem;
        .title{
          line-height: 45/50rem;
          font-size: 25/50rem;
          padding-left: 30/50rem;
          font-weight: 550;
        }
        .reportItem{
          .one,.three{
            width: 48%;
            line-height: 50/50rem;
            float: left;
            padding-left: 30/50rem;
          }
          .two,.four{
            width: 48%;
            line-height: 50/50rem;
            float: left;
          }
        }
          border-bottom: 10/50rem solid #EEEFF2;
        

      }
      .reportInfo{
        .SalesPerformanceItem{
          padding: 15/50rem 30/50rem;
          line-height: 45/50rem;
          border-bottom: 10/50rem solid #EEEFF2;
          color: #555;
          .CustomerName{
            line-height: 60/50rem;
            font-size: 32/50rem;
            font-weight: 550;
          }
        }
      }

      #main{
        margin: 60/50rem 0;
      }
    }
  }

  ::-webkit-scrollbar {
    /*隐藏滚轮*/
    display: none;
  }
</style>


