<template>
  <div class="ChooseAddress">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer" >
      <div class="line" @click='toChooseAddress(index)' v-for='(dataItem,index) in AddressListData'  :id="index"  :key='index' >
        {{dataItem.name}}
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"选择地址",//页面标题
        AddressListData:[],//地址列表
        Address:'',//地址
        AddressSum:'',//地址汇总
        city_master_id:'',//上一级地址id
        AddressInfo:'',//地址信息
        types:'',//地址类型
        province:'',//省
        cityChinese:'',//市
        districict:'',//区
        street:'',//街道

        CustomerName:'',
        Channel:'',
        VisitRouteName:'',
        VisitRouteNameInsert:'',
        selectedLine:[],
        CustomerRemark:'',
        AddressDetail:'',
        longitude:'',
        latitude:'',


      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      var that = this;
      var AddressData = {
          "types":'PROVINCE',//地址类型(省、市区、街道)
          "city_master_id":that.AddressInfo.city_master_id,//上级地址id
          }
      // 获取门店信息
      that.httpRequest("findCityByType.do",AddressData,function(AddressRes){
            that.AddressListData = AddressRes.data;
      })
      that.AddressSum += that.Address;
      //获取新增门店页面已填写的值
      if(that.$route.query.CustomerName){
        that.CustomerName = that.$route.query.CustomerName;
      }
       if(that.$route.query.Channel){
        that.Channel = that.$route.query.Channel;
      }
       if(that.$route.query.VisitRouteId){
        that.VisitRouteId = that.$route.query.VisitRouteId;
      }
       if(that.$route.query.VisitRouteName){
        that.VisitRouteName = that.$route.query.VisitRouteName;
      }
      if(that.$route.query.VisitRouteNameInsert){
        that.VisitRouteNameInsert = that.$route.query.VisitRouteNameInsert;
      }
      if(that.$route.query.selectedLine){
        that.selectedLine = that.$route.query.selectedLine;
      }
      
       if(that.$route.query.CustomerRemark){
        that.CustomerRemark = that.$route.query.CustomerRemark;
      }
       if(that.$route.query.AddressDetail){
        that.AddressDetail = that.$route.query.AddressDetail;
      }
       if(that.$route.query.longitude){
        that.longitude = that.$route.query.longitude;
      }
       if(that.$route.query.latitude){
        that.latitude = that.$route.query.latitude;
      }

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
        console.log(11)
    		this.$router.go(-1);
    	},

     // 跳转到 下一级地址页面
      toChooseAddress(index){
        var that = this;
        var CurrAddressInfo = that.AddressListData[index];
        that.Address = that.AddressListData[index].name;
        
        if(!that.province){
          that.province=that.Address;
        }else if(!that.cityChinese){
          that.cityChinese=that.Address;
        }else if(!that.districict){
          that.districict=that.Address;
        }else if(!that.street){
          that.street=that.Address;
        }

        that.AddressSum += that.Address;
        if(CurrAddressInfo.types!=""){
          that.selectAddress(CurrAddressInfo);
        }else{
          //将所有以获取的值存到全局
          that.$store.state.StoreInfo.Location = that.AddressSum,
          that.$store.state.StoreInfo.province = that.province,//省
          that.$store.state.StoreInfo.cityChinese = that.cityChinese,//市
          that.$store.state.StoreInfo.districict = that.districict,//区
          that.$store.state.StoreInfo.street = that.street//街道
          if(this.$route.query.create_whoPush && this.$route.query.create_whoPush =='WeChat'){
            this.$router.push({
              name:"CreateCustomerWeChat",
            })
          }else{
            this.$router.push({
              name:"CreateNewCustomer",
            })
          }
        }
      },

      //查询地址
      selectAddress(AddressInfo){
        var that = this;
        var AddressData = {
          "types":AddressInfo.types,//手机号
          "city_master_id":AddressInfo.city_master_id,//上级地址id
        }
        // 获取门店信息
        that.httpRequest("findCityByType.do",AddressData,function(AddressRes){
          that.AddressListData = AddressRes.data;
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ChooseAddress{
    overflow-x: hidden;
    text-align: center;
    .line{
      width: 100%;
      height: 80/50rem;
      line-height: 80/50rem;
      border-bottom:1/50rem solid @PublicBorderColor;



    }
  }
</style>


