<template>
  <div class="WarehousingDetail">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="CustomerSearch">
        <div>
          <input v-model='searchValue' placeholder="按 入库单号 或 供货商 搜索">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore  topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class='putWarehouseList' @click='toPutDetails(Outboundindex)' v-for='(OutboundItem,Outboundindex) in OutboundOrderList'  :id="Outboundindex"  :key='Outboundindex'>
            <div>入库单号：<span>{{OutboundItem.IN_NO}}</span></div>
            <div v-if="OutboundItem.TMS_NO">采购单号：<span>{{OutboundItem.TMS_NO}}</span></div>
            <div v-if="OutboundItem.OUT_NO">原销售单号：<span>{{OutboundItem.OUT_NO}}</span></div>
            <div>供&nbsp;应&nbsp;商&nbsp;：<span>{{OutboundItem.CONSIGNOR_NAME}}</span></div>
            <div>下&nbsp;单&nbsp;人&nbsp;：<span>{{OutboundItem.ADDWHO}}</span></div>
            <div>创建日期：<span>{{OutboundItem.ADDDATE}}</span><span class='float red' style='color:#28B45F;'>{{OutboundItem.TRANSFER_TYPE_text}}</span></div>
            <div>下单数量：<span>{{OutboundItem.ORDER_QTY}}</span><span class='float red' :style='{color:OutboundItem.STATUS_color}'>{{OutboundItem.STATUS_text}}</span></div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"入库列表",//页面标题
        OutboundOrderList:[],//入库单列表
        noDataShow:false,//没有数据图标 是否显示
        searchValue:'',//搜索的值
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      this.reqPutInList();
    },
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqPutInList();
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.push({
          name:"InventoryManage",
        })
    	},
      // 请求入库单列表
      reqPutInList(load){
        var that = this;

        var LineData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "strConsignorName":that.searchValue,//搜索 入库单号 或 供应商
          "transferType":'',//类型 区分于采购入库列表查询
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }
        console.log(LineData)

        // 获取入库订单列表
        that.httpRequest("searchTransferReceipt.do",LineData,function(SupRes){
           console.log(SupRes)

           for(var i =0;i<SupRes.data.length;i++){

            if(SupRes.data[i].TRANSFER_TYPE == 'PURCHASE_IN'){

              SupRes.data[i].TRANSFER_TYPE_text = '采购入库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'OTHER_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '其它入库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN_ALL'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '销售退货入库(全退)';

            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '销售退货入库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN_PART'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '销售退货入库(部分)';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'TRANSFER_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '调拨入库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '销售出库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'TRANSFER_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '调拨出库';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'PURCHASE_RETURN_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '采购退货出库';
            }

            if(SupRes.data[i].STATUS == 'NEW'){

              SupRes.data[i].STATUS_color = '#FE5256';
              SupRes.data[i].STATUS_text = '新建';

            }else if(SupRes.data[i].STATUS == 'FINISH'){

              SupRes.data[i].STATUS_color = '#28B45F';
              SupRes.data[i].STATUS_text = '已审核';

            }else if(SupRes.data[i].STATUS == 'CANCEL'){

              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = '此入库单已取消';
            }else{
              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = SupRes.data[i].STATUS;
            }
          }

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          //上拉加载后 数据拼接
          if (that.loadPage > 1) {

            if(SupRes.data.length){
              that.OutboundOrderList = that.OutboundOrderList.concat(SupRes.data);
            }
          } else {
            that.OutboundOrderList = SupRes.data;
          }

          // 判断是否能上拉加载
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.OutboundOrderList.length){
            that.noDataShow = false;
            
          }else{
            // 没有数据
            that.noDataShow = true;
          }
        })
      },
      // 跳转到明细页面
      toPutDetails(idx) {
        var that = this;
        this.$router.push({
          name:"PutInStorageDetails",
          query:{
            TRANSFER_ID:that.OutboundOrderList[idx].TRANSFER_ID
          }
        })
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqPutInList("TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqPutInList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .WarehousingDetail{
    height: 100%;
    .container{
      margin-top: 0;
      height: 100%;
      .CustomerSearch{
        font-size: 32/50rem;
        position: fixed;
        z-index: 50;
        background-color: #fff;
        top: 89/50rem;
        left: 0;
        .TopSearchBox;
      }
      .ListBox{
        padding-top:189/50rem;
        height: 100%;
        box-sizing:border-box;
      }
    }
    .putWarehouseList{
      width:100%;
      color: #555;
      padding: 20/50rem 15/50rem;
      box-sizing: border-box;
      border-bottom: 20/50rem solid #EEEFF2;
      background-color: #fff;
      div{
        line-height: 46/50rem;
      }
    }
    .float{
      float: right;
    }
  }
  .kong{
    height: 100/50rem;
  }
</style>


