//http请求 测试服务器
Vue.prototype.httpRequest=function (url,params,success,isLoading_){

  var that = this;

  if(isLoading_ && isLoading_ == 'isLoading_'){

    that.$emit('isLoading_', true,"拼命加载中");

  }else{

    that.$emit('isLoading', true,"拼命加载中");
  }

  Axios.post(that.HOST+url,{"params": JSON.stringify(params)})
  .then(function(res){
  console.log(res)
    
  if(isLoading_ && isLoading_ == 'isLoading_'){

    that.$emit('isLoading_', false);

  }else{

    that.$emit('isLoading', false);
  }

  if(res.data.status==1){
    success(res.data);

  }else{

    var msg = "请求失败";
    if(res.data.Msg){

      msg = res.data.Msg;
    }
    that.$message.error(msg);
  }
  })
  .catch(function(res){

  if(isLoading_ && isLoading_ == 'isLoading_'){

    that.$emit('isLoading_', false);

  }else{

    that.$emit('isLoading', false);
  }
  that.$message.error("请求失败");
  })
}
// -----------------------------------------------------设置cookie （手机号，密码） 开始--------------------------------------------------------------

//设置cookie
Vue.prototype.setCookie = function(c_name,c_pwd,exdays) {

    var exdate=new Date();//获取时间

    exdate.setTime(exdate.getTime() + 24*60*60*1000*exdays);//保存的天数

    //字符串拼接cookie
    window.document.cookie="easyToSellUserName"+ "=" +c_name+";path=/;expires="+exdate.toGMTString();
    window.document.cookie="easyToSellUserPwd"+"="+c_pwd+";path=/;expires="+exdate.toGMTString();
}
//读取cookie
Vue.prototype.getCookie = function (e) {

    if (document.cookie.length>0) {

      var arr=document.cookie.split('; ');//这里显示的格式需要切割一下自己可输出看下

      for(var i=0;i<arr.length;i++){

        var arr2=arr[i].split('=');//再次切割

        //判断查找相对应的值
        if(arr2[0]=='easyToSellUserName'){

          this.userName=arr2[1];//保存到保存数据的地方

        }else if(arr2[0]=='easyToSellUserPwd'){

          this.password=arr2[1];
        }
      }
    }
}
//清除cookie
Vue.prototype.clearCookie = function () {

    this.setCookie("","",-1);//修改2值都为空，天数为负1天就好了
}
