// 冒泡排序
Vue.prototype.BubbleSort=function (arr,FieldName,SortCondition){

    if(SortCondition){
      var newArr = arr.sort(function sequence(a,b){
          return a[FieldName] - b[FieldName];
      })
      
    }else{
      var newArr = arr.sort(function sequence(a,b){
          return b[FieldName] - a[FieldName];
      })
    }
  
    return newArr;
}
