<template>
  <div class="PerformanceTrack">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	<div class="container haveHeadcontainer">
    <div class="DataPicker">
        <div class="period">
          <div class="month" @click='toMonth'>月</div>
          <div class="quarter" @click='toQuarter'>季</div>
          <div class="year" @click='toYear'>年</div>
        </div>
        <div class="elDate">
          <el-date-picker  v-model="InventoryMonth" type="month" :editable='false'  placeholder="选择月" format="yyyy 年 MM 月" value-format="yyyy-MM"></el-date-picker>
        </div>
      </div>
    <div class="CustomerVisitStatistics AssessmentSales">
      <div class="StatisticsHead">
        <div><span>销售量考核</span></div>
        <div><!-- <span>本月</span><i class="iconfont icon-xiangxia"></i> --></div>
      </div>
      <div>
        <div>
          <div>{{TargetNum}}</div>
          <div>目标销售量</div>
        </div>
        <div>
          <div>{{ActualNum}}</div>
          <div>实际销售量</div>
        </div>
        <div>
          <div>{{NumRate}}</div>
          <div>达成率</div>
        </div>
      </div>
    <!--   <div class="SalesReports">
        <div id="SalesReports" style="width:100%;height: 300px;"></div>
      </div> -->
    </div>
    <div class="CustomerVisitStatistics AssessmentSales">
      <div class="StatisticsHead">
        <div><span>销售额考核</span></div>
        <div><!-- <span>本月</span><i class="iconfont icon-xiangxia"></i> --></div>
      </div>
      <div>
        <div>
          <div>{{TargetMoney}}</div>
          <div>目标销售额</div>
        </div>
        <div>
          <div>{{ActualMoney}}</div>
          <div>实际销售额</div>
        </div>
        <div>
          <div>{{MoneyRate}}</div>
          <div>达成率</div>
        </div>
      </div>
      <div class="SalesReports">
        <div id="SaleroomReports" style="width:100%;height: 300px;"></div>
      </div>
    </div>
    <!-- 暂不开放 -->
   <!--  <div class="CustomerVisitStatistics DevelopCustomerStatistics">
      <div class="StatisticsHead">
        <div><span>开发客户统计</span></div>
        <div><span>本月</span><i class="iconfont icon-xiangxia"></i></div>
      </div>
      <div>
        <div>
          <div>10</div>
          <div>订单量</div>
        </div>
        <div>
          <div>10</div>
          <div>新开发客户</div>
        </div>
        <div>
          <div>100</div>
          <div>客户总量</div>
        </div>
      </div>
    </div>
    <div class="CustomerVisitStatistics stockStatistics">
      <div class="StatisticsHead">
        <div><span>进货统计</span></div>
        <div><span>本月</span><i class="iconfont icon-xiangxia"></i></div>
      </div>
      <div>
        <div>
          <div>10</div>
          <div>订单量</div>
        </div>
        <div>
          <div>10</div>
          <div>进货金额（元）</div>
        </div>
        <div>
          <div>100%</div>
          <div>达成率</div>
        </div>
      </div>
    </div> -->
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import echarts from 'echarts'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"业绩追踪",//页面标题
        InventoryMonth:'',//年月选择框的值
        TargetNum:0,
        ActualNum:0,
        NumRate:0,
        TargetMoney:0,
        ActualMoney:0,
        MoneyRate:0,
        SalesNumdata:{},
        SalesMoneydata:{},
        crrentDateFormat:'MONTH',//当前是选择月还是年
      }
    },
    components:{
	    componentHeader,
	  },
    watch:{
      // 月份的值
      InventoryMonth(val, oldVal){
        this.GetData();
      }
    },
    created(){

      $(".month").css({"background-color":"#1e8cf8",'color':'#fff'});
      this.InventoryMonth = new Date();
      // this.GetData();
    },
    mounted(){
      // this.$nextTick(function() {
      //   this.drawArea('SalesReports');
      //   this.drawArea('SaleroomReports');
      // })
    },
    
    methods:{
      // 返回上一页
      f_Fanhui(){
      	this.$router.go(-1);
      },
      GetRate(){
        var that = this;
            that.NumRate = (that.ActualNum/that.TargetNum*100).toFixed(1)+"%";
            that.MoneyRate = (that.ActualMoney/that.TargetMoney*100).toFixed(1)+"%";
      },
      GetData(){
        var that = this;
        var postData = {
          "appUserId":that.$store.state.userInfo.user_id,
          "dateTime":that.InventoryMonth,
        }
        //获取销售量
        that.httpRequest("GetSalesVolumeNum.do",postData,function(NumRes){
              that.SalesNumdata = NumRes.data;
              if(that.SalesNumdata.lenght){
              if(that.crrentDateFormat == "MONTH"){
                that.TargetNum = that.SalesNumdata.MONTH.TARGET_NUM;
                that.ActualNum = that.SalesNumdata.MONTH.ACTUAL_NUM;
                that.GetRate();
              }
              else if(that.crrentDateFormat == "QUARTER"){
                that.TargetNum = that.SalesNumdata.QUARTER.TARGET_NUM;
                that.ActualNum = that.SalesNumdata.QUARTER.ACTUAL_NUM;
                that.GetRate();
              }
              else if(that.crrentDateFormat == "YEAR"){
                that.TargetNum = that.SalesNumdata.YEAR.TARGET_NUM;
                that.ActualNum = that.SalesNumdata.YEAR.ACTUAL_NUM;
                that.GetRate();
              }
              }
        })
        //获取销售额
        that.httpRequest("GetSalesVolumeMoney.do",postData,function(MoneyRes){
              that.SalesMoneydata = MoneyRes.data;
              if(that.SalesMoneydata.lenght){
              if(that.crrentDateFormat == "MONTH"){
                that.TargetMoney = that.SalesMoneydata.MONTH.TARGET_MONEY;
                that.ActualMoney = that.SalesMoneydata.MONTH.ACTUAL_MONEY;
                that.GetRate();
              }
              else if(that.crrentDateFormat == "QUARTER"){
                that.TargetMoney = that.SalesMoneydata.QUARTER.TARGET_MONEY;
                that.ActualMoney = that.SalesMoneydata.QUARTER.ACTUAL_MONEY;
                that.GetRate();
              }
              else if(that.crrentDateFormat == "YEAR"){
                that.TargetMoney = that.SalesMoneydata.YEAR.TARGET_MONEY;
                that.ActualMoney = that.SalesMoneydata.YEAR.ACTUAL_MONEY;
                that.GetRate();
              }
              }
        })
      },
      //选择月
      toMonth(){
        var that = this;
        that.crrentDateFormat = "MONTH";
        $(".month").css({"background-color":"#1e8cf8",'color':'#fff'})
        $(".quarter").css({"background-color":"#fff",'color':'#000'})
        $(".year").css({"background-color":"#fff",'color':'#000'})
        that.TargetNum = that.SalesNumdata.MONTH.TARGET_NUM;
        that.ActualNum = that.SalesNumdata.MONTH.ACTUAL_NUM;
        that.TargetMoney = that.SalesMoneydata.MONTH.TARGET_MONEY;
        that.ActualMoney = that.SalesMoneydata.MONTH.ACTUAL_MONEY;
        that.GetRate();
      },
      //选择季
      toQuarter(){
        var that = this;
        that.crrentDateFormat = "QUARTER";
        $(".quarter").css({"background-color":"#1e8cf8",'color':'#fff'})
        $(".month").css({"background-color":"#fff",'color':'#000'})
        $(".year").css({"background-color":"#fff",'color':'#000'})
        that.TargetNum = that.SalesNumdata.QUARTER.TARGET_NUM;
        that.ActualNum = that.SalesNumdata.QUARTER.ACTUAL_NUM;
        that.TargetMoney = that.SalesMoneydata.QUARTER.TARGET_MONEY;
        that.ActualMoney = that.SalesMoneydata.QUARTER.ACTUAL_MONEY;
        that.GetRate();
      },
      // 选择年
      toYear(){
        var that = this;
        that.crrentDateFormat = "YEAR";
        $(".year").css({"background-color":"#1e8cf8",'color':'#fff'})
        $(".month").css({"background-color":"#fff",'color':'#000'})
        $(".quarter").css({"background-color":"#fff",'color':'#000'})
        that.TargetNum = that.SalesNumdata.YEAR.TARGET_NUM;
        that.ActualNum = that.SalesNumdata.YEAR.ACTUAL_NUM;
        that.TargetMoney = that.SalesMoneydata.YEAR.TARGET_MONEY;
        that.ActualMoney = that.SalesMoneydata.YEAR.ACTUAL_MONEY;
        that.GetRate();
      },
      
      // drawArea(id) {
      //   this.charts = echarts.init(document.getElementById(id))
      //   this.charts.setOption({
      //     xAxis: {
      //       type: 'category',
      //       boundaryGap: false,
      //       data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
      //     },
      //     yAxis: {
      //       type: 'value'
      //     },
      //     series: [{
      //       data: [820, 932, 901, 934, 1290, 1330, 1320],
      //       type: 'line',
      //       areaStyle: {},
      //       markLine:{
      //         itemStyle:{
      //           normal:{
      //             lineStyle:{type:'solid',color:'#0078D7'},
      //             label:{show:true,position:'left'}
      //           }
      //         },
      //         large:true,
      //         effect:{
      //           show: false,
      //           loop: true,
      //           period: 0,
      //           scaleSize : 2,
      //           color : null,
      //           shadowColor : null,
      //           shadowBlur : null
      //         },
      //         data:[
      //           [
      //             {name: '标线1起点', value: 400, xAxis: 'Mon', yAxis: 400},      // 当xAxis为类目轴时，数值1会被理解为类目轴的index，通过xAxis:-1|MAXNUMBER可以让线到达grid边缘
      //             {name: '标线1终点', xAxis: 'Sun', yAxis: 400},             // 当xAxis为类目轴时，字符串'周三'会被理解为与类目轴的文本进行匹配
      //           ],
      //         ]
      //       }
      //     }]
      //   })
      // },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .PerformanceTrack{
    overflow-x: hidden;
    min-height:100%;
    background-color: @GrayBackground;
    .container{
      .AssessmentSales{
        height: 300/50rem;
        .SalesReports{
          padding-left:20/50rem;
        }
        .StatisticsHead{
          border-top: 1/50rem solid @PublicBorderColor;
        }
      }
      .DataPicker{
        overflow: hidden;
        width: 100%;
        height: 80/50rem;
        margin-top: 20/50rem;
        text-align: center;
        line-height: 74/50rem;
        .elDate{
          width: 200/50rem;
          height: 55/50rem;
          float: right;
          margin-right: 15%;
        }
        .period{
          margin-left:14%;
          font-size: 30/50rem;
          width: 200/50rem;
          height: 65/50rem;
          float: left;
          padding-top: 6/50rem;
          .month,.year{
            width: 65/50rem;
            line-height: 65/50rem;
            border:1/50rem solid @PublicBorderColor;
            float: left;
          }
          .month{
            background-color: #1e8cf8;
            color: #fff;
          }
          .quarter{
            float: left;
            width: 65/50rem;
            line-height: 65/50rem;
            border-top: 1/50rem solid @PublicBorderColor;
            border-bottom: 1/50rem solid @PublicBorderColor;
          }
        }
      }
    }
  }
</style>


