<template>
  <div class="InventoryQuery" >
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer" style="overflow: scroll;">
      <div class="headFixed">
        <div class="CustomerSearch">
          <div>
            <input v-model='searchValue' placeholder="按商品名称搜索">
            <i class="iconfont icon-sousuo"></i>
          </div>
        </div>
        <div class="ConditionsSort">
          <div class="tabHead">
            <div><span class="tabHead_active" @click="SwitchBtnWarehouse">仓库库存</span></div>
            <div><span @click="SwitchBtnCar">车销库存</span></div>
          </div>

          <div class='ChooseLicensePlate' v-show='LicensePlate_Show'>
            <div class='CurrentLicensePlate'  @click='SelectLicensePlate'>
              <div>
                <span>{{PlateDisplay}}</span>
                <i v-if='LicenseList&&LicenseList.length>1' class='iconfont icon-xiangxia'></i>
              </div>
            </div>
            <div class='LicenseList'>
              <div v-for='(LicenseItem,LicenseIdx) in LicenseList'  :id="LicenseIdx"  :key='LicenseIdx' @click='ChooseLicense(LicenseIdx)'>{{LicenseItem.USER_NAME}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore   topDropText="下拉刷新"   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="上拉加载" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class="ListContent">
          <!--  @click='toInventoryDetails' -->
          <div class="ListItem" v-for='(itemName,idx) in goodsArr'  :id="idx"  :key='idx'>
            <div class="listLeft">
              <div>{{itemName.PRODUCT_NAME}}</div>
              <div><span>产品编号：</span><span>{{itemName.PRODUCT_CODE}}</span></div>
            </div>
            <div class="listCenter">
              <span>{{dealQty(itemName)}}</span>
            </div>
           <!--  <div class="listRight">
              <i class="iconfont icon-tiaozhuanqianwangyoujiantouxiangyouxiayibuxianxing"></i>
            </div> -->
          </div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span>没有数据</span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"库存查询",//页面标题
        totalNum:0,//总库存数量
        SortCondition:false,//库存数量排序
        ObjectType:'OSP',//当前是门店还是客户
        searchValue:'',//搜索的值
        goodsArr:[],//页面数据列表
        noDataShow:false,//没有数据显示
        numberPerPage:15,//每页显示几条
        loadPage: 1 ,//当前第几页
        allLoaded:true,//上拉刷新还能上拉刷新
        autoFill:false,//
        LicensePlate_Show:false,//车是否显示
        LicensePlateList_Show:false,//车列表是否显示
        LicenseList:[],//车列表
        PlateDisplay:'',//当前展示的车牌
        UserIdx:'',//选中的车的id
      }
    },
    components:{
	    componentHeader,
	  },
    mounted(){
      var LowerBodyHeight = parseFloat($('.headFixed').css('height')) + parseFloat($('header').css('height')) + 'px';

      $('.ListBox').css('paddingTop',LowerBodyHeight);
    },
    created(){
      this.SwitchBtnWarehouse();
    }, 
    watch:{

      // 搜索input框的值
      searchValue(val, oldVal){
        var that = this;
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects(that.ObjectType);
      }
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	},
      // 处理库存数量，显示效果 --> 7箱3瓶
      dealQty(itemName){
        // 没有大单位
        if(itemName.UNIT_BX_STR == ""){
          return itemName.QUANTITY +  itemName.UNIT_STR;
        }
        // 有大单位
        else{
          // 向下取整
          let bigQty = Math.floor(itemName.QUANTITY / itemName.BX_ZHL);
          let littleQty = itemName.QUANTITY % itemName.BX_ZHL;
          var r = "";
          if(bigQty != 0){
            r = bigQty + itemName.UNIT_BX_STR;
          }
          if(littleQty != 0){
            r = r + littleQty +  itemName.UNIT_STR;
          }
          return r;
        }
      },
      // 查询库存列表
      SwitchRegObjects(ObjectType,load){
        var that = this;

        this.ObjectType = ObjectType;

        if(ObjectType == 'OSP'){
          $('.tabHead>div:eq(0)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(1)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','block');

        }else if(ObjectType == 'CarSales'){
          $('.tabHead>div:eq(1)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(0)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','none');
        }

        var appUserId;
        if(that.LicenseList.length>0){
          appUserId = that.LicenseList[that.UserIdx].APP_USERS_ID;
        }else{
          appUserId = that.$store.state.userInfo.user_id;
        }

        var histData = {
          "types":ObjectType,//类型：门店/客户
          "appUserId":appUserId,//用户id
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "strProductName":that.searchValue,//按照商品名称搜索
          "currentPage":that.loadPage,//当前页数
          "numberPerPage":that.numberPerPage,//一页多少条
        }

        console.log(histData)
        // 获取库存数据列表
        that.httpRequest("searchTransferInventory.do",histData,function(histRes){
          console.log(histRes)
          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          //上拉加载后 数据拼接
          if (that.loadPage > 1) {
            if(histRes.data.detail.length){
              that.goodsArr = that.goodsArr.concat(histRes.data.detail);
            }
          } else {
            that.goodsArr = histRes.data.detail;
          }
          // 判断是否能上拉加载
          if (histRes.data.detail.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          that.totalNum = histRes.data.sumQTY;
          
          if(that.goodsArr.length){
            that.noDataShow = false;

          }else{
            // 没有数据显示
            that.noDataShow = true;
          }
        })
      },
      // 切换成 仓库库存
      SwitchBtnWarehouse(){
        this.LicensePlate_Show = false;
        this.UserIdx = '';
        this.LicenseList = [];

        this.$nextTick(function() {

          var LowerBodyHeight = parseFloat($('.headFixed').css('height')) + parseFloat($('header').css('height')) + 'px';
          $('.ListBox').css('paddingTop',LowerBodyHeight);
        })
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('OSP');
      },
      // 切换成 车销
      SwitchBtnCar(){
        var that = this;

        var ProductData = {
          "storeName":'',//货品名称
          "cellphone":that.$store.state.userInfo.cellphone,//电话
          "userId":that.$store.state.userInfo.user_id,//用户id
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
        }
        console.log(ProductData)
        // 查询业务员列表
        that.httpRequest("findUserList.do",ProductData,function(Res){
          console.log(Res)
          if(Res.data.length){
            that.PlateDisplay = Res.data[0].USER_NAME;

            that.LicenseList = Res.data;
            that.UserIdx = 0;//当前查看第一个车的库存

            that.LicensePlate_Show = true;
            that.$nextTick(function() {

              var LowerBodyHeight = parseFloat($('.headFixed').css('height')) + parseFloat($('header').css('height')) + 'px';
              $('.ListBox').css('paddingTop',LowerBodyHeight);
            })
            that.loadPage = 1;
            that.allLoaded = false;
            that.SwitchRegObjects('CarSales');
          }else{

            that.LicenseList = [];
            that.$alert("未绑定业务员", '提示', {
              confirmButtonText: '确定',
            })
          }
        })
      },
      // 选中车
      ChooseLicense(idx){

        this.PlateDisplay = this.LicenseList[idx].USER_NAME;
        $('.LicenseList').animate({'height':0},500);
        $('.icon-xiangxia').css({'transform':'rotate(0)'})
        this.LicensePlateList_Show = false;
        this.UserIdx = idx;//当前查看选中的车的库存
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('CarSales');
      },
      // 选择车牌号下拉框出现
      SelectLicensePlate(){

        if(this.LicenseList.length<=1){
          return;
        }

        if(!this.LicensePlateList_Show){
          let LicenseItemHeight = parseFloat($('.LicenseList>div').css('height'));

          let LicenseHeight = this.LicenseList.length * (LicenseItemHeight + 1);

          $('.LicenseList').animate({'height':LicenseHeight},500);
          $('.icon-xiangxia').css({'transform':'rotate(180deg)'})

        }else{

          $('.LicenseList').animate({'height':0},500);
          $('.icon-xiangxia').css({'transform':'rotate(0)'})
        }
        this.LicensePlateList_Show = !this.LicensePlateList_Show;
      },
      // 跳转页面 库存详情 
      toInventoryDetails(){
        this.$router.push({
          name:"InventoryDetails",
        })
      },
      // 下拉刷新
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.SwitchRegObjects(that.ObjectType,"TopLoad");
      },
      // 上拉加载
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.SwitchRegObjects(that.ObjectType,"BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .InventoryQuery{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .ListBox{
        height: 100%;
        overflow: scroll;
        box-sizing:border-box;
        padding-top:273/50rem;
      }
    }
    .headFixed{
      width: 100%;
      position:fixed;
      background-color:#fff;
      top: 89/50rem;
      left: 0;
      z-index: 50;
      .CustomerSearch{
        .TopSearchBox;
      }
      .InventoryTotal{
        padding:10/50rem 0;
        border-bottom:10/50rem solid #f0f0f0;
        background-color: #fff;
        .totalNum{
          text-align: center;
          &>span{
            line-height: 60/50rem;
            font-size: 44/50rem;
            font-weight: 600;
          }
          &>div{
            line-height: 60/50rem;
            font-size: 34/50rem;
            i{
              font-size: 45/50rem;
            }
          }
        }
      }
      .ConditionsSort{
        width: 100%;
        background-color:#fff;
        .tabHead{
          line-height: 80/50rem;
          border-bottom:2/50rem solid @PublicBorderColor;
          margin-bottom: 0;
          margin-top: 0;
          background-color: #fff;
          width: 100%;
          overflow-x: hidden;
          box-sizing: border-box;
          &>div{
            padding-left:50/50rem;
            text-align: center;
            float: left;
            span.tabHead_active{
              padding-bottom: 10/50rem;
              color: @PublicColor;
              border-bottom:3/50rem solid @PublicColor;
            }
          }
        }
        /* 头部 */
        .ChooseLicensePlate{
          width: 100%;
          border-bottom: 1/50rem solid #e0e0e0;
          background-color: #fff;
          font-size:26/50rem;
          position:relative;
          .CurrentLicensePlate{
            width: 100%;
            height: 70/50rem;
            line-height: 70/50rem;
            text-align: center;
            div{
              display: inline-block;
              color: @PublicColor;
              overflow: hidden;
              span{
                float: left; 
                display: inline-block;
                line-height: 70/50rem;
              }
              .icon-xiangxia{
                float: left; 
                font-size: 34/50rem;
                margin-left: 15/50rem;
                transition: all 0.5s;
              }
            }
          }
          /* 车牌选择 */
          .LicenseList{
            width:100%;
            max-height: 700/50rem;
            height: 0;
            overflow: scroll;
            text-align: center;
            position: absolute;
            color: #fff;
            top:71/50rem;
            left: 0;
            z-index: 50;
            background-color: rgba(13,111,222,0.8);
          }
          .LicenseList>div{
            width: 100%;
            height: 70/50rem;
            line-height: 70/50rem;
            border-bottom: 1px solid #E4E4E6;
          }
        }
      }
    }
    .ListContent{
      background-color:#fff;
      padding:0 20/50rem;
      overflow: hidden;
        box-sizing:border-box;
      .ListItem{
        width: 100%;
        // border-bottom: 8/50rem solid @PublicBorderColor;
        margin-top: 20/50rem;
        background-color:#f0f0f0;
        box-sizing:border-box;
        border-radius: 12/50rem;
        padding: 10/50rem 20/50rem;
        display: table;
        &>div{
          display: table-cell;
          color: #666;
        }
        .listLeft{
          width: 500/50rem;
          padding-top:10/50rem;
          &>div{
            line-height: 60/50rem;
            &:first-child{
              font-size: 30/50rem;
              // font-weight: 600;
              color: #000;
            }
            &:nth-child(2){
              font-size: 28/50rem;
            }
          }
        }
        .listRight,.listCenter{
          vertical-align: middle; 
          text-align: center;
        }
        .listRight{
          width: 80/50rem;
          i{
            font-size: 45/50rem; margin-right: 20/50rem;
          }
        }
      }
    }
    .kong{
      height: 30/50rem;
    }
  }
</style>


