<template>
  <div class="componentOutboundOrder">
  	<div class='putWarehouseDetail'>
      <div class='putWarehouseList' @click='toPutDetails(Outboundindex)' v-for='(OutboundItem,Outboundindex) in OutboundOrderList'  :id="Outboundindex"  :key='Outboundindex'>
        <i v-if="IFQuerySalesOrder && MergeChooseBtnShow" class="iconfont icon-weixuanzhong"></i>
        <!-- <i v-if="IFQuerySalesOrder && MergeChooseBtnShow" class="iconfont icon-1"></i> -->
        <div>
          <span>{{$store.state.G_AppOrWX != "WX"?'出库单号':'入库单号'}}：</span>
          <span>{{OutboundItem.OUT_NO}}</span>
        </div>
        <div v-if='$store.state.G_AppOrWX != "WX"'>
          <span>{{OutboundItem.TRANSFER_OUT_TYPE=='TRANSFER_OUT'?'收&nbsp;货&nbsp;方&nbsp;':'门店名称'}}：</span>
          <span>{{OutboundItem.CONSIGNEE_NAME}} </span>
        </div>
        <div v-if='OutboundItem.TRANSFER_OUT_TYPE!="TRANSFER_OUT" && $store.state.G_AppOrWX != "WX"'>
          <span>门店地址：</span>
          <span>{{OutboundItem.CONSIGNEE_ADDRESS}}</span>
        </div>
        <div>
          <span>下单人名：</span>
          <span>{{OutboundItem.ADDWHO}}</span>
        </div>
        <div>
          <span>下单日期：</span>
          <span>{{OutboundItem.ADDDATE}}</span>
          <span class='float red' :style='{color:OutboundItem.TYPE_color}'>{{OutboundItem.TYPE_text}}</span>
        </div>
        <div>
          <span>下单数量：</span>
          <span>{{OutboundItem.PIRCHASE_QUANTITY}}</span>
          <span class='float red' :style='{color:OutboundItem.STATUS_color}'>{{OutboundItem.STATUS_text}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  export default{
    data(){
      return{
      }
    },
    props:{
      OutboundOrderList:{
        type:Array,
        default: () => {
  	      return []
  	    }
      },
      IFQuerySalesOrder:false,//判断是否是 销售订单页面
      MergeChooseBtnShow:false,//合并选择订单按钮是否显示
      push_stack:false,//合并选择订单按钮是否显示
    },
    components:{},
    created(){

    },
    methods:{
      toPutDetails (index) {

        var that = this;

        // 合并订单选择订单功能
        if(that.IFQuerySalesOrder && that.MergeChooseBtnShow){

          let iconElement = $('.putWarehouseList:eq('+index+')>i.iconfont');

          if(iconElement.hasClass("icon-1")){

            iconElement.removeClass("icon-1").addClass("icon-weixuanzhong")

          }else if(iconElement.hasClass("icon-weixuanzhong")){

            iconElement.removeClass("icon-weixuanzhong").addClass("icon-1")
          }
          return;
        }

        // 告诉父组件，使用进栈方式push，不重置部分vuex
        this.$emit('push_stack_click', true);
        
        this.$router.push({
          name:"OutboundDetail",
          query:{
            transferOutId:that.OutboundOrderList[index].TRANSFER_OUT_ID,
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .componentOutboundOrder{
    overflow-x: hidden;
    .putWarehouseDetail{
      width:100%;
      font-size: 28/50rem;
      .putWarehouseList{
        width:100%;
        color: #555;
        padding: 20/50rem 15/50rem;
        box-sizing: border-box;
        border-bottom: 20/50rem solid #EEEFF2;
        background-color: #fff;
        position:relative;
        &>div{
          line-height: 46/50rem;
          display: flex;
          &>span:first-child{
            min-width: 130/50rem;
            flex-shrink:0;
          }
          &>span:nth-child(2){
            flex-grow: 1;
          }
        }
        &>i.iconfont{
          position:absolute;
          top: 20/50rem;
          right: 20/50rem;
          color:#285DCE;
          font-size: 40/50rem;
          &.icon-weixuanzhong{
            color: #888;
          }
        }
      }
      .float{
        float: right;
      }
    }
  }
</style>


