<template>
  <div class="sale_of_salesman_kb">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container haveHeadcontainer">

      <div class="el_date">
        <div>
          <el-date-picker v-model="start_date" type="date" placeholder="开始日期" value-format="yyyy-MM-dd" @change="start_date_click" :editable="false"></el-date-picker>
        </div>
        <span>至</span>
        <div>
          <el-date-picker v-model="end_date" type="date" placeholder="截止日期" value-format="yyyy-MM-dd" @change="end_date_click" :editable="false"></el-date-picker>
        </div>
      </div>

      <div class="scroll-content">

        <div class="summary">
          <div>时间进度：{{ timeSchedule }}</div>
          <div>下单达成：{{ qtyWcl }}</div>
          <div>发货达成：{{ fqtyWcl }}</div>
        </div>

        <div class="kb">
          <div class="__h1" @click='PackUp("__h1","__c1")'>销量达成（按下单统计）</span><i class="iconfont icon-xiangxia"></i></div>
          <div class="table __c1">
            <div class="tb-header_1_2">

              <div>业务员</div>

              <div>
                <div>销量达成（按照下单统计）有效单据</div>
                <div>
                  <div>目标数量</div>
                  <div>目标金额</div>
                  <div>完成率(数量)</div>
                  <div>完成率(金额)</div>
                </div>
              </div>

            </div>

            <div class="tb-content_1_2">
              <div class="v-f" v-for='(item, index) in datas' :id="index" :key="index">

                <div :class="'fds' + index">{{ item.userName }}</div>

                <div :class="'fds' + index">{{ item.salesVolume }}</div>
                <div :class="'fds' + index">{{ item.amount }}</div>
                <div :class="'fds' + index">{{ item.qtyWcl }}</div>
                <div :class="'fds' + index">{{ item.amountWcl }}</div>

              </div>
            </div>

          </div>
        </div>

        <div class="kb">
          <div class="__h2" @click='PackUp("__h2","__c2")'>销量达成（按送货统计）</span><i class="iconfont icon-xiangxia"></i></div>
          <div class="table __c2">
            <div class="tb-header_1_2">

              <div>业务员</div>

              <div>
                <div>销量达成（按照送货统计）有效单据</div>
                <div>
                  <div>目标数量</div>
                  <div>目标金额</div>
                  <div>完成率(数量)</div>
                  <div>完成率(金额)</div>
                </div>
              </div>

            </div>

            <div class="tb-content_1_2">
              <div class="v-f" v-for='(item, index) in datas' :id="index" :key="index">

                <div :class="'fds' + index">{{ item.userName }}</div>

                <div :class="'fds' + index">{{ item.salesVolume }}</div>
                <div :class="'fds' + index">{{ item.amount }}</div>
                <div :class="'fds' + index">{{ item.fqtyWcl }}</div>
                <div :class="'fds' + index">{{ item.famountWcl }}</div>

              </div>
            </div>
          </div>
        </div>

        <div class="kb">
          <div class="__h3" @click='PackUp("__h3","__c3")'>销售单</span><i class="iconfont icon-xiangxia"></i></div>
          <div class="table __c3">
            <div class="tb-header_3">

              <div>业务员</div>

              <div>
                <div>订单份数/单</div>
                <div>
                  <div>已下单</div>
                  <div>已出库</div>
                  <div>未出库</div>
                </div>
              </div>

              <div>
                <div>订单数量/箱</div>
                <div>
                  <div>已下单</div>
                  <div>已出库</div>
                  <div>未出库</div>
                </div>
              </div>

              <div>
                <div>订单金额/元</div>
                <div>
                  <div>已下单</div>
                  <div>已出库</div>
                  <div>未出库</div>
                </div>
              </div>

            </div>

            <div class="tb-content_3">
              <div class="v-f" v-for='(item, index) in datas' :id="index" :key="index">

                <div :class="'fds' + index">{{ item.userName }}</div>

                <div :class="'fds' + index">{{ item.outCount }}</div>
                <div :class="'fds' + index">{{ item.foutCount }}</div>
                <div :class="'fds' + index">{{ item.woutCount }}</div>

                <div :class="'fds' + index">{{ item.outQTY }}</div>
                <div :class="'fds' + index">{{ item.foutQTY }}</div>
                <div :class="'fds' + index">{{ item.woutQTY }}</div>

                <div :class="'fds' + index">{{ item.outAmount }}</div>
                <div :class="'fds' + index">{{ item.foutAmount }}</div>
                <div :class="'fds' + index">{{ item.woutAmount }}</div>

              </div>
            </div>
          </div>
        </div>

        <div class="kong"></div>

      </div>

    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    name:"sale_of_salesman_kb",
    data(){
      return{
        headerText:"销量看板",
        start_date:this.getNowTime("getMonthFirstDay"),
        end_date:this.getNowTime('getDate'),
        datas:[],
        timeSchedule:"", // 时间进度
        qtyWcl:"",       // 下单达成
        fqtyWcl:""       // 发货达成
      }
    },
    components:{
      componentHeader,
    },
    created(){

      this.resquertData()
    },
    methods:{

      // 返回上一页
      f_Fanhui(){

        this.$router.go(-1)
      },
      // 收起 展开
      PackUp(head_class, body_class){
        if($('.' + body_class).css("display") == "block"){
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(-90deg)"})
          $('.' + body_class).hide(200);
        }else{
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(0deg)"})
          $('.' + body_class).show(200);
        }
      },
      start_date_click(value){

        this.start_date = value;
        this.resquertData();
      },
      end_date_click(value){

        if(this.start_date > value ){
          this.$alert('截止时间不可小于开始时间', '提示', {
            confirmButtonText: '确定',
          })
          this.end_date = this.getNowTime('getDate')
        }else{
          this.end_date = value
          this.resquertData()
        }
      },
      // 请求数据
      resquertData(){

        var that = this

        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "StartDate":that.start_date,//开始时间
          "EndDate":that.end_date,//结束时间
        }

        this.httpRequest("salesDataView.do", postData, function(res){

          if(res.data.datas.length > 0){

            that.datas = res.data.datas
            that.timeSchedule = res.data.timeSchedule
            var count = that.datas.length
            that.datas.push(JSON.parse(JSON.stringify(that.datas[0])))
            that.datas[count].userName = "总计"
            that.datas[count].outCount = 0
            that.datas[count].foutCount = 0
            that.datas[count].woutCount = 0

            that.datas[count].outQTY = 0
            that.datas[count].foutQTY = 0
            that.datas[count].woutQTY = 0
            
            that.datas[count].outAmount = 0
            that.datas[count].foutAmount = 0
            that.datas[count].woutAmount = 0

            that.datas[count].salesVolume = 0
            that.datas[count].amount = 0

            that.datas[count].outQTY = 0
            that.datas[count].outAmount = 0
            that.datas[count].foutQTY = 0
            that.datas[count].foutAmount = 0

            for(var i = 0; i < count; i++){
              that.datas[count].userName = "总计"
              that.datas[count].outCount += that.datas[i].outCount
              that.datas[count].foutCount += that.datas[i].foutCount
              that.datas[count].woutCount += that.datas[i].woutCount

              that.datas[count].outQTY += that.datas[i].outQTY
              that.datas[count].foutQTY += that.datas[i].foutQTY
              that.datas[count].woutQTY += that.datas[i].woutQTY
              
              that.datas[count].outAmount += that.datas[i].outAmount
              that.datas[count].foutAmount += that.datas[i].foutAmount
              that.datas[count].woutAmount += that.datas[i].woutAmount

              // 目标数量，总计
              that.datas[count].salesVolume += that.datas[i].salesVolume
              // 目标金额，总计
              that.datas[count].amount += that.datas[i].amount
            }

            // 已下单，数量完成率
            that.datas[count].qtyWcl = that.datas[count].salesVolume == 0 ? 0 + "%" : parseFloat(that.datas[count].outQTY / that.datas[count].salesVolume * 100 ).toFixed(2) + "%"
            // 已下单，金额完成率
            that.datas[count].amountWcl = that.datas[count].amount == 0 ? 0 + "%" : parseFloat(that.datas[count].outAmount / that.datas[count].amount * 100 ).toFixed(2) + "%"
            // 已送货，数量完成率
            that.datas[count].fqtyWcl = that.datas[count].salesVolume == 0 ? 0 + "%" : parseFloat(that.datas[count].foutQTY / that.datas[count].salesVolume * 100 ).toFixed(2) + "%"
            // 已送货，金额完成率
            that.datas[count].famountWcl = that.datas[count].amount == 0 ? 0 + "%" : parseFloat(that.datas[count].foutAmount / that.datas[count].amount * 100 ).toFixed(2) + "%"
            console.log(that.datas[count].foutAmount + "/" + that.datas[count].amount)

            that.qtyWcl = that.datas[count].qtyWcl
            that.fqtyWcl = that.datas[count].fqtyWcl

            // 最后共计加粗
            that.$nextTick(function(){
              $("." + "fds" + count).css({"font-weight":"bold"})
            })
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  .sale_of_salesman_kb{
    overflow: hidden;
    background-color: #eee;
    height: 100%;
    .container{
      overflow: hidden;
      height: 100%;
      font-size: 23/50rem;
      .el_date{
        height: 100/50rem;
        line-height: 100/50rem;
        padding: 0 60/50rem;
        background-color: #EEEFF2;
        &>div:first-child{
          float: left;
          width: 260/50rem;
        }
        span{
          float: left;
          width: 110/50rem; 
          text-align: center;
        }
        &>div:last-child{
          width: 260/50rem;
          float: right;
        }
      }
      .summary{
        height: 80/50rem;
        margin-top: 5/50rem;
        padding-left: 12/50rem;
        margin-bottom: 5/50rem;
        background-color: white;
        &>div{
          width: 33.33%;
          height: 100%;
          float: left;
          line-height: 80/50rem;
        }
      }
      .scroll-content{
        width: 100%;
        overflow: scroll;
        height: calc(100% - 3.8rem);
        .kb{
          &>div{
            background-color: white;
            margin-top: 3/50rem;
          }
          .__h1, .__h2, .__h3{
            width: calc(100% - 0.48rem);
            height: 70/50rem;
            background-color: white;
            line-height: 70/50rem;
            padding: 12/50rem;
            position: relative;
            i{
              position: absolute;
              right: 40/50rem;
              font-size: 40/50rem;
              top: 50%;
              transform: translateY(-50%) rotate(0deg);
              transition: all 0.4s;
            }
          }
          .table{
            width: 100%;
            height: 100%;
            margin-top: 5/50rem;
            font-size: 20/50rem;
            overflow: scroll;
            text-align: center;
            background-color: white;
            .tb-header_1_2{
              height: 90/50rem;
              border-top: 1.25/50rem solid black;
              border-bottom: 1.25/50rem solid black;
              font-weight: bold;
              &>div{
                float: left;

                &:nth-child(1){
                  width: (75 - 2.5)/50rem;
                  height: 90/50rem;
                  line-height: 90/50rem;
                  border-left: 1.25/50rem solid black;
                  border-right: 1.25/50rem solid black;
                }

                &:nth-child(2){
                  width: (675 - 1.25)/50rem;
                  height: 90/50rem;
                  border-right: 1.25/50rem solid black;
                  &>div{
                    line-height: 45/50rem;
                    &:nth-child(1){
                      width: 100%;
                      height: 50%;
                      border-bottom: 1.25/50rem solid black;
                    }
                    &:nth-child(2){
                      width: 100%;
                      height: 50%;
                      &>div{
                        float: left;
                        width: (168.75 - 1.25)/50rem;
                        height: 100%;
                        &:nth-child(2){
                          border-left: 1.25/50rem solid black;
                        }
                        &:nth-child(3){
                          border-left: 1.25/50rem solid black;
                        }
                        &:nth-child(4){
                          border-left: 1.25/50rem solid black;
                        }
                      }
                    }
                  }
                }
              }
            }
            .tb-content_1_2{
              font-size: 20/50rem;
              .v-f{
                &>div{
                  line-height: 45/50rem;
                  height: 45/50rem;
                  float: left;

                  &:nth-child(1){

                    width: (75 - 2.5)/50rem;
                    border-left: 1.25/50rem solid black;
                    border-bottom: 1.25/50rem solid black;
                  }
                  &:nth-child(2),&:nth-child(3),&:nth-child(4),&:nth-child(5){

                    width: (168.75 - 1.25)/50rem;
                    border-left: 1.25/50rem solid black;
                    border-bottom: 1.25/50rem solid black;
                  }
                  &:nth-child(5){

                    border-right: 1.25/50rem solid black;
                  }
                }
              }
            }
            .tb-header_3{
              height: 90/50rem;
              border-top: 1.25/50rem solid black;
              border-bottom: 1.25/50rem solid black;
              font-weight: bold;
              &>div{
                float: left;

                &:nth-child(1){
                  width: (75 - 1.25)/50rem;
                  height: 90/50rem;
                  line-height: 90/50rem;
                  border-left: 1.25/50rem solid black;
                }

                &:nth-child(2),&:nth-child(3),&:nth-child(4){
                  width: (225 - 1.25)/50rem;
                  height: 90/50rem;
                  border-left: 1.25/50rem solid black;
                  &>div{
                    line-height: 45/50rem;
                    &:nth-child(1){
                      width: 100%;
                      height: 50%; 
                      border-bottom: 1.25/50rem solid black;
                    }
                    &:nth-child(2){
                      width: 100%;
                      height: 50%;
                      &>div{
                        float: left;
                        width: (75 - 1.25)/50rem;
                        height: 100%;
                        &:nth-child(2){
                          width: (75 - 1.25)/50rem;
                          border-left: 1.25/50rem solid black;
                        }
                        &:nth-child(3){
                          width: (75 - 2.5)/50rem;
                          border-left: 1.25/50rem solid black;
                        }
                      }
                    }
                  }
                }
                &:nth-child(4){
                  width: (225 - 2.5)/50rem;
                  border-right: 1.25/50rem solid black;
                }
              }
            }
          }
          .tb-content_3{
            font-size: 16/50rem;
            .v-f{
              &>div{
                line-height: 45/50rem;
                height: 45/50rem;
                float: left;

                width: (75 - 1.25)/50rem;
                border-left: 1.25/50rem solid black;
                border-bottom: 1.25/50rem solid black;

                &:nth-child(10){
                  width: (75 - 2.5)/50rem;
                  border-right: 1.25/50rem solid black;
                }
              }
            }
          }
        }
        .kong{
          height: 25/50rem;
        }
      }
    }
  }
</style>


