<template>
  <div class='Index'>
    <componentHeader :headerText="headerText" :IfXiaoxiShow='true' @f_Xiaoxi='f_Xiaoxi' ></componentHeader>
    <div class="indexContainer haveHeadcontainer">
      <div class="swipper">
        <div class="block">
          <el-carousel trigger="click"  style="overflow: hidden;">
            <div v-for='(picItem,index) in swipperImageList'  :id="index"  :key='index'>
              <el-carousel-item>
                <img id="swipperImg" style="width: 100%" :src="picItem.Product_Url">
              </el-carousel-item>
            </div>
          </el-carousel>
        </div>
      </div>
      <div class='option'>
        <a v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.CustomerVisit" @click="toCustomerVisit()">
          <i class="iconfont icon-kehubaifang"></i>
          <span>客户拜访</span>
        </a>
        <router-link v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.PurchaseOrder"  to="/PurchaseOrder">
          <i class="iconfont icon-xiadan"></i>
          <span>采购下单</span>
        </router-link>
        <router-link v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.SalesOrder"  to="/SalesOrder">
          <i class="iconfont icon-xiadan"></i>  
          <span>销售下单</span>
        </router-link>
        <a v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.InventoryManage"  @click="toSelectOSP('InventoryManage')">
          <!-- InventoryManage -->
          <i class="iconfont icon-kucun"></i>
          <span>库存管理</span>
        </a>
        <router-link v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.PerformanceTrack"  to="/PerformanceTrack">
          <i class="iconfont icon-yeji"></i>
          <span>业绩追踪</span>
        </router-link>
        <router-link v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.ReportCenter"  to="/ReportCenter">
          <i class="iconfont icon-baobiao"></i>
          <span>报表中心</span>
        </router-link>
        <a v-if="$store.state.G_AppOrWX != 'WX' && $store.state.CurrentRoleData.CustomerList"  @click='toCustomerList("CustomerList")'>
          <i class="iconfont icon-kehuguanli"></i>
          <span>门店管理</span>
        </a>
        <router-link v-if="$store.state.G_AppOrWX == 'WX'"  to="/SalesOrder">
          <i class="iconfont icon-chadan"></i>
          <span>产品订购</span>
        </router-link>
        <router-link v-if="$store.state.G_AppOrWX == 'WX'"  to="/QuerySalesOrder">
          <i class="iconfont icon-chadan"></i>
          <span>订单查询</span>
        </router-link>
        <a v-if="$store.state.G_AppOrWX != 'WX' && FeeBill"  @click='toFeeBill()'>
          <i class="iconfont icon-kehuguanli"></i>
          <span>费用账单</span>
        </a>
      <!--    <a @click='toWarningCenter("CustomerList")'>
          <i class="iconfont icon-yujing"></i>
          <span>预警中心</span>
        </a> -->
      </div>
    </div>
    <componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='BusinessList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
    <componentFooter></componentFooter>
  </div>
</template>
<script type="text/javascript">

  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import componentFooter from '@/components/componentFooter'
  import $ from "jquery"
  import Axios from "axios"
  export default{
    name:"Login",
    data(){
      return{
        headerText:"首页",
        swipperImageList:[],//轮播图列表
        BusinessList:[],//客户列表
        SelectBoxFlag:false,//弹出选择框是否显示
        BouncedName:'',//弹框名字
        currentClickPage:'',//当前点击页面
        FeeBill:false,//是否有费用账单权限
      }
    },
    components:{
      componentHeader,
      componentFooter,
      componentSelectBox
    },
    created(){
      var that = this;

      // 这14个业务代码才能使用【费用账单】功能
      var code = that.$store.state.businessInfo.businessCode
      if(code == "YIB10" || code == "YIB15" || code == "YIB16" || code == "YIB17" || code == "YIB20" ||
         code == "YIB21" || code == "YIB22" || code == "YIB23" || code == "YIB24" || code == "YIB25" ||
         code == "YIB26" || code == "TTX01" || code == "YIB31" || code == "YIB34"){
        that.FeeBill = true
      }
      
      // 获取首页轮播图
      var params = {
        "businessId":that.$store.state.businessInfo.businessId,//业务id
        "appUsersId":that.$store.state.userInfo.user_id,//用户id
      }

      Axios.post(that.HOST_lxq+'GetHomePic.do',{"params": JSON.stringify(params)})
      .then(function(res){
      console.log(res)

        if(res.data.status==1){

          if(res.data.data.length == 0){

            that.swipperImageList=[];

            for(var i = 0;i<6;i++){
              if(i==5){
                that.swipperImageList[i] = {"Product_Url":"static/img/Index/"+(i+1)+".png"};
              }else{
                that.swipperImageList[i] = {"Product_Url":"static/img/Index/"+(i+1)+".jpg"};
              }
            }
          }else{
            that.swipperImageList = res.data.data;
          }

        }else{

          var msg = "请求失败";
          if(res.data.Msg){

            msg = res.data.Msg;
          }

          that.$alert(msg, '提示', {
            confirmButtonText: '确定',
          })
        }
      })
      .catch(function(res){
          
        that.$alert("请求失败", '提示', {
          confirmButtonText: '确定',
        })  
      })
    },
    mounted() {
      
      // this.headerText= this.$route.query.active;
      
      this.$store.state.VisitConditionUserHabits = {
        VisitLine:'',//拜访线路
        VisitState:'',//拜访状态
      }//拜访筛选条件用户习惯记录清空
      this.$store.state.visitObject = ""
    },
    methods:{
        //消息
      f_Xiaoxi(){
         this.$router.push({
            name:"MessageList",
          })
      },
      toFeeBill(){
        this.$router.push({
          name:"PurchaseOrder",
          query:{
            whoPush:"Index",
          },
        })
      },
      toCustomerVisit(){
        // 一级业务员选择
        if(this.$store.state.userInfo.business == 5){
          this.BouncedName = '拜访/查看拜访'
          this.BusinessList = [{"name":"拜访终端服务商"},{"name":"查看终端门店"}]
          this.SelectBoxFlag = true
        }
        else{
          this.$router.push({
            name:"CustomerVisit",
          })
        }
      },
      toCustomerList(pageName){
        var that = this
        if(that.$store.state.userInfo.business == 5){
          var that = this;

          var i_do = "findClassA.do"

          var SupplierData = {
            "cellphone":that.$store.state.userInfo.cellphone,//手机号
            "userId":that.$store.state.userInfo.user_id,//用户id
            "businessId":that.$store.state.businessInfo.businessId,//业务ID
          }
          // 获取供货商
          this.httpRequest(i_do,SupplierData,function(res){
            if(res.data.length){
              that.BusinessList = res.data
              if(res.data.length == 1){
                that.$store.state.CustomerInfoOspInfo = that.BusinessList[0];
                that.$router.push({
                  name:"CustomerList",
                })
              }else{
                that.BouncedName = '供货商'
                that.SelectBoxFlag = true
                that.currentClickPage = pageName
              }
            }else{
              that.$alert('未获取到供货商，请联系管理员', '提示', {
                confirmButtonText: '确定',
              })
            }
          })
        }else{

          this.toSelectOSP(pageName)
        }
      },
      //库存管理
      toSelectOSP(pageName){
        var that = this;

        var LineData = {
          "appUsersId":that.$store.state.userInfo.user_id,//用户id
          "businessId":that.$store.state.businessInfo.businessId,//业务id
          "searchName":"",//搜索名称
          // "currentPage":1,//当前页数
          // "numberPerPage":15,//每页显示多少行数据

        // "cellphone":that.$store.state.userInfo.cellphone,//手机号
        // "userId":that.$store.state.userInfo.user_id,//用户id
        // "businessId":that.$store.state.businessInfo.businessId,//业务ID
        }

        // 获取osp列表
        that.httpRequest("searchCustomerList.do",LineData,function(SupRes){
          
           if(SupRes.data.length){

            for(var k=0;k<SupRes.data.length;k++){
              SupRes.data[k].name = SupRes.data[k].CUSTOMER_NAME;
            }
           
            if(SupRes.data.length == 1){

              if(pageName == 'InventoryManage'){
                // osp信息
                that.$store.state.InventoryOspInfo = SupRes.data[0];

                that.$router.push({
                  name:"InventoryManage",
                })
              }else if(pageName == 'CustomerList'){
                // osp信息
                that.$store.state.CustomerInfoOspInfo = SupRes.data[0];
                that.$router.push({
                  name:"CustomerList",
                })
              }
            }else{

              that.SelectBoxFlag = true;
              that.currentClickPage = pageName;
              

              if(pageName == 'InventoryManage'){
                that.BouncedName = '客户'
                that.BusinessList = SupRes.data;
              }else if(pageName == 'CustomerList'){

                if(that.$store.state.userInfo.business == 5){

                  that.BouncedName = 'OSP/门店'
                  that.BusinessList = [{"name":"OSP"},{"name":"门店"}]
                }else{

                  that.BouncedName = '门店供货商'
                  that.BusinessList = SupRes.data;
                }
              }
            }
           }else{
              // 没有数据
              that.$alert("未获取到客户信息，请联系管理员", '提示', {
                confirmButtonText: '确定',
              })
           }
        })
      },
     
      // 选中某个业务代码
      selectedItem(index){

        var that = this;

        if(that.currentClickPage == 'InventoryManage'){
          // osp信息
          that.$store.state.InventoryOspInfo = that.BusinessList[index];

          that.$router.push({
            name:"InventoryManage",
          })
        }else if(that.currentClickPage == 'CustomerList'){

          // osp信息
          that.$store.state.CustomerInfoOspInfo = that.BusinessList[index];

          that.$router.push({
            name:"CustomerList",
          })
        }else if(this.$store.state.userInfo.business == 5){

          if(index == 0){
            this.$store.state.visitObject = "OSP" //客户拜访里，供货商显示一级，列表显示OSP
          }else if(index == 1){
            this.$store.state.visitObject = "SOTRE" // 客户拜访里，供货商显示OSP，列表显示终端
          }
          this.$router.push({
            name:"CustomerVisit",
          })
        }
        
        this.SelectBoxFlag = false;
      },
      // 取消选择业务代码
      selectCancel(){
        this.SelectBoxFlag = false;
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Index{
    width:100%;
    height:100%;
    overflow: hidden;
    background-color: @GrayBackground;
    .swipper,#swipperImg{
      height: 370/50rem;
    }
  }
  .option{
    width:100%;
    overflow: hidden;
    background-color: @GrayBackground;
    &>a{
      width:33%;
      height:230/50rem;
      float: left;
      line-height: 78/50rem;
      margin-right: 0.5%;
      margin-bottom: 0.5%;
      background-color: #fff;
      text-align: center;
      color: #000;
      i{
        font-size:67/50rem;
        color: @PublicColor;
        display: block;
        margin-top: 65/50rem;
        &.icon-chadan{
          font-size: 60/50rem;
        }
      }
      &:nth-child(3),&:nth-child(6){
        margin-right:0;
      }
      &>span{
        font-size: 28/50rem;
      }
    }
  }
</style>


