<template>
  <div class="WXAssociatedStores">
  	<componentHeader :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	<div class="container haveHeadcontainer">
	  <div class="headTitle">
	  	<div>关联门店</div>
	  	<div>关联手机号:{{phoneNum}}</div>
	  </div>
	  <div class='identeContainer'>
	    <div class="mobilePhone">
	      <div class="mobilePhoneInput">
	      	<input placeholder='门店名称' ref="storeName"  v-model="storeName"></input>
	      	<input placeholder='门店代码' ref="storeCode"  v-model="storeCode"></input>
	      	<input placeholder='门店登录密码' type="password" ref="storePswd" @keyup.enter="toAssociated" v-model="storePswd"></input>
	      </div>
	      <div class="ErrMsg"><span>{{ErrMsg}}</span></div>
	    </div>
	    <div class="g_signBtn" :style="{color:storeName.length&&storeCode.length&&storePswd.length?'rgba(255,255,255,1)':'rgba(255,255,255,0.6)'}" @click='toAssociated'>关联门店</div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from "jquery"
  export default {
 	name:"WXAssociatedStores",
 	data(){
 	  return{
	    phoneNum:"",//手机号
	    openId:"",//
	    storeName:'',//门店名称
	    storeCode:'',//门店代码
	    storePswd:'',//门店登录密码
	    ErrMsg:'',//错误信息
	    LoginOrReg:'',//是注册还是登录
	    yzm:'',//验证码内容
 	  }
 	},
	components:{
      componentHeader,
    },
	watch:{
	  storeName(val){
	  	if(this.ErrMsg){
	  	  this.ErrMsg = '';	
	  	}
	  },
	  storeCode(val){
	  	if(this.ErrMsg){
	  	  this.ErrMsg = '';	
	  	}
	  },
	  storePswd(val){
	  	if(this.ErrMsg){
	  	  this.ErrMsg = '';	
	  	}
	  }
	},
 	created(){

 	  var that = this;

 	  if(this.$route.query.phoneNum){
 	  	this.phoneNum = this.$route.query.phoneNum;
 	  }
 	  if(this.$route.query.LoginOrReg){
 	  	this.LoginOrReg = this.$route.query.LoginOrReg;
 	  }
 	  if(this.$route.query.yzm){
 	  	this.yzm = this.$route.query.yzm;
 	  }
	},
	methods:{
	  f_Fanhui(){
	  	var that = this;
		this.$router.push({
          name:'WXVerify',
          query:{
          	phoneNum:that.phoneNum,
          	LoginOrReg : that.LoginOrReg,//是注册还是登录
          	yzm:that.yzm,//是注册还是登录
          }
        })
	  },
      // 关联门店
      toAssociated(){
      	var that = this;
      	if(!that.storeName.length || !that.storeCode.length || !that.storePswd.length){return;}

      	let AssociatedParams = {
          cellphone: that.phoneNum,//手机号
          storeCode:that.storeCode,//门店代码
          storeName:that.storeName,//门店名称
          storePswd:that.storePswd,//门店登录密码
        }

        this.httpRequest("wxReStore.do",AssociatedParams,function(Res){
        	
          console.log(Res);

          if(Res.data.returnCode == 'SUCCESS'){
			// 改变登录状态
			
			that.setWXCookie(that.storeCode,'',30);

			that.$alert("关联成功", '提示', {
				confirmButtonText: '确定',
				callback(){
				  that.$router.push({
				    name:"WXLogin",
				  })
				}
			})
          }else{
          	that.ErrMsg = Res.Msg;
          }
        })
      },
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .WXAssociatedStores{
  	overflow:hidden;
  	header{
  	  color: #959595;
  	}
	.container{
	  padding-left:50/50rem;
	  padding-right:50/50rem;
	  font-size: 30/50rem;
	  .headTitle{
	  	margin: 40/50rem 0 60/50rem;
	  	&>div:first-child{
	  	  font-size: 50/50rem;
	  	}
	  	&>div:nth-child(2){
		  color: #636363;
		  margin-top: 5/50rem;
		  font-size: 28/50rem;
	  	}
	  }
	  .identeContainer{
	  	.mobilePhone{
	  	  height: 400/50rem;
	  	  .mobilePhoneInput{
	  	    position:relative;
	  	  	input{
		  	  width: 100%;
		  	  margin-top: 20/50rem;
			  font-size: 30/50rem;
			  line-height: 75/50rem;
			  padding:0 10/50rem;
			  border-radius:0;
			  border:none;
			  border-bottom: 1/50rem solid #ccc;
		  	}
	  	  }
	  	  .ErrMsg{
	  	  	span{
			  color: #ff4E32;
			  line-height: 70/50rem;
			  font-size: 28/50rem;
	  	    }
	  	  }
	  	}
	  }
	}
  }
</style>


