	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <ul class="ReportList">

      <!-- <router-link  to="/ProductSalesReport" tag='li'>
        <div><i class="iconfont icon-baobiao1"></i></div>
        <div><span>产品销量报表</span></div>
        <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link>
	  	<router-link  to="/CustomerSalesReport" tag='li'>
        <div><i class="iconfont icon-xiaoshoubaobiao2"></i></div>
	  	  <div><span>客户销量、销售额报表</span></div>
	  	  <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link> -->
      <router-link  to="/CustomerVisitReport" tag='li'>
        <div><i class="iconfont icon-kehu"></i></div>
        <div><span>客户拜访报表</span></div>
        <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link>
      <router-link  to="/SalesPerformanceReport" tag='li' v-if='$store.state.userInfo.business != "5"'>
