      </router-link>
      <!-- <router-link  to="/Report" tag='li'>
        <div><img src="static/img/Visit/VividDisplay.png"></div>
	  	  <div><span>客户订单总计</span></div>
	  	  <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link>
      <router-link  to="/Report" tag='li'>
        <div><img src="static/img/Visit/VividDisplay.png"></div>
	  	  <div><span>产品销量、销售额排名</span></div>
	  	  <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link>
        <router-link  to="/Report" tag='li'>
        <div><img src="static/img/Visit/VividDisplay.png"></div>
	  	  <div><span>月度总销量、销额对比</span></div>
	  	  <div><i class="iconfont icon-xiangyou"></i></div>
      </router-link> -->
	  </ul>
	</div>
  </div>
