<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"报表中心",//页面标题
      	ReArr:[{
      	  ReportName:'客户与销量',
      	  ReportSrc:'static/img/Visit/VividDisplay.png'
      	}],//数组

      }
    },
    components:{
	  componentHeader,
	},
    created(){

    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ReportCenter{
  	width: 100%;
    overflow-x: hidden;
    background-color: #EDEFF3;
    height: 100%;
    padding: 0 20/50rem;
    box-sizing: border-box;
    .container{
      ul.ReportList{
      	width: 100%;
      	overflow: hidden;
      	&>li{
      	  margin-top: 20/50rem;
      	  border-radius: 10/50rem;
      	  display: flex;
      	  background-color:#fff;
      	  height: 120/50rem;
      	  line-height: 120/50rem;
      	  &>div{
	        &:first-child{
	          text-align: center;
	          width: 120/50rem;
	          i{
              font-size: 60/50rem;
              color: @PublicColor;
	          }
	        }
	        &:nth-child(2){
	          flex-grow: 1;
	        }
	        &:last-child{
	          width: 70/50rem;
	          text-align: center;
	          i{
	          	font-size: 44/50rem;
	          }
	        }
	      }
      	}
      }
    }
  }
</style>


