<template>
  <div class="Answer">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="QuestionTitle">
        <i class="iconfont icon-jiantouzuo"></i>{{Answerdata.problem }} 
      </div>
      <!-- <img class="img" src="static/img/Index/1.jpg"> -->
      <div class="AnswerText">{{Answerdata.answer }}</div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
        Answerdata:[],
        index:0,
        Questionid:'',
      	headerText:"问题解答",//页面标题
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      var that = this;

      this.Questionid = this.$route.query.QuestionItem.id;
      this.index = this.$route.query.index;

      var postData = {
        "problemId":that.Questionid,
      }
      // 获取问题解答信息
      that.httpRequest("GetCommonProblem.do",postData,function(Res){
            that.Answerdata = Res.data;
      })


    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	}
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";

  .Answer{
    overflow-x: hidden;
    .QuestionTitle{
      width: 100%;
      height: 80/50rem;
      line-height: 80/50rem;
      background-color: #fff;
      margin-bottom: 20/50rem;
      padding-left: 20/50rem;
      box-sizing: border-box;
    }
    .AnswerText{
      width: 90%;
      line-height:60/50rem; 
      margin: 0 auto;
      // border: 1/50rem solid red;



    }

  }

  .img{
    width:100%;
  }
</style>


