<template>
  <div class="PurchaseOrderDetail">
	 <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>

  	<div class="container haveHeadcontainer">
  	  <div class='oDetailContainer'>
    		<div class='orderInfo'>
    		  <div class='infoHead'>订单信息</div>
          <div class="infoContainer">
            <div><span>订单号为：</span><span>{{order.ORD_NO}}</span></div>
            <div><span>创建时间：</span><span>{{order.ORD_DATE_ADD}}</span></div>
            <div><span>发货单位：</span><span>{{order.ORD_FROM_NAME}}</span></div>
            <div><span>发货地址：</span><span>{{order.ORD_FROM_ADDRESS}}</span></div>
            <div><span>收货单位：</span><span>{{order.ORD_TO_NAME}}</span></div>
            <div><span>收货地址：</span><span>{{order.ORD_TO_ADDRESS}}</span></div>
            <div>
              <div><span>订单状态：</span><span>{{order.TMS_STATE}}</span></div>
            </div>
            <div v-if='order.ORD_REMARK'><span>订单备注：</span><span>{{order.ORD_REMARK}}</span></div>
            <div>
              <div><span>重量：</span><span>{{order.ORD_WEIGHT}} 吨</span></div>
              <div><span>体积：</span><span>{{order.ORD_VOLUME}} m³</span></div>
            </div>
            <div>
              <div><span>数量：</span><span>{{order.ORD_QTY}}</span></div>
              <div><span>货款金额：</span><span>¥ {{order.ORD_ACT_PRICE}}</span></div>
            </div>
            <!-- 部分业务需要改订单数量和价格,这里记录汇总 开始 -->
            <div v-if='$store.state.businessInfo.isEdit == "T"'>
              <div><span>提货数量：</span><span>{{order.finalDeliveryNum}}</span></div>
              <div><span>提货金额：</span><span>¥ {{order.finalDeliveryAmount}}</span></div>
            </div>
            <!-- 部分业务需要改订单数量和价格,这里记录汇总 结束 -->
            <div>
              <div v-if='order.ORD_MJ_PRICE'><span>已优惠：</span><span>¥ {{order.ORD_MJ_PRICE}}</span></div>
            </div>
            <div>送货时间：{{ dealDeliveryDte(order.requestedDeliveryDte) }}</div>
          </div>
    		</div>
        <div v-if="merges.length > 0" class='merge-info'>
          <div class='infoHead' @click='PackUp("infoHead","merge-c")'>合并订单<i class="iconfont icon-xiangxia"></i></div>
          <div class="infoContainer merge-c">
            <componentOutboundOrder :IFQuerySalesOrder='true' :MergeChooseBtnShow='false' :OutboundOrderList='merges'></componentOutboundOrder>
          </div>
        </div>
  	    <div class='goodsInfo'>
  	      <div class='infoHead'>货物信息</div>
  	      <div class='goodsContent'>
  	        <div class='goodsArr' v-for='(goodsItem,goodsindex) in goodsArr'  :id="goodsindex"  :key='goodsindex'>
              <div>
                <div class='LeftIcon'>
                  <img :src="goodsItem.PRODUCT_PIC?goodsItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
                </div>
                <div class='listRight'>

                  <div class="PRODUCT_NAME">
                    <span style="color: #000;font-weight: 900;">{{goodsItem.PRODUCT_NAME}}</span>
                  </div>
                  <div>产品编号：
                    <span>{{goodsItem.PRODUCT_NO}}</span>
                  </div>

                  <div>
                    <div>数量：<span>{{goodsItem.PRODUCT_QTY}} {{goodsItem.UNIT_STR}}</span></div>
                    <div>付款单价：<span>¥ {{goodsItem.PRODUCT_ACT_PRICE}}</span></div>
                  </div>

                  <!-- 部分业务需要改订单数量和价格,这里记录汇总 开始 -->
                  <div v-if='$store.state.businessInfo.isEdit == "T"' class="product-item-3">
                    <div>
                      配送数量：<span>{{goodsItem.finalDeliveryNum}} {{goodsItem.UNIT_STR}}</span>
                      <i @click="edit_click(goodsItem, '数量')" class="iconfont icon-xie"></i>
                    </div>
                    <div>
                      配送单价：<span>¥ {{goodsItem.finalDeliveryAmount}}</span>
                      <i @click="edit_click(goodsItem, '价格')" class="iconfont icon-xie"></i>
                    </div>
                  </div>
                  <!-- 部分业务需要改订单数量和价格,这里记录汇总 结束 -->

                  <div>
                    <div v-if='goodsItem.PRODUCT_ORG_PRICE'>优惠单价：<span>¥ {{goodsItem.PRODUCT_ORG_PRICE}}</span></div>
                  </div>
                </div>
              </div>
  	        </div>
            <div v-if='order.detail&&!order.detail.length' class='noneGift'>无</div>
  	      </div>
  	    </div>
  		  <div class='goodsInfo' v-if="giftArr.length">
  		    <div class='infoHead'>赠品信息</div>
  		    <div class='goodsContent'>
  		      <div class='goodsArr' v-for='(giftItem,giftindex) in giftArr'  :id="giftindex"  :key='giftindex'>
              <div class="goodsArr_item">
                <div class='LeftIcon'>
                  <img :src="giftItem.PRODUCT_PIC?giftItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
                </div>
                <div class='listRight'>
                  <div class="PRODUCT_NAME">
                    <span style="color: #000;font-weight: 900;">{{giftItem.PRODUCT_NAME}}</span>
                  </div>
                  <div>产品编号：
                    <span>{{giftItem.PRODUCT_NO}}</span>
                  </div>

                  <div>数量：<span>{{giftItem.PRODUCT_QTY}} {{giftItem.UNIT_STR}}</span></div>

                  <!-- 部分业务需要改订单数量和价格,这里记录汇总 开始 -->
                  <div>
                    配送数量：<span>{{giftItem.finalDeliveryNum}} {{giftItem.UNIT_STR}}</span>
                    <i @click="edit_click(giftItem, '数量')" class="iconfont icon-xie"></i>
                  </div>
                  <!-- 部分业务需要改订单数量和价格,这里记录汇总 结束 -->

                </div>
              </div>
              <div class="bottleFee" v-if='giftItem.GIFT_TYPE && giftItem.GIFT_TYPE == "WINE"'>
                <div>
                  <div><span>{{giftItem.BOTTLE_NAME}}</span></div>
                  <div><span>单价：</span><span>¥ {{giftItem.BOTTLE_PRICE}}</span>&nbsp;&nbsp;&nbsp; <span>总价：</span><span>¥ {{parseFloat(parseFloat(giftItem.BOTTLE_PRICE) * giftItem.PRODUCT_QTY).toFixed(2)}}</span></div>
                </div>
              </div>
  		      </div>
  		    </div>
		    </div>
      </div>
      <div v-if='order.TMS_STATE!="新建"&&order.TMS_STATE!="待装车"&&order.TMS_STATE!="已审核"&&order.TMS_STATE!="取消"' class='footer' @click='toLogistics'>物流信息</div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentOutboundOrder from '@/components/Widgets/componentOutboundOrder'
  import $ from 'jquery'
  export default{
    data(){
      return{
      	headerText:"订单详情",//页面标题
        TextContent:'打印',
      	orderId:'',
        order:{},//订单信息列表
        goodsArr:[],//正常下单货品信息列表
        giftArr:[],//赠品信息列表
        merges:[],//合并订单
      } 
    },
    components:{
	    componentHeader,
      componentOutboundOrder,
	  },
    created(){

      if(this.$route.query.orderId){
        this.orderId = this.$route.query.orderId;
      }
      this.request()
    },
    methods:{
    	// 返回上一页
    	f_Fanhui(){
    		this.$router.go(-1);
    	},
      // 跳转页面 查看物理信息
      toLogistics(){
        this.$router.push({
          name:"LogisticsInfo",
          query:{
            orderId:this.orderId
          }
        })
      },
      // 打印
      f_Text(){

      },
      // 收起 展开
      PackUp(head_class,body_class){
        if($('.' + body_class).css("display") == "block"){
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(-90deg)"})
          $('.' + body_class).hide(200);
        }else{
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(0deg)"})
          $('.' + body_class).show(200);
        }
      },
      // 请求数据
      request(){

        var that = this;
        var postData = {
            "orderId":this.orderId,
        }
        this.httpRequest( "getPurchaseOrderDetail.do",postData,function(res){

          console.log(res);
          that.order = res.data.order;

          var SumGoodsArr = res.data.order.detail,giftArr = [],goodsArr = [];

          for(var i=0;i<SumGoodsArr.length;i++){

           if(SumGoodsArr[i].PRODUCT_TYPE == 'GF'){

              giftArr[giftArr.length]=SumGoodsArr[i];
            }else{
              goodsArr[goodsArr.length]=SumGoodsArr[i];
            }
          }

          that.giftArr = giftArr;
          that.goodsArr = goodsArr;
          // that.order.ORD_STATE = that.matchState(that.order.ORD_STATE);

          if(res.data.TransferOutOrder.length > 0){

            that.merges = res.data.TransferOutOrder
          }
        })
      },
      modify_qty_price(p){

        var that = this

        var SellOrderDetails = [{"productNo":p.PRODUCT_NO, "orderDetailId":p.ORDER_DETAIL_ID, "finalDeliveryNum":p.finalDeliveryNum, "finalDeliveryAmount":p.finalDeliveryAmount}]

        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id,          //用户id
          "orderId":that.orderId,                                   //订单id
          "finalDeliveryNum":that.order.finalDeliveryNum,
          "finalDeliveryAmount":that.order.finalDeliveryAmount,    
          "SellOrderDetails":SellOrderDetails
        }
        // 修改配送数量、价格
        that.httpRequest("updSellOrderDelivery.do",postData,function(res){

          that.ifTips = true
          that.tips_Msg = "操作成功"

          setTimeout(function(){
            that.ifTips = false
            that.request()
          },1000)
        })
      },
      // 修改配送数量、价格
      edit_click(p, type){

        var that = this

        var inputValue = 0
        if(type == "数量"){
          inputValue = p.finalDeliveryNum
        }else if(type == "价格"){
          inputValue = p.finalDeliveryAmount
        }

        this.$prompt('请输入' + type, '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^\d+(\.\d+)?$/,
          inputErrorMessage: '请输入数字',
          inputValue: inputValue,
          inputType: 'number',
          closeOnClickModal: false
        }).then(({ value }) => {

          if(type == "数量"){
            p.finalDeliveryNum = value
          }else if(type == "价格"){
            p.finalDeliveryAmount = value
          }

          var total_qty = 0
          var total_price = 0
          for(var i = 0; i < that.goodsArr.length; i++){
            total_qty += parseInt(that.goodsArr[i].finalDeliveryNum)
            total_price +=  that.goodsArr[i].finalDeliveryAmount * that.goodsArr[i].finalDeliveryNum
          }
          for(var i = 0; i < that.giftArr.length; i++){
            total_qty += parseInt(that.giftArr[i].finalDeliveryNum)
            total_price +=  that.giftArr[i].finalDeliveryAmount * that.giftArr[i].finalDeliveryNum
          }
          console.log(total_qty)
          console.log(total_price)
          that.order.finalDeliveryNum = total_qty
          that.order.finalDeliveryAmount = total_price

          that.modify_qty_price(p)

        }).catch(() => { })

        // 自动聚集
        this.$nextTick(function(){

          $(".el-input__inner").select()
        })
      },
      dealDeliveryDte(date){
        return  date.replace(/00:00:00/g, "")
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .PurchaseOrderDetail{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    .oDetailContainer{
      padding-bottom: 80/50rem;
      .orderInfo{
        span{
          margin-left: 0;
        }
        .infoContainer{
          div{
            &>span:first-child{
              color: #000;
            }
          }
          &>div{
            display: flex;
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
      }
      .merge-info{
        padding: 0 8/50rem;
        box-sizing: border-box;
        .infoHead{
          position: relative;
          i{
            position: absolute;
            right: 20/50rem;
            font-size: 40/50rem;
            top: 50%;
            transform: translateY(-50%) rotate(-90deg);
            transition:all 0.4s;
          }
        }
        span{
          margin-left: 0;
        }
        .infoContainer{
          div{
            &>span:first-child{
              color: #000;
            }
          }
          &>div{
            display: flex;
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
      }
      // 订单、赠品列表
      .goodsInfo{
        padding: 0 8/50rem;
        box-sizing: border-box;
        padding-bottom: 12/50rem;
        .goodsArr{
          font-size: 28/50rem;
          width:100%;
          background-color: #fff;
          border-radius: 10/50rem;
          margin-bottom: 5/50rem;
          &>div:first-child{
            display: table;width:100%;
            &>div{
              display: table-cell;
              &:first-child{
                width: 120/50rem;
                vertical-align: middle;
                text-align: center;
                i{
                  font-size:81/50rem;
                  color: @PublicColor;
                }
              }
              &:last-child{
                padding:16/50rem;
                &>div{
                   margin-bottom:10/50rem;
                   overflow: hidden;
                   span{
                    color: #555;
                  }
                   &>div{
                    float: left;
                    width: 50%;
                  }
                }
              }
            }
          }
        }
        .noneGift{
          color: #555;
          width: 200/50rem;
          margin-left: 30/50rem;
        }
      }
      // 产品略缩图
      .LeftIcon{
        width:120/50rem;
        position:relative;
        flex-shrink:0;
        img{
          width: 90/50rem;
          position:absolute;
          top:50%;
          left: 50%;
          transform: translate(-50%,-50%);
        }
      }
      .product-item-3{
        font-size: 25/50rem;
        i{
          padding: 15/50rem;
        }
      }
    }
    .footer{ 
      width:200/50rem;
      height:60/50rem;
      line-height: 60/50rem;
      position: fixed;
      bottom: 20/50rem;
      left: 50%;
      transform: translateX(-50%);
      background-color: @PublicColor;
      color: #fff;
      text-align: center;
      font-size: 26/50rem;
      border-radius: 30/50rem;
    }
  }
</style>


