<template>
  <div class="CreateNewVisit">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="VisitDate">
	  	<span>拜访日期：</span>
	  	<span>{{CurrentDate}}</span>
	  </div>
	  <div class="orderInfo">
		<div class="infoContainer">
		  <div>
		  	<span>客户代码：</span>
		  	<span>{{storesData.CODE}}</span>
		  </div>
		  <div>
		  	<span>客户名称：</span>
		  	<span>{{storesData.NAME}}</span>
		  </div>
		  <div>
		  	<span>客户等级：</span>
		  	<span>{{storesData.CUSTOMER_GRADE}}</span>
		  </div>
		 <!--  <div>
		  	<span>客户状态码：</span>
		  	<span>{{storesData.CUSTOMER_STATUS}}</span>
		  </div> -->
		  <div>
		  	<span>渠 &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;道：</span>
		  	<span>{{storesData.CHANNEL}}</span>
		  </div>
		  <div>
		  	<span>拜访线路：</span>
		  	<span>{{storesData.VISIT_ROUTE}}</span>
		  </div>
		  <!-- <div>
		  	<span>每周拜访频率：</span>
		  	<span>{{storesData.WEEKLY_VISITING_FREQUENCY}}</span>
		  </div> -->
		 <!--  <div>
		  	<span>单店销售量/天：</span>
		  	<span>{{storesData.SALES}}</span>
		  </div> -->
	  	</div>
	  	<div class="infoContainer">
	  	  <div>
	  	  	<span>联系人名：</span>
	  	  	<span>{{storesData.CONTACT_NAME}}</span>
	  	  </div>
	  	  <div>
	  	  	<span>联系电话：</span>
	  	  	<span>{{storesData.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='storesData.CONTACT_TEL' @click="callPhone(storesData.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span>
	  	  </div>
	  	  <div class="ADDRESS1">
	  	  	<span>地址详情：</span>
	  	  	<span>{{storesData.ADDRESS1}}</span>
	  	  </div>
	    </div>
	  </div>
	  <div class="v_remark">
	  	<textarea v-model="v_remark" placeholder="请输入备注信息"></textarea>
	  </div>
	  <div class="BtnComponent bottomBtn">
	  	<div @click='toModifyCustomerInfo'>修改客户信息</div>
	  	<div @click='toIntoTheShop'>确认</div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"确认客户信息",//页面标题
      	CurrentDate:'',//当前日期
      	storesData:{},//店铺信息
      	v_remark:"",//页面备注信息
      }
    },
    components:{
  	  componentHeader,
  	},
    created(){

      var that = this;
    	
      this.CurrentDate = this.getNowTime('getDate');
      var storesData = {
        "cellphone":that.$store.state.userInfo.cellphone,//手机号
        "userId":that.$store.state.userInfo.user_id,//用户id
        "storesId":that.$store.state.CurrentStoresId,//门店id
      }
      // 获取门店信息
      that.httpRequest("GetStoreOne.do",storesData,function(storesRes){
         that.storesData = storesRes.data;
      })
    },
    methods:{
      // 返回上一页
      f_Fanhui(){

      	if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush =='IntoTheShop'){
          this.$router.push({
            name:"CustomerVisit",
          })
        }else{
          this.$router.go(-1);
        }
      },
      // 跳转到修改客户信息 页面
      toModifyCustomerInfo(){
      	console.log(this.storesData)
      	this.$router.push({
          name:"ModifyCustomerInfo",
          query:{
          	// "storesId":this.$store.state.CurrentStoresId,//门店id
          	"storeInfo":this.storesData,//店铺信息
          },
        })
      },
      // 跳转到 检查库存 页面
      toIntoTheShop(){

	      var that = this;
	    	
	      this.CurrentDate = this.getNowTime('getDate');
	      var sureData = {
          "cellphone":that.$store.state.userInfo.cellphone,//用户账号/手机号
	      	"types":"CONFIRM",
	      	"visitId":that.$store.state.CurrentVisitId,//拜访id
	        "remarks":that.v_remark,//备注信息
	      }
	      // 确认门店信息
	      that.httpRequest("GetVisitPicStatus.do",sureData,function(sureRes){

          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){

            that.$router.go(-1);
            
          }else{
            that.$router.push({
              name:"CheckInventory",
              query:{
                  visit_whoPush:"CreateNewVisit"
                }
            })
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateNewVisit{
  	overflow-x: hidden;
  	min-height: 100%;
  	padding:16/50rem;
    box-sizing: border-box;
  	background-color: @GrayBackground;
  	.VisitDate{
  	  text-align: center;
  	  line-height: 70/50rem;
  	}
  	.orderInfo{
  	  .infoContainer{
		&>div{
		  display: flex;
		  &>span:first-child{
	        min-width: 130/50rem;
            flex-shrink:0;
	      }
	      span:nth-child(2){
	        flex-grow: 1;
	      }
		}
  	  }
  	  span{
  	  	color: #000;
  	  }
  	}
  	.v_remark{
  	  textarea{
  	    width: 100%;
  	    height: 120/50rem;
  	    border-radius:15/50rem;
  	    outline: none;
  	    padding:16/50rem;
  	    box-sizing:border-box;
  	    font-size: 28/50rem;
  	    border:none;
  	  }
  	}
  }
</style>


